import { ParseObj } from "./Parser"
import { Token } from "./Token";

export type Forest = Array<node>;
class node {
    token: Token;
    left: node | Array<Forest | null> | Forest | null;
    right: node | Array<Forest | null> | Forest | null;
    parent: node | Array<Forest | null> | Forest | null;
    TokObject: any;
    constructor(token: Token) {
        if (token == null) {
            throw "(Parser)token is null";
        }
        this.token = token;
        this.left = null;
        this.right = null;
        this.parent = null;
        this.TokObject = ParseObj.Parse.Dict[this.token.type + "s"][this.token.value];
        this.TokObject = this.TokObject == null ? ParseObj.Parse.Dict[this.token.type + "s"].main : this.TokObject;
        if (this.TokObject == null) {
            throw "(Parser)token is not an expected token: " + this.Summarize();
        }
    }

    Summarize() {
        return this.token.Summarize();
    }
    ShortSummarize() {
        var shortSum = this.token.type;
        switch (this.token.type) {
            case Token.Types.operator:
                shortSum += "<<" + this.token.value + ">>";
                break;
        }
        return shortSum;
    }
}

export { node as Node }
