import { Logger, LogType } from "../../../shrimp/helpers/logger";

export class OSError {
    message: string;
    source: string;
    node: string;

    constructor(source, message, node) {
        this.message = message;
        this.source = source;
        this.node = node;
    }

    public static throwWithMessage(source, message, node) {
        throw new OSError(source, message, node);
    }

    public static errorWithMessage(source, message, node) {
        Logger.Log({ logMessage: `${message} | source: ${source} `, logType: LogType.Warning });
        Logger.Log({ logMessage: node, logType: LogType.Warning });
    };
}