import { IQuickInputFormatOptions } from "../../externalInterfaces";
import { Context } from "../core/context";
import { ICompositor } from "./ICompositor";

export class Regex implements ICompositor {

    private inputValue: string = "";

    constructor(
        private readonly context: Context,
    ) { }

    public compose(): void {
        
        const inputElm = this.context.inputElm;
        const options = this.context.options;

        if(options.regex){
            const reg = new RegExp(options.regex);

            {
                inputElm.addEventListener("input", ev => {
                    const target = ev.target as HTMLInputElement | HTMLTextAreaElement;;

                    const regTest = reg.test(target.value);
                    
                    if(regTest){
                        this.inputValue = target.value;
                    }else{
                        target.value = this.inputValue;
                    }
                }, true);
            }
        }
    }

    public getUnmaskedValue(): string {
        return this.inputValue;
    }
}