import { ContextManager } from "../../../shrimp/context";
import { IDictionary } from "../../../shrimp/interfaces/IDictionary";
import { IStore } from "../../../shrimp/interfaces/quick/IStore";
import { BagStoreContext } from "../Context/Store/BagStoreContext";
import { GlobalStoreContext } from "../Context/Store/GlobalStoreContext";
import { PageStoreContext } from "../Context/Store/PageStoreContext";
import { SharedStoreContext } from "../Context/Store/SharedStoreContext";
import { Dory } from "../Dory";

class StoreContextInfo {
    public targetStoreContext: IStore;
    public targetFieldName: string;
    constructor(targetStoreContext: IStore, targetName: string) {
        this.targetStoreContext = targetStoreContext;
        this.targetFieldName = targetName;
    }
}

export class StoreManager {
    private context: ContextManager;
    constructor(context: ContextManager) {
        this.context = context;
    }
    private decideStoreContext(name: string): StoreContextInfo {
        let contextName: string | null;
        let newName: string;
        let targetContext = this.context;
        const dory = this.context.GetItemSingle<Dory>(Dory.ContextName)!;
        switch (name[0]) {
            case "$":
                contextName = GlobalStoreContext.ContextName;
                newName = name.substring(1);
                break;
            default:
                contextName = PageStoreContext.ContextName;
                newName = name;
                break;
            case "#":
                contextName = BagStoreContext.ContextName;
                newName = name.substring(1);
                break;
            case "@":
                contextName = null;
                newName = name.substring(1);
                break;
            case "&":
                contextName = SharedStoreContext.ContextName;
                targetContext = dory.GetInterDoryContext();
                newName = name.substring(1);
                break;
        }

        let targetStoreContext: IStore;
        if (contextName) {
            targetStoreContext = targetContext.GetItemSingle<GlobalStoreContext | PageStoreContext | BagStoreContext | SharedStoreContext>(contextName)!;
        } else {
            const dory = targetContext.GetItemSingle<Dory>(Dory.ContextName);
            targetStoreContext = dory?.shellConfigs.storeAccess!;
        }

        const sci = new StoreContextInfo(targetStoreContext, newName);

        return sci;
    }
    public set(name: string, value: any) {
        const storeContext = this.decideStoreContext(name);
        storeContext.targetStoreContext.set(storeContext.targetFieldName, value);
    }
    public get(name) {
        const storeContext = this.decideStoreContext(name);
        return storeContext.targetStoreContext.get(storeContext.targetFieldName);
    }
    public delete(name) {
        const storeContext = this.decideStoreContext(name);
        storeContext.targetStoreContext.delete(storeContext.targetFieldName);
    }
    public getAll(name) {
        const storeContext = this.decideStoreContext(name);
        return storeContext.targetStoreContext.getAll?.();
    }
    public deleteAll(name) {
        const storeContext = this.decideStoreContext(name);
        return storeContext.targetStoreContext.deleteAll?.();
    }

    public bulkSet(items: IDictionary<any>) {
        Object.keys(items).forEach(key => this.set(key, items[key]));
    }

    public getTargetStore(name: string) {
        const storeContext = this.decideStoreContext(name);
        return storeContext;
    }


}