import { IDictionary } from "../IDictionary";

export class TSComponentBase {
    public selfEvents: IDictionary<Function>;
    constructor({ events, selfEventList }: { events: IDictionary<Function>, selfEventList: Array<string> }) {
        this.selfEvents = {};
        selfEventList.forEach(event => {
            this.selfEvents[event] = events[event];
            delete events[event];
        });
    }
    emit(eventName: string, ...args: Array<Object>) {
        const targetEvent = this.selfEvents[eventName];
        if (!targetEvent) {
            return;
        }
        return targetEvent.apply(this, ...args);
    }
}