import { IComponentCollection } from "../../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { ObjectInjectionHelper } from "../../../../../common/shrimp/helpers/objectInjectionHelper";
import { IComponent } from "../../../../../common/shrimp/interfaces/ComponentInterfaces/IComponent";

export class InstanceInjectionManager {


    public static Manage(compCollection: IComponentCollection) {
        const compManager = this.compExtDic[compCollection.compType]

        if (!compManager) { return; }

        const compFieldAttacher = (component: IComponent) => {
            if (!component) {
                return;
            }
            compManager(component);
        };
        compFieldAttacher(compCollection.component!);

        compCollection.WatchCreatedFields((created) => {
            if (!created.instance) { return; }
            compFieldAttacher(created.instance);
        });
    }

    private static compExtDic: Record<string, (comp: IComponent) => void> = {
        'VFileInput': (compInst) => {

            ObjectInjectionHelper.injectMethod(compInst, "browse", () => {
                compInst['$refs'].input.click()
            })
        }
    }

}