import { Dory } from "../../Dory";
import { Formatter } from "../../Helpers/Formatter";

export class QsExtensions{
    constructor(){

    }
    generateExtensions(dory: Dory) {
        const protoTypes = {
            String: {
                length: function (stringParam: string) {
                    return stringParam.length;
                },
                toString(stringParam: string) {
                    return stringParam.toString();
                },
                lowerCase(stringParam: string) {
                    return stringParam.toLowerCase();
                }
            },
            Object: {
                toString(object: Object) {
                    return JSON.stringify(object, null, 4);
                },
                toJSONString(object: Object) {
                    return JSON.stringify(object);
                }
            },
            Array: {
                remove: function (array: Array<Object>, index: number) {
                    array.splice(index, 1);
                },
                filter: function (array: Array<Object>, operation: Function) {
                    return array.filter(operation())
                },
                length: function (array: any) {
                    return array.length;
                },
                toString(array: Array<any>) {
                    return array.toString();
                }
            },
            Date: {
                toFormattedString: function (date: Date, formatType: string) {
                    if (!formatType) {
                        formatType = dory.shellConfigs.formatConfiguration.dateFormat!();
                    }
                    return new Formatter().formatDate(date, formatType);
    
                },
                getDate: function (date: Date) {
                    return new Formatter().getDate(date)
                },
                toISOString: function (date: Date) {
                    return date?.toISOString();
                },
                addDays: function (date: Date, value: number) {
                    return new Formatter().addDays(date, value);
                },
                addMonths: function (date: Date, value: number) {
                    return new Formatter().addMonths(date, value);
                },
                addYears: function (date: Date, value: number) {
                    return new Formatter().addYears(date, value);
                },
                getTime: function (date: Date) {
                    return new Date(date).getTime();
                }
    
            },
            Number: {
                toString(number: Number) {
                    return number.toString();
                },
                toFormattedString: function (number: Number, formatType: string, opt?: Intl.NumberFormatOptions) {
                    if (!formatType) {
                        formatType = dory.shellConfigs.formatConfiguration!.moneyFormat!();
                    }
                    return new Formatter().formatNumber(number, formatType, opt);
                },
                fixed: function (number: number, digits: number) {
                    return number.toFixed(digits);
                }
            }
        };
    
        const stringPrototypeNameArray = Object.getOwnPropertyNames(String.prototype);
        const extensionStringKeys = Object.keys(protoTypes.String);
    
        for (let index = 0; index < stringPrototypeNameArray.length; index++) {
            const protoTypeName = stringPrototypeNameArray[index];
            if (!extensionStringKeys.includes(protoTypeName)) {
                protoTypes.String[protoTypeName] = (stringParam: string, ...args) => { return stringParam[protoTypeName](...args) }
            }
        }
    
        const objectPrototypeNameArray = Object.getOwnPropertyNames(Object.prototype);
        const extensionObjectKeys = Object.keys(protoTypes.Object);
        for (let index = 0; index < objectPrototypeNameArray.length; index++) {
            const protoTypeName = objectPrototypeNameArray[index];
            if (!extensionObjectKeys.includes(protoTypeName)) {
                protoTypes.Object[protoTypeName] = (objectParam: string, ...args) => { return objectParam[protoTypeName](...args) }
            }
        }
    
        const arrayPrototypeNameArray = Object.getOwnPropertyNames(Array.prototype);
        const extensionArrayKeys = Object.keys(protoTypes.Array);
    
        for (let index = 0; index < arrayPrototypeNameArray.length; index++) {
            const protoTypeName = arrayPrototypeNameArray[index];
            if (!extensionArrayKeys.includes(protoTypeName)) {
                protoTypes.Array[protoTypeName] = (arrayParam: string, ...args) => { return arrayParam[protoTypeName](...args) }
            }
        }
    
        const datePrototypeNameArray = Object.getOwnPropertyNames(Date.prototype);
        for (let index = 0; index < datePrototypeNameArray.length; index++) {
            const protoTypeName = datePrototypeNameArray[index];
            if (!protoTypes.Date[protoTypeName]) {
                protoTypes.Date[protoTypeName] = (dateParam: string, ...args) => { return dateParam[protoTypeName](...args) }
            }
        }
    
        const numberPrototypeNameArray = Object.getOwnPropertyNames(Number.prototype);
        for (let index = 0; index < numberPrototypeNameArray.length; index++) {
            const protoTypeName = numberPrototypeNameArray[index];
            if (!protoTypes.Number[protoTypeName]) {
                protoTypes.Number[protoTypeName] = (numberParam: string, ...args) => { return numberParam[protoTypeName](...args) }
            }
        }
        return protoTypes;
    }
    
}