import { IQuickInputFormatOptions } from "./externalInterfaces";
import { Context } from "./src/core/context";

export class QuickInputFormat {
    constructor(
        inputElm: HTMLInputElement | HTMLTextAreaElement,
        options: IQuickInputFormatOptions,
        private readonly context = new Context(inputElm, options),
    ) { }

    public run() {
        this.context.inputFormat.run();
    }

    public getUnmaskValue(){
        return this.context.inputFormat.getUnMaskedValue();
    }
}