import { INavigationDemandType } from "../../../../common/shrimp/interfaces/quick/INavigationDemand";
import { IDoryRenderer, IRenderer } from "../../../../common/shrimp/interfaces/RenderingInterfaces/IRenderer";
import { thresholdedTrigger } from "../../../../common/shrimp/helpers/thresholdedTrigger";
import { AppComputedAdditional } from "./AdditionalType";
import { IComponentCollection } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IHistoryItem } from "../../../../common/shrimp/interfaces/RenderingInterfaces/IHistoryItem";


let latestLayout: {
    vapp: HTMLElement;
    renderer: IDoryRenderer;
    historyItem: IHistoryItem | undefined;
};
let timer: number | undefined;
window.addEventListener("resize", (event) => {
    if (!latestLayout) { return; }

    const cb = () => AppLayoutManager.ArrangeAppLayout(latestLayout.vapp, latestLayout.renderer, latestLayout.historyItem);
    timer = thresholdedTrigger(timer, cb, undefined, 400);
});
export class AppLayoutManager {
    public static ArrangeAppLayout(vapp: HTMLElement, renderer: IDoryRenderer, historyItem: IHistoryItem | undefined) {
        latestLayout = { vapp, renderer, historyItem };

        const appComputedAdditional = historyItem?.getAdditional<AppComputedAdditional>(AppComputedAdditional.additionalName);
        if (appComputedAdditional?.appComputed) {
            vapp.style.paddingTop = appComputedAdditional.appComputed.paddingTop;
            vapp.style.paddingBottom = appComputedAdditional.appComputed.paddingBottom;
        } else {
            const compDict = renderer.GetComponentCollection();
            if (!compDict) { return; }

            let styles: { paddingBottom?: string, paddingTop?: string } = {};
            const fixedHandler = (compColl: IComponentCollection) => {
                const compDom = compColl.domElement!;
                const computed = getComputedStyle(compDom);
                if (computed.position == "fixed") {
                    let targetStyle = computed.bottom?.startsWith("0") ? "paddingBottom" : "paddingTop";
                    styles[targetStyle] = computed.height;
                }
            };
            const addClickListenerToDatagrid = (compCollection: IComponentCollection) => {
                if (compCollection.compType !== "DxDataGrid") {
                    return;
                }

                const gridInstance = compCollection.component?.["instance"];
                if (!gridInstance) {
                    return;
                }
                const chooserObserver = new MutationObserver(() => {
                    const chooserEl = document.querySelector(".dx-popup-wrapper.dx-datagrid-column-chooser");
                    if (chooserEl) {
                        const handleClickOutside = (event: any) => {
                            const target = event.target;
                            const gridEl = document.querySelector(".dx-datagrid");
                            const isInsideGrid = gridEl?.contains(target);
                            const isInsideChooser = chooserEl.contains(target);

                            if (!isInsideGrid && !isInsideChooser) {
                                // Kapatma denemesi (iki yntemle birden)
                                try {
                                    if (typeof gridInstance?.columnChooser?.hideColumnChooser === "function") {
                                        gridInstance.columnChooser.hideColumnChooser();
                                    } else if (typeof gridInstance?.hideColumnChooser === "function") {
                                        gridInstance.hideColumnChooser();
                                    }
                                } catch (err) {
                                    //" Kapatma srasnda hata:", err
                                }

                                //document.removeEventListener("mousedown", handleClickOutside, true);
                                chooserObserver.disconnect();
                            }
                        };

                        document.addEventListener("mousedown", handleClickOutside, true);
                    }
                });
                chooserObserver.observe(document.body, {
                    childList: true,
                    subtree: true,
                });
            };
            const addMenuPositionFixToDatatable = (compCollection: IComponentCollection) => {
                if (compCollection.compType !== "VDataTable" || (compCollection.compType == "VDataTable" && !compCollection.vDomElement?.data.props?.attachMenu)) {
                    return;
                }
                
                const tableEl = compCollection.domElement;
                if (!tableEl) return;

                const observer = new MutationObserver(() => {
                    const menuEl = document.querySelector(".v-menu__content.menuable__content__active");
                    if (!menuEl || !tableEl) return;

                    const footerSelects = tableEl.querySelectorAll('.v-data-footer .v-select');

                    footerSelects.forEach(selectEl => {
                        if ((selectEl as any)._clickListenerAttached) return;
                        (selectEl as any)._clickListenerAttached = true;

                        selectEl.addEventListener('click', () => {
                            setTimeout(() => {
                                requestAnimationFrame(() => {
                                    const menuEl = document.querySelector(".v-menu__content.menuable__content__active.v-menu-transition-enter.v-menu-transition-enter-active") as HTMLElement;
                                    const activatorRect = selectEl.querySelector('.v-input__control')?.getBoundingClientRect();
                                    if (!menuEl || !activatorRect) return;

                                    let scrollTop = window.scrollY || window.pageYOffset;
                                    const narWrapper = document.querySelector(compCollection.vDomElement?.data.props?.attachMenu);
                                    const narDiff = narWrapper ? narWrapper.getBoundingClientRect().height : 0;
                                    const scrollLeft = window.scrollX || window.pageXOffset;
                                    const menuTop = activatorRect.top + activatorRect.height + scrollTop - narDiff;
                                    const menuLeft = activatorRect.left + scrollLeft;
                                    const viewportBottom = scrollTop + window.innerHeight;

                                    const menuRect = menuEl.getBoundingClientRect();
                                    const menuHeight = menuRect.height || 192; // fallback
                                    const menuWidth = menuRect.width || 50; // fallback
                                    if (menuTop + menuHeight > viewportBottom) {
                                        menuEl.style.setProperty("top",`${viewportBottom - menuHeight - 8}px`,"important");
                                        menuEl.style.setProperty("left",`${menuLeft - (menuWidth - activatorRect.width) / 2}px`,"important");
                                        menuEl.style.setProperty("transform-origin", "left top");
                                    } else {
                                        menuEl.style.setProperty("top", `${menuTop}px`, "important");
                                        menuEl.style.setProperty("left",`${menuLeft - (menuWidth - activatorRect.width) / 2}px`,"important");
                                        menuEl.style.setProperty("transform-origin", "left top");
                                    }
                                });
                            }, 10); // Vuetify'n render tamamlamas iin bekleme
                        });
                    });
                });

                observer.observe(document.body, {
                    childList: true,
                    subtree: true,
                });
            };
            const addMenuPositionFixToInlineDatePicker = (compCollection: IComponentCollection) => {
                if (compCollection.compType !== "VInlineDatepicker" || (compCollection.compType == "VInlineDatepicker" && !compCollection.vDomElement?.data.props?.attachMenu)) {
                    return;
                }
                const attachMenu = compCollection.vDomElement?.data.props?.attachMenu;
                (compCollection.component?.["$refs"].menu1 as any).$props.eager = true;
                const datepickerEl = compCollection.domElement;
                if (!datepickerEl) return;

                const observer = new MutationObserver(() => {
                    const menuEl = document.querySelector(`.v-menu__content.${compCollection.compEID}`) as HTMLElement;
                    if (!menuEl || !datepickerEl) return;

                    const textFieldSlot = datepickerEl.querySelector('.v-input__slot input') || document.querySelector(`.v-inline-date-picker-div-class#${compCollection.compEID} .v-input__slot input`);
                    if (!textFieldSlot) return;
                    const iconSlot = datepickerEl.querySelector('.v-input__prepend-inner button.v-icon') || document.querySelector(`.v-inline-date-picker-div-class#${compCollection.compEID} .v-input__prepend-inner button.v-icon`);
                    if (!iconSlot) return;
                    const activatorSlots = [textFieldSlot, iconSlot];
                    activatorSlots.forEach(activatorSlot => {
                        if ((activatorSlot as any)._clickListenerAttached) return;
                        (activatorSlot as any)._clickListenerAttached = true;

                        activatorSlot.addEventListener('click', () => {
                            setTimeout(() => {
                                requestAnimationFrame(() => {
                                    const menuEl = document.querySelector(`.v-menu__content.menuable__content__active.${compCollection.compEID}`) as HTMLElement;
                                    const activatorRect = (document.querySelector(`.v-inline-date-picker-div-class#${compCollection.compEID} .v-input__control`) as HTMLElement)?.getBoundingClientRect();
                                    if (!menuEl || !activatorRect) return;

                                    const narWrapper = document.querySelector(attachMenu);
                                    const narDiff = narWrapper ? narWrapper.getBoundingClientRect().height : 0;

                                    const scrollTop = window.scrollY || window.pageYOffset;
                                    const menuRect = menuEl.getBoundingClientRect();
                                    const menuHeight = menuRect.height || 290; //  DatePicker default 290
                                    const menuTop = activatorRect.top + activatorRect.height + scrollTop - narDiff;
                                    const viewportBottom = scrollTop + window.innerHeight;

                                    if (menuTop + menuHeight > viewportBottom) {
                                        menuEl.style.setProperty("top", `${viewportBottom - menuHeight - 8}px`, "important");
                                    } else {
                                        menuEl.style.setProperty("top", `${menuTop}px`, "important");
                                    }
                                });
                            }, 10);
                        }, true);
                    });
                });
                observer.observe(document.body, { childList: true, subtree: true });
            };
            const addMenuPositionFixToInlineTimePicker = (compCollection: IComponentCollection) => {
                if (compCollection.compType !== "VInlineTimePicker" || (compCollection.compType == "VInlineTimePicker" && !compCollection.vDomElement?.data.props?.attachMenu)) {
                    return;
                }
                const attachMenu = compCollection.vDomElement?.data.props?.attachMenu;
                (compCollection.component?.["$refs"].menu as any).$props.eager = true;
                const compEl = document.querySelector(`#${compCollection.compEID}`) as HTMLElement | null;
                if (!compEl) return;

                const observer = new MutationObserver(() => {
                    const menuEl = document.querySelector(`.v-menu__content.${compCollection.compEID}`) as HTMLElement;
                    if (!menuEl) return;
                    const textFieldSlot = compEl.querySelector('.v-input__slot') || document.querySelector(`#${compCollection.compEID} .v-input__slot`);
                    if (!textFieldSlot) return;

                    if ((textFieldSlot as any)._clickListenerAttached) return;
                    (textFieldSlot as any)._clickListenerAttached = true;
                    textFieldSlot.addEventListener('click', () => {
                        setTimeout(() => {
                            requestAnimationFrame(() => {
                                const menuEl = document.querySelector(`.v-menu__content.${compCollection.compEID}`) as HTMLElement;
                                if (!menuEl) return;

                                const activatorRect = (document.querySelector(`#${compCollection.compEID} .v-input__control`) as HTMLElement)
                                    ?.getBoundingClientRect();
                                if (!activatorRect) return;

                                const narWrapper = document.querySelector(attachMenu);
                                const narDiff = narWrapper ? narWrapper.getBoundingClientRect().height : 0;

                                const scrollTop = window.scrollY || window.pageYOffset;
                                const menuRect = menuEl.getBoundingClientRect();
                                const menuHeight = menuRect.height || 392; //  TimePicker default 392
                                const menuTop = activatorRect.top + activatorRect.height + scrollTop - narDiff;
                                const viewportBottom = scrollTop + window.innerHeight;

                                if (menuTop + menuHeight > viewportBottom) {
                                    menuEl.style.setProperty("top", `${viewportBottom - menuHeight - 8}px`, "important");
                                } else {
                                    menuEl.style.setProperty("top", `${menuTop}px`, "important");
                                }
                            });
                        }, 10);
                    }, true);
                });

                observer.observe(document.body, { childList: true, subtree: true });
            };

            const compItems: Record<string, ((compColl: IComponentCollection) => void) | undefined> = {
                VAppBar: fixedHandler,
                VFooter: fixedHandler,
                DxDataGrid: addClickListenerToDatagrid,
                VDataTable: addMenuPositionFixToDatatable,
                VInlineDatepicker: addMenuPositionFixToInlineDatePicker,
                VInlineTimePicker: addMenuPositionFixToInlineTimePicker,
            };

            const traverseCompDict = (compDicts: Array<Record<string, IComponentCollection>>) => {
                const jrCompDicts: typeof compDicts = [];
                compDicts.forEach(compDict => Object.keys(compDict).forEach(compId => {
                    const compColl = compDict[compId];
                    compItems[compColl.compType]?.(compColl);
                    const doryJr = compColl.GetDoryJr();
                    if (!doryJr) { return; }

                    jrCompDicts.push(doryJr.GetComponentCollection()!);
                }));
                if (!jrCompDicts.length) { return; }

                traverseCompDict(jrCompDicts);
            }

            traverseCompDict([compDict]);

            vapp.style.paddingTop = styles.paddingTop || "";
            vapp.style.paddingBottom = styles.paddingBottom || "";

            const appComputed = renderer.DoryInst.GetLastHistory()?.getOrCreateAdditional<AppComputedAdditional>(AppComputedAdditional.additionalName, () => {
                return new AppComputedAdditional();
            }) || new AppComputedAdditional();
            appComputed.appComputed = { paddingTop: vapp.style.paddingTop, paddingBottom: vapp.style.paddingBottom };
        }
    }
}