import { IPermanentStoreOperator, IPermanentStoreObject, Plateau_UI_PermanentStore_Name } from "../../../../../common/shrimp/interfaces/RenderingInterfaces/Operators/IPermanentStoreOperator";
import { PermanentStoreManager } from "../PermanentStoreManager";

export class PermanentStoreOperator implements IPermanentStoreOperator {

    constructor() {
        this.createOrChangeStore(Plateau_UI_PermanentStore_Name);
    }

    public async set(object: IPermanentStoreObject<unknown>) {

        await new PermanentStoreManager().insert(object);
    }

    public async get(key: string) {

        return await new PermanentStoreManager().get(key);
    }

    public async delete(key: string) {

        return await new PermanentStoreManager().delete(key);
    }

    public async getAll() {
        return await new PermanentStoreManager().list();
    }

    public async createOrChangeStore(storeName: string) {
        PermanentStoreManager.changeDb(storeName);
    }
    public async deleteStore(storeName: string) {
        await new PermanentStoreManager().deleteDb(storeName);
    }

    public getCurrentStoreName() {
        return PermanentStoreManager.getCurrentStoreName();
    }

}