export enum StackReason {
    root = "root",
    if = "if",
    foreach = "foreach",
    string = "string"
}
export enum StackMessage{
    break = "break",
    continue = "continue"
}
export class Stack {
    private reason: StackReason;
    public get Reason() {
        return this.reason;
    }
    private message: StackMessage | null = null;
    public get Message(){
        return this.message;
    }
    private contextIndex: number | null = null;
    public get ContextIndex(){
        return this.contextIndex;
    }
    constructor({ reason }: { reason: StackReason }) {
        this.reason = reason;
    }

    public setContextIndex(index: number){
        this.contextIndex = index;
    }
    public clearContextIndex(){
        this.contextIndex = null;
    }

    public setMessage(message: StackMessage){
        this.message = message;
    }
    public hasReturnMessage(){
        switch(this.message){
            case StackMessage.break:
            case StackMessage.continue:
                return true;
            default:
                return false;        
        }
    }
}