import { thresholdedTrigger } from "../../../../../common/shrimp/helpers/thresholdedTrigger";
import { CssOrderManager } from "./cssOrderManager";

export class CssTagManager {
    private backupTags?: Array<Element>;
    private timer: number | undefined;
    private pageStateDictionary: Record<string, boolean> = {};

    constructor(private readonly styleClass: string) {}

    public hideStyleTags(debounceTimer: number, pageName: string) {
        this.pageStateDictionary[pageName] = false;

        const cb = () => {
            const hasActivePages = Object.values(this.pageStateDictionary).some(value => value);
            if (hasActivePages) { return; } 

            if (this.backupTags) { return; }
            this.backupTags = Array.from(document.getElementsByClassName(this.styleClass));
            this.backupTags.forEach(style => style.remove());
        };

        if (debounceTimer == 0) { cb(); }
        else { this.timer = thresholdedTrigger(this.timer, cb, this, debounceTimer); }
    }

    public showStyleTags(debounceTimer: number, pageName: string) {
        this.pageStateDictionary[pageName] = true;

        const cb = () => {
            const hasActivePages = Object.values(this.pageStateDictionary).some(value => value);
            if (!hasActivePages) { return; }

            if (!this.backupTags) { return; }
            this.backupTags.forEach(style => CssOrderManager.getPlaceHolderDom("plateau").insertAdjacentElement("afterend", style));
            this.backupTags = undefined;
        };

        if (debounceTimer == 0) { cb(); }
        else { this.timer = thresholdedTrigger(this.timer, cb, this, debounceTimer); }
    }

    public destroyStyleTagNames() {
        this.hideStyleTags(0, "default");
        this.backupTags = undefined;
        this.pageStateDictionary = {};
    }
}