import { IContextItem } from "../../../../shrimp/context";
import { IDictionary } from "../../../../shrimp/interfaces/IDictionary";
import { ICompDepTarget, IContextDepTarget, IStore } from "../../../../shrimp/interfaces/quick/IStore";
import { PropCalcContext } from "../../Managers/PropCalcContext";

export class BaseStoreContext implements IStore {
    public contextItems: IDictionary<any>;
    public dependants: Record<string, Record<string, Record<string,IContextDepTarget>>>={};
    public constructor() {
        this.contextItems = {};
    }

    public addDependant(field: string | number | symbol, pageId: string, propCalcContext: IContextItem) {
        const fieldVal=field.toString();
        const contextDepTarget = (propCalcContext as PropCalcContext).propContextDepGetter(propCalcContext as PropCalcContext);

        if(!contextDepTarget) return; // get from an event        
        const targetInstanceRef = (contextDepTarget?.instanceHolder.targetInstance as ICompDepTarget).ref;
        const compEID = targetInstanceRef.compEID;
        const compUID = targetInstanceRef.GetCompUID();
        const propTkey = compUID + "." + compEID + "." + contextDepTarget?.field;
        this.dependants[pageId] = this.dependants[pageId] ? this.dependants[pageId] : {};
        this.dependants[pageId][fieldVal] = this.dependants[pageId][fieldVal] ? this.dependants[pageId][fieldVal]: {};
        this.dependants[pageId][fieldVal][propTkey] = this.dependants[pageId][fieldVal][propTkey] ?? contextDepTarget;
    }
    public getDependants( field:string | number | symbol, pageId: string ): Record<string,IContextDepTarget> {
        const fieldVal = field.toString();
        return this.dependants?.[pageId]?.[fieldVal];
    }
    public set(name: string, value: any | undefined): void {
        if (value === undefined) {
            this.delete(name);
            return;
        }
        this.contextItems[name] = value;
    }

    public get(name: string): any | undefined {
        return this.contextItems[name];
    }
    
    public getAll(): any | undefined {
        return this.contextItems;
    }

    public delete(name: string): void {
        delete this.contextItems[name];
    }

    public deleteAll(): void {
        this.clear();
    }

    public clear(): void{
        this.contextItems = {};
    }
}