import { WebSDK } from "../../WebSDK";
import { IAuthenticationOperator } from "../../../../../common/shrimp/interfaces/RenderingInterfaces/Operators/IAuthenticationOperator";

export class AuthenticationOperator implements IAuthenticationOperator {

    public async getIamToken(): Promise<string | undefined>{

        await WebSDK.getPlateauIAM()?.refreshPromise();
        var token = WebSDK.getPlateauIAM()?.getToken();
        return token;
    }
}