import XLSX, { Sheet2JSONOpts, BookType } from 'xlsx-color';
import { IExcel, IExcelCellOptions, IExcelList, IExcelSheet, IExcelJsonData, IExcelToJsonData, ExcelCellType, IExcelItemStyle } from '../../../../common/shrimp/interfaces/quick/IExcel';


export class ExportManager {

    sheetList: Array<IExcelSheet> = [];
    fileName: string = "";
    bookType: BookType = "xlsx";

    constructor(excelFile: IExcel | IExcelList | undefined) {
        if (excelFile) {
            this.setExcelList(excelFile);
        }
    }

    private setExcelList(excelFile: IExcel | IExcelList) {
        if ((<any>excelFile).sheetList && Array.isArray((<any>excelFile).sheetList)) {
            let multipleExcelFile = <IExcelList>excelFile;
            this.fileName = multipleExcelFile.fileName;
            this.bookType = multipleExcelFile.bookType || "xlsx";
            multipleExcelFile.sheetList.forEach(sheet => {
                this.sheetList.push({
                    items: sheet.items,
                    headers: sheet.headers,
                    sheetName: sheet.sheetName,
                });
            });
        }
        else {
            let singleExcelFile = <IExcel>excelFile;
            this.fileName = singleExcelFile.fileName;
            this.bookType = singleExcelFile.bookType || "xlsx";
            this.sheetList.push({
                items: singleExcelFile.items,
                headers: singleExcelFile.headers,
                sheetName: singleExcelFile.sheetName,
            });
        }
    }

    private getItemValue(item: object, itemKey: string) {
        if (!item || !itemKey) {
            return "";
        }

        let index = itemKey.indexOf(".");
        if (index > -1) {
            let newKey = itemKey.substring(0, index);
            return this.getItemValue(item[newKey], itemKey.substring(index + 1));
        }
        else {
            return item[itemKey];
        }
    }

    public exportToXlsx() {
        var wb = XLSX.utils.book_new();
        this.sheetList.forEach(sheet => {
            let ws = this.createXlsxSheet(sheet);
            XLSX.utils.book_append_sheet(wb, ws, sheet.sheetName);
        });

        XLSX.writeFile(wb, `${this.fileName}.${this.bookType}`, { bookType: this.bookType });
    }

    private createXlsxSheet(sheet: IExcelSheet) {
        //Create a worksheet and add headers
        var ws = XLSX.utils.aoa_to_sheet([
            sheet.headers.map(header => {
                if (header.cellOptions?.headerStyle) {
                    let cellOptions = header.cellOptions as IExcelCellOptions;
                    return { v: header["text"] ?? undefined, s: cellOptions.headerStyle };
                }
                return header["text"];
            })
        ]);

        let headerMaxLength = {};
        if (sheet.items && sheet.items.length > 0) {
            //Add items to new worksheet
            XLSX.utils.sheet_add_aoa(ws, sheet.items.map(item => sheet.headers.map(header => {
                let itemvalue = this.getItemValue(item, header["value"]);

                let headerLength = header["text"]?.length ?? 0;
                let currentLength = headerMaxLength[header["value"]] ?? 0;
                let itemLength = itemvalue?.toString().length ?? 0;
                headerMaxLength[header["value"]] = Math.max(headerLength, currentLength, itemLength);


                let itemStyle = <IExcelItemStyle |undefined>item["cellOptions"]?.["itemStyle"]?.find(style => style?.headerValue == header["value"]);
                let cellInfo;   
                if (header.cellOptions) {
                    let headerCellOptions = header.cellOptions as IExcelCellOptions;
                    cellInfo = { v: itemvalue ?? undefined, t: headerCellOptions.type, z: headerCellOptions.numberFormat, s: (itemStyle?.style || headerCellOptions.itemStyle) };
                }
                else if (itemStyle?.style) {
                    cellInfo = { v: itemvalue ?? undefined, s: itemStyle.style };
                }
                if (cellInfo?.v && !cellInfo.t) {
                    switch (typeof cellInfo.v) {
                        case 'string':
                            cellInfo.t = ExcelCellType.text;
                            break;
                        case 'number':
                            cellInfo.t = ExcelCellType.number;
                            break;
                        case 'boolean':
                            cellInfo.t = ExcelCellType.boolean;
                            break;
                        case 'object':
                            if (cellInfo.v instanceof Date) {
                                cellInfo.t = ExcelCellType.date;
                            }
                            break;
                        default:
                            break;
                    }
                }

                return (cellInfo || itemvalue);
            })), { origin: -1 });
        }

        ws['!cols'] = this.fitToColumn(headerMaxLength);

        return ws;
    }

    private fitToColumn(items: Object) {
        let returnLength = <any>[];
        Object.keys(items).forEach(key => returnLength.push({ wch: items[key] }));
        return returnLength;
    }

    private base64ToAB(base64: string) {
        const binary_string = window.atob(base64);
        const len = binary_string.length;
        const bytes = new Uint8Array(len);
        for (let i = 0; i < len; i++) {
            bytes[i] = binary_string.charCodeAt(i);
        }
        return bytes.buffer;
    }

    public xlsxToJson(excelToJsonData: IExcelToJsonData): IExcelJsonData[] {
        // https://docs.sheetjs.com/docs/api/utilities/array
        let excelDataArray: IExcelJsonData[] = [];

        // let data = new Uint8Array(excelToJsonData.excelBuffer);
        // let arr = new Array();
        // for (let i = 0; i != data.length; ++i) arr[i] = String.fromCharCode(data[i]);
        // let bstr = arr.join("");
        let bufferData;
        let parsingOptions: XLSX.ParsingOptions = { type: "array" };
        if (excelToJsonData.excelData instanceof ArrayBuffer) {
            bufferData = excelToJsonData.excelData;
        }
        else if (excelToJsonData.excelData?.startsWith("data:")) {
            bufferData = this.base64ToAB(excelToJsonData.excelData.split(",")[1]);
        }

        let workbook = XLSX.read(bufferData, parsingOptions);

        Object.keys(workbook.Sheets).forEach(sheet => {

            let options: Sheet2JSONOpts = {
                blankrows: excelToJsonData.options?.blankRows,
                dateNF: excelToJsonData.options?.dateFormat,
                defval: excelToJsonData.options?.defaultValue,
                header: excelToJsonData.options?.header || 1,
                range: excelToJsonData.options?.range,
                raw: excelToJsonData.options?.raw || false
            };

            let excelData: IExcelJsonData = {};
            excelData.sheetName = sheet;
            excelData.sheetData = XLSX.utils.sheet_to_json(workbook.Sheets[sheet], options);
            excelDataArray.push(excelData);
        });

        return excelDataArray;
    }

}