import { IRendererConfig } from "../../../../common/shrimp/interfaces/RenderingInterfaces/IRendererConfig";
import { IProcessManager } from "../../../../common/qshell";

export class ProcessManager implements IProcessManager {
    contextName: string;    
    getMetadata: undefined | (()=>any) = undefined;
    setMetadata: undefined | (({ Metadata }: { Metadata: any }) => void) = undefined;
    changeProcessMetadata: undefined | (({ key, onSuccessFunc }: { key: string | undefined, onSuccessFunc: Function }) => void) = undefined;

    constructor({ contextName, config }: { contextName: string, config?: IRendererConfig }) {
        this.contextName = contextName
        if(config){
            this.createFunctions(config);
       }
    }
    

    createFunctions(config: IRendererConfig) {
        if (config.processManager?.getMetadata) {
            this.getMetadata = () => {
                return config!.processManager!.getMetadata!();
            }
        }
        if (config.processManager?.setMetadata) {
            this.setMetadata = ({ Metadata }: { Metadata: any }) => {
                return config!.processManager!.setMetadata!({Metadata});
            }
        }
        if(config.processManager?.changeProcessMetadata){
            this.changeProcessMetadata = ({ key = '', onSuccessFunc }: { key: string | undefined, onSuccessFunc: Function }) => {
                return config!.processManager!.changeProcessMetadata!({ key, onSuccessFunc });
            }
        }
    }

}