import { IComponentCollection } from "../../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IComponentOperator } from "../../../../../common/shrimp/interfaces/RenderingInterfaces/Operators/IComponentOperator";
import { StyleManager } from "../../managers/style/StyleManager";

export class ComponentOperator implements IComponentOperator {
    constructor() { }
    compListOfText = ["VIcon"];
    setClassString(compColl: IComponentCollection, classString: string): void {
        if (!compColl.domElement) { return; }

        compColl.domElement.className = classString;
    }
    getClassString(compColl: IComponentCollection): string {
        if (!compColl.domElement) { return ""; }

        return compColl.domElement?.className;
    }

    setTextAndMount(compColl: IComponentCollection, text: string): void {
        if (!compColl.component) { return };
        compColl.component.$slots.default[0].text = text;
        compColl.component.$mount();
    }

    getTextString(compColl: IComponentCollection) {
        if (!compColl.component) { return };
        return compColl.component.$slots.default[0].text;
    }

    public applyStyle(compColl: IComponentCollection) {
        StyleManager.current(compColl).applyStyle(compColl);
    }

    resetStyleCompCollection(compCollection: IComponentCollection) {
        StyleManager.current(compCollection).deleteCompCollectionStyle(compCollection);
    }

}