













import Vue from "vue";

export default Vue.extend({
  name: "DError",
  props: {
    visible: {
      type: Boolean,
      default: true
    },
    componentName: {
      type: String,
      default: null
    },
    componentID: {
      type: String,
      default: null
    },
    errorMessage: {
      type: String,
      default: null
    }
  },
  destroyed() {
      this.$el?.remove();
   }
});
