
import { IComponent } from "../../../../../../common/shrimp/interfaces/ComponentInterfaces/IComponent";
import { ICreatedComponent } from "../../../../../../common/shrimp/interfaces/ComponentInterfaces/ICreatedComponent";
import { IVnode } from "../../../../../../common/shrimp/interfaces/ComponentInterfaces/IVnode";
import { IDictionary } from "../../../../../../common/shrimp/interfaces/IDictionary";
import { CompGroupType } from "../../../../../../common/shrimp/interfaces/quick/IWebSDK";
import { ComponentProvider, ComponentTarget } from "../../providers/ComponentProvider";
import GeneralWrapper from "../LocalComponentWrappers/GeneralWrapper.vue";
import Chart from 'chart.js/auto';
import ChartDataLabels from "chartjs-plugin-datalabels";
import 'chartjs-adapter-date-fns';

export class ChartComponentManager {

    constructor() {
        Chart.register(ChartDataLabels);
        // // // Change default options for ALL charts
        // // Chart.defaults.set('plugins.datalabels', {
        // //     color: 'white',
        // //     font: {
        // //         weight: 'bold'
        // //     }
        // // });
    }


    public createChildTypeChartComponent(props: IDictionary<any>, target: ComponentTarget, componentName: string, parentInstance) {
        let rightToLeft: boolean = parentInstance?.$vuetify?.rtl ?? false;
        let instance, el;
        ({ instance, el } = this.createInnerChart(props, target, componentName, rightToLeft));
        props.__domElements = el;
        props.customStyle = true;
        const createdComp: IComponent = new GeneralWrapper({ parent: parentInstance, propsData: props });
        createdComp.$mount();
        return { instance, el: createdComp.$el }
    }
    public createSlotTypeChartComponent(props: IDictionary<any>, target: ComponentTarget, componentName: string, retVal: ICreatedComponent, parentInstance) {
        let rightToLeft: boolean = parentInstance?.$vuetify?.rtl ?? false;
        let instance, el;
        ({ instance, el } = this.createInnerChart(props, target, componentName, rightToLeft));
        props.__domElements = el;
        let createdComp: IComponent = new GeneralWrapper({ parent: parentInstance, propsData: props });
        const vel: IVnode = createdComp.$createElement(createdComp.$options.name, { props: props });
        retVal = { vel, instance, compGroupType: CompGroupType.Chart };
        return { createdComp, retVal };
    }

    private createInnerChart(props: IDictionary<any>, target: ComponentTarget, componentName: string, rightToLeft: boolean) {
        const comp = document.createElement("canvas");
        comp.setAttribute("aria-label", "Chart");
        comp.setAttribute("role", "img");
        const createdComp = new target.compInstance(comp, this.chartOptions(componentName, props, rightToLeft));

        return { instance: createdComp, el: createdComp["canvas"] };
    }



    private chartOptions(componentName: string, props: IDictionary<any>, rightToLeft: boolean) {
        let type = componentName.toLowerCase();
        let options = {};
        // let chartDataLabels = target.compInstance.registry.plugins.items.datalabels;
        let plugins: any[] = [ChartDataLabels];
        let data = { labels: props.labels, datasets: this.chartPropSetter(props) }
        if (componentName == "Bar") {
            options = {
                indexAxis: props.indexAxis ?? 'x',
                scales: {
                    x: this.chartXPropSetter(props),
                    y: this.chartYPropSetter(props)
                },
                plugins: {
                    // Change options for ALL labels of THIS CHART
                    datalabels: this.chartDataLabelsPropSetter(props),
                    title: {
                        display: props.titleDisplay ?? false,
                        text: props.title ?? "",
                        font: {
                            size: props.titleFontSize ?? 12
                        }
                    },
                    legend: {
                        rtl: rightToLeft,
                        textDirection: rightToLeft ? "rtl" : "ltr",
                        display: props.legendDisplay ?? true,
                        position: props.legendPosition ?? "top",
                        labels: {
                            // This more specific font property overrides the global property
                            font: {
                                size: props.legendFontSize ?? 12
                            }
                        },
                    },
                    tooltip: {
                        rtl: rightToLeft,
                        textDirection: rightToLeft ? "rtl" : "ltr",
                        bodyFont: {
                            family: props.tooltipBodyFontFamily,
                            size: props.tooltipBodyFontSize ?? 12,
                            style: props.tooltipBodyFontStyle,
                            weight: props.tooltipBodyFontWeight,
                            lineHeight: props.tooltipBodyFontLineHeight
                        },
                        callbacks: {
                            label: props.tooltipLabel
                        }

                    }
                },
                onClick: (event, legendItem, legend) => {
                    const handler = props.onClick;
                
                    if (legendItem && legendItem.length > 0) {
                        const rawValue = legendItem[0].element.$context.raw;
                    }
                
                    if (typeof handler === "string" && typeof (props as any).__vueParent?.[handler] === "function") {
                        (props as any).__vueParent[handler](event, legendItem, legend);
                    }
                    else if (typeof handler === "function") {
                        handler(event, legendItem, legend);
                    }
                }
                
            };

            return { type, data, options, plugins };
        } else if (componentName == "Doughnut") {
            if (props.insideText) {
                let plugin = this.getDoughnutInsideTextPlugin();
                plugins.push(plugin);
            }
            options = {
                elements: {
                    center: {
                        text: props.insideText,
                        color: '#FF6384', // Default is #000000
                        fontStyle: 'Arial', // Default is Arial
                        sidePadding: 20, // Default is 20 (as a percentage)
                        minFontSize: 25, // Default is 20 (in px), set to false and text will not wrap.
                        lineHeight: 25 // Default is 25 (in px), used for when text wraps
                    }
                },
                responsive: props.responsive ?? true,
                animation: {
                    animateScale: true,
                    animateRotate: true
                },
                plugins: {
                    // Change options for ALL labels of THIS CHART
                    datalabels: this.chartDataLabelsPropSetter(props),
                    title: {
                        display: props.titleDisplay ?? true,
                        text: props.title ?? "",
                        font: {
                            size: props.titleFontSize ?? 12
                        }
                    },
                    legend: {
                        rtl: rightToLeft,
                        textDirection: rightToLeft ? "rtl" : "ltr",
                        display: props.legendDisplay ?? true,
                        position: props.legendPosition ?? "top",
                        labels: {
                            // This more specific font property overrides the global property
                            font: {
                                size: props.legendFontSize ?? 12
                            }
                        }
                    },
                    tooltip: {
                        rtl: rightToLeft,
                        textDirection: rightToLeft ? "rtl" : "ltr",
                        bodyFont: {
                            family: props.tooltipBodyFontFamily,
                            size: props.tooltipBodyFontSize ?? 12,
                            style: props.tooltipBodyFontStyle,
                            weight: props.tooltipBodyFontWeight,
                            lineHeight: props.tooltipBodyFontLineHeight
                        },
                        callbacks: {
                            label: props.tooltipLabel
                        }
                    }
                },
                onClick: (event, legendItem, legend) => {
                    const handler = props.onClick;
                
                    if (legendItem && legendItem.length > 0) {
                        const rawValue = legendItem[0].element.$context.raw;
                    }
                
                    if (typeof handler === "string" && typeof (props as any).__vueParent?.[handler] === "function") {
                        (props as any).__vueParent[handler](event, legendItem, legend);
                    }
                    else if (typeof handler === "function") {
                        handler(event, legendItem, legend);
                    }
                },
                cutout: props.cutoutPercentage ?? 50,
                aspectRatio: props.aspectRatio ?? 2
            }

            return { type, data, options, plugins };
        } else if (componentName == "Line") {
            options = {
                scales: {
                    x: this.chartXPropSetter(props),
                    y: this.chartYPropSetter(props)
                },
                plugins: {
                    // Change options for ALL labels of THIS CHART
                    datalabels: this.chartDataLabelsPropSetter(props),
                    filler: {
                        propagate: false,
                    },
                    title: {
                        display: props.titleDisplay ?? false,
                        text: props.title ?? "",
                        font: {
                            size: props.titleFontSize ?? 12
                        }
                    },
                    legend: {
                        rtl: rightToLeft,
                        textDirection: rightToLeft ? "rtl" : "ltr",
                        display: props.legendDisplay ?? true,
                        position: props.legendPosition ?? "top",
                        labels: {
                            // This more specific font property overrides the global property
                            font: {
                                size: props.legendFontSize ?? 12
                            }
                        }
                    },
                    tooltip: {
                        rtl: rightToLeft,
                        textDirection: rightToLeft ? "rtl" : "ltr",
                        bodyFont: {
                            family: props.tooltipBodyFontFamily,
                            size: props.tooltipBodyFontSize ?? 12,
                            style: props.tooltipBodyFontStyle,
                            weight: props.tooltipBodyFontWeight,
                            lineHeight: props.tooltipBodyFontLineHeight
                        },
                        callbacks: {
                            label: props.tooltipLabel
                        }
                    }
                },
                interaction: {
                    intersect: false,
                },
                elements: {
                    line: {
                        tension: props.tension ?? "0"
                    }
                },
                onClick: (event, legendItem, legend) => {
                    const handler = props.onClick;
                
                    if (legendItem && legendItem.length > 0) {
                        const rawValue = legendItem[0].element.$context.raw;
                    }
                
                    if (typeof handler === "string" && typeof (props as any).__vueParent?.[handler] === "function") {
                        (props as any).__vueParent[handler](event, legendItem, legend);
                    }
                    else if (typeof handler === "function") {
                        handler(event, legendItem, legend);
                    }
                }
            }
            return { type, data, options, plugins };
        }
    }

    private chartYTicksFont(props) {
        //https://www.chartjs.org/docs/latest/api/interfaces/FontSpec.html
        //https://www.chartjs.org/docs/latest/api/interfaces/CartesianScaleOptions.html
        return {
            family: props.yTicksFontFamily,
            size: props.yTicksFontSize ?? 12,
            style: props.yTicksFontStyle ?? "normal",
            weight: props.yTicksFontWeight ?? "normal",
            lineHeight: props.yTicksFontLineHeight ?? "1.2"
        };
    }
    private chartYPropSetter(props) {
        let yProps = {
            beginAtZero: true,
            grid: {
                display: props.yDisplay ?? true,
                drawOnChartArea: props.yDrawOnChartArea ?? true,
                drawBorder: true
            },
            ticks: {
                display: props.yTicksDisplay ?? true,
                color: props.yTicksColor,
                font: this.chartYTicksFont(props)
            },
            reverse:props.yReverse ?? false
        };
        return yProps;
    }


    private chartXTicksFont(props) {
        //https://www.chartjs.org/docs/latest/api/interfaces/FontSpec.html
        //https://www.chartjs.org/docs/latest/api/interfaces/CartesianScaleOptions.html
        return {
            family: props.xTicksFontFamily,
            size: props.xTicksFontSize ?? 12,
            style: props.xTicksFontStyle ?? "normal",
            weight: props.xTicksFontWeight ?? "normal",
            lineHeight: props.xTicksFontLineHeight ?? "1.2"
        };
    }


    private chartXPropSetter(props) {

        let xProps = {
            grid: {
                display: props.xDisplay ?? true,
                drawOnChartArea: props.xDrawOnChartArea ?? true,
                drawBorder: true
            },
            ticks: {
                display: props.xTicksDisplay ?? true,
                color: props.xTicksColor,
                font: this.chartXTicksFont(props)
            },
            reverse:props.xReverse ?? false
        };


        if (props.xType) {
            xProps["type"] = props.xType;
            if (props.xType == "time") {
                let timeProp = this.chartXTimePropSetter(props);
                if (timeProp) {
                    xProps["time"] = timeProp;
                    xProps["min"] = props.xMin;
                    xProps["max"] = props.xMax;
                }
            }
        }

        return xProps;
    }


    private chartXTimePropSetter(props) {

        //DEFAULT
        // timeOpts.displayFormats.millisecond  'h:mm:ss.SSS aaaa'
        // timeOpts.displayFormats.second  'h:mm:ss aaaa'
        // timeOpts.displayFormats.minute 'h:mm aaaa'
        // timeOpts.displayFormats.hour 'ha'
        // timeOpts.displayFormats.day 'MMM d'
        // timeOpts.displayFormats.week 'PP'
        // timeOpts.displayFormats.month 'MMM yyyy'
        // timeOpts.displayFormats.quarter 'qqq - yyyy'
        // timeOpts.displayFormats.year 'yyyy'

        // timeOpts.isoWeekday  false
        // timeOpts.parser	false
        // timeOpts.round  false
        // timeOpts.tooltipFormat undefined
        // timeOpts.unit  false
        // timeOpts.minUnit 'millisecond'

        /*
        const FORMATS = {
            datetime: 'MMM d, yyyy, h:mm:ss aaaa',
            millisecond: 'h:mm:ss.SSS aaaa',
            second: 'h:mm:ss aaaa',
            minute: 'h:mm aaaa',
            hour: 'ha',
            day: 'MMM d',
            week: 'PP',
            month: 'MMM yyyy',
            quarter: 'qqq - yyyy',
            year: 'yyyy'
          };
          */

        let xTimeProps = {};

        let displayFormats = this.chartXTimeDisplayFormatsPropSetter(props);

        if (displayFormats) {
            xTimeProps["displayFormats"] = displayFormats;
        }

        if (props.xTimeIsoWeekday) {
            xTimeProps["isoWeekday"] = props.xTimeIsoWeekday;
        }

        if (props.xTimeParser) {
            xTimeProps["parser"] = props.xTimeParser;
        }

        if (props.xTimeRound) {
            xTimeProps["round"] = props.xTimeRound;
        }

        if (props.xTimeTooltipFormat) {
            xTimeProps["tooltipFormat"] = props.xTimeTooltipFormat;
        }

        if (props.xTimeUnit) {
            xTimeProps["unit"] = props.xTimeUnit;
        }

        if (props.xTimeMinUnit) {
            xTimeProps["minUnit"] = props.xTimeMinUnit;
        }

        return Object.keys(xTimeProps).length > 0 ? xTimeProps : undefined;


    }

    private chartXTimeDisplayFormatsPropSetter(props) {

        if (!props.xTimeDisplayFormatsMillisecond &&
            !props.xTimeDisplayFormatsSecond &&
            !props.xTimeDisplayFormatsMinute &&
            !props.xTimeDisplayFormatsHour &&
            !props.xTimeDisplayFormatsDay &&
            !props.xTimeDisplayFormatsWeek &&
            !props.xTimeDisplayFormatsMonth &&
            !props.xTimeDisplayFormatsQuarter &&
            !props.xTimeDisplayFormatsYear) {
            return undefined;
        }

        let formats = {};
        if (props.xTimeDisplayFormatsMillisecond) {
            formats["millisecond"] = props.xTimeDisplayFormatsMillisecond;
        }

        if (props.xTimeDisplayFormatsSecond) {
            formats["second"] = props.xTimeDisplayFormatsSecond;
        }

        if (props.xTimeDisplayFormatsMinute) {
            formats["minute"] = props.xTimeDisplayFormatsMinute;
        }

        if (props.xTimeDisplayFormatsHour) {
            formats["hour"] = props.xTimeDisplayFormatsHour;
        }

        if (props.xTimeDisplayFormatsDay) {
            formats["day"] = props.xTimeDisplayFormatsDay;
        }

        if (props.xTimeDisplayFormatsWeek) {
            formats["week"] = props.xTimeDisplayFormatsWeek;
        }

        if (props.xTimeDisplayFormatsMonth) {
            formats["month"] = props.xTimeDisplayFormatsMonth;
        }

        if (props.xTimeDisplayFormatsQuarter) {
            formats["quarter"] = props.xTimeDisplayFormatsQuarter;
        }

        if (props.xTimeDisplayFormatsYear) {
            formats["year"] = props.xTimeDisplayFormatsYear;
        }

        return formats;
    }



    private chartDataLabelsPropSetter(props) {

        let dataLabelProp = {
            align: props.labelAlign ?? "center",
            anchor: props.labelAnchor ?? "center",
            backgroundColor: props.labelBackgroundColor,
            borderColor: props.labelBorderColor,
            borderRadius: props.labelBorderRadius ?? 0,
            borderWidth: props.labelBorderWidth ?? 0,
            clamp: props.labelClamp ?? false,
            clip: props.labelClip ?? false,
            color: props.labelColor,
            display: props.labelDisplay ?? false,
            font: this.chartDataLabelsFontPropSetter(props),
            formatter: props.labelFormatter,
            labels: props.labelLabels,
            listeners: props.labelListeners,
            offset: props.labelOffset ?? "4",
            opacity: props.labelOpacity ?? 1,
            padding: this.chartDataLabelsPaddingPropSetter(props),
            rotation: props.labelRotation ?? 0,
            textAlign: props.labelTextAlign ?? "start",
            textStrokeColor: props.labelTextStrokeColor,
            textStrokeWidth: props.labelTextStrokeWidth ?? 0,
            textShadowBlur: props.labelTextShadowBlur ?? 0,
            textShadowColor: props.labelTextShadowColor
        };

        return dataLabelProp;
    }


    private chartDataLabelsPaddingPropSetter(props) {

        return {
            top: props.labelPaddingTop ?? 4,
            right: props.labelPaddingRight ?? 4,
            bottom: props.labelPaddingBottom ?? 4,
            left: props.labelPaddingLeft ?? 4
        };
    }

    private chartDataLabelsFontPropSetter(props) {

        return {
            family: props.labelFontFamily,
            size: props.labelFontSize,
            style: props.labelFontStyle,
            weight: props.labelFontWeight ?? "normal",
            lineHeight: props.labelFontLineHeight ?? "1.2"
        };
    }



    private chartPropSetter(props) {
        return [{
            backgroundColor: props.backgroundColor,
            borderColor: props.borderColor,
            borderSkipped: props.borderSkipped,
            borderWidth: props.borderWidth,
            data: props.data,
            hoverBackgroundColor: props.hoverBackgroundColor,
            hoverBorderColor: props.hoverBorderColor,
            hoverBorderWidth: props.hoverBorderWidth ?? 1,
            label: props.label || '',
            order: props.order,
            xAxisID: props.xAxisID,
            yAxisID: props.yAxisID,
            barPercentage: props.barPercentage ?? 0.9,
            categoryPercentage: props.categoryPercentage ?? 0.8,
            barThickness: props.barThickness,
            maxBarThickness: props.maxBarThickness,
            minBarLength: props.minBarLength,
            fill: props.fill
        }]
    }

    private getDoughnutInsideTextPlugin() {
        const plugin = {
            beforeDraw: (chart) => {

                // Get options from the center object in options
                var centerConfig = chart.config.options.elements.center;
                var fontStyle = centerConfig.fontStyle || 'Arial';
                var txt = centerConfig.text;
                var color = centerConfig.color || '#000';
                var maxFontSize = centerConfig.maxFontSize || 75;
                var sidePadding = centerConfig.sidePadding || 20;
                let innerRadius = chart._metasets[chart._metasets.length - 1].controller.innerRadius;
                var sidePaddingCalculated = (sidePadding / 100) * (innerRadius * 2)
                // Start with a base font of 30px
                chart.ctx.font = "30px " + fontStyle;

                // Get the width of the string and also the width of the element minus 10 to give it 5px side padding
                var stringWidth = chart.ctx.measureText(txt).width;
                var elementWidth = (innerRadius * 2) - sidePaddingCalculated;

                // Find out how much the font can grow in width.
                var widthRatio = elementWidth / stringWidth;
                var newFontSize = Math.floor(30 * widthRatio);
                var elementHeight = (innerRadius * 2);

                // Pick a new font size so it will not be larger than the height of label.
                var fontSizeToUse = Math.min(newFontSize, elementHeight, maxFontSize);
                var minFontSize = centerConfig.minFontSize;
                var lineHeight = centerConfig.lineHeight || 25;
                var wrapText = false;

                if (minFontSize === undefined) {
                    minFontSize = 20;
                }

                if (minFontSize && fontSizeToUse < minFontSize) {
                    fontSizeToUse = minFontSize;
                    wrapText = true;
                }

                // Set font settings to draw it correctly.
                chart.ctx.textAlign = 'center';
                chart.ctx.textBaseline = 'middle';
                var centerX = ((chart.chartArea.left + chart.chartArea.right) / 2);
                var centerY = ((chart.chartArea.top + chart.chartArea.bottom) / 2);
                chart.ctx.font = fontSizeToUse + "px " + fontStyle;
                chart.ctx.fillStyle = color;

                if (!wrapText) {
                    chart.ctx.fillText(txt, centerX, centerY);
                    return;
                }

                var words = txt.split(' ');
                var line = '';
                var lines: String[] = [];

                // Break words up into multiple lines if necessary
                for (var n = 0; n < words.length; n++) {
                    var testLine = line + words[n] + ' ';
                    var metrics = chart.ctx.measureText(testLine);
                    var testWidth = metrics.width;
                    if (testWidth > elementWidth && n > 0) {
                        lines.push(line);
                        line = words[n] + ' ';
                    } else {
                        line = testLine;
                    }
                }

                // Move the center up depending on line height and number of lines
                centerY -= (lines.length / 2) * lineHeight;
                for (var n = 0; n < lines.length; n++) {
                    chart.ctx.fillText(lines[n], centerX, centerY);
                    centerY += lineHeight;
                }
                //Draw text in center
                chart.ctx.fillText(line, centerX, centerY);
            }
        };

        return plugin;
    }
}
