import { ContextManager } from "../../../../shrimp/context";
import { IDictionary } from "../../../../shrimp/interfaces/IDictionary";
import { INavigationDemand, INavigationDemandType } from "../../../../shrimp/interfaces/quick/INavigationDemand";
import { BagStoreContext } from "../../Context/Store/BagStoreContext";
import { GlobalStoreContext } from "../../Context/Store/GlobalStoreContext";
import { Dory } from "../../Dory";
import { HistoryManager } from "../../Managers/HistoryManager";
import CustomComponent from "./CustomComponent";

export class RenderingComponent extends CustomComponent {
    public static ComponentName = "RenderingComponent";
    private callPartialDisplay?: (override: boolean) => void;


    constructor({ props, events, parent, context }: { props: IDictionary<Object>, events: IDictionary<Function>, parent: any, context: ContextManager }) {
        super({ props: props, events: events, context: context, parent: parent });
        const dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
        this.displayCb = (domEls, history, pageId, pageName, navigationDirection, override, doryJr) => {
            // delete comment after june 9 2022
            // below code is not enough in nested RenderingComponents -> ex: Dory-HM-HI-RC-DoryJr-HM-HI-RC-DoryJr-HM-HI..
            // let currPageID = historyManager.currentLevel().last()?.pageId
            // currPageID = currPageID ? currPageID + "." + pageId : pageId;

            let historyManager: HistoryManager = context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!;
            let rootPageId = historyManager!.getRootHM!().currentLevel().last()!.pageId;
            let calculatePageId = (context, pageId) => {
                let hm = context.GetItemSingle("HistoryManager");
                let creatorComp = hm?.doryLike?.createrComp;
                if (creatorComp) {
                    pageId = hm.currentLevel().last().pageId + "." + pageId;
                    return calculatePageId(creatorComp.context, pageId);
                }
                return pageId;
            }
            let currPageID = rootPageId + "." + calculatePageId(context, pageId);
            
            const callPartialDisplay = (override: boolean) => dory?.PartialDisplayHook.triggerer(domEls, history, currPageID, undefined, navigationDirection, override, this.doryJr);
            this.callPartialDisplay = callPartialDisplay;
            const firstRenderingComp = !this.doryJr?.checkIfHistoryExist({
                navigationDemandType: INavigationDemandType.back,
                ownerHistoryManager: historyManager
            });

            if (firstRenderingComp) {
                this.callPartialDisplay(true);
            } else {
                this.callPartialDisplay(false);
            }
        };
    }
    public RenderGo(path: string) {
        const createrComp = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.getCreaterRenderingComponent();
        // Root RenderingComp's path is changing 
        if (!createrComp) {
            this.beforeRootPathChange();
        }
        this.QJsonPath = path;
    }
    public HasHistory(pageId: string) {
        return this.doryJr?.hasHistory(pageId);
    }
    // public hasHistoryWithDirection(navType: INavigationDemandType) {
    //     return this.doryJr?.checkIfHistoryExist({navigationDemandType:navType});
    // }

    public searchTargetHistoryManager(pageId: string) {
        return this.doryJr?.searchTargetHistoryManager({ pageId });
    }
    public GoHistory(navigationDemand: INavigationDemand): boolean {
        if (!navigationDemand.pageId || !this.doryJr) {
            return false;
        }
        var history = this.doryJr.hasHistory(navigationDemand.pageId);
        if (!history) {
            return false;
        }
        this.doryJr.goHistory({ navigationDemand });
        return true;
    }

    private beforeRootPathChange() {
        const bagStoreContext = this.context.GetItemSingle<BagStoreContext>(BagStoreContext.ContextName);
        bagStoreContext?.clear();

        const globalStoreContext = this.context.GetItemSingle<GlobalStoreContext>(GlobalStoreContext.ContextName);
        globalStoreContext?.delete('$process');
    }

    protected destroyInner(): void {
        return;
    }
}