import { ContextManager, IContextItem } from "../../../shrimp/context";
import { IDictionary } from "../../../shrimp/interfaces/IDictionary";
import { IEvaluator } from "../../../shrimp/interfaces/Scripting/IEvaluator";
import { ScriptLang } from "../../../shrimp/interfaces/Scripting/scriptLang";
import { Dory } from "../Dory";
import { DoryBasedLRManager } from "./DoryBasedLRManager";
import { Evaluator } from "./Evaluator";

export class HistoryItemBasedLRManager implements IContextItem {
    static ContextName: string = "HistoryItemBasedLRManager";
    contextName: string = HistoryItemBasedLRManager.ContextName;
    evaluator: IEvaluator
    currentContext: ContextManager;
    resourceDict: IDictionary<string> = {};
    private currentLRType?: string;
    private currentLRTypeRTL?: boolean = false;
    private qLang?: ScriptLang;
    private RTL: string = "_%RTL%_";


    constructor({ currentContext, qLang }: { currentContext: ContextManager, qLang?: ScriptLang }) {
        this.currentContext = currentContext;
        this.evaluator = new Evaluator();
        let doryLRManager = this.currentContext.GetItemSingle<DoryBasedLRManager>(DoryBasedLRManager.ContextName);
        this.currentLRType = doryLRManager?.LRType;
        this.currentLRTypeRTL = doryLRManager?.LRTypeRTL;
        this.qLang = qLang;
    }
    public get LRType() {
        return this.currentLRType;
    }

    public get LRTypeRTL() {
        let doryLRManager = this.currentContext.GetItemSingle<DoryBasedLRManager>(DoryBasedLRManager.ContextName);
        if(doryLRManager?.LRTypeRTL) return doryLRManager.LRTypeRTL;
        
        return this.currentLRTypeRTL;
    }

    public SetResourceDict(CLRs: IDictionary<string>, lrType: string | undefined) {
        this.currentLRType = lrType;
        this.currentLRTypeRTL = CLRs && CLRs[this.RTL] ? true : false;

        this.resourceDict = CLRs;
    }

    GetResource({ key, dataSource, returnKey }: { key: string, dataSource?: object | null , returnKey?: boolean}) {
        const dory = this.currentContext.GetItemSingle<Dory>(Dory.ContextName);
        // HistoryContext based LR
        var result: string | null | undefined
        if (this.resourceDict) {
            result = this.resourceDict[key];
            if (result != null) {
                result = this.evaluator.Evaluate({ script: result, dataSource: dataSource, context: this.currentContext, lang: this.qLang });
                return result;
            }
        }

        //Dorybased LR
        const DoryBasedLR = this.currentContext.GetItemSingle<DoryBasedLRManager>(DoryBasedLRManager.ContextName);
        result = DoryBasedLR?.GetResource({ context: this.currentContext, key, dataSource })
        if (result != null) {
            return result;
        }

        //from shell 
        if (dory?.shellConfigs.lridHelper) {
            return dory.shellConfigs.lridHelper(key, dataSource);
        }

        //return key.
        return returnKey !== false ? key : undefined;
    }
}

