import { Hook } from "../../../../common/shrimp/helpers/hook";
import { IStoreManager } from "../../../../common/qshell";

export type StoreManagerHookCb = (oldValue: any, newValue: any) => void;
export class StoreManager implements IStoreManager {
    contextName: string;
    private dataStore: Record<string, any>;
    private hooks: Record<string, undefined | Hook<StoreManagerHookCb>>;

    constructor({ contextName }: { contextName: string }) {
        this.contextName = contextName
        this.dataStore = {};
        this.hooks = {};
    }

    set(name: string, value: any): void {
        const old = this.dataStore[name];
        this.dataStore[name] = value;
        this.hooks[name]?.triggerer(old, value);
    }
    get(name: string) {
        return this.dataStore[name];
    }
    delete(name: string): void {
        // todo
    }
    subscribe(name: string, cb: StoreManagerHookCb) {
        const currentHook = this.hooks[name] = this.hooks[name] || new Hook("StoreManager_" + name);
        currentHook.subscribe(cb);
    }
    unsubscribe(name: string, cb: StoreManagerHookCb) {
        const currentHook = this.hooks[name];
        if (!currentHook) { return; }
        currentHook.unsubscribe(cb);
    }

    setSessionStorage?({ key, value }: { key: string, value: string }) {
        window.sessionStorage.setItem(key, value);
    }

    clearSessionStorage?() {
        window.sessionStorage.clear();
    }

    getItemFromSessionStorage?({ key }: { key: string }) {
        return window.sessionStorage.getItem(key);
    }

    removeItemFromSessionStorage?({ key }: { key: string }) {
        window.sessionStorage.removeItem(key);
    }

    /* Local Storage Start*/
    setLocalStorage?({ key, value }: { key: string, value: string }) {
        window.localStorage.setItem(key, value);
    }

    clearLocalStorage?() {
        window.localStorage.clear();
    }

    getItemFromLocalStorage?({ key }: { key: string }) {
        return window.localStorage.getItem(key);
    }

    removeItemFromLocalStorage?({ key }: { key: string }) {
        window.localStorage.removeItem(key);
    }
    /* Local Storage End*/

    /* Key Storage Start*/
    setEncryptedStorage?({ key, value }: { key: string, value: string }) {
       // this.setLocalStorage?.({ key, value });
    }

    clearEncryptedStorage?() {
      //this.clearLocalStorage?.();
    }

    getItemFromEncryptedStorage?({ key }: { key: string }) {
        return null;
        //return this.getItemFromLocalStorage?.({key}) || null;
    }

    removeItemFromEncryptedStorage?({ key }: { key: string }) {
       //this.removeItemFromLocalStorage?.({key});
    }
    /* Key Storage End */
}