import { Logger } from "../../../../shrimp/helpers/logger";
import { Dory } from "../../Dory";
import { DoryJr } from "../../DoryJr";

export class DoryLikeHelper {
    public static TriggerPageComplete(dory: Dory, pageId: string) {
        Logger.Log({ logMessage: "Page completed Plateau" });
        dory.PageCompletedHook.triggerer({ pageId });
    }
    public static TriggerPageCompleteJr(doryJr: DoryJr, pageId: string) {
        Logger.Log({ logMessage: "JrPage completed Plateau" });
        const dory = doryJr.ContextJr.GetItemSingle<Dory>(Dory.ContextName);
        doryJr.PageJrCompletedHook.triggerer({ pageId });
    }
}