import { IDictionary } from "../interfaces/IDictionary";

export enum LayoutOption {
    display = "d",
    column = "c",
    flexDirection = "fd",
    align = "a",
    justify = "j",
    wrap = "w",
    spacing = "sp",
    textTypography = "tt",
    textColor = "tc",
    textEmphasis = "te",
    marginTop = "mt",
    marginRight = "mr",
    marginBottom = "mb",
    marginLeft = "ml",
    marginAll = "ma",
    paddingTop = "pt",
    paddingRight = "pr",
    paddingBottom = "pb",
    paddingLeft = "pl",
    paddingAll = "pa",
    italic = "fi"
}

const spacingPrefix = "sp";
const colPrefix = "col-";
const displayOptions = [{ text: "", key: "d-flex" }];
const columnOptions = [{ text: "0", key: "d-none" }, { text: "1", key: "col-1" }, { text: "2", key: "col-2" }, { text: "3", key: "col-3" }, { text: "4", key: "col-4" }, { text: "5", key: "col-5" }, { text: "6", key: "col-6" }, { text: "7", key: "col-7" }, { text: "8", key: "col-8" }, { text: "9", key: "col-9" }, { text: "10", key: "col-10" }, { text: "11", key: "col-11" }, { text: "12", key: "col-12" }, { text: "auto", key: "col-auto" }];
const directionOptions = [{ text: "Horizontal", key: "flex-row", iconClass: "mdi mdi20 mdi-chevron-triple-right" }, { text: "Vertical", key: "flex-column", iconClass: "mdi mdi20 mdi-chevron-triple-down" }];
const justifyOptions = [{ text: "Start", key: "justify-start", iconClass: "icon icon-justify-horizontal-start" }, { text: "End", key: "justify-end", iconClass: "icon icon-justify-horizontal-end" }, { text: "Center", key: "justify-center", iconClass: "icon icon-justify-horizontal-center" }, { text: "Space Between", key: "justify-space-between", iconClass: "icon icon-horizontal-space-between" }, { text: "Space Arround", key: "justify-space-around", iconClass: "icon icon-horizontal-space-around" }];
const alignOptions = [{ text: "Start", key: "align-start", iconClass: "icon icon-align-vertical-top" }, { text: "End", key: "align-end", iconClass: "icon icon-align-vertical-bottom" }, { text: "Center", key: "align-center", iconClass: "icon icon-align-vertical-center" }, { text: "Stretch", key: "align-stretch", iconClass: "icon icon-stretch-vertical" }, { text: "Baseline", key: "align-baseline", iconClass: "icon icon-baseline-top" }];
const wrapOptions = [{ text: "No Wrap", key: "flex-nowrap" }, { text: "Wrap", key: "flex-wrap" }];
const typographyOptions = [{ text: "", key: "" }, { text: "Heading 1", key: "text-h1" }, { text: "Heading 2", key: "text-h2" }, { text: "Heading 3", key: "text-h3" }, { text: "Heading 4", key: "text-h4" }, { text: "Heading 5", key: "text-h5" }, { text: "Heading 6", key: "text-h6" }, { text: "Subtitle 1", key: "text-subtitle-1" }, { text: "Subtitle 2", key: "text-subtitle-2" }, { text: "Body 1", key: "text-body-1" }, { text: "Body 2", key: "text-body-2" }, { text: "Button", key: "text-button" }, { text: "Caption", key: "text-caption" }, { text: "Overline", key: "text-overline" }];
const textColorOptions = [{ text: "", key: "" }, { text: "primary", key: "primary--text" }, { text: "Secondary", key: "secondary--text" }, { text: "Accent", key: "accent--text" }, { text: "Success", key: "success--text" }, { text: "Error", key: "error--text" }, { text: "Info", key: "info--text" }, { text: "Warning", key: "warning--text" }];
const textEmphasisOptions = [{ text: "Black", key: "font-weight-black" }, { text: "Bold", key: "font-weight-bold" }, { text: "Medium", key: "font-weight-medium" }, { text: "Regular", key: "font-weight-regular" }, { text: "Light", key: "font-weight-light" }, { text: "Thin", key: "font-weight-thin" }];
const spacingOptionsPrefix = ["mt-", "mr-", "mb-", "ml-", "ma-", "pt-", "pr-", "pb-", "pl-", "pa-"];

let allComponentClassList: IDictionary<Array<string>> = {};
allComponentClassList[spacingPrefix] = spacingOptionsPrefix;
let qlabelClassList: IDictionary<Array<string>> = {};
qlabelClassList[LayoutOption.textTypography] = typographyOptions.map(opt => opt.key);
qlabelClassList[LayoutOption.textColor] = textColorOptions.map(opt => opt.key);
qlabelClassList[LayoutOption.textEmphasis] = textEmphasisOptions.map(opt => opt.key);
let vColClassList: IDictionary<Array<string>> = {};
vColClassList[LayoutOption.display] = displayOptions.map(opt => opt.key);
vColClassList[LayoutOption.column] = columnOptions.map(opt => opt.key);
vColClassList[LayoutOption.flexDirection] = directionOptions.map(opt => opt.key);
vColClassList[LayoutOption.justify] = justifyOptions.map(opt => opt.key);
vColClassList[LayoutOption.align] = alignOptions.map(opt => opt.key);

let layoutBarComponentClassList: IDictionary<IDictionary<Array<string>>> = {};
layoutBarComponentClassList["All"] = allComponentClassList;
layoutBarComponentClassList["QLabel"] = qlabelClassList
layoutBarComponentClassList["VCol"] = vColClassList;

function getOptionsClass(dOpt: IDictionary<Array<string>>): string {
    let tempOpt: Array<string> = [];
    Object.keys(dOpt).forEach(key => {
        let options: Array<string> = [];
        dOpt[key].forEach(opt => {
            //from 'fd:flex-row' to 'flex-row' and "te:font-bold,fontblack" to [font-bold, font-black] 
            let splitOpt = opt.split(":");
            splitOpt[1].split(",").forEach(sOpt => {
                options.push(sOpt);
            });
        });

        if (key === "xs") {
            tempOpt = tempOpt.concat(options);
        } else {
            options.forEach(opt => {
                //from 'flex-row' to flex-{breakpoint}-row
                let splitOpt = opt.split("-");
                splitOpt.splice(1, 0, key);
                tempOpt.push(splitOpt.join("-"));
            });
        }
    });

    return tempOpt.join(" ");
}



export { columnOptions, directionOptions, justifyOptions, alignOptions, wrapOptions, typographyOptions, textColorOptions, textEmphasisOptions, getOptionsClass, spacingPrefix, spacingOptionsPrefix, layoutBarComponentClassList };