export class CssHelper {

    public static IncludeCSS(cssPath: string, options: { type: "after", prevSibling: HTMLElement } | { type: "document" }): HTMLLinkElement {
        let externalCSSPath = document.createElement("link");
        externalCSSPath.setAttribute("rel", "stylesheet");
        externalCSSPath.setAttribute("type", "text/css");
        externalCSSPath.setAttribute("href", cssPath);
        externalCSSPath.setAttribute("id", cssPath);
        options.type == "after" ? options.prevSibling.insertAdjacentElement("afterend", externalCSSPath) : document.head.appendChild(externalCSSPath);

        return externalCSSPath;
    }
    public static IncludeCSSString(css: string, cssFileName: string, options: { type: "after", prevSibling: HTMLElement } | { type: "document", contentDocument?: Document }) {
        const currentDoc = options.type == "document" && options.contentDocument ? options.contentDocument : document;
        let cssStringTag = <HTMLStyleElement>currentDoc.getElementById(cssFileName) || currentDoc.createElement("style");
        cssStringTag.id = cssFileName;
        cssStringTag.innerHTML = css;
        options.type == "after" ? options.prevSibling.insertAdjacentElement("afterend", cssStringTag) : currentDoc.head.appendChild(cssStringTag);
        
        return cssStringTag;
    }

    public static deleteCSS(id:string, contentDocument?: Document) { 
        const currentDoc = contentDocument ?? document;
        let cssTag = currentDoc.getElementById(id);
        cssTag?.remove();
    }
}