import { ContextManager } from "../../shrimp/context";
import { Hook } from "../../shrimp/helpers/hook";
import { Logger, LogType } from "../../shrimp/helpers/logger";
import { IComponentCollection } from "../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IExternalEvents } from "../../shrimp/interfaces/ComponentInterfaces/IExternalEvents";
import { IExternalProp } from "../../shrimp/interfaces/ComponentInterfaces/IExternalProp";
import { IQJSon } from "../../shrimp/interfaces/ComponentInterfaces/IQJson";
import { IDictionary } from "../../shrimp/interfaces/IDictionary";
import { INavigationDemand, INavigationDemandType } from "../../shrimp/interfaces/quick/INavigationDemand";
import { IDomElement } from "../../shrimp/interfaces/RenderingInterfaces/IDomElement";
import { IGoHistoryOptions, PartialDisplayHookCb } from "../../shrimp/interfaces/RenderingInterfaces/IDory";
import { IDoryJr } from "../../shrimp/interfaces/RenderingInterfaces/IDoryJr";
import { IPageJrCompletedCb } from "../../shrimp/interfaces/RenderingInterfaces/ILifeCycleCb";
import { ScopeContext } from "./Context/ScopeContext";
import { Dory } from "./Dory";
import { HistoryManager } from "./Managers/HistoryManager";
import { MessagingManager } from "./Managers/MessagingManager";
import { PropCalcContext } from "./Managers/PropCalcContext";
import { DoryLikeHelper } from "./Model/History/DoryLikeHelper";
import { HistoryItem } from "./Model/History/HistoryItem";

export class DoryJr implements IDoryJr {
    public static ContextName: string = "DoryJr";

    public contextName: string = DoryJr.ContextName;

    private contextJr: ContextManager;
    private subMessaging: IComponentCollection | null = null;
    private displayHook: Hook<PartialDisplayHookCb>;
    public PageJrCompletedHook: Hook<IPageJrCompletedCb> = new Hook<IPageJrCompletedCb>("PageJrCompletedHook", { manageState: true });

    constructor(contextJr: ContextManager) {
        this.contextJr = contextJr;
        this.displayHook = new Hook("DoryJrDisplayHook");
    }

    public delayTillPostDisplay(cb: () => void) {
        this.displayHook.postSubscribe(cb);
    }

    public setCallbackDisplay(callBackFunc: PartialDisplayHookCb) {
        this.displayHook.forceClearAllSubscibers();
        this.displayHook.subscribe(callBackFunc);
    }

    public async Render({ qjson, props, events, compParentInst, storeItems, pageId, pageName, noHistory, children }:
        { qjson: IQJSon, props?: IDictionary<any>, events?: IDictionary<Function>, compParentInst: any, storeItems?: IDictionary<any>, pageId: string, pageName?: string, noHistory?: boolean, children?: Record<string, Array<unknown>> }) {
        this.PageJrCompletedHook.ResetAndStartState();
        this.displayHook.clearPostSubscribers(true);
        this.contextJr.RegisterItem(new ScopeContext({ events, props, children }));
        const hm = this.contextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!;
        const oldHistoryItem = hm.currentLevel().last();

        const dory = this.contextJr.GetItemSingle<Dory>(Dory.ContextName)!;
        const historyItem = new HistoryItem({ qjson, context: dory.CloneCleanContext({ context: this.contextJr, additionalKeys: [PropCalcContext.ContextName, ScopeContext.ContextName] }), storeItems, pageId, pageName });
        await historyItem.Render({ compParentInst, noHistory });
        //const domEls = historyItem.controlList.map((item) => item.domElement!); //seni seviyoruz son of west
        const mm = new MessagingManager(this.contextJr);
        this.subMessaging = mm.GetMessagingComponentByLevelIndex(hm.CurrentLevelIndex);
        
        this.CallRender(historyItem, oldHistoryItem ? [oldHistoryItem] : []);
        this.PageCompleted();
    }

    public CallRender(historyItem: HistoryItem, olds: Array<HistoryItem>, navigationDirection?: INavigationDemandType) {
        this.displayHook.triggerer(historyItem.controlList.map((item) => item.domElement!), { new: historyItem, old: olds }, historyItem.pageId, historyItem.pageName, navigationDirection, undefined, undefined);
    }

    private PageCompleted() {
        const lastHistory = this.contextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.currentLevel().last()!;
        const doryJrs = lastHistory.GetDoryJrs();
        if (!doryJrs) {
            DoryLikeHelper.TriggerPageCompleteJr(this, lastHistory.pageId);
            return;
        }
        doryJrs?.forEach(doryJr => {
            doryJr.PageJrCompletedHook.subscribe(() => {
                if (doryJrs.find(jr => !jr.PageJrCompletedHook.IsTriggered())) {
                    return;
                }
                DoryLikeHelper.TriggerPageCompleteJr(this, lastHistory.pageId);
            });
            doryJr.PageJrCompletedHook.listenHookState(() => {
                this.PageJrCompletedHook.ResetAndStartState();
            });
        });
    }

    public getComponentCollectionByDom({ element, deep = false }: { element: IDomElement, deep?: boolean }): IComponentCollection | undefined {
        let lastHistory = this.GetLastHistory();
        return lastHistory?.getComponentCollectionByDom({ element, deep });

    }

    public getComponentCollectionByIdProperty({ id, deep }: { id: string, deep: boolean }): IComponentCollection | undefined {
        let lastHistory = this.GetLastHistory();
        return lastHistory?.getComponentCollectionByIdProperty({ id, deep });
    }

    public Destroy() {
        this.contextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.ClearHistory();
    }

    public Update() {
        const lastHist = this.GetLastHistory();
        if (lastHist) {
            lastHist.update();
        }
    }

    /**
     * Get custom events from sub component's on Messaging
     */
    public GetMethods(): IDictionary<Function> | undefined {
        if (!this.subMessaging) {
            const dory = this.contextJr.GetItemSingle<Dory>(Dory.ContextName);
            const engineYamlLogType = dory?.GetLogParams()?.minEngineLogLevel;
            Logger.Log({ logMessage: "Couldn' t find any Page Component", logType: LogType.Warning, minEngineLogLevel: engineYamlLogType });
            return undefined;
        }
        return this.subMessaging.events.GetExternalEventBoundFunctions?.();
    }

    /**
     * Get custom created props from sub component's on Messaging
     */
    public GetProps(): Array<IExternalProp> | undefined {
        if (!this.subMessaging) {
            const dory = this.contextJr.GetItemSingle<Dory>(Dory.ContextName);
            const engineYamlLogType = dory?.GetLogParams()?.minEngineLogLevel;
            Logger.Log({ logMessage: "Couldn' t find any Page Component", logType: LogType.Warning, minEngineLogLevel: engineYamlLogType });
            return undefined
        }
        return (<any>this.subMessaging.component).props["externalProps"];
    }

    public GetEvents(): Array<IExternalEvents> | undefined {
        if (!this.subMessaging) {
            const dory = this.contextJr.GetItemSingle<Dory>(Dory.ContextName);
            const engineYamlLogType = dory?.GetLogParams()?.minEngineLogLevel;
            Logger.Log({ logMessage: "Couldn' t find any Page Component", logType: LogType.Warning, minEngineLogLevel: engineYamlLogType });
            return undefined
        }
        return (<any>this.subMessaging.component).props["externalEvents"];

    }
    public hasHistory(pageId: string, checkExternalRouter?: { currPageId: string }): INavigationDemand | null {

        const historyManager = this.contextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!;
        let navigationDemand = (historyManager).hasHistory(pageId)!;
        if (checkExternalRouter && historyManager?.currentLevel().last()?.pageId != checkExternalRouter?.currPageId) { // is this still necessary ?
            navigationDemand.externalHistoryRouter = true;
        }
        return navigationDemand;
    }
    searchTargetHistoryManager({ pageId }: { pageId: string }) {
        const historyManager = this.contextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        return historyManager!.searchTargetHistoryManager({ pageId });
    }
    public checkIfHistoryExist(navigationDemand: INavigationDemand) {
        const historyManager = this.contextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        return historyManager!.checkIfHistoryExist(navigationDemand);
    }
    public goHistory(options: IGoHistoryOptions) {
        if (options.navigationDemand) {
            options.navigationDemand.navigationDemandType == INavigationDemandType.back ?
                this.goHistoryBack(options.navigationDemand) :
                this.goHistoryForward(options.navigationDemand);
        }
    }
    private goHistoryBack(navigationDemand: INavigationDemand) {
        this.contextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.Back(navigationDemand);
    }
    private goHistoryForward(navigationDemand: INavigationDemand) {
        this.contextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.Forward(navigationDemand);
    }


    public get ContextJr() {
        return this.contextJr;
    }
    public GetLastHistory(): HistoryItem | undefined {
        return this.contextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.currentLevel().last() || undefined;
    }

    public GetComponentList() {
        return this.GetLastHistory()?.GetComponentList();
    }

    public GetComponentCollection(): Record<string, IComponentCollection> | undefined {
        let lastHistory = this.GetLastHistory();
        return lastHistory?.GetComponentCollection() || undefined;
    }
    public GetPreviousHistory() {
        return this.contextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.currentLevel().previous() || undefined;
    }


}