import { CType, IChildJson, ICompJson, PType } from "../interfaces/ComponentInterfaces/ICompJson";
import { IComponent } from "../interfaces/ComponentInterfaces/IComponent";
import { IComponentCollection } from "../interfaces/ComponentInterfaces/IComponentCollection";
import { ICreatedComponent } from "../interfaces/ComponentInterfaces/ICreatedComponent";
import { IDictionary } from "../interfaces/IDictionary";
import { TypeHelper } from "./typeHelper";

export enum ValueTypeClasses {
    saved = "sftt-value-saved",
    updated = "sftt-value-updated",
    removed = "sftt-value-removed",
    none = "sftt-value-none"
}

export enum ValueStatus {
    saved = "saved",
    updated = "updated",
    removed = "removed",
    none = "none"
}

export enum ComparisonEnum {
    labelField = "label",
    valueField = "internalValue",
    compareValueField = "compareValue",
    compareTextValue = "compareText",
    compareTableValue = "compareTableValue",
    tooltipCompType = "Tooltip"
}

export type CompComparisonType = {
    labelField: ComparisonEnum.labelField | string,
    valueField: ComparisonEnum.valueField | string,
    classCompare?: boolean
    applyClassName?: string
}

export class compDiffHelper {

    static componentValueList: Record<string, CompComparisonType> = {
        VTextField: { labelField: ComparisonEnum.labelField, valueField: ComparisonEnum.valueField },
        VSelect: { labelField: ComparisonEnum.labelField, valueField: ComparisonEnum.valueField },
        VAutocomplete: { labelField: ComparisonEnum.labelField, valueField: ComparisonEnum.valueField },
        VCombobox: { labelField: ComparisonEnum.labelField, valueField: ComparisonEnum.valueField },
        VRadioGroup: { labelField: ComparisonEnum.labelField, valueField: ComparisonEnum.valueField },
        VCheckbox: { labelField: ComparisonEnum.labelField, valueField: ComparisonEnum.valueField },
        VInlineDatepicker: { labelField: ComparisonEnum.labelField, valueField: ComparisonEnum.valueField, applyClassName: "textFieldClass" },
        VDataTable: { labelField: ComparisonEnum.labelField, valueField: "items", classCompare: true },
        VInlineTimePicker: { labelField: ComparisonEnum.labelField, valueField: "value", applyClassName: "textFieldClass" },
        VTextarea: { labelField: ComparisonEnum.labelField, valueField: ComparisonEnum.valueField },
        VSwitch: { labelField: ComparisonEnum.labelField, valueField: ComparisonEnum.valueField },
    }

    static comparisonComponent = "QLegend"

    /**
     * Compares component with the given old and new value.
     * Returns:
     * "saved" when there is no old value and new value is not empty,
     * "updated" when there is an old value different than the new value
     * "removed" when there is an old value and no new value     * 
     * @param props Props of the component will be compared with the given list
     * @param valueList Value list of the component includes the old and new value          
     */
    static valueCompare({ value, compareValue, compareDataField }: { value?: any, compareValue?: any, compareDataField?: boolean }): ValueStatus {
        const typeHelper = new TypeHelper();
        if (value === compareValue || (compareValue === undefined && !compareDataField)){
            return ValueStatus.none;
        }
        if ((value === undefined || value === null || value === "") && compareValue) {
            return ValueStatus.removed;
        } else if (value && (compareValue === undefined || compareValue === null || compareValue === "")) {
            return ValueStatus.saved;
        } else if (value != compareValue) {
            if (typeHelper.isObject(value) && typeHelper.isObject(compareValue) && JSON.stringify(value) == JSON.stringify(compareValue)) {
                return ValueStatus.none;
            }
            return ValueStatus.updated;
        }

        return ValueStatus.none
    }

    static multipleValueCompare(value: any, compareValue: any): ValueStatus {
        let statu: ValueStatus = ValueStatus.none;
        if (!compareValue) {
            return statu;
        }

        var valueList = value ? (<any[]>value).reduce((prev, cur) => {
            prev[cur.value] = cur.value;
            return prev;
        }, {}) : {};

        var compareValueList = compareValue ? (<any[]>compareValue).reduce((prev, cur) => {
            prev[cur.value] = cur.value;
            return prev;
        }, {}) : {};

        var valueDiffResult = (<any[]>value)?.filter(item => {
            return !compareValueList[item.value];
        });

        var compareValueDiffResult = (<any[]>compareValue)?.filter(item => {
            return !valueList[item.value];
        });

        if (Object.keys(valueList).length <= 0 && Object.keys(compareValueList).length > 0) {
            statu = ValueStatus.removed;
        }
        else if (Object.keys(valueList).length > 0 && Object.keys(compareValueList).length <= 0) {
            statu = ValueStatus.saved;
        }
        else if ((valueDiffResult && valueDiffResult.length > 0) || (compareValueDiffResult && compareValueDiffResult.length > 0)) {
            statu = ValueStatus.updated;
        }

        return statu;
    }

    static getValueCompareClass({ value, compareValue, compareDataField }: { value?: string, compareValue?: string, compareDataField?: boolean }): ValueTypeClasses {

        let statu: ValueStatus = ValueStatus.none;
        if (!compareDataField && (Array.isArray(value) || Array.isArray(compareValue))) {
            statu = this.multipleValueCompare(value, compareValue);
        }
        else {
            statu = this.valueCompare({ value, compareValue, compareDataField });
        }
        return ValueTypeClasses[statu];
    }

    static decideTooltipContent({ componentInstance, toolTipValue }: { componentInstance: IComponent, toolTipValue?: string | Array<any> }): string | undefined {

        if (Array.isArray(toolTipValue) && toolTipValue.length >= 0) {
            return toolTipValue.reduce((prev, cur) => {
                prev += " | " + cur.text;
                return prev;
            }, "").slice(3);
        }

        return (<any>componentInstance)[ComparisonEnum.compareTextValue] ? (<any>componentInstance)[ComparisonEnum.compareTextValue] || toolTipValue : toolTipValue;
    }

    static propValueListener({ compCollection, propKey, props }:
        {
            compCollection: IComponentCollection,
            propKey: string,
            props: IDictionary<any>
        }) {
        compCollection.WatchCreatedFields((updatedComp: ICreatedComponent) => {
            const componentInstance = updatedComp.instance;
            const componentCollection = compCollection;
            if (!componentInstance) {
                return;
            }
            // let compObj = { compareValue: componentInstance[propKey] }
            let valueProp = Object.getOwnPropertyDescriptor(componentInstance.constructor.prototype, propKey);
            if (valueProp) {
                let set = valueProp.set, get = valueProp.get;
                if (!Object.getOwnPropertyDescriptor(componentInstance, propKey)) {
                    Object.defineProperty(componentInstance, propKey, {
                        get: () => {
                            return get?.call(componentInstance);
                        },
                        set: v => {
                            set?.call(componentInstance, v);
                        }
                    });
                }
            } else if (Object.getOwnPropertyDescriptor(componentInstance, propKey)) {
                throw "There is no implementation on converted component instance from component collection";
            } else {
                let val: undefined, textValue: undefined = undefined, tableValue: undefined = undefined, valueStatus: ValueTypeClasses = ValueTypeClasses.none;
                if (!Object.getOwnPropertyDescriptor(componentInstance, ComparisonEnum.compareTextValue)) {
                    Object.defineProperty(componentInstance, ComparisonEnum.compareTextValue, {
                        get: () => { return textValue },
                        set: v => {
                            textValue = v
                            this.displayTooltip({ compCollection, toolTipValue: v, valueStatus });
                            componentInstance.$el?.setAttribute?.("class", componentInstance.$el.className)
                        }
                    })
                }
                if (!Object.getOwnPropertyDescriptor(componentInstance, ComparisonEnum.compareTableValue)) {
                    Object.defineProperty(componentInstance, ComparisonEnum.compareTableValue, {
                        get: () => { return tableValue },
                        set: v => {
                            tableValue = v
                            componentInstance.$el?.setAttribute?.("class", componentInstance.$el.className)
                        }
                    })
                }
                Object.defineProperty(componentInstance, propKey, {
                    get: () => {
                        return val;
                    },
                    set: (newValue) => {
                        val = newValue;
                        if (!props.CompareDataField && !compDiffHelper.componentValueList[componentCollection.compType].classCompare) {
                            const calculatedClasses = compDiffHelper.calcComparisonClass(compCollection);
                            valueStatus = calculatedClasses.valueCompare;
                            compCollection.class = componentCollection.compDomClass = calculatedClasses.class

                            this.displayTooltip({ compCollection, toolTipValue: newValue, valueStatus });
                            componentInstance.$el?.setAttribute?.("class", componentInstance.$el.className)
                        }
                    }
                });
            }
        });
    }
    private static displayTooltip({ compCollection, toolTipValue, valueStatus }: { compCollection: IComponentCollection, toolTipValue: string | Array<any>, valueStatus: ValueTypeClasses }) {
        const compareComponent = compDiffHelper.componentValueList[compCollection.compType];
        const componentInstance = compCollection.component!;

        if (compareComponent && valueStatus != ValueTypeClasses.none) {
            toolTipValue = compDiffHelper.decideTooltipContent({ componentInstance, toolTipValue })!;
            (<any>compCollection.parentCompCollection!.component)["disabled"] = false;
            (<any>compCollection.parentCompCollection!.component)["toolTipContent"] = toolTipValue;
        }
        else if (compareComponent) {
            (<any>compCollection.parentCompCollection!.component)["disabled"] = true;
            (<any>compCollection.parentCompCollection!.component)["toolTipContent"] = undefined;
        }
    }

    private static calcComparisonClass(compCollection: IComponentCollection) {
        let classValue: string | undefined = compDiffHelper.removeValueClass(compCollection.class);
        const compareComponent = compDiffHelper.componentValueList[compCollection.compType];
        let valueCompare = compDiffHelper.getValueCompareClass({ value: (<any>compCollection.component)[compareComponent.valueField], compareValue: (<any>compCollection.component)[ComparisonEnum.compareValueField] });
        return { class: classValue + " ", valueCompare };
    }

    static removeValueClass(componentClass?: string) {
        let finalClass = componentClass ?? "";
        if (componentClass) {
            finalClass = componentClass.split("sftt-value-").join("");
        }
        return finalClass.trim();
    }

    static includeNoneValueClass(componentClass?: string){
        if(componentClass && componentClass.indexOf(ValueTypeClasses[ValueStatus.none]) > -1){
            return true;
        }  
        
        return false;
    }

    /**
     * Compare given parameters and set the Tooltip, Chip and Label fields
     * @param compJson Reference component json
     * @param valueField Setted for the current value
     * @param compareValue Compare value
     * @param compareDataField Field that will be compared on
     * @param headerValue Whenever each column is setted, set the fields based on this param 
     */
    static compareAndSetFields({ compJson, valueField, compareValue, compareDataField, headerValue }: { compJson: IChildJson, valueField: Record<string, any>, compareValue: Array<Record<string, any>>, compareDataField: string, headerValue: string }) {
        //Find the compare object from the given compareValue based on the current valueField array
        const comparableObj = compareValue.find(compObj => compObj[compareDataField] === valueField[compareDataField]);

        if (comparableObj && compJson.c[0]!.P["toolTipContent"]) {
            compJson.c[0]!.P["toolTipContent"].H = comparableObj[headerValue];
            const comparedClass = compDiffHelper.getValueCompareClass({ value: valueField[headerValue], compareValue: comparableObj[headerValue], compareDataField: true });
            compJson.c[0]!.C["default"].c[0]!.P["color"]!.H = comparedClass;

            if (valueField[headerValue] !== undefined) {
                compJson.c[0]!.C["default"].c[0]!.C["default"].c[0]!.P["text"]!.H = valueField[headerValue];
            }
        }
    }

    /**
     * Create Tooltip within the VChip component if the slot is empty 
     * @param compJson Current compJson
     * @param props Check slot fields on the headers prop
     */
    static createChipComponentOnNonSlotChilds(compJson: ICompJson, props: IDictionary<any>) {
        for (let index = 0, arrLength = props["headers"].length; index < arrLength; index++) {
            const header = props["headers"][index];
            if (!compJson.C["item." + header.value]) {
                compJson.C["item." + header.value] = { c: [compDiffHelper.createChipComponent(compJson)], cType: CType.scopedSlot };
            }
        }
    }

    /**
     * Create tooltip and chip component 
     * @param compJson Reference component json
     */
    static createChipComponent(compJson: ICompJson) {
        let chipChildComponent: IChildJson = { c: [{ T: "QLabel", P: { ID: { H: compJson.P.ID.H + "_compareLabel", T: PType.prop }, "text": { H: " ", T: PType.prop, DE: true } }, C: {}, E: {}, _Editor: { eID: compJson.P.ID.H + "_compareLabel" } }], cType: CType.slot };
        let chipComponent: ICompJson = { T: "VChip", P: { ID: { H: compJson.P.ID.H + "_compareChip", T: PType.prop }, "color": { H: " ", T: PType.prop, DE: true } }, E: {}, C: { "default": chipChildComponent }, _Editor: { eID: compJson.P.ID.H + "_compareChip" } };
        let tooltipComponent: ICompJson = { T: "Tooltip", P: { ID: { H: compJson.P.ID.H + "_compareTooltip", T: PType.prop }, "toolTipContent": { H: " ", T: PType.prop, DE: true }, "position": { H: "'top'", T: PType.prop, DE: true } }, E: {}, C: { "default": { c: [chipComponent], cType: CType.slot } }, _Editor: { eID: compJson.P.ID.H + "_compareTooltip" } };
        return tooltipComponent;
    }

    static replaceTooltipComponent(parentComp: ICompJson, childComp: ICompJson) {
        let childKey!: string, childIndex!: number;
        for (const key in parentComp.C) {
            if (Object.prototype.hasOwnProperty.call(parentComp.C, key)) {
                const children = parentComp.C[key];
                childIndex = children.c.findIndex(compJson => compJson.T == childComp.T)
                if (childIndex != -1) {
                    childKey = key;
                    break;
                }
            }
        }
        parentComp.C[childKey].c[childIndex] = compDiffHelper.createTooltipJson(childComp);
    }

    /**
     * Creates a new tooltip json based on referenced child comp. ChildComp parameter will be placed into the new tooltip
     * @param childComp replaced component and will be referenced by new tooltip comp
     * @returns Tooltip json: ICompJson
     */
    private static createTooltipJson(childComp: ICompJson) {
        const tooltipJson: ICompJson = {
            T: ComparisonEnum.tooltipCompType,
            P: { ID: { H: childComp.P.ID.H + "_tooltip", T: PType.prop }, "disabled": { H: "true", T: PType.prop }, "position": { H: "'top'", T: PType.prop } },
            E: {},
            C: { "default": { c: [childComp], cType: CType.slot } },
            _Editor: { eID: childComp._Editor.eID + "_tooltip" },
            O: {
                layout: {
                    class: "sftt-compare-value-tooltip",
                    dOpt: {}
                }
            }
        }
        return tooltipJson;
    }
}