import { ContextManager } from "../../../../shrimp/context";
import { HTTPHeader } from "../../../../shrimp/helpers/httpHeaderHelper";
import { Logger, LogType } from "../../../../shrimp/helpers/logger";
import { AlertType, ErrorSource } from "../../../../shrimp/interfaces/ComponentInterfaces/IAlert";
import { CreatePromiseData } from "../../../../shrimp/interfaces/IPromiseData";
import { IRequest } from "../../../../shrimp/interfaces/NetworkInterfaces/IRequest";
import { INetwork } from "../../../../shrimp/interfaces/quick/IContainerServices";
import { INetworkResponse } from "../../../../shrimp/interfaces/quick/INetworkResponse";
import { INetworkError } from "../../../../shrimp/interfaces/NetworkInterfaces/INetworkError";
import { IDory } from "../../../../shrimp/interfaces/RenderingInterfaces/IDory";
import { PerfSingles } from "../../../../shrimp/performance";
import { BlockInfo } from "../../Context/BlockInfo";
import { RenderingContext } from "../../Context/RenderingContext";
import { ResponseContext } from "../../Context/ResponseContext";
import { Dory } from "../../Dory";
import { HistoryManager } from "../HistoryManager";
import { MockManager, MockMode } from "./MockManager";

export class RequestManager {
    context: ContextManager

    constructor(context: ContextManager) {
        this.context = context;
    }

    /**
     * Handle Async Request
     * @param requestObject Request obejct with paramaters
     * @returns void
     */
    async RequestHandler(requestObject: IRequest, eventName?: string) {
        const dory = this.context.GetItemSingle<Dory>(Dory.ContextName)!;
        const engineYamlLogType = dory.GetLogParams()?.minEngineLogLevel;
        const awaitProm = CreatePromiseData<INetworkResponse<Record<string, any>>>();

        if (requestObject.blockRender) {
            const blockInfo = this.context.GetItemSingle<BlockInfo>(BlockInfo.ContextName);
            blockInfo?.addBlockPromise(awaitProm.promise);
        }

        const renderingContext = this.context.GetItemSingle<RenderingContext>(RenderingContext.ContextName);
        renderingContext?.IsRenderingEventsComplete();

        const ownerComp = renderingContext?.ownerComponent;
        const ownerEvent = renderingContext?.ownerEvent;

        try {
            if (!requestObject.headers) {
                requestObject.headers = {};
            }
            await dory?.SettingModelsContext.TriggerPipeline("onBeforeRequest", { requestObject });
        }
        catch (ex) {
            awaitProm.reject(ex);
            throw ex;
        }

        const mm = MockManager.GetMocker<Promise<INetworkResponse>>({ mockMode: MockMode.off, overrideCapture: false }, dory);
        let promise = mm.Execute(requestObject.url, () => {
            const requestCanBeLoggedByName = Logger.checkEventNameIsSpecial(eventName);
            return this.requestMethod(requestObject, requestCanBeLoggedByName);
        });



        if (ownerComp) { PerfSingles.Instance().Start(ownerComp.compId + "." + ownerEvent?.eventName + "->"); }

        //Make the setted request
        promise.then(async (response) => {
            Logger.Log({ logMessage: "Response has successfully returned", logType: LogType.Trace, minEngineLogLevel: engineYamlLogType, ownerEventName: eventName });
            requestObject.responseField = requestObject.responseField || null;
            this.MergeResponseToMainResponse(requestObject.responseField, response);

            try {
                await dory?.SettingModelsContext.TriggerPipeline("onAfterResponse", { response });
            }
            catch (ex) {
                awaitProm.reject(ex);
                throw ex;
            }

            if (ownerComp) { PerfSingles.Instance().End(ownerComp.compId + "." + ownerEvent?.eventName + "->", ownerEvent); }
            if (requestObject.onSuccess) {
                if (!ownerComp) {
                    Logger.Log({ logMessage: "RenderingContext has no owner component", logType: LogType.Error, minEngineLogLevel: engineYamlLogType, ownerEventName: eventName });
                }
                else {
                    ownerComp.events.Trigger({ eventName: requestObject.onSuccess, eventArguments: requestObject.args, immediate: ownerEvent?.immediate });
                }
            }
            awaitProm.promise.then(() => { if (ownerComp) { PerfSingles.Instance().End(ownerComp.compId + "." + ownerEvent?.eventName + " after await", ownerEvent); } });
            awaitProm.resolver(response as INetworkResponse<object>);
            if (ownerComp) { PerfSingles.Instance().Start(ownerComp.compId + "." + ownerEvent?.eventName + " after await"); }
        });

        //Block all rendering
        promise.catch(async (err: { response?: INetworkResponse, stack?: string, message?: string }) => {
            Logger.Log({ logMessage: `Response has not successfully returned. Error: ${err}`, logType: LogType.Error, minEngineLogLevel: engineYamlLogType, ownerEventName: eventName });
            if (err?.response) {
                try {
                    await dory.SettingModelsContext.TriggerPipeline("onAfterErrorResponse", { response: err.response });
                }
                catch (ex) {
                    awaitProm.reject(ex);
                    throw ex;
                }
            }
            if (ownerComp) { PerfSingles.Instance().End(ownerComp.compId + "." + ownerEvent?.eventName + "->", ownerEvent); }
            if (requestObject.onFail) {
                if (!ownerComp) {
                    Logger.Log({ logMessage: "RenderingContext has no owner component", logType: LogType.Error, minEngineLogLevel: engineYamlLogType, ownerEventName: eventName });
                } else {
                    const error = <INetworkError>new Error(err.message);
                    error.response = err.response;
                    error.stack = err.stack;
                    error.request = requestObject;
                    ownerComp.events.Trigger({ eventName: requestObject.onFail, eventArguments: { error }, immediate: ownerEvent?.immediate });
                }
            } else {
                if (requestObject.showErrorMessage != false) {
                    if (dory.shellConfigs.shellGlobalMethods?.alert) {
                        const currentPageId = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.getRootHM().currentLevel().last()!.pageId;
                        dory.shellConfigs.shellGlobalMethods?.alert({ category: AlertType.Error, error: { errorMessage: err.message, errorCode: 'HTTP_' + err.response?.status, errorSource: ErrorSource.Network, response: err.response } }, { currentPageId });
                    }
                }
            }
            awaitProm.promise.catch(() => { if (ownerComp) { PerfSingles.Instance().End(ownerComp.compId + "." + ownerEvent?.eventName + " after catch", ownerEvent); } });
            awaitProm.reject(err);
            if (ownerComp) { PerfSingles.Instance().Start(ownerComp.compId + "." + ownerEvent?.eventName + " after catch"); }
        });
        return awaitProm.promise;
    }

    /**
     * Merge response to the main response object with returned response
     * @param responseKey Key that will be attached to the response on last history item
     * @param returnedResponse Response value that has been returned from the request
     * @returns void
     */
    MergeResponseToMainResponse(responseKey: string | null, returnedResponse: INetworkResponse): void {
        const returnedResponseObject = <INetworkResponse<object>>returnedResponse;
        const responseContext = this.context.GetItemSingle<ResponseContext>(ResponseContext.ContextName);
        if (!responseContext) return;
        responseContext.response = responseContext.response || {};

        if (responseKey) {
            responseContext.response[responseKey] = returnedResponseObject.data;
            responseContext.response["headers"] = responseContext.response["headers"] || {};
            responseContext.response["status"] = responseContext.response["status"] || {};
            returnedResponseObject["headers"] ? responseContext.response["headers"][responseKey] = returnedResponseObject["headers"] : undefined
            returnedResponseObject["status"] ? responseContext.response["status"][responseKey] = returnedResponseObject["status"] : undefined;
        }
        else {
            responseContext.response = { ...responseContext.response, ...returnedResponseObject.data };
            (returnedResponseObject["headers"] && !responseContext.response["__headers"]) ? responseContext.response["__headers"] = returnedResponseObject["headers"] : undefined;
            (returnedResponseObject["status"] && !responseContext.response["__status"]) ? responseContext.response["__status"] = returnedResponseObject["status"] : undefined;
        }
    }

    private requestMethod(request: IRequest, requestCanBeLoggedByName: boolean) {
        const dory = this.context.GetItemSingle<Dory>(Dory.ContextName)!;
        HTTPHeader.createDefaultHeaders(dory as unknown as IDory, request);
        const instance = new Promise<INetworkResponse<string | object>>(function (resolve, reject) {
            let network: INetwork = dory.shellConfigs.network!;
            const promHandler = (prom: Promise<INetworkResponse<string | object>>) => {
                prom.then(resolve).catch(reject);
                return prom;
            };
            switch (request.http.toUpperCase()) {
                case "GET": promHandler(network.get({ url: request.url, urlParams: request.data, headers: request.headers, options: { disableLoading: request.disableLoading ?? false, withCredentials: request.withCredentials ?? false, timeout: request.timeout }, requestCanBeLoggedByName })); break;
                case "POST": promHandler(network.post({ url: request.url, body: request.data, headers: request.headers, options: { disableLoading: request.disableLoading ?? false, withCredentials: request.withCredentials ?? false, timeout: request.timeout }, requestCanBeLoggedByName })); break;
                case "MULTIPART": promHandler(network.multipart({ url: request.url, body: request.data, headers: request.headers, options: { disableLoading: request.disableLoading ?? false, withCredentials: request.withCredentials ?? false, timeout: request.timeout }, requestCanBeLoggedByName })); break;
                case "PUT": promHandler(network.put({ url: request.url, body: request.data, headers: request.headers, options: { disableLoading: request.disableLoading ?? false, withCredentials: request.withCredentials ?? false, timeout: request.timeout }, requestCanBeLoggedByName })); break;
                case "POSTBLOB": promHandler(network.post({ url: request.url, config: { responseType: "blob" }, body: request.data, headers: request.headers, options: { disableLoading: request.disableLoading ?? false, withCredentials: request.withCredentials ?? false, timeout: request.timeout }, requestCanBeLoggedByName })); break;
                case "GETBLOB": promHandler(network.get({ url: request.url, urlParams: request.data, config: { responseType: "blob" }, headers: request.headers, options: { disableLoading: request.disableLoading ?? false, withCredentials: request.withCredentials ?? false, timeout: request.timeout }, requestCanBeLoggedByName })); break;
                case "DELETE": promHandler(network.delete({ url: request.url, body: request.data, headers: request.headers, options: { disableLoading: request.disableLoading ?? false, withCredentials: request.withCredentials ?? false, timeout: request.timeout }, requestCanBeLoggedByName })); break;
                case "PATCH": promHandler(network.patch({ url: request.url, body: request.data, urlParams: request.data, headers: request.headers, options: { disableLoading: request.disableLoading ?? false, withCredentials: request.withCredentials ?? false, timeout: request.timeout }, requestCanBeLoggedByName })); break;
            }
        });
        return instance;
    }
}