
import { ICookieManager } from "../../../../common/qshell";
import { ICookieValue } from "../../../../common/shrimp/interfaces/quick/ICookieAccess";


export class CookieManager implements ICookieManager{
    contextName: string;

    constructor({ contextName }: { contextName: string }) {
        this.contextName = contextName
    }

    setCookie?({ key, cookieValue }: { key: string, cookieValue: ICookieValue }) {
        var c_value = "";
        if(cookieValue.value) {
            c_value = escape(cookieValue.value);
        } else {
            var i, x, y, ARRcookies = document.cookie.split(";");
            for (i = 0; i < ARRcookies.length; i++) {
                x = ARRcookies[i].substr(0, ARRcookies[i].indexOf("="));
                y = ARRcookies[i].substr(ARRcookies[i].indexOf("=") + 1);
                x = x.replace(/^\s+|\s+$/g, "");
                if (x == key) {
                    c_value = unescape(y);
                    break;
                }
            }
        }
        if(c_value !== "") {
            var cookie = key + "=" + c_value + ";path=/;"
            if(cookieValue.expire) {
                if(typeof cookieValue.expire === "number") {
                    cookie += "Max-Age=" + cookieValue.expire + ";"
                }
                if( typeof cookieValue.expire === "string") {
                    cookie += "expires=" + new Date(cookieValue.expire).toUTCString() + ";"
                }
            }
            document.cookie = cookie;
        }       
    }

    clearCookies?(){
        var cookies = document.cookie.split(";");
        for (let i = 0; i < cookies.length; i++) {
            var key = cookies[i].split("=")[0];
            document.cookie = key +"=; Max-Age=-99999999;";
        }
    }

    getItemFromCookies?({ key }: { key: string }) {
        var i, x, y, ARRcookies = document.cookie.split(";");
        for (i = 0; i < ARRcookies.length; i++) {
            x = ARRcookies[i].substr(0, ARRcookies[i].indexOf("="));
            y = ARRcookies[i].substr(ARRcookies[i].indexOf("=") + 1);
            x = x.replace(/^\s+|\s+$/g, "");
            if (x == key) {
                return unescape(y);
            }
        }
        return undefined;
    }

    getAllCookies() {
        const retVal = document.cookie.split(";").reduce((prev, cookieItem) => {
            const eqIndex = cookieItem.indexOf("=");
            prev[cookieItem.substr(0, eqIndex)] = cookieItem.substr(eqIndex + 1);
            return prev;
        }, {} as Record<string, string>);

        return retVal;
    }


    removeItemFromCookies?({ key }: { key: string }) {
        document.cookie = key +"=;path=/;Max-Age=-99999999;";
    }

}