import { QShell } from "../../../../common/qshell";
import { ContextManager } from "../../../../common/shrimp/context";
import { ComparisonEnum, compDiffHelper } from "../../../../common/shrimp/helpers/compDiffHelper";
import { CType } from "../../../../common/shrimp/interfaces/ComponentInterfaces/ICompJson";
import { IComponent } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IComponent";
import { IComponentCollection } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IComponentInstanceTracker } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentInstanceTracker";
import { ICreatedComponent } from "../../../../common/shrimp/interfaces/ComponentInterfaces/ICreatedComponent";
import { IDictionary } from "../../../../common/shrimp/interfaces/IDictionary";
import { IContainerServicesWrapper } from "../../../../common/shrimp/interfaces/quick/IContainerServices";
import { CompGroupType, IWebSDKSettingsWrapper } from "../../../../common/shrimp/interfaces/quick/IWebSDK";
import { IDomElement } from "../../../../common/shrimp/interfaces/RenderingInterfaces/IDomElement";
import { VTextFieldFormattingExtensionData } from "../managers/extension/extensionData/VTextFieldFormattingExtensionData";
import { ExtensionManager } from "../managers/extension/ExtensionManager";
import { InstanceInjectionManager } from "../managers/extension/InstanceInjectionManager";
import { ComponentDOMAttrsHelper } from "./operators/ComponentDOMAttrsHelper";
import { StyleManager } from "../managers/style/StyleManager";
import { IHistoryManager } from "../../../../common/shrimp/interfaces/quick/IHistoryManager";
import { willFixMount } from "./memLeakFix";

export class ComponentInstanceTracker implements IComponentInstanceTracker {
    contextName: string = "sadsadsad";

    private servicesWrapper: IContainerServicesWrapper;
    private settingsWrapper: IWebSDKSettingsWrapper;
    private websdkContext: ContextManager;

    constructor(options: { servicesWrapper: IContainerServicesWrapper, settingsWrapper: IWebSDKSettingsWrapper, websdkContext: ContextManager }) {
        this.servicesWrapper = options.servicesWrapper;
        this.settingsWrapper = options.settingsWrapper;
        this.websdkContext = options.websdkContext;
    }



    TrackCompInstance(compData: { asChildType: CType | null, component: ICreatedComponent, compCollection: IComponentCollection, attrs: any, postRenderExecutor: (callBack: () => void) => void, props: IDictionary<any> }): void {
        this.newCompInstanceAndDomTracker(compData);
    }

    private newCompInstanceAndDomTracker({ asChildType, asChildName, component, compCollection, attrs, postRenderExecutor, props }:
        { asChildType: CType | null, asChildName?: string, component: ICreatedComponent, compCollection: IComponentCollection, attrs: any, postRenderExecutor: (callBack: () => void) => void, props: IDictionary<any> }) {


        compCollection.WatchCreatedFields(field => {
            const listeners = ComponentInstanceTracker.componentDOMListeners();
            for (const key in listeners) {
                if (Object.prototype.hasOwnProperty.call(listeners, key)) {
                    const listener = listeners[key];
                    listener(compCollection);
                }
            }
        });

        ComponentDOMAttrsHelper.createDOMAttrsOnCompInstance(compCollection);

        if (asChildType != CType.slot && asChildType != CType.scopedSlot) {
            ExtensionManager.ManageComponent(compCollection, attrs, this.settingsWrapper, this.websdkContext);
            InstanceInjectionManager.Manage(compCollection);
            return;
        }

        const vel = component.vel!;
        const compInstancePropName = "componentInstance";
        const compInstProp = Object.getOwnPropertyDescriptor(vel, compInstancePropName)!;
        let val = compInstProp.value;
        compInstProp.set = (value: IComponent) => {
            val = value;

            this.fixSlotsFromDefault(value)

            if (!willFixMount(compCollection)) {
                value.$mount(); // ?? This is a workaround for Vue 2.6.14 and below, where $mount() is needed to properly render the component.
            }

            if (component.compGroupType != CompGroupType.Chart) {
                compCollection.SetComponentCreatedFields({ createdComponent: { instance: value, el: value.$el, compGroupType: component.compGroupType } });
            }
            if (compCollection.additionals?.createdComp && compCollection.component) {
                compCollection.additionals.createdComp.$destroy();
                compCollection.additionals.createdComp = undefined;
            }
        };

        compInstProp.get = () => val;
        compInstProp.configurable = true;
        delete compInstProp.writable;
        delete compInstProp.value;
        Object.defineProperty(vel, compInstancePropName, compInstProp);

        if (compCollection.qjsonInfo.ver && compCollection.qjsonInfo.ver > 1.0) {
            compCollection.WatchCreatedFields((field) => {
                if (!field.el) { return; }
                StyleManager.current(compCollection).applyStyle(compCollection);
            });
        }
        // Dom
        let domEl = vel.elm;
        Object.defineProperty(vel, "elm", {
            set: (value: IDomElement) => {
                domEl = value;
                domEl.id = compCollection.compId;
                compCollection.SetComponentCreatedFields({ createdComponent: { el: domEl, compGroupType: component.compGroupType } });
                domEl.setAttribute("q_UID", attrs?.["q_UID"]);
                this.componentDomLocator(compCollection, compCollection.component!, compCollection.asChildIndex, postRenderExecutor);
                ExtensionManager.UpdateComponent(compCollection, attrs, this.settingsWrapper!, this.websdkContext)
            }, get: () => domEl
        });
        ExtensionManager.ManageComponent(compCollection, attrs, this.settingsWrapper!, this.websdkContext);
        //InstanceInjectionManager.Manage(compCollection);

        compCollection.subscribeOnDestroy(() => {
            delete vel.elm;
            delete vel.componentInstance;
            vel.elm = domEl;
        });

    }

    /**
     * Look child component slotType and fix it to the right place where it's needed to be real place which can be found in vnode.data.slot
     * @param compInstance Component instance that will be looked on its children
     */
    private fixSlotsFromDefault(compInstance: IComponent) {
        for (const key in compInstance.$slots) {
            if (Object.prototype.hasOwnProperty.call(compInstance.$slots, key)) {
                const childNodeKey = compInstance.$slots[key];
                for (let index = childNodeKey?.length - 1; index >= 0; index--) {
                    const childNode = childNodeKey[index];
                    if (childNode.data.slot && key != childNode.data.slot) {
                        compInstance.$slots[childNode.data.slot] = compInstance.$slots[childNode.data.slot] || [];
                        compInstance.$slots[childNode.data.slot][index] = childNode;
                        childNodeKey.splice(index, 1);
                    }
                }
            }
        }
    }

    private componentDomLocator(compCollection: IComponentCollection, compInst: IComponent, index: number, postRenderExecutor: (callBack: () => void) => void) {
        //WebSDK'da domLocator OLMAMALI. Dom locator DesignTime ile ilgili, Runtime ile ilgili deil. Buras Qeditor -> Dory(componentInstanceTracker subscribe olunan bir array olmal) arasnda zlmeli.
        const compDomLocator = this.servicesWrapper.services.getCompDomLocator?.(compCollection.compType);
        if (!compDomLocator) {
            return;
        }
        postRenderExecutor(() => {
            const compDom = compDomLocator(compInst, index) || compInst.$el;
            compCollection.SetComponentCreatedFields({ createdComponent: { instance: compInst, el: compDom } });
        });
    }


    /**
     * Listen component dom changes on this method 
     * @returns key fields
     */
    static componentDOMListeners(): Record<string, (componentCollection: IComponentCollection) => void> {
        return {
            setAttribute: (componentCollection: IComponentCollection) => {
                if (!componentCollection.component || !componentCollection.component.$el) {
                    return;
                }
                const componentEl: HTMLElement = componentCollection.component.$el;
                let preventPaste = componentCollection.component.attrs$?.preventPaste ?? false;
                if (preventPaste) {
                    let inputComp = VTextFieldFormattingExtensionData.findInputDOM(componentCollection);
                    if (inputComp && inputComp.elm) {
                        inputComp.elm.onpaste = (e) => {
                            e.preventDefault();
                            return false;
                        };
                        inputComp.elm.ondrag = (e) => {
                            e.preventDefault();
                            return false;
                        };
                        inputComp.elm.ondrop = (e) => {
                            e.preventDefault();
                            return false;
                        }
                    }
                }
                if (!(componentEl as any).origSetAttribute) {
                    (componentEl as any).origSetAttribute = componentEl.setAttribute;
                }
                componentEl.setAttribute = (function (context, orig) {
                    return function () {
                        var args: any = Array.prototype.slice.call(arguments);
                        const compareComponent = compDiffHelper.componentValueList[componentCollection.compType];
                        let showUnchanged: boolean = false;

                        if (compareComponent && componentCollection.compareComponent) {
                            componentCollection.class = compDiffHelper.removeValueClass(componentCollection.class);
                            args[1] = compDiffHelper.removeValueClass(args[1]);
                            let comparedClass = "";
                            showUnchanged = (componentCollection.context.GetItemSingle('HistoryManager') as any as IHistoryManager)?.GetComponentByType("QLegend")?.component?.["_renderingProps"]?.props()?.showUnchanged ?? false;

                            const compareValue = componentCollection.parentCompCollection?.compType == "VDataTable" ? componentCollection.vDomElement?.data.props?.[ComparisonEnum.compareValueField] : componentCollection.component![ComparisonEnum.compareValueField];
                            if (showUnchanged || compareValue !== componentCollection.component![compareComponent.valueField]) {
                                comparedClass = compDiffHelper.getValueCompareClass({ value: componentCollection.component![compareComponent.valueField], compareValue: compareValue });
                                args[1] += " " + comparedClass;
                            }
                            if (compareComponent.applyClassName) {
                                (<any>componentCollection.component)[compareComponent.applyClassName] = comparedClass;
                            }
                        }
                        else if (args[1] && args[1].indexOf && args[1].indexOf(componentCollection.class) < 0 && args[0] == 'class') {
                            if (compDiffHelper.includeNoneValueClass(args[1])) {
                                showUnchanged = (componentCollection.context.GetItemSingle('HistoryManager') as any as IHistoryManager)?.GetComponentByType("QLegend")?.component?.["_renderingProps"]?.props()?.showUnchanged ?? false;
                                if (!showUnchanged) {
                                    args[1] = compDiffHelper.removeValueClass(args[1]);
                                }
                            }
                            args[1] += " " + componentCollection.class;
                        }

                        return orig?.apply(context, args);
                    }
                })(componentEl, (componentEl as any).origSetAttribute);
                componentCollection.subscribeOnDestroy(() => {
                    (componentEl as any).setAttribute = (componentEl as any).origSetAttribute;
                    delete (componentEl as any).origSetAttribute;
                })
            },
        }
    }
}
