import { IComponentCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IDictionary } from "../../../../shrimp/interfaces/IDictionary";
import { IGlobalsQS } from "../../../../shrimp/interfaces/Scripting/IGlobals";
import { Dory } from "../../Dory";
import { MessagingManager } from "../MessagingManager";

export class QSProxy {
    functionDict: IDictionary<Function> = {};
    CreateMethodProxy(func: Function) {
        this.functionDict.MethodProxy = func;
    }

    CreateAssignProxy(func: (fieldName: string, value: any) => void) {
        this.functionDict.AssignProxy = func;
    }

    CreateGetValueProxy(func: (fieldName: string) => any) {
        this.functionDict.GetValueProxy = func;
    }

    mountOnObject(mountObject: object) {
        mountObject["QSProxy"] = this.functionDict;
    }

}

export class QsProxyGenerator {
    constructor() {

    }
    generateProxies(globals: IGlobalsQS, messagingManager: MessagingManager, dory: Dory, ownerComponent?: IComponentCollection) {
        const self = this;

        let storeQSProxy = new QSProxy();
        storeQSProxy.CreateAssignProxy(globals.store.set);
        storeQSProxy.CreateGetValueProxy(globals.store.get);
        storeQSProxy.mountOnObject(globals.store);

        let mmOSProxy = new QSProxy();
        mmOSProxy.CreateMethodProxy((eventName: string, parameters: Array<any> | undefined) => messagingManager.Trigger({ eventName: eventName, parameters: parameters }));
        mmOSProxy.mountOnObject(globals.MM);

        /**
         * Get current shell objects from current Dory
         * Add them into globals as their name (dory.shellObjects)
         * Call shell custom method
         * 
         */

        for (let key in dory.shellConfigs.osGlobalObjects) {
            if (dory.shellConfigs.osGlobalObjects[key]) {
                let keyField = key;
                (function (field) {
                    self.globalProxyGenerator({ proxyObject: globals, objectDict: dory.shellConfigs.osGlobalObjects, fieldKey: field, ownerComp: ownerComponent });
                })(keyField)
            }
        }
    }
    globalProxyGenerator({ proxyObject, objectDict, fieldKey, ownerComp }: { proxyObject: IDictionary<any>, objectDict: IDictionary<any>, fieldKey: string, ownerComp?: IComponentCollection }) {
        if (proxyObject.hasOwnProperty(fieldKey)) {
            return;
        }
        let shellProxy = new QSProxy();
        proxyObject[fieldKey] = {};
        let proxyFunc = function (eventName: string, ...args: Array<any>) {
            if (objectDict[fieldKey][eventName] && objectDict[fieldKey][eventName].get == null) {
                return objectDict[fieldKey][eventName]({ ownerComp: ownerComp, parameters: args });
            }
            return null;
        };

        shellProxy.CreateAssignProxy((fieldName, value) => {
            const field = objectDict[fieldKey][fieldName];
            field?.set?.({ ownerComp: ownerComp, parameters: value });
        });

        shellProxy.CreateGetValueProxy((fieldName) => {
            const field = objectDict[fieldKey][fieldName];
            return field?.get?.();
        });
        shellProxy.CreateMethodProxy(proxyFunc);
        shellProxy.mountOnObject(proxyObject[fieldKey]);
    }

}