import { Download } from "../../../shrimp/helpers/download";
import { IDownloadRequest } from "../../../shrimp/interfaces/NetworkInterfaces/IRequest";

export class DownloadManager {

    generalDownload = new Download();

    RequestHandler(request: IDownloadRequest) {
        let formElement = document.createElement("form");
        formElement.method = request.http;
        formElement.action = request.url;
        formElement.target = "blank";
        document.body.appendChild(formElement);
        if (request.data) {
            Object.keys(request.data).map(val => {
                let formInput = document.createElement("input");
                formInput.value = request.data[val];
                formInput.name = val;
                formInput.type = "hidden";
                return formInput;
            }).forEach(formInput => formElement.appendChild(formInput));
        }
        formElement.submit();
        document.body.removeChild(formElement);
    }    
}