export class ObjectInjectionHelper {
    public static injectMethod(obj: Object, methodName: string, cb: Function, options: {override: Boolean} = {override : false}) {
        // debugger
        // if(!options.override){
        //     throw new Error ("This method already exists, you cannot override it.")
        // }
        Object.defineProperty(obj, methodName, {set: () => {}, get: () => cb})
        
    }

}