import { IQuickInputFormatOptions } from "../../externalInterfaces";
import { Context } from "../core/context";
import { ICompositor } from "./ICompositor";
import Big from 'big.js';

export class Decimal implements ICompositor {

    private numberValue: string = "";
    private _maskedValue : string = "";
    private _unmaskedValue: string = "";
    private _skipInputEvent: boolean = false;
    private _isMinValueSet: boolean = false;
    private _isPasted: boolean = false;
    private _enforceOnInput: boolean = true;

    constructor(
        private readonly context: Context,
    ) { }

    public compose(): void {
        
        const inputElm = this.context.inputElm;
        const options = this.context.options;
        const radixPointASCII = options.radixPoint ? options.radixPoint.charCodeAt(0): undefined;

        const valueProperty = inputElm instanceof HTMLInputElement ?    Object.getOwnPropertyDescriptor(HTMLInputElement.prototype, "value") : 
                                                                        Object.getOwnPropertyDescriptor(HTMLTextAreaElement.prototype, "value");

        {
            inputElm.setAttribute("inputmode", "decimal");
        }

        //value get set
        {

            Object.defineProperty(inputElm, "value", {
                get: () => valueProperty!.get!.call(inputElm),
                set:  v => {
                            let chechVal = v == this.numberValue ? true : false;
                            
                            if(this._isPasted){
                                v = this.numberValue;
                                this._isPasted = false;
                            }else{
                                v = this.changeValue(v, options); 
                            }
                            valueProperty!.set!.call(inputElm, v);  

                            if(!chechVal){
                                this._skipInputEvent = true;
                                inputElm.dispatchEvent(new Event('input', {'bubbles': true}));
                            }
                        }
            });
        }

        //rightAlign
        {
            (options.rightAlign == false) ? (inputElm.style.textAlign = "left") : (inputElm.style.textAlign = "right");
        }

        //placeholder
        {
            if(options.placeholder)
            {
                inputElm.placeholder = options.placeholder;
            }
        }

        //sadece saysal deerler
        {
            //Bunu keydown'a evir
            inputElm.addEventListener("keypress", event => {
                const target = event.target as HTMLInputElement | HTMLTextAreaElement;
                let keyboardEvent = event as KeyboardEvent;
                let ASCIICode = (keyboardEvent.which) ? keyboardEvent.which : keyboardEvent.keyCode
                //45 = -
                if (ASCIICode > 31 && (ASCIICode < 48 || ASCIICode > 57) && (radixPointASCII && ASCIICode != radixPointASCII) && ASCIICode != 45){
                    event.preventDefault();
                    return false;
                }
                //radixPoint
                else if(ASCIICode == radixPointASCII && options.radixPoint){
                        let radixPointIndex = target.value.indexOf(options.radixPoint);
                        if(radixPointIndex != -1 || target.selectionStart == 0){
                            target.setSelectionRange(radixPointIndex+1,radixPointIndex+1);
                            event.preventDefault();
                            return false;
                        }
                        target.value += options.radixPoint;
                        const cursorPos = radixPointIndex == -1 ? target.value.length : radixPointIndex;
                        target.setSelectionRange(cursorPos,cursorPos);
                        event.preventDefault();
                        return false;
                }
                //allowMinus
                else if(ASCIICode == 45){
                    if(options.allowMinus){
                    let negativeSymbol = target.value.indexOf('-');
                        if(negativeSymbol != -1){
                            target.setSelectionRange(negativeSymbol+1,negativeSymbol+1);
                            event.preventDefault();
                            return false;
                        }
                        target.value =  "-"+target.value;
                        negativeSymbol = target.value.indexOf('-');
                        const cursorPos = negativeSymbol == -1 ? target.value.length : negativeSymbol;
                        target.setSelectionRange(cursorPos+1,cursorPos+1);
                        event.preventDefault();
                        return false;
                    }
                    else{
                        event.preventDefault();
                        return false;
                    }
                }
                return true;
            });
        }

        //input kontrol ve formatlama
        {
            inputElm.addEventListener("input", event => {
                const target = event.target as HTMLInputElement | HTMLTextAreaElement;

                if(this._isMinValueSet && this._skipInputEvent){
                    this.setMinValueOnBlur(options, target);
                    this._isMinValueSet = false;
                    this._skipInputEvent = false;
                    return event.preventDefault();
                }
                
                let cursorPos = target.selectionStart!;
                let originalLength = target.value.length;

                let val = this.formatValue(target.value, options);

                if((event as InputEvent).inputType == 'deleteContentBackward'){
                    if(val[cursorPos] == options.groupSeparator){
                        val = val.slice(0, cursorPos-1).concat(val.slice(cursorPos+1, val.length))
                    }                    
                }

                if((event as InputEvent).inputType == 'deleteContentForward'){
                    if(val[cursorPos] == options.groupSeparator){
                        val = val.slice(0, cursorPos).concat(val.slice(cursorPos+2, val.length))
                    }                    
                }
                
                val = this.formatValue(val, options);
                let radixPos = -1;
                
                this._enforceOnInput = false;

                target.value = val;
                // if(inputElm._value)

                if(options.radixPoint) radixPos = val.indexOf(options.radixPoint);
                let updatedLength = val.length;
                cursorPos = updatedLength - originalLength + cursorPos;
                cursorPos > radixPos ? cursorPos+=1 : cursorPos;
                target.setSelectionRange(cursorPos, cursorPos);
            }, true);
        }

        {
            inputElm.addEventListener("focus", event => {
                this._enforceOnInput = false;
                if(options.selectAllOnClick){
                    inputElm.select();
                }
            });
        }

        {
            inputElm.addEventListener("focusout", event => {
                const target = event.target as HTMLInputElement | HTMLTextAreaElement;
                this.setMinValueOnBlur(options, target);
            });
        }
        

        //blur event --> min, enforceDigitsOnBlur, radixPoint position
        {
            inputElm.addEventListener("blur", event => {
                const target = event.target as HTMLInputElement | HTMLTextAreaElement;
                this.setMinValueOnBlur(options, target);
            });
        }

        //paste event
        {
            inputElm.addEventListener("paste", event => {
                event.preventDefault();
                event.stopPropagation();

                const target = event.target as HTMLInputElement | HTMLTextAreaElement;
                let clipBoardEvent = event as ClipboardEvent ;
                
                if(!options.preventPaste && clipBoardEvent.clipboardData){
                    let pastedVal = clipBoardEvent.clipboardData.getData('text');
                    if(this.validate(pastedVal, options)){
                        let val = this.formatValue(pastedVal, options);
                        target.value = val;
                        this._isPasted = true;
                    }
                }
            });
        }
    }

    private setMinValueOnBlur(options: IQuickInputFormatOptions, target: HTMLInputElement | HTMLTextAreaElement){
        let valueChanged = false;
        let inpValue = target.value;
        if( options.min){
            let isTrue = this.compareNumbers(this.convertToNumber(options.min.toString(), options), this.convertToNumber(inpValue, options))
            if(isTrue){
                inpValue = this.formatValue(options.min.toString(), options);
                this.numberValue = inpValue;
                valueChanged = true;
            }
        }
        if(options.enforceDigitsOnBlur){
            this._enforceOnInput = true;
            inpValue = this.alignDigits(inpValue, options);
            target.value = inpValue;
            // this.numberValue = inpValue;
            // valueChanged = true;
        }
        // if(valueChanged){
        //     target.value = inpValue;
        //     this._isMinValueSet = true;
        // }
    }

    private changeValue(val: string, options: IQuickInputFormatOptions): string{
        val = this.formatValue(val, options);
        if(this._enforceOnInput && options.enforceDigitsOnBlur){
            val = this.alignDigits(val, options);
        }
        return val;
    }


    private formatValue(val: string, options: IQuickInputFormatOptions): string {
        // if(val == this.numberValue){
        //     return this.numberValue;
        // }
        let leftSide = "", rightSide = "";
        let radixPos = -1;
        
        let numbValue = new Big(this.convertToNumber(val, options));

        if(!((Big(0).eq(numbValue)) || (Big(-1).lt(numbValue) && Big(numbValue).lt(0)) || (Big(0).lt(numbValue) && Big(numbValue).lt(1)))) {
            val = this.deleteLeadingZero(val, options);
        }
        
        val = this.deleteBeforeNegativeSymbol(val, options);
        const thousands = /\B(?=(\d{3})+(?!\d))/g;
        if(options.groupSeparator){
            val = val.replaceAll(options.groupSeparator, '');
            if(options.radixPoint){
                radixPos = val.indexOf(options.radixPoint);
                if(radixPos >= 0){
                    leftSide = val.substring(0, radixPos).replaceAll(thousands, options.groupSeparator);
                    if(Big(0).eq(this.convertToNumber(leftSide, options))){
                        leftSide = "0";
                    }
                    rightSide = val.substring(radixPos+1);
                    if(options.digits){
                        rightSide = rightSide.slice(0, Number(options.digits));
                    }
                    val = leftSide +options.radixPoint+ rightSide;
                    
                }else{
                    val = val.replaceAll(thousands, options.groupSeparator);
                }
            }else{
                val = val.replaceAll(thousands, options.groupSeparator);
            }         
        }
        
        if(options.max ){
            let isTrue = this.compareNumbers(this.convertToNumber(val, options), this.convertToNumber(options.max.toString(), options))
            if(isTrue)
            return this.numberValue;
        }
        this.numberValue = val;
        return val;
    }

    private compareNumbers(value1: string, value2: string): boolean {

        let big1 = new Big(value1);
        
        let result = big1.gt(value2);
        return result;
    }

    public getUnmaskedValue(): string {
        return this.unMask(this.numberValue, this.context.options);
    }

    private unMask(maskedValue: string, options: IQuickInputFormatOptions): string{
        if(options.groupSeparator){
            maskedValue = maskedValue.replaceAll(options.groupSeparator, "");
        }
        return maskedValue;
    }

    private validate(value: string, options: IQuickInputFormatOptions): boolean{
        let groupSeparator = '\\' + options.groupSeparator;
        let radixPoint = '\\' + options.radixPoint;
        let validateRegex = new RegExp("^[-]?(([1-9]\\d{0,2})("+groupSeparator+"\\d{3})*|(\\d*))(" +radixPoint+ "\\d+)?$", 'gi');

        return validateRegex.test(value);
    }

    private convertToNumber(maskedValue: string, options: IQuickInputFormatOptions): string{
        if(maskedValue && maskedValue != ''){
            if(options.groupSeparator){
                maskedValue = maskedValue.replaceAll(options.groupSeparator, '');
            }
            if(options.radixPoint && options.radixPoint != '.'){
                maskedValue = maskedValue.replace(options.radixPoint, '.');
            }

            return maskedValue;
        }
        
        return "0";
    }

    private alignDigits(val: string, options: IQuickInputFormatOptions) : string{
        if(!val || val == ""){
            val = "0";
        }
        let buffer = val.split('');
        if(options.radixPoint && options.digits){
            if (buffer.length > 0 && Number(options.digits) > 0) {
                let radixPosition = buffer.indexOf(options.radixPoint);
        
                if (radixPosition === -1) {
                    buffer.push(options.radixPoint);
                    radixPosition = buffer.length - 1;
                }
                for (var i = 1; i <= Number(options.digits); i++) {
                    if (!isFinite(Number(buffer[radixPosition + i]))) {
                        buffer[radixPosition + i] = "0";
                    }
                }
            }
        }
        return buffer.join('');
    }

    private deleteLeadingZero(val:string, options: IQuickInputFormatOptions):string{
        let isSymbolDeleted = false;
        if(options.allowMinus){
            let negativeSymbolPos = val.indexOf('-');
            if(negativeSymbolPos != -1) 
            {
                isSymbolDeleted = true;
                val = val.substring(negativeSymbolPos+1);
            }
        }

        val = val.replace(/^0+/, '');
        if(val.length == 0) val = '0';
        if(isSymbolDeleted){
            val = '-'+val
        }
        return val;
    }

    private deleteBeforeNegativeSymbol(val: string, options: IQuickInputFormatOptions):string{
        if( options.allowMinus){
            let negativeSymbolPos = val.indexOf('-');
            val = val.substring(negativeSymbolPos);
        }
        return val;
    }
}