import { ILoadingOperator } from "../../../../../common/shrimp/interfaces/RenderingInterfaces/Operators/ILoadingOperator";
import { StoreManager } from "../StoreManager";

export class LoadingOperator implements ILoadingOperator {

    constructor(private readonly storeManager: StoreManager) { }

    public showLoading(visible: boolean): void {

        let loadingCounter: number = this.storeManager.get("loadingCounter") || 0;                         
        this.storeManager.set("loadingCounter", visible ? loadingCounter + 1 : 0 );

    }

}