export type TokenType = (typeof Token.Types)[keyof typeof Token.Types];

export interface IPosition {
    line: number;
    column: number;
}

export class Token {
    public static reservedIdentifierTypes = {
        response: "native",
        data: "native",
        "true": "boolean",
        "false": "boolean",
        "null": "null"
    };
    public static Types = {
        identifier: "identifier",
        keyword: "keyword",
        string: "string",
        number: "number",
        operator: "operator"
    } as const;

    public type: TokenType;
    public value: any;
    public position: IPosition;
    public endPosition?: IPosition;
    constructor(type: TokenType, value: any, position: IPosition, endPosition?: IPosition) {
        this.type = type;
        this.value = value;
        this.position = position;
        this.endPosition = endPosition;
    }

    public Summarize() {
        var pos = this.position == null ? " @?" : " @line:" + this.position.line + "-column:" + this.position.column;
        pos += this.endPosition != null ? " to: @line" + this.endPosition.line + "-column:" + this.endPosition.column : "";
        return "type: " + this.type + " value: " + this.value + pos;
    }
}