import { IComponentCollection } from "../../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";

export class ComponentDOMAttrsHelper {

    /**
     * List of components that needs to set on the DOM 
     */
    static componentDOMAttributes: Record<string, { [tag: string]: boolean }> =
        {
            VCombobox: {
                step: true,
                preventPaste: true,
                tabindex: true,
                avoidClearableTabIndex: true
            },
            VTextField: {
                maxlength: true,
                tabindex: true,
                step: true,
                preventPaste: true,
                avoidClearableTabIndex: true,
                name: true
            },
            VSelect: {
                tabindex: true
            },
            VBtn: {
                tabindex: true
            },
            VAutocomplete:{
                maxlength: true,
                tabindex: true,
                avoidClearableTabIndex: true
            },
            VImg: {
                src: true
            },
            QImgZoom: {
                src: true
            },
            QAnimation: {
                source: true
            },
            VTextarea: {
                maxlength: true,
                tabindex: true,
                preventPaste: true
            },
            VForm: {
                method: true,
                action: true
            },
            VFileInput:{
                accept: true,
                tabindex: true
            }            
        };

    /**
     * Create HTML attributes as property on component instance.
     * Listen changes on these properties to set attributes on the real field and mount the component again.
     * @param compCollection Component collection that attributes will be set on its instance
     */
    static createDOMAttrsOnCompInstance(compCollection: IComponentCollection) {
        const compAttrs = ComponentDOMAttrsHelper.componentDOMAttributes;
        if (compAttrs[compCollection.compType]) {

            compCollection.WatchCreatedFields(field => {
                if (field.instance) {
                    Object.keys(compAttrs[compCollection.compType]).forEach(attribute => {
                        let attrValue;
                        if (!Object.getOwnPropertyDescriptor(field.instance, attribute)) {
                            switch(attribute) {
                                case 'avoidClearableTabIndex':
                                    if (compCollection.component?.$attrs?.avoidClearableTabIndex) {
                                        this.AvoidClearableTabIndex(compCollection);
                                    }
                                    break;
                                case 'src':
                                    attrValue = field.instance!['src'];
                                    Object.defineProperty(field.instance, attribute, {
                                        get: () => { return attrValue },
                                        set: value => {
                                            attrValue = value.includes('data:image/tiff') ? ComponentDOMAttrsHelper.convertTiffToImage(value) :  value;
                                            if(field.instance!.$props) {
                                                field.instance!.$props[attribute] = attrValue;
                                                field.instance!['currentSrc'] = attrValue;
                                            }
                                            field.instance?.$mount();
                                        }
                                    });
                                    break;
                                default:
                                    Object.defineProperty(field.instance, attribute, {
                                        get: () => { return attrValue },
                                        set: value => {
                                            attrValue = value;
                                            field.instance!.attrs$ = field.instance!.attrs$ || {};
                                            field.instance!.$attrs = field.instance!.attrs$ || { [attribute]: value };
                                            field.instance!.attrs$[attribute] = value;
                                            field.instance?.$mount()
                                        }
                                    });
                                    break;
                            }
                        }
                    })
                }
            })

        }

    }

    public static convertTiffToImage(attribute: string) {
        const UTIF  = require("utif");
        
        const base64ToArrayBuffer = (base64: any) => {
            var binary_string = window.atob(base64);
            var len = binary_string.length;
            var bytes = new Uint8Array(len);
            for (var i = 0; i < len; i++) {
                bytes[i] = binary_string.charCodeAt(i);
            }
            return bytes;
        };

        let data = attribute.replace(/^data:image\/\w+;base64,/, "");
        let buffer_data = base64ToArrayBuffer(data);

        const ifds = UTIF.decode(buffer_data);
        UTIF.decodeImage(buffer_data, ifds[0]);
        const rgba = UTIF.toRGBA8(ifds[0]);
        const widht = ifds[0].width;
        const height = ifds[0].height;
        const canvas = document.createElement('canvas');
        canvas.width = widht;
        canvas.height = height;
        const ctx = canvas.getContext("2d");
        const imgData = new ImageData(
          new Uint8ClampedArray(rgba.buffer),
          widht,
          height
        );
        ctx?.putImageData(imgData, 0, 0);
        
        return canvas.toDataURL();
    }

    static AvoidClearableTabIndex(compCollection) {
        let textFieldBtnDom = compCollection.component?.$el?.querySelector('button');

        if (textFieldBtnDom?.getAttribute('aria-label') == 'clear icon') {
            textFieldBtnDom.setAttribute('tabindex', '-1')
        }


    }
}