import { IClientDBConnection, IClientDB } from "@stechquick/algae/lib/helpers/clientDB/IClientDB";
import { ClientDB } from "@stechquick/algae/lib/helpers/clientDB/clientDB";
import { IPermanentStoreObject,Plateau_UI_PermanentStore_Name_Prefix } from "../../../../common/shrimp/interfaces/RenderingInterfaces/Operators/IPermanentStoreOperator";
import { LogType, Logger } from "../../../../common/shrimp/helpers/logger";

const plateauKvpStoreName = "Plateau_UI_Kvp";

export class PermanentStoreManager {

  private static permanentDbName = "";

  public static changeDb(dbName: string) {
    PermanentStoreManager.permanentDbName = Plateau_UI_PermanentStore_Name_Prefix + dbName;
  }

  public async deleteDb(dbName: string) {
    const clientDB = this.createDbClient();
    clientDB.deleteDB(Plateau_UI_PermanentStore_Name_Prefix + dbName);
  }
  
  public static getCurrentStoreName() {
   return PermanentStoreManager.permanentDbName.substring(Plateau_UI_PermanentStore_Name_Prefix.length);
  }

  private dbConnection: IClientDBConnection | null = null;


  private createDbClient() {
    const clientDB: IClientDB = new ClientDB({
      log: (logItem) => {
        let logType: LogType;

        switch (logItem.level) {
          case "log": logType = LogType.Trace; break;
          case "debug": logType = LogType.Debug; break;
          case "info": logType = LogType.Trace; break;
          case "warning": logType = LogType.Warning; break;
          case "error": logType = LogType.Error; break;
        }

        Logger.Log({ logMessage: logItem.message, logType: logType, error: logItem.error, additional: logItem.additionals });

      }
    });
    return clientDB;
  }

  private async openDB(): Promise<IClientDBConnection> {

    const clientDB = this.createDbClient();

    const connection = await clientDB.openDB(PermanentStoreManager.permanentDbName, 1, {
      upgradeNeeded: async (upgrader, versionInfo) => {
        // Customize your database upgrade logic here if needed.
        switch (versionInfo.old) {
          case 0: // 0 > 1
            upgrader.createStore<IPermanentStoreObject<unknown>>(plateauKvpStoreName, { keyPath: "key" });
          case 1: // 1 > 2
          // versiyon 2 'ye geildiinde buras doldurulacak.
        }
      },
    });
    return connection;
  }

 

  async insert(object: IPermanentStoreObject<unknown>): Promise<void> {

    this.dbConnection = await this.openDB();

    await this.dbConnection.scope(plateauKvpStoreName, "readwrite", async (tx) => {
      const store = tx.getStore<IPermanentStoreObject<unknown>>();
      await store.insert(object, "upsert");
    });

  }


  async get(key: string): Promise<IPermanentStoreObject<unknown> | undefined> {
    this.dbConnection = await this.openDB();

    const result = await this.dbConnection.scope(plateauKvpStoreName, "readonly", async (tx) => {
      const store = tx.getStore<IPermanentStoreObject<unknown>>();
      return await store.query(key, { type: "single" });
    });
    return result;
  }

  async delete(key: string): Promise<void> {
    this.dbConnection = await this.openDB();

    await this.dbConnection.scope(plateauKvpStoreName, "readwrite", async (tx) => {
      const store = tx.getStore<IPermanentStoreObject<unknown>>();
      await store.delete(key);
    });
  }




  async list(): Promise<IPermanentStoreObject<unknown>[]> {
    this.dbConnection = await this.openDB();

    const result = await this.dbConnection.scope(plateauKvpStoreName, "readonly", async (tx) => {
      const store = tx.getStore<IPermanentStoreObject<unknown>>();
      return await store.allRecords();
    });
    return result;
  }



}