export interface IJSLoaderOptions {
    charset?: string;
    scriptTagProps?: Record<string, any>;
}
export class JSLoader {
    JSName: string
    SuccessCB: Function | undefined;
    FailCB: Function
    TryCount: number
    noCache: boolean;
    cacheVersion: number;
    options: IJSLoaderOptions;

    constructor(jsName: string, successCb: Function | undefined, failCb: Function, tryCount: number, noCache = false, cacheVersion = 0, options = {}) {
        this.JSName = jsName;
        this.SuccessCB = successCb;
        this.FailCB = failCb;
        this.TryCount = tryCount;
        this.noCache = noCache;
        this.cacheVersion = cacheVersion;
        this.options = options;
    }

    public LoadJS(type?: string) {
        var cbObject = this.createCallBackObject(this.JSName, this.SuccessCB, this.FailCB, this.TryCount, this.noCache, this.cacheVersion, this.options);
        cbObject.goLoad(type);
    }

    private createCallBackObject(jsName: string, successCb: Function | undefined, failCb: Function, tryCount: number, noCache: boolean, cacheVersion: number, options: IJSLoaderOptions) {
        let retryLeft: number = tryCount;
        let scriptElem: HTMLScriptElement;
        let isJSLoaded: boolean = false;
        let interval: number = 100;
        const isBrowser = !!(typeof window !== 'undefined' && window.document);
        const readyRegExp = isBrowser ? /^complete$/ : /^(complete|loaded)$/;

        var cbObject = {
            retryLogic(isLoaded: boolean) {
                isJSLoaded = isLoaded;
                retryLeft--;
                if (isJSLoaded || retryLeft < 1) {
                    return;
                }
                scriptElem!.parentElement!.removeChild(scriptElem);
                interval *= 3;
                setTimeout(function () { cbObject.goLoad(); }, interval);
            },
            onScriptLoad(evt: Event) {
                cbObject.retryLogic(true);
                var node = evt.currentTarget || evt.srcElement;
                if (evt.type === 'load' || (readyRegExp.test((<any>node!).readyState))) {
                    cbObject.clearListeners(evt);
                    if (successCb) {
                        successCb();
                    }
                }
            },
            onScriptError(evt: Event) {
                cbObject.clearListeners(evt);
                cbObject.retryLogic(false);
                if (failCb) {
                    failCb(evt);
                }
            },
            addListeners() {
                scriptElem.addEventListener('load', cbObject.onScriptLoad, false);
                scriptElem.addEventListener('error', cbObject.onScriptError, false);
            },
            clearListeners(evt: Event) {
                var node = evt.currentTarget || evt.srcElement;
                node!.removeEventListener('load', cbObject.onScriptLoad, false);
                node!.removeEventListener('error', cbObject.onScriptError, false);
            },
            goLoad(type?: string) {
                scriptElem = document!.createElement('script');
                cbObject.addListeners();

                scriptElem.src = noCache ?  jsName +  "?v=" + cacheVersion : jsName;;
                scriptElem.type = type || "text/javascript";
                cbObject.setTagProps();
                if (options.charset) {
                    scriptElem.charset = options.charset;
                }
                document.head.appendChild(scriptElem);
            },
            setTagProps() {
                if (!options.scriptTagProps) { return; }
                var props = options.scriptTagProps;
                Object.keys(props).reduce((prev, curKey) => { (prev as any)[curKey] = props[curKey]; return prev; }, scriptElem);
            }
        };

        return cbObject;

    };
}