import { ContextManager, IContextItem } from "../../../shrimp/context";
import { IHistoryManager } from "../../../shrimp/interfaces/quick/IHistoryManager";
import { INavigationDemand, INavigationDemandType } from "../../../shrimp/interfaces/quick/INavigationDemand";
import { RenderingComponent } from "../Components/ContainerComponents/RenderingComponent";
import { DoryLikeData } from "../Model/History/DoryLikeData";
import { HistoryItem } from "../Model/History/HistoryItem";


export class HistoryManager implements IHistoryManager, IContextItem {
    static ContextName: string = "HistoryManager";

    contextName: string = HistoryManager.ContextName;

    HistoryLevels: Array<HistoryLevel> = [];
    CurrentLevelIndex = 0;
    context: ContextManager;
    doryLike: DoryLikeData;
    private rootHMCache?: HistoryManager;

    constructor(context: ContextManager, doryLike: DoryLikeData) {
        this.context = context;
        this.ClearHistory();
        this.doryLike = doryLike;
        // this.OverrideBrowserBackEvent();
    }

    GetCreaterComp() {
        return this.doryLike.GetCreaterComp();
    }

    currentLevel() {
        return this.HistoryLevels[this.CurrentLevelIndex];
    }

    addLevel() {
        ++this.CurrentLevelIndex;
        this.HistoryLevels[this.CurrentLevelIndex] = new HistoryLevel();
    }

    isCurrentlyOnLightbox() {
        return this.CurrentLevelIndex !== 0;
    }

    dropLevel() {
        delete this.HistoryLevels[this.CurrentLevelIndex];
        this.CurrentLevelIndex--;
        return this.currentLevel().last();
    }

    /**
     * @param callback 
     * @param traverseBeginLevelIndex
     * traverse from this level downwards or null for all levels
     */
    traverseLevelsUpwards(callback: Function, traverseBeginLevelIndex: number) {
        for (var i = (traverseBeginLevelIndex || this.CurrentLevelIndex); i >= 0; i--) {
            var currentLevelHistoryItems = this.HistoryLevels[i].historyItems();
            var retVal = callback(currentLevelHistoryItems[currentLevelHistoryItems.length - 1]);
            if (!retVal) {
                break;
            }
        }
    }

    getLastHistoryOnLevel(levelIndex: number) {
        if (levelIndex === null || levelIndex === undefined || !this.HistoryLevels[levelIndex]) {
            return null;
        }
        return this.HistoryLevels[levelIndex].last();
    }

    AddTempHistory_delete(historyItem: HistoryItem) {
        this.currentLevel().historyItems().push(historyItem);
    }

    RemoveTempHistory_delete() {
        this.currentLevel().historyItems().splice(-1);
    }

    /**
     * * Add a history item for back support
     * @param historyItem: Item to be added to history
     * type="HistoryItem"
     * value="new HistoryItem()"
     */
    AddHistory(historyItem: HistoryItem) {
        const historyLevel = this.currentLevel();
        const oldHistoryItem = historyLevel.previous();
        if (oldHistoryItem) {
            oldHistoryItem.hibernate();
        }
        historyLevel.addHistory(historyItem);
    };


    Forward(options: INavigationDemand) {

        const historyItems = this.currentLevel().forward(options);
        if (!historyItems?.new) {
            return;
        }

        historyItems.old.forEach(historyItem => {
            historyItem.hibernate();
        });
        historyItems.new.resurrect();
        this.doryLike.OwnerDoryLike.CallRender(historyItems.new, historyItems.old, INavigationDemandType.forward);
    };


    Back(options: INavigationDemand) {

        var historyItems = this.currentLevel().back(options);
        if (!historyItems?.new) {
            return;
        }
        historyItems.old.forEach(historyItem => {
            historyItem.hibernate();
        });
        historyItems.new.resurrect();

        this.doryLike.OwnerDoryLike.CallRender(historyItems.new, historyItems.old, INavigationDemandType.back);
    };

    /**
     * @param typeName GetControlWithTypeName
     * @param levelIndex Which index of level will be searched. undefined for CurrentLevel
     * @returns ComponentCollection | null
     */
    GetComponentByType(typeName: string, levelIndex?: number) {
        levelIndex = levelIndex || this.CurrentLevelIndex;
        let lastLevel = this.getLastHistoryOnLevel(levelIndex);

        if (!lastLevel) {
            return null;
        }

        let component = lastLevel.GetControlWithTypeName(typeName);
        if (!component) {
            return null;
        }

        return component;
    }

    ClearHistory() {
        const curLevel = this.currentLevel();
        this.HistoryLevels = new Array<HistoryLevel>();
        this.CurrentLevelIndex = 0;
        this.addLevel();
        curLevel?.destroy();
    }

    checkIfHistoryExist(options: INavigationDemand) {
        return this.currentLevel().checkIfHistoryExist(options).historyExist;
    }

    hasHistory(pageId: string): INavigationDemand | null {
        let retVal = this.searchTargetHistoryManager({ pageId });
        if (!retVal) {
            return this.getNavDemand(pageId);
        }
        return retVal.ownerHistoryManager.getNavDemand(retVal.pageId ? retVal.pageId : pageId);

    }



    public searchTargetHistoryManager({ pageId }: { pageId: string }): { ownerHistoryManager: IHistoryManager, pageId: string } | null {
        let pageIdArray = pageId?.split('.');

        if (pageId && pageIdArray && this.currentLevel().last()?.pageId != pageIdArray[0]) {
            //back is on root or rshell
            return { ownerHistoryManager: this, pageId };
        }

        const renderingComponent = <RenderingComponent | null | undefined>this.GetComponentByType(RenderingComponent.ComponentName)?.component;
        if (!renderingComponent) {
            //rShell will return from here.
            return { ownerHistoryManager: this, pageId };
        }

        let newPageIdString = pageId;
        if (pageId && pageIdArray && pageIdArray[1]) {
            //browser back
            let firstPageId = pageIdArray[0] + '.'
            newPageIdString = pageId.replace(firstPageId, '');
            return renderingComponent.searchTargetHistoryManager(newPageIdString) || null;
        }
        return null;
    }

    getNavDemand(pageId: string): INavigationDemand | null {
        let navigationDemand: INavigationDemand | null = null;

        const historyItems = this.HistoryLevels[this.CurrentLevelIndex].historyItems();
        const futureItems = this.HistoryLevels[this.CurrentLevelIndex].futureItems();


        for (let index = historyItems.length - 1; index >= 0; index--) {
            const curItem = historyItems[index];
            if (curItem.pageId == pageId) {
                navigationDemand = { pageId, pageName: curItem.pageName, navigationDemandType: INavigationDemandType.back, step: historyItems.length - index - 1, ownerHistoryManager: this }; //when calculating step extract current historyItem (-1)
                break;
            }
        }
        if (!navigationDemand) {
            for (let index = futureItems.length -1; index >= 0; index--) {
                if (index != futureItems.length && futureItems[index].pageId == pageId) {
                    navigationDemand = { pageId, pageName: futureItems[index].pageName, navigationDemandType: INavigationDemandType.forward, step: futureItems.length - index, ownerHistoryManager: this };
                    break;
                }
            }
        }
        return navigationDemand
    }

    public getCreaterRenderingComponent() {
        return this.doryLike.GetCreaterRenderingComponent();
    }
    public getRootHM(): HistoryManager {
        if (!this.rootHMCache) {
            const creator = this.doryLike.GetCreatorComponent();
            this.rootHMCache = creator ? creator.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)!.getRootHM() : this;
        }
        return this.rootHMCache;
    }

    public GetCompUIDPrefix() {
        return this.doryLike.GetCompUIDPrefix();
    }

    static traverseUpwards(retVal: Array<string>, historyItem: HistoryItem | null | undefined):Array<string> {
        if (!historyItem) {
            return retVal;
        }
        retVal = retVal ?? [];
        if (historyItem.pageName) {
            retVal.push(historyItem.pageName);
        }
        const ownerComp = historyItem.getOwnerHistoryManager()?.doryLike.GetCreaterComp();
        if (!ownerComp) {
            return retVal;
        }
        const upwardHistoryItem = ownerComp.context.GetItemSingle<HistoryManager>("HistoryManager")?.currentLevel().last();
        return HistoryManager.traverseUpwards(retVal, upwardHistoryItem);
    }
}

class HistoryLevel {

    private HistoryItems: Array<HistoryItem>;
    private FutureItems: Array<HistoryItem>;
    private MomentaryItem?: HistoryItem;

    constructor() {
        this.HistoryItems = [];
        this.FutureItems = [];
    }

    /**
     * clear future on every new action
     * @param historyItem 
     */
    addHistory(historyItem: HistoryItem) {
        this.destroyer(this.FutureItems);
        this.FutureItems = [];
        this.HistoryItems.push(historyItem);
        delete this.MomentaryItem;
    };

    destroy() {
        this.destroyer(this.FutureItems);
        this.destroyer(this.HistoryItems);
    }


    historyItems() {
        return this.HistoryItems;
    };

    futureItems() {
        return this.FutureItems;
    };

    last() {
        if (this.MomentaryItem) {
            return this.MomentaryItem
        }
        if (this.HistoryItems.length < 1) {
            return null;
        }
        return this.HistoryItems[this.HistoryItems.length - 1];
    };

    setMomentaryItem(momentaryItem: HistoryItem) {
        this.MomentaryItem = momentaryItem;
    }


    previous() {
        var offset = (!this.MomentaryItem) ? 2 : 1;

        if (this.HistoryItems.length < offset) {
            return null;
        }
        return this.HistoryItems[this.HistoryItems.length - offset];
    };

    checkIfHistoryExist(options: INavigationDemand) {
        if (options.externalHistoryRouter) { //#legacyCode : this if is for rallyUi Quick 1.0
            return { historyExist: true };
        }

        options.step = options.step ? options.step : 1;
        if (options.navigationDemandType == INavigationDemandType.back) {
            if (this.HistoryItems.length > Math.abs(options.step)) {
                return { historyExist: true, step: options.step };
            }
        } else {
            if (this.FutureItems.length >= options.step) {
                return { historyExist: true, step: options.step };
            }
        }

        // //below code is for checking if it is coming from doryJr. 
        // let doryLikeData = (<HistoryManager><unknown>options.ownerHistoryManager).doryLike;
        // if (doryLikeData.OwnerDoryLike.contextName == "DoryJr") {
        //     let ownerHistoryManagerStep =
        //         options.step - (options.navigationDemandType == INavigationDemandType.back ? (this.HistoryItems.length-1) : (this.FutureItems.length));

        //     const ownerHistoryManager = <HistoryManager><unknown>doryLikeData.GetCreaterComponent()?.context?.GetItemSingle(HistoryManager.ContextName);
        //     if (ownerHistoryManager) {
        //         (ownerHistoryManager)?.Back({
        //             ownerHistoryManager: (ownerHistoryManager),
        //             step: ownerHistoryManagerStep,
        //             navigationDemandType: options.navigationDemandType
        //         });
        //     }
        //     return { historyExist: true, step: this.HistoryItems.length };

        // }
        return { historyExist: false };
    }

    back(options: INavigationDemand) {
        options.navigationDemandType = options.navigationDemandType ?? INavigationDemandType.back
        let retVal = this.checkIfHistoryExist(options);
        if (!retVal.historyExist) {
            return undefined;
        }
        const step = options.step ? (options.step == retVal.step ? options.step : retVal.step) : 1;

        let oldItems: Array<HistoryItem> = [];

        for (let index = 0; index < Math.abs(step!); index++) {
            let currentItem = this.HistoryItems.pop();
            if (!currentItem) { throw new Error('abcdeb'); }

            this.FutureItems.push(currentItem);
            oldItems.push(currentItem);
        }

        var newHistoryItem = this.HistoryItems.length > 0 ? this.HistoryItems[this.HistoryItems.length - 1] : null;
        return { old: oldItems, new: newHistoryItem };
    };

    forward(options: INavigationDemand) {
        options.navigationDemandType = options.navigationDemandType ?? INavigationDemandType.forward
        let retVal = this.checkIfHistoryExist(options);
        if (!retVal.historyExist) {
            return undefined;
        }

        const step = options.step ? (options.step == retVal.step ? options.step : retVal.step) : 1;

        let oldItems: Array<HistoryItem> = [];
        let newHistoryItem: HistoryItem | undefined;
        const currentHistoryItem = this.last()!;
        oldItems.push(currentHistoryItem);
        for (let index = 0; index < step!; index++) {
            let futureItem = this.FutureItems.pop();
            if (!futureItem) { throw new Error('there is no future in this land.'); }
            this.HistoryItems.push(futureItem);
            (index == step! - 1) ? newHistoryItem = futureItem : oldItems.push(futureItem);
        }
        return { old: oldItems, new: newHistoryItem! };
    };


    private destroyer(historyItems?: Array<HistoryItem>) {
        if (historyItems) {
            historyItems.forEach(item => item.destroy());
        }
    };
}