
import { loadMessages, locale } from "devextreme/localization";
import trMessages from "devextreme/localization/messages/tr.json";
import { IContainerServices } from "../../../../common/shrimp/interfaces/quick/IContainerServices";
import { IFormattingDefinition } from "../../../../common/shrimp/interfaces/quick/IFormattingDefinition";
import { IWebSDKSettingsWrapper } from "../../../../common/shrimp/interfaces/quick/IWebSDK";
import { IRegionalManager } from "../../../../common/qshell";

export class RegionalManagerImpl implements IRegionalManager {
    public static ContextName = "RegionalManagerImpl";

    public contextName: string = RegionalManagerImpl.ContextName;
    private currentRegion = "tr";
    settingsWrapper: IWebSDKSettingsWrapper;
    services?: IContainerServices;

    constructor(settingsWrapper: IWebSDKSettingsWrapper) {
        this.settingsWrapper = settingsWrapper;

    }

    public getRegionList() {
        let result = Array<string>();
        var i;
        // for (i = 0; i < 2; i++) {
        for (i = 0; i < (this.settingsWrapper.settings.regionalDefinition?.length || 0); i++) {
            result.push(this.settingsWrapper.settings.regionalDefinition![i].region);
        }

        // for(this.settings!.regionalDefinition?.region)

        // result = this.settings?.regionalDefinition?.region;
        return result;
    }

    public setRegion(regionName: string) {
        this.currentRegion = regionName;
    }

    public getRegion() {
        return this.currentRegion;
    }


    public getFormattingDefinitionByCurrentRegion(): IFormattingDefinition | undefined {
        return this.getFormattingDefinitionBySelectedRegion(this.currentRegion);

    }

    public getFormattingDefinitionBySelectedRegion(selectedRegion: string): IFormattingDefinition | undefined {
        //    return this.settings?.regionalDefinition!.region[this.currentRegion];
        let result = this.settingsWrapper.settings.regionalDefinition?.filter(obj => {
            return obj.region === selectedRegion
        })
        if (result != undefined) {
            return result[0]!.FormattingDefinition;
        } else {
            return undefined;
        }

    }

    public onLRChange(language?: string) {
        language && locale(language);
    }

    public loadMessagesOnce() {
        trMessages.tr["dxDataGrid-headerFilterEmptyValue"] = "(Bo)";
        trMessages.tr["dxDataGrid-trueText"] = "Evet";
        trMessages.tr["dxDataGrid-falseText"] = "Hayr";
        loadMessages(trMessages)
    }

}