import { IPerfable } from "./IPerfable";

// WTH in node and mobile ?
let perf: Performance;// | import("perf_hooks").Performance;

if (typeof performance != "undefined") {
    perf = performance;
}


export class PerfManager {
    private isActive = false;

    public get IsActive(): boolean {
        return this.isActive;
    }

    public Activate(): void {
        if (typeof perf == "undefined") { return; }
        this.isActive = true;
    }

    public Reset(): void {
        if (!this.IsActive) { return; }

        
        perf.clearMarks();

        // if (!(<any>perf).getEntriesByName) { return };
        // perf = <Performance>perf;

        perf.clearMeasures();
    }

    public async Measure<T>(name: string, codeBlock: () => Promise<T> | T){
        this.Start(name);
        const retVal = await codeBlock();
        this.End(name);
        return retVal;
    }

    public Start(name: string): void {
        if (!this.IsActive) { return; }

        perf.mark(this.getStartName(name));
    }

    public End(name: string, perfableObject?: IPerfable): void {
        if (!this.IsActive) { return; }
        const endName = this.getEndName(name);
        perf.mark(endName);
        let x = <{ duration: number, startTime: number } | undefined><unknown>perf.measure(name, this.getStartName(name), endName); // .measure returns in chrome but not in IE
        if (perfableObject) {
            if (!x) {
                const measures = (<{ getMeasures: () => Record<string, Array<number>> }><unknown>perf).getMeasures()[name];// try to extract measure for non returning perf.measure
                x = { duration: measures[measures.length - 1], startTime: 0 };
            }
            if (x) {
                if (!perfableObject.duration) { perfableObject.duration = [x.duration]; perfableObject.sum = x.duration; perfableObject.startTime = x.startTime; perfableObject.endTime = (x.startTime + x.duration) }
                else { perfableObject.duration.push(x.duration); perfableObject.sum! += x.duration; perfableObject.startTime = x.startTime; perfableObject.endTime = (x.startTime + x.duration) }
            }
        }
    }

    public GetAvg(name?: string) {
        if (!this.IsActive) { return undefined; }

        // if (!(<any>perf).getEntriesByName) { return };
        // perf = <Performance>perf;
        
        const entries = name ? perf.getEntriesByName(name) : perf.getEntriesByType("measure");
        return this.avg(entries);
    }
    public GetSum(name?: string) {
        if (!this.IsActive) { return undefined; }
        
        // if (!(<any>perf).getEntriesByName) { return };
        // perf = <Performance>perf;
        
        const entries = name ? perf.getEntriesByName(name) : perf.getEntriesByType("measure");
        return this.sum(entries);
    }
    public GetList(name?: string){
        if (!this.IsActive) { return undefined; }
        const entries = name ? perf.getEntriesByName(name) : perf.getEntriesByType("measure");
        return entries;
    }





    private avg(entries: PerformanceEntryList) {
        const entriesCumulative = entries.reduce((prev, cur) => { prev.sum += cur.duration; prev.counter++; return prev; }, { sum: 0, counter: 0 });
        return entriesCumulative.sum / entriesCumulative.counter;
    }

    private sum(entries: PerformanceEntryList) {
        return entries.reduce((prev, cur) => { prev += cur.duration; return prev; }, 0);
    }






    private getStartName(name: string) {
        return `${name} start`;
    }

    private getEndName(name: string) {
        return `${name} end`;
    }
}