import { Download } from "../../../../common/shrimp/helpers/download";
import { TypeHelper } from "../../../../common/shrimp/helpers/typeHelper";
import { IDownloadRequest } from "../../../../common/shrimp/interfaces/NetworkInterfaces/IRequest";
export class DownloadManager {

    generalDownload = new Download();

    RequestHandler(request: IDownloadRequest) {
        let formElement = document.createElement("form");
        formElement.method = request.http;
        formElement.action = request.url;
        formElement.target = "blank";
        document.body.appendChild(formElement);
        if (request.data) {
            Object.keys(request.data).map(val => {
                let formInput = document.createElement("input");
                formInput.value = request.data[val];
                formInput.name = val;
                formInput.type = "hidden";
                return formInput;
            }).forEach(formInput => formElement.appendChild(formInput));
        }
        formElement.submit();
        document.body.removeChild(formElement);
    }

    extractFileNameFromHeader(headers: Object) {
        let disposition = headers["content-disposition"] || headers["Content-Disposition"]
        if (!disposition) {
            return null;
        }
        let responseFilename = /filename="([^;]*);?"/i.exec(disposition) ?? /filename=([^;]*);?/i.exec(disposition)
        if (responseFilename && responseFilename.length > 1) {
            return responseFilename[1]
        }
        return null;
    }

    download(data, fileName: String) {
        const typeHelper = new TypeHelper();
        try {
            if (!typeHelper.isBlob(data)) {
                data = new Blob([data])
            }
        } catch (error) {
            return;
        }
        return this.generalDownload.downloadInner(data, fileName, "text/plain");
    }
}