import { ContextManager } from "../../../../shrimp/context";
import { WindowHelper } from "../../../../shrimp/helpers/windowHelper";
import { IComponentCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { TSComponentBase } from "../../../../shrimp/interfaces/ComponentInterfaces/TSComponentBase";
import { IDictionary } from "../../../../shrimp/interfaces/IDictionary";
import { RenderingContext } from "../../Context/RenderingContext";
export default class QTimer extends TSComponentBase {
    private props: IDictionary<any>;
    private events: IDictionary<Function>
    private parentInstance: any
    private context: ContextManager;
    private timerId: any;
    private status: boolean;


    constructor({ props, events, parent, context }: { props: IDictionary<Object>, events: IDictionary<Function>, parent: any, context: ContextManager }) {
        super({ events: events, selfEventList: ["setInterval"] });
        this.props = props;
        this.events = events;
        this.parentInstance = parent;
        this.context = context;
        this.status = false;
    }

    Render() {
        this.startTimerInterval();
        let renderingContext = this.context.GetItemSingle<RenderingContext>(RenderingContext.ContextName);
        const selfCompColl = this.props._renderingProps.compCollCb() as IComponentCollection;
        return renderingContext?.HistoryBasedConf.createContainerView!(selfCompColl.GetCompUID());
    }
    triggerInterval() {
        const self = this;
        this.setTimerId = WindowHelper.Instance.Worker?.worker?.(
            URL.createObjectURL(
                new Blob(
                    [`onmessage=function (event){setInterval(function(){postMessage(1);},${<number>self.props.time})}`

                    ],
                    { type: "application/javascript" }
                )
            )
        );

        this.timerId.onmessage = function (event) {
            self.emit("setInterval");
        };
        this.timerId.postMessage("start");
    }
    startTimerInterval() {
        if (!(<any>this.context.GetItemSingle("Dory")).GetEditMode()) {
            if (this.props.time && !this.timerId && <boolean>this.props.autostart) {
                this.triggerInterval();
                this.setStatus = true;
            }
        }
    }
    start() {
        let self = this;
        if (!(<any>self.context.GetItemSingle("Dory")).GetEditMode()) {
            if (this.props.time && !this.timerId) {
                this.triggerInterval();
                this.setStatus = true;
            }
        }
    }
    public reStart() {
        this.stop();
        this.triggerInterval();
        this.setStatus = true;
    }
    public stop() {
        if (this.timerId) {
            this.timerId.terminate?.();
            this.setTimerId = undefined;
            this.setStatus = false;
        }
    }

    set setTimerId(timerId: any) {
        this.timerId = timerId;
    }
    set setStatus(status: boolean) {
        this.status = status;
    }
    get getStatus() {
        return this.status;
    }

    hibernate(passHibernate?: boolean) {
        if (passHibernate) return;
        if (this.timerId) {
            this.timerId.terminate?.();
            this.setTimerId = undefined;
            this.setStatus = false;
        }
    }

    destroy(){
        this.hibernate();
    }
}