import { IQJSon } from "../../../shrimp/interfaces/ComponentInterfaces/IQJson";
import { IDory, PartialDisplayHookCb } from "../../../shrimp/interfaces/RenderingInterfaces/IDory";
import { IDoryJr } from "../../../shrimp/interfaces/RenderingInterfaces/IDoryJr";
import { IDoryRendererChild, IRendererChild } from "../../../shrimp/interfaces/RenderingInterfaces/IRenderer";
import { QjsonData, QJsonPool } from "./QJsonHelpers/QJsonPool";
import { Renderer, DoryRenderer } from "./Renderer";

// SDK'lara alan DorjJR - (loading ve alert gibi... Pipeline deil, nk o SDK'dan deil, engine'den direk doryJR ile alyor.)
export class RendererChild implements IRendererChild {
    protected doryJr: IDoryJr;
    protected doryRenderer: DoryRenderer;

    constructor(renderer: Renderer, compUIDPrefix?: string) {
        this.doryRenderer = <DoryRenderer>renderer;
        this.doryJr = this.doryRenderer.DoryInst.CreateDoryJr({ compUIDPrefix });
    }

    public SetCallbackDisplay(callBackFunc: PartialDisplayHookCb) {
        this.doryJr.setCallbackDisplay(callBackFunc);
    }

    public Destroy() {
        this.doryJr.Destroy();
    }

    public async Render(options: { qjson: IQJSon, props?: Record<string, any>, events?: Record<string, Function>, compParentInst: any, storeItems?: Record<string, any>, pageId?: string, pageName?: string, noHistory?: boolean }) {
        const qjsonPool = this.doryRenderer.getContextItem<QJsonPool>(QJsonPool.contextName)!;
        const qjsonData: QjsonData = { type: "content", content: options.qjson };
        const dory = this.doryRenderer.DoryInst;
        const collectResult = await qjsonPool.retrieveAndFillQjsonAsync({ qjson: qjsonData, dory, doryJr: this.doryJr });
        if (collectResult.changedPath) { throw new Error("unexpected path change: possible masterPage"); }
        return this.doryJr.Render(options);
    }
}
export class DoryRendererChild extends RendererChild implements IDoryRendererChild {
    public get DoryJrInst() {
        return this.doryJr;
    }
}