import { Logger, LogType } from "../../../../../../common/shrimp/helpers/logger";
import { GlobalMethodsImpl } from "../../../implementation/GlobalMethodsImpl";
import { StoreManager } from "../../../implementation/StoreManager";
import utils from '../utils';

const errorHandler = ((error: any, storeManager: StoreManager) => {
    if (utils.isHandlerEnabled(error.config)) {
        const minLogType = GlobalMethodsImpl.settingsWrapper.settings.minEngineLogLevel ? LogType[GlobalMethodsImpl.settingsWrapper.settings.minEngineLogLevel] : undefined
        if (error.response) {
            // The request was made and the server responded with a status code
            // that falls out of the range of 2xx
            Logger.Log({ logMessage: "", additional: { data: error.response.data, status: error.response.status }, logType: LogType.Error, minEngineLogLevel: minLogType });
        } else if (error.request) {
            // The request was made but no response was received
            // `error.request` is an instance of XMLHttpRequest in the browser and an instance of
            // http.ClientRequest in node.js
            Logger.Log({ logMessage: "", additional: error.request, logType: LogType.Error, minEngineLogLevel: minLogType });
        } else {
            // Something happened in setting up the request that triggered an Error
            Logger.Log({ logMessage: "Error", additional: error.message, logType: LogType.Error, minEngineLogLevel: minLogType });
        }
    }
    return Promise.reject(error)
});

export default errorHandler