import Vue from "vue";
import { IComponentManager } from "../../../../common/qshell";
import { ContextManager } from "../../../../common/shrimp/context";
import { ElementLocation } from "../../../../common/shrimp/interfaces/ComponentInterfaces/ElementLocation";
import { CType } from "../../../../common/shrimp/interfaces/ComponentInterfaces/ICompJson";
import { IComponent } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IComponent";
import { IComponentCollection, ILightComponentCollection, ScopedSlotCreatorFunc } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { ICreatedComponent } from "../../../../common/shrimp/interfaces/ComponentInterfaces/ICreatedComponent";
import { IDirectiveElement } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IDirectiveCollection";
import { IVnode } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IVnode";
import { IDictionary } from "../../../../common/shrimp/interfaces/IDictionary";
import { CompGroupType } from "../../../../common/shrimp/interfaces/quick/IWebSDK";
import { IDomElement } from "../../../../common/shrimp/interfaces/RenderingInterfaces/IDomElement";
import { LocalPropMapping, VuetifyPropMapping } from "../components/providers/ComponentPropertyMapping";
import { ComponentProvider } from "../components/providers/ComponentProvider";
import { VuetifyDirectiveMap } from "../components/providers/VuetifyDirectiveMap";
import { ChartComponentManager } from "../components/quick/Chart/ChartComponentManager";
import GeneralWrapper from "../components/quick/LocalComponentWrappers/GeneralWrapper.vue";
import TSComponentWrapper from "../components/quick/LocalComponentWrappers/TSComponentWrapper.vue";
import { controlledDestroy, willFixMemLeak } from "./memLeakFix";
import { ComponentDOMAttrsHelper } from "./operators/ComponentDOMAttrsHelper";
import { StyleManager } from "../managers/style/StyleManager";

export class ComponentManagerImpl implements IComponentManager {
    contextName: string;
    parentInstance: any;

    constructor(contextName: string, parentInstance: any) {
        this.contextName = contextName;
        this.parentInstance = parentInstance;
    }

    public componentHasProp(componentName: string, compInstance: IComponent | undefined, propName: string): boolean {
        if (propName == "disabled" && compInstance) {
            return compInstance["_props"] && compInstance["_props"].hasOwnProperty(propName) ? true : false;
        }
        return true;
    }

    private compPropModifier(compName: string, props: IDictionary<any>) {
        if (VuetifyPropMapping[compName]) {
            let val = props["value"];
            let modifiedProp = VuetifyPropMapping[compName];
            props[modifiedProp] = val;
        }
    }

    createComponent({ componentName, props, events, asChildType, asChildName, slots, scopedSlots, compClass, text, attrs, componentCollection, directives }:
        { componentName: string, props: IDictionary<any>, events?: IDictionary<Function> | undefined, asChildType: CType | null, asChildName?: string, slots?: IDictionary<Array<IVnode>>, scopedSlots?: IDictionary<ScopedSlotCreatorFunc>, compClass?: string, text?: string, attrs?: IDictionary<any>, componentCollection: ILightComponentCollection, directives?: Array<IDirectiveElement> }) {

        const compCollection = componentCollection as IComponentCollection;
        let historyLRManager = props._renderingProps?.context().GetItemSingle("HistoryItemBasedLRManager");
        let rightToLeft: boolean = historyLRManager?.LRTypeRTL ?? false;
        this.parentInstance.$vuetify.rtl = rightToLeft; //Vapplicationa denk geliyor
        props.rtl = rightToLeft; // vue
        props.rtlEnabled = rightToLeft; //devex

        if (componentName !== "QDocumentViewer" && props.src && props.src.includes('data:image/tiff')) {
            props.src = ComponentDOMAttrsHelper.convertTiffToImage(props.src);
      }
        if (menuCompTypeList[componentName] != undefined && !props.menuProps) {
            props.menuProps = {
                contentClass: componentCollection.GetCompUID()
            }
        }
        if (contentClassAppliedTypeList[componentName] != undefined) {
            props.contentClass = componentCollection.GetCompUID();
        }
        var target = ComponentProvider.getComponentConst(componentName);
        if (!target) {
            target = { compInstance: Vue.component(componentName) }
        }

        let scopedFuncs: IDictionary<(scopeObject?: IDictionary<any>) => Array<IVnode>> = {};

        if (scopedSlots) {
            Object.keys(scopedSlots).forEach(key => {
                scopedFuncs[key] = (scopeObject?: IDictionary<any>) => scopedSlots[key](scopeObject).map(compColl => compColl.vDomElement!);
            })
        }

        let createdComp: IComponent;
        let retVal: ICreatedComponent = {};
        let classDict: { [key: string]: boolean } | undefined;
        compClass?.split(" ").forEach(classKey => {
            if (classKey) {
                classDict = classDict || {};
                classDict[classKey] = true
            }
        });

        this.compPropModifier(componentName, props);
        let vuetifyDirectives: Array<IDirectiveElement> | undefined;
        if (directives && directives.length > 0) {
            vuetifyDirectives = [];
            for (let index = 0; index < directives.length; index++) {
                const directive = directives[index];
                vuetifyDirectives.push(VuetifyDirectiveMap.convertDricetiveToVuetify(directive));
            }
        }

        switch (asChildType) {
            case CType.slot:
            case CType.scopedSlot:
                if (target.CompType == CompGroupType.Chart) {
                    props.customStyle = true;
                    ({ createdComp, retVal } = new ChartComponentManager().createSlotTypeChartComponent(props, target, componentName, retVal, this.parentInstance));
                    return retVal;
                }
                let flatSlots: Array<IVnode> | undefined;
                createdComp = new target.compInstance({
                    parent: this.parentInstance,
                    propsData: props,
                    staticClass: compClass
                });

                if (slots) {
                    flatSlots = Object.keys(slots).map((key) => ({ inst: slots![key], key }))
                        .reduce<Array<IVnode>>((prev, cur) => { cur.inst.forEach(curChild => { curChild["context"] = createdComp; }); return prev.concat(cur.inst); }, []);
                }

                if (text) {
                    flatSlots = [{ text: text, data: {} }];
                }
                let vel = createdComp.$createElement(createdComp.$options.name, {
                    props: props,
                    on: events,
                    scopedSlots: scopedSlots ? scopedFuncs : undefined,
                    class: classDict,
                    key: props.ID,
                    slot: asChildName,
                    attrs: attrs,
                    directives: vuetifyDirectives                //directives: ComponentDirectiveInjector.injectSlotDirective()
                }, flatSlots);

                if (willFixMemLeak()) {
                    controlledDestroy(createdComp, compCollection);
                } else {
                    compCollection.additionals = compCollection.additionals || {};
                    compCollection.additionals.createdComp = createdComp;
                }

                if (!compClass) {
                    delete vel?.["context"]?.$options?.staticClass;
                }

                //This section added for DevExpress Component's wrapper problem fix.
                if (!vel.componentOptions) {
                    vel.componentOptions = {};
                    vel.componentOptions["propsData"] = createdComp.$options["propsData"];
                    vel.componentOptions["Ctor"] = createdComp.$options["_Ctor"][0];
                    vel.componentOptions["children"] = flatSlots ? flatSlots : null;
                    vel.componentOptions["events"] = events;
                }

                retVal = { vel, compGroupType: target.CompType };
                return retVal;
            case CType.child:
            default:
                if (target.CompType == CompGroupType.Chart) {
                    ({ createdComp, retVal } = this.createChartComp(props, target, componentName, retVal));
                    return retVal;
                }

                createdComp = new target.compInstance({
                    parent: this.parentInstance,
                    propsData: props
                });


                if (attrs) {
                    createdComp.$attrs = attrs;
                }

                if (createdComp['customCompClass'] && compClass) {
                    createdComp['customCompClass'] = compClass
                } else if (classDict) {
                    createdComp.classes = classDict;
                }
                for (var key in events) {
                    createdComp.$on(key, events[key]);
                }
                if (slots) {
                    createdComp.$slots = slots;
                }
                if (scopedSlots) {
                    createdComp.$scopedSlots = scopedFuncs;
                }
                createdComp.$mount();
                if (compClass && classDict && createdComp.$el?.classList?.toString().indexOf(compClass) < 0) {
                    Object.keys(classDict).forEach(classKey => createdComp.$el?.classList.add(classKey));
                }

                createdComp.$el.id = props.ID; // This line added also selenium tests but difference with createdComp.id is some vuetify components has problems. (VBtn has no id property on createdComp)
                createdComp.$el.setAttribute?.("q_UID", attrs?.["q_UID"]);
                if (compCollection.qjsonInfo && compCollection.qjsonInfo.ver && compCollection.qjsonInfo.ver > 1.0) {
                    // StyleManager.matchComponentStyles(compCollection);
                    compCollection.WatchCreatedFields?.((field) => {
                        if (!field.el) { return; }
                        StyleManager.current(compCollection).applyStyle(compCollection);
                    });
                }
                retVal = { instance: createdComp, el: createdComp.$el, compGroupType: target.CompType };
                return retVal;
        }
    }

    private createChartComp(props: IDictionary<any>, target: any, componentName: string, retVal: ICreatedComponent) {
        let instance, el: IDomElement;
        ({ instance, el } = new ChartComponentManager().createChildTypeChartComponent(props, target, componentName, this.parentInstance));
        props.__domElements = el;
        let createdComp: IComponent = new GeneralWrapper({ parent: this.parentInstance, propsData: props });
        createdComp.$mount();
        retVal = { instance, el: createdComp.$el, compGroupType: target.CompType };
        return { createdComp, retVal };
    }

    public createContainerView(compUID: string) {
        return document.createElement("div");
    }

    public updateContainerView({ givenObject, elements }: { givenObject: any; elements?: string | any[] | undefined; pageShift: boolean; }) {
        if (!elements) {
            return;
        }
        if (typeof elements === "string") {
            givenObject.innerHTML = elements;
        }
        else {
            givenObject.innerHTML = "<div></div>";
            elements!.forEach(element => {
                givenObject.appendChild(element);
            });
        }
    }

    tsComponentWrapper() {
        return TSComponentWrapper;
    }

    /**
     * Add new slot(VDomElement) to the parent component and mount it 
     * @param parentCompCollection Parent component collection created new component
     * @param slot Created component vDomElement that will be pushed into the parentcompslot array
     * @param compLocater Location of new component will be placed into
     */
    addNewSlotToParentComp({ parentCompCollection, slots, compLocater, childName }: { parentCompCollection: IComponentCollection, slots: Array<IVnode>, compLocater: ElementLocation, childName: string }) {
        if (!parentCompCollection.component) {
            return;
        }
        const slotComponents = parentCompCollection.component.$slots[childName] = parentCompCollection.component.$slots[childName] || [];

        if (compLocater == ElementLocation.After) {
            slots.forEach(slot => slotComponents.unshift(slot));
        } else {
            slots.forEach(slot => slotComponents.push(slot));
        }
        parentCompCollection.component.$mount();
    }

    public setVisibility(compColl: IComponentCollection, compVisibility: boolean): void {
        // compColl.domElement!.style.setProperty("display", compVisibility ? "" : "none", compVisibility ? "" : "important");

        //ask before judge..
        let compStyle = compColl.domElement?.getAttribute?.('style') ?? "";
        let displayNoneCss = "display: none !important";
        if (compVisibility) {
            compStyle = compStyle!.indexOf(displayNoneCss) >= 0 ? compStyle!.replace(displayNoneCss, '') : compStyle;
        } else {
            compStyle = compStyle!.indexOf(displayNoneCss) >= 0 ? compStyle : compStyle + " " + displayNoneCss;
        }
        compColl.domElement!.setAttribute?.('style', compStyle);
        
    }
    public setDisable(compColl: IComponentCollection, compDisable: boolean): void {
        compColl.component!["disabled"] = compDisable;
    }

    public getComponentValue(compColl: IComponentCollection): any {
        let valueProp = "";
        switch (compColl.compGroupType) {
            case CompGroupType.Local:
                valueProp = LocalPropMapping[compColl.compType] ?? "value";
                return compColl.component![valueProp];
        }
        if (compColl.component!.internalValue !== undefined) {
            return compColl.component!.internalValue;
        }
        return compColl.component!.value;
    }

    public setComponentValue(compColl: IComponentCollection, value: string): void {
        let valueProp = "";
        switch (compColl.compGroupType) {
            case CompGroupType.Local:
                valueProp = LocalPropMapping[compColl.compType] ?? "value";
                compColl.component![valueProp] = value;
                break;
            default:
                if (compColl.compType === 'VCheckbox') {
                    compColl.component!['inputValue'] = value;
                } else {
                    compColl.component!.value = value;
                }
                compColl.component!.internalValue = value;
                break;
        }
    }
    public getComponentStyle(comp: IComponent): any {
        return comp.style;
    }
    public setComponentStyle(compCollection: IComponentCollection, prop: string, value: string): void {
        StyleManager.current(compCollection).applyStyle(compCollection);
    }
    public onComponentDestroy({ parentComponent, compColl, childKey }: { parentComponent?: IComponent, compColl: IComponentCollection, childKey?: string }) {
        //compColl.onDestroy(); TODO: 1 EKM'de  silinecek.  01.10.2025 de silinecek.

        if (!parentComponent || !childKey) { return; }
        const childIndex = parentComponent.$slots[childKey]?.findIndex(vDom => vDom == compColl.vDomElement);
        if (childIndex === undefined || childIndex == -1) { return; }
        parentComponent?.$slots?.[childKey]?.splice(childIndex, 1);
        parentComponent?.$mount?.();
    }

    public createComponentQValue(compCollection: IComponentCollection) {
        if (!compCollection.component) { return; }
        
        const component = compCollection.component;
        const valueKey = "qValue";
        let valueProp = Object.getOwnPropertyDescriptor(component, valueKey);
        if (!valueProp) {
            Object.defineProperty(component, valueKey, {
                get: () => this.getComponentValue(compCollection),
                set: v => this.setComponentValue(compCollection, v),
                configurable: true,
            });
            compCollection.subscribeOnDestroy(() => { delete component[valueKey]; });
        }
    }
}

enum menuCompTypeList {
    VSelect,
    VAutocomplete,
    VCombobox
}

enum contentClassAppliedTypeList {
    VBottomSheet,
    VDialog,
    VCombobox,
    VMenu
}
