export class qjsonVersionHelper {
    static traversePageVersions(url: string, pageVersions: Record<string, Date | unknown>): Date {
        let pageName = url.substring(url.lastIndexOf("/") + 1);
        let retVal = new Date();
        for (let page in pageVersions) {
            if (typeof pageVersions[page] === "object" && pageVersions[page] != null) {
                retVal = qjsonVersionHelper.traversePageVersions(url, (<Record<string, unknown>>pageVersions[page]));
            }
            if (page === pageName) {
                retVal = <Date>pageVersions[pageName];
            }
        }
        return retVal;
    }
}