export  class QJsonManagerForAIService {   
    constructor(){ }

    private Columns = {
        exist: false,
        parentalLevel: 0
    }

    public rootLevelObjects: { model: any, minifiedModel: any, minifiedModelForLayouts: any, layoutModels: Array<any> } = {
        model: {}, minifiedModel: {},minifiedModelForLayouts:{}, layoutModels: []
    }

    public setModelChild(compID: string, key: string, qJsonManagerForAIService: any) {
        var { model, minifiedModel, minifiedModelForLayouts }: { model: any; minifiedModel: any; minifiedModelForLayouts: any; } = this.getModels({ compID });  
        model.C[key]= qJsonManagerForAIService.rootLevelObjects.model;
        minifiedModel.C[key]= qJsonManagerForAIService.rootLevelObjects.minifiedModel;
        minifiedModelForLayouts.C[key]= qJsonManagerForAIService.rootLevelObjects.minifiedModelForLayouts;
        const prevLayoutModels: Array<any> = qJsonManagerForAIService.rootLevelObjects.layoutModels;
        this.rootLevelObjects.layoutModels = this.rootLevelObjects.layoutModels.concat(prevLayoutModels);
       
        if (qJsonManagerForAIService.Columns.exist == true){
            if( qJsonManagerForAIService.Columns.parentalLevel < 2){
                this.Columns.parentalLevel = qJsonManagerForAIService.Columns.parentalLevel + 1;
                this.Columns.exist = qJsonManagerForAIService.Columns.exist;
            }            
        }
       
    }
    public setModels(ctrl: any) {  
        var { model, minifiedModel, minifiedModelForLayouts }: { model: any; minifiedModel: any; minifiedModelForLayouts: any; } = this.getModels({ ctrl });  
       
        Object.keys(ctrl).forEach(item => {
            switch (item) {             
                case "C": { //children
                    break;
                }
                case "T": { //types
                    model[item] = ctrl[item];
                    minifiedModel[item] = ctrl[item];
                    minifiedModelForLayouts[item] = ctrl[item];
                    break;
                }
                case "P":{ //properties
                    model[item] = ctrl[item];
                    break;
                }
                case "E": { // events
                    model[item] = ctrl[item];
                    break;
                }
                case "_Editor": { //editorJson
                    model["eID"] = ctrl[item].eID;
                    minifiedModel["eID"] = ctrl[item].eID;
                    minifiedModelForLayouts["eID"] = ctrl[item].eID;
                    break;
                }
                case "D": { //directives
                    break;
                }
                case "O": { //mustafa anlamsz dedi.
                    break;
                }
                case "S": {
                    let minifiedS=this.minifyS(ctrl[item]);
                    if(!minifiedS) { break; }
                    model[item] = ctrl[item];
                    minifiedModel[item] = minifiedS;
                    minifiedModelForLayouts[item] = minifiedS;
                    break;
                }
                default: {
                    model[item] = ctrl[item];
                    minifiedModel[item] = ctrl[item];
                    minifiedModelForLayouts[item] = ctrl[item];
                    break;
                }
            }
        });
        this.seperateLayouts(ctrl)
    }

    private seperateLayouts(ctrl: any) {
         if(ctrl.T=="VCol"){
             this.Columns.exist = true;
         }
         if(this.Columns.parentalLevel == 2){
            let minifiedContainer = this.rootLevelObjects.minifiedModelForLayouts[ctrl.P.ID.H];
            this.rootLevelObjects.layoutModels.push({[ctrl.P.ID.H]:JSON.parse(JSON.stringify(minifiedContainer))});
            minifiedContainer.C = {};
        }
    }
    private getModels({ ctrl, compID }: { ctrl?: any, compID?: string }) {
        var retVal:any={};
        if(compID){ //if it is the deepest component it is coming with compID
            retVal.model = (<any>this.rootLevelObjects.model)[compID] = (<any>this.rootLevelObjects.model)[compID] || { C: {} };
            retVal.minifiedModel = (<any>this.rootLevelObjects.minifiedModel)[compID] = (<any>this.rootLevelObjects.minifiedModel)[compID] || { C: {} };
            retVal.minifiedModelForLayouts = (<any>this.rootLevelObjects.minifiedModelForLayouts)[compID] = (<any>this.rootLevelObjects.minifiedModelForLayouts)[compID] || { C: {} };
        }
        else if (ctrl) { // otherwise ctrl
            retVal.model = (<any>this.rootLevelObjects.model)[ctrl.P.ID.H] = (<any>this.rootLevelObjects.model)[ctrl.P.ID.H] || { C: {} };
            retVal.minifiedModel = (<any>this.rootLevelObjects.minifiedModel)[ctrl.P.ID.H] = (<any>this.rootLevelObjects.minifiedModel)[ctrl.P.ID.H] || { C: {} };
            retVal.minifiedModelForLayouts = (<any>this.rootLevelObjects.minifiedModelForLayouts)[ctrl.P.ID.H] = (<any>this.rootLevelObjects.minifiedModelForLayouts)[ctrl.P.ID.H] || { C: {} };

        }
        
        return retVal;
    }    
    private minifyP(properiesJson: any) {
        var minifiedProperties:any = {};
        Object.keys(properiesJson).forEach(prop => {
            if (prop != ("Render" || "DiscardSettings")) { minifiedProperties[prop] = properiesJson[prop].H.replaceAll("", "").replace("", ""); }
        });
        return minifiedProperties;
    }

    private minifyS(styleJson:any){      
        var map: Array<string> = ["col", "flexDirection", "alignItems","justifyContent", "width", "height", "unlink" ]; 
        var retVal: Record<string, string | Record<string, string>> = {};
        if(!styleJson) { return; }
        Object.keys(styleJson).forEach(style=>{
            if(typeof(styleJson[style])=="object"){
                let innerStyleObj:Record<string,string> = {};
                Object.keys(styleJson[style]).forEach(innerStyle=>{
                    if(map.includes(innerStyle)){
                        innerStyleObj[innerStyle]=styleJson[style][innerStyle];
                      }
                });
                retVal[style] = innerStyleObj;
            }
            else{
              if(map.includes(style)){
                retVal[style]=styleJson[style];
              }
            }
        });
        return retVal;
    }

    private minifySLayout(styleJson:any){
        var map: Record<string, string> = {
            "marginLeft": "ML", "marginRight": "MR", "marginBottom": "MB", "marginTop": "MT", "paddingTop": "PT", "paddingBottom": "PB", "paddingLeft": "PL", "paddingRight": "PR",
            "flexDirection": "FD", "alignItems": "AI","justifyContent":"JC", "break-point": "BP", "width": "W", "height": "H", "unlink": "UL"
        }
        var retVal: Record<string, string | Record<string, string>> = {};
        Object.keys(styleJson).forEach(style=>{
            if(typeof(styleJson[style])=="object"){
                let innerStyleObj:Record<string,string> = {};
                Object.keys(styleJson[style]).forEach(innerStyle=>{
                    if(map[innerStyle] && styleJson[style][innerStyle] != ""){
                        innerStyleObj[map[innerStyle]]=styleJson[style][innerStyle];
                      }
                });
                retVal[style] = innerStyleObj;
            }
            else{
              if(map[style] && styleJson[style] != ""){
                retVal[map[style]]=styleJson[style];
              }
            }
        });
        return retVal;
    }  
    
    
}
