import { ContextManager, IContextItem } from "../../../shrimp/context";
import { ComparisonEnum, compDiffHelper, ValueStatus } from "../../../shrimp/helpers/compDiffHelper";
import { IComponentCollection } from "../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { DoryJr } from "../DoryJr";
import { HistoryManager } from "../Managers/HistoryManager";
import { HistoryItem } from "../Model/History/HistoryItem";
import { ScopeContext } from "./ScopeContext";

export type comparedValueType = { label: string, value: string, compareValue: string, statu: ValueStatus }

export class ValueContext implements IContextItem {
    static ContextName: string = "ValueContext";
    contextName: string = ValueContext.ContextName;

    private context: ContextManager;

    constructor({ context }: { context: ContextManager }) {
        this.context = context;
    }

    private valueContextList: Array<comparedValueType> = [];
    private _valueContext = {};
    public getValueContext() {
        return this._valueContext;
    }
    public getValueContextList(referenceCompId: string, iterateAllSubPages?: boolean) {
        this.valueContextList = [];
        let valueControlList: IComponentCollection[] = [];
        const historyContext = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        const historyItem = historyContext?.currentLevel().last();

        if (!historyItem) return this.valueContextList;

        //find the components in page or referenceComp children
        if (referenceCompId) {
            const referenceControl = historyItem.controlDict[referenceCompId];
            if (!referenceControl) return this.valueContextList;

            const y: IComponentCollection[] = this.getAllChildrenComponentCollection(historyItem, referenceControl);

            valueControlList = valueControlList.concat(y);
        } else {
            Object.keys(historyItem.controlDict).forEach(key => {
                valueControlList.push(historyItem.controlDict[key]);
            });

            const subCollections = historyItem.getComponentSubCollections(iterateAllSubPages);
            valueControlList = valueControlList.concat(subCollections);
        }

        valueControlList.forEach(control => {
            if (compDiffHelper.componentValueList[control.compType] && !compDiffHelper.componentValueList[control.compType].classCompare) {
                const valueCompInfo = compDiffHelper.componentValueList[control.compType];
                const component = control.component!;
                if (component === undefined) { return; }
                const value = component[valueCompInfo.valueField];
                const compareValue = component[ComparisonEnum.compareValueField];

                let label = component[valueCompInfo.labelField];
                let valueText = value;
                let compareValueText = compareValue;
                let status: ValueStatus = ValueStatus.none;
                status = compDiffHelper.valueCompare({ value: value, compareValue: compareValue });

                if (control.compType == "VCombobox") {
                    let comboValue = value;
                    let comboCompareValue = compareValue;

                    if (Array.isArray(comboValue) || Array.isArray(comboCompareValue)) {
                        //Multiple Select
                        status = compDiffHelper.multipleValueCompare(comboValue, comboCompareValue);
                        valueText = compDiffHelper.decideTooltipContent({ componentInstance: component, toolTipValue: value });
                        compareValueText = compDiffHelper.decideTooltipContent({ componentInstance: component, toolTipValue: compareValue });
                    }
                    else {
                        if (value && value.hasOwnProperty("value")) {
                            comboValue = value["value"];
                            valueText = value["text"];
                        }
                        if (compareValue && compareValue.hasOwnProperty("value")) {
                            comboCompareValue = compareValue["value"];
                            compareValueText = compareValue["text"];
                        }
                        status = compDiffHelper.valueCompare({ value: comboValue, compareValue: comboCompareValue });
                    }
                } else if (control.compType == "VSelect" || control.compType == "VAutocomplete") {

                    if (Array.isArray(value) || Array.isArray(compareValue)) {
                        //Multiple Select
                        status = compDiffHelper.multipleValueCompare(value, compareValue);
                        valueText = compDiffHelper.decideTooltipContent({ componentInstance: component, toolTipValue: value });
                        compareValueText = compDiffHelper.decideTooltipContent({ componentInstance: component, toolTipValue: compareValue });
                    }
                    else {
                        const items: Array<object> = component["items"];
                        const valueItem = items.find(i => i["value"] == value);
                        if (valueItem) {
                            valueText = valueItem["text"];
                        }

                        const compareValueItem = items.find(i => i["value"] == compareValue);
                        if (compareValueItem) {
                            compareValueText = compareValueItem["text"];
                        }
                    }
                }

                if (status == ValueStatus.none) {
                    return;
                }

                let assignedValue: comparedValueType = { label: label, value: component[ComparisonEnum.compareTableValue] || valueText, compareValue: component[ComparisonEnum.compareTextValue] || compareValueText, statu: status };
                this.valueContextList.push(assignedValue);

            }
        });

        return this.valueContextList;
    }

    private getAllChildrenComponentCollection(historyItem: HistoryItem, referenceControl: IComponentCollection) {
        let compCollection: IComponentCollection[] = this.getCustomComponentCollectionsByCompId(referenceControl.compId);

        if (referenceControl.children && Object.keys(referenceControl.children).length > 0) {
            Object.keys(referenceControl.children).forEach(key => {
                referenceControl.children![key].forEach(comp => {
                    const childrenCollection = this.getAllChildrenComponentCollection(historyItem, comp);
                    compCollection = compCollection.concat(childrenCollection);
                });
            });
        } else {
            compCollection.push(referenceControl);
        }

        return compCollection;
    }

    getCustomComponentCollectionsByCompId(customComponentId: string) {
        const dicts: IComponentCollection[] = [];

        this.context.GetItemGroup<DoryJr>(DoryJr.ContextName)?.forEach(doryJr => {
            const scopeProps = doryJr.ContextJr.GetItemSingle<ScopeContext>(ScopeContext.ContextName)?.GetProps();
            if (scopeProps && scopeProps["ID"] == customComponentId) {
                const dict = doryJr.ContextJr.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last()?.controlDict;
                if (dict) {
                    Object.keys(dict).forEach(key => dicts.push(dict[key]));
                }
            }
        });

        return dicts;
    }
}