import { ContextManager } from "../../../shrimp/context";
import { ComparisonEnum, compDiffHelper } from "../../../shrimp/helpers/compDiffHelper";
import { customCompHelper } from "../../../shrimp/helpers/customCompHelper";
import { CType, IOptJson, PType } from "../../../shrimp/interfaces/ComponentInterfaces/ICompJson";
import { IComponent, IComponentClassEngine } from "../../../shrimp/interfaces/ComponentInterfaces/IComponent";
import { IComponentCollection, ScopedSlotCreatorFunc } from "../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { ICreatedComponent } from "../../../shrimp/interfaces/ComponentInterfaces/ICreatedComponent";
import { IDirectiveElement } from "../../../shrimp/interfaces/ComponentInterfaces/IDirectiveCollection";
import { IEventCollection } from "../../../shrimp/interfaces/ComponentInterfaces/IEventCollection";
import { IEventFunctions } from "../../../shrimp/interfaces/ComponentInterfaces/IEventFunctions";
import { IPropOverrideType } from "../../../shrimp/interfaces/ComponentInterfaces/IPropExtension";
import { StyleValue } from "../../../shrimp/interfaces/ComponentInterfaces/IStyle";
import { IVnode } from "../../../shrimp/interfaces/ComponentInterfaces/IVnode";
import { IDictionary } from "../../../shrimp/interfaces/IDictionary";
import { IHistoryManager } from "../../../shrimp/interfaces/quick/IHistoryManager";
import { ISiteSettings } from "../../../shrimp/interfaces/quick/ISiteSettings";
import { CompGroupType } from "../../../shrimp/interfaces/quick/IWebSDK";
import { IDomElement } from "../../../shrimp/interfaces/RenderingInterfaces/IDomElement";
import { IRendererProps, IRenderingProps } from "../../../shrimp/interfaces/RenderingInterfaces/IRenderer";
import { RenderingContext } from "../Context/RenderingContext";
import { Dory } from "../Dory";


let _components: any = null;
function loadComponents() {
    if (!_components) {
        _components = require("../Components/index");
    }
    return _components;
}

export class ComponentCommunicator {
    public CreateComponent({ componentName, props, events, context, asChildType, asChildName, children, parentInst, childrenCreators, compCollection, compClass, text, attrs, compareCompValue, directives, styles }:
        {
            componentName: string, props: IDictionary<any>, events: IEventCollection, context: ContextManager, asChildType: CType | null, asChildName?: string,
            children?: IDictionary<Array<IComponentCollection>>, parentInst: any | null, childrenCreators?: IDictionary<ScopedSlotCreatorFunc>,
            compCollection: IComponentCollection, compClass?: string, text?: string, attrs?: IDictionary<any>, compareCompValue: boolean, directives?: Array<IDirectiveElement>, styles?: StyleValue
        }): ICreatedComponent {

        var dory: Dory = context.GetItemSingle(Dory.ContextName)!;
        const outBoundEvents = events.GetEventBoundFunctions();
        const rc = context.GetItemSingle<RenderingContext>(RenderingContext.ContextName)!;
        rc.ComponentInCreation = compCollection;

        this.setDoryProps(props, dory.isDark);

        props = this.setRenderingProps(props as IRendererProps, outBoundEvents, dory, context, compCollection);

        compCollection.subscribeOnDestroy(() => {
            props._renderingProps.compCollCb = undefined;
        });
        
        const arrangedChildren = this.arrangeChildren(children, props, childrenCreators);
        props = arrangedChildren.props || props;

        if (dory.siteSettings && !arrangedChildren.props.DiscardSettings) {
            const compArrangedSettings = this.arrangeSiteSettingsProps({ componentName: componentName, props: arrangedChildren.props || props, siteSettings: dory.siteSettings, compClass: compClass, attrs });
            ({ props, compClass, attrs } = compArrangedSettings);
        }

        const customCompName = new customCompHelper().customCompList.find(name => componentName == name);
        if (customCompName) {
            componentName = "CustomComponent";
        }

        const targetLocalComponent = this.findLocalComponent({ compName: componentName, dory });

        let createdComponent: ICreatedComponent | null = null;
        if (targetLocalComponent) {
            createdComponent = this.createLocalComponent(targetLocalComponent, componentName, props, outBoundEvents.eventFunctions, context, dory, asChildType, asChildName, compCollection, arrangedChildren.scopedSlots, arrangedChildren.slots, parentInst, compClass, text);
        } else {
            createdComponent = rc.HistoryBasedConf.createComponent({ componentName, props, events: outBoundEvents.eventFunctions, asChildType, asChildName, scopedSlots: arrangedChildren.scopedSlots, slots: arrangedChildren.slots, compClass, text, attrs, componentCollection: compCollection, directives, styles });
        }
        if (compareCompValue) {
            this.calcComponentDiff({ createdComponent, compCollection, props });
        }
        ComponentCommunicator.compCreatePostActions({ compCollection, createdComponent, context, asChildType, asChildName, attrs, props });
        return createdComponent;

    }

    private setRenderingProps(props: IRendererProps, outBoundEvents: IEventFunctions, dory: Dory, context: ContextManager, compCollection: IComponentCollection) {
        props = { ...props, ...outBoundEvents.propertyEventFunctions };

        let renderingProps: IRenderingProps = {
            shellAutoLridActivated: dory.shellConfigs.compPropsAutoLrid,
            contextLridHelper: dory.shellConfigs.lridHelper,
            isEditMode: dory.isEditorInEditState,
            context: () => context,
            events: () => outBoundEvents.eventFunctions,
            props: () => props,
            compCollCb: () => compCollection,
            getEditor: dory.GetEditor(),
        };
        props._renderingProps = renderingProps;
        return props;
    }

    private setDoryProps(props: IDictionary<any>, isDark?: boolean) {
        if (isDark) {
            props.dark = isDark;
        }
        if (props["qValue"] !== undefined) {
            props["value"] = props["qValue"];
            delete props.qValue;
        }
    }

    private static compCreatePostActions({ compCollection, createdComponent, context, asChildType, asChildName, attrs, props }: { compCollection: IComponentCollection, createdComponent: ICreatedComponent, context: ContextManager, asChildType: CType | null, asChildName?: string, attrs?: IDictionary<any>, props?: IDictionary<any> }) {
        const dory = context.GetItemSingle<Dory>(Dory.ContextName)!;
        const rc = context.GetItemSingle<RenderingContext>(RenderingContext.ContextName)!;

        if (props) {
            compCollection.SetInitialProps(props);
        }
        compCollection.WatchCreatedFields((created) => this.injectFields(created, compCollection, context));

        compCollection.SetComponentCreatedFields({ createdComponent, keepAnEyeOnCreatedCompInst: true });
        dory.shellConfigs.componentInstanceTracker?.TrackCompInstance({
            asChildType, asChildName, component: createdComponent, compCollection, attrs, postRenderExecutor: (cb) => {
                rc.executeOrDelayEvent({ cb, immediateEvent: false, triggerOrder: 0 });
            }, isEditMode: dory.isEditorInEditState
        });
    }

    private static injectFields(created: ICreatedComponent, compColl: IComponentCollection, context: ContextManager) {
        if (!created.instance) { return; }

        if (!Object.getOwnPropertyDescriptor(compColl.component, "visible")) {
            Object.defineProperty(compColl.component, "visible", { get: () => compColl.componentVisibility, set: (value) => compColl.componentVisibility = value, configurable: true });
        }

        const dory = context.GetItemSingle<Dory>(Dory.ContextName);
        const compOperator = dory!.OperatorCollection?.componentOperator;
        if (!Object.getOwnPropertyDescriptor(compColl.component, "class")) {
            Object.defineProperty(compColl.component, "class", { get: () => compOperator?.getClassString(compColl), set: value => compOperator?.setClassString(compColl, value), configurable: true });
        }
        if (!Object.getOwnPropertyDescriptor(compColl.component, "text") && compOperator?.compListOfText?.includes(compColl.compType)) {
            Object.defineProperty(compColl.component, "text", { get: () => compOperator?.getTextString?.(compColl), set: value => compOperator?.setTextAndMount?.(compColl, value), configurable: true });
        }
        compColl.subscribeOnDestroy(() => {
            if (compColl.component) {
                delete compColl.component["class"];
                delete compColl.component["text"];
                delete compColl.component["visible"];
            }
        });
    }

    /**
     * Define component properties of the component dictionary from the quick-shrimp.
     * Listen those defined props to calculate its border and tooltip.
     * Also listen component instance whenever a change has been made on it.
     * @param createdComponent Created component instance
     * @param compCollection Created component collection(can't use only this collection to get comp instance bc the instance is not on it yet)
     */
    private calcComponentDiff({ createdComponent, compCollection, props }: { createdComponent: ICreatedComponent | null, compCollection: IComponentCollection, props: IDictionary<any> }) {
        compCollection.compareComponent = true;
        if (createdComponent && compDiffHelper.componentValueList[compCollection.compType]) {

            // Prop value listener has watcher on the component collection 
            compDiffHelper.propValueListener({
                compCollection, propKey: compDiffHelper.componentValueList[compCollection.compType].valueField, props
            });
            compDiffHelper.propValueListener({
                compCollection, propKey: ComparisonEnum.compareValueField, props
            });
        }
    }

    public ComponentHasProp(componentName: string, options: IOptJson, propName: string, context: ContextManager): boolean {
        if (options.dynProps) {
            return true;
        }
        const rc = context.GetItemSingle<RenderingContext>(RenderingContext.ContextName)!;
        var dory: Dory = context.GetItemSingle(Dory.ContextName)!;
        const targetLocalComponent = this.findLocalComponent({ compName: componentName, dory });
        if (targetLocalComponent) {
            return true;
        }
        return rc.HistoryBasedConf.componentHasProp({ componentName, compInstance: undefined, propName });
    }

    private arrangeChildren(children: IDictionary<Array<IComponentCollection>> | undefined, props: IDictionary<any>, childrenCreators?: IDictionary<ScopedSlotCreatorFunc>) {
        let retVal: { scopedSlots?: IDictionary<ScopedSlotCreatorFunc>, slots?: IDictionary<Array<IVnode>>, props: IDictionary<any> } = { scopedSlots: childrenCreators, props: props };

        for (var childKey in children) {
            children[childKey].forEach(child => {
                switch (child.asChildType) {
                    case CType.slot:
                        retVal.slots = retVal.slots || {};
                        retVal.slots[childKey] = retVal.slots[childKey] || [];
                        retVal.slots[childKey].push(child.vDomElement!);
                        break;
                    case CType.child:
                        retVal.props.__domElements = retVal.props.__domElements || {};
                        retVal.props.__domElements[childKey] = retVal.props.__domElements[childKey] || [];
                        retVal.props.__domElements[childKey].push(child.domElement);
                        break;
                }
            });
        }

        return retVal;
    }
    private arrangeSiteSettingsProps({ componentName, siteSettings, props, compClass, attrs }: { componentName: string, siteSettings?: ISiteSettings, props: IDictionary<any>, compClass?: string, attrs?: IDictionary<any> }) {
        const compArrangedSettings = { props, compClass, attrs };
        if (siteSettings && siteSettings?.compSettings && siteSettings?.compSettings[componentName]) {
            let componentProps = siteSettings.compSettings[componentName];
            for (const propName in componentProps) {
                if (componentProps[propName].runTimeDefaultValue !== undefined || componentProps[propName].runTimeOverrideValue !== undefined) {
                    const uxProp = componentProps[propName];
                    if (uxProp.propType == PType.prop || uxProp.propType === undefined) {
                        if (propName == 'class') {
                            if (typeof uxProp.runTimeOverrideValue == "object") {
                                switch (uxProp.runTimeOverrideValue.type) {
                                    case IPropOverrideType.Append:
                                        compArrangedSettings.compClass += ` ${uxProp.runTimeOverrideValue.value}`;
                                        break;
                                    case IPropOverrideType.Override:
                                        compArrangedSettings.compClass = uxProp.runTimeOverrideValue.value.toString();
                                        break;
                                    default:
                                        break;
                                }
                            } else {
                                compArrangedSettings.compClass = (uxProp.runTimeOverrideValue || uxProp.runTimeDefaultValue)?.toString();
                            }
                        }
                        compArrangedSettings.props[propName] = uxProp.runTimeOverrideValue || compArrangedSettings.props[propName] || uxProp.runTimeDefaultValue;
                    } else if (uxProp.propType == PType.attr) {
                        compArrangedSettings.attrs = compArrangedSettings.attrs || {}
                        compArrangedSettings.attrs[propName] = uxProp.runTimeOverrideValue || compArrangedSettings.attrs[propName] || uxProp.runTimeDefaultValue;
                    }
                }
            }
        }
        return compArrangedSettings;
    }
    private findLocalComponent({ compName, dory }: { compName: string, dory: Dory }): IComponentClassEngine | null | false {
        const Components = loadComponents();
        let targetLocalComponent = <IComponentClassEngine>(<IDictionary<Function>>Components)[compName];
        if (targetLocalComponent) {
            if (dory.shellConfigs.coreComponentOverrider) {
                return dory.shellConfigs.coreComponentOverrider(compName) != null ? false : targetLocalComponent;
            }
        }
        return targetLocalComponent;
    }

    private createLocalComponent(targetLocalComponent: IComponentClassEngine, componentName: string, props: IDictionary<any>, outBoundEvents: IDictionary<Function>, context: ContextManager, dory: Dory,
        asChildType: CType | null, asChildName: string | undefined, componentCollection: IComponentCollection, scopedSlots?: IDictionary<ScopedSlotCreatorFunc>, slots?: IDictionary<Array<IVnode>>, parentInst?: any | null, compClass?: string, text?: string): ICreatedComponent {

        let scopedFuncs: ({ compCollection, scopeObject }: { compCollection?: IComponentCollection, scopeObject?: IDictionary<any> }) => Array<IVnode> | undefined;
        if (scopedSlots) {
            Object.keys(scopedSlots).forEach(key => {
                scopedFuncs = scopedFuncs || {};
                scopedFuncs[key] = (scopeObject?: IDictionary<any>) => scopedSlots[key]({ compCollection: componentCollection, scopeObject }).map(compColl => compColl.vDomElement!);
            })
        }
        const compCreator = new targetLocalComponent({
            props: props,
            events: outBoundEvents,
            parent: parentInst,
            context: context
        });
        if (outBoundEvents["onPageClose"] && componentName == "Messaging") {
            dory.shellConfigs.setPageClose?.();
        }
        let retVal: ICreatedComponent = {};
        switch (asChildType) {
            case CType.scopedSlot:
            case CType.slot:
                props._renderingProps.targetLocalComponent = () => compCreator;
                const localComponentWrapperName = "TSComponentWrapper";
                const rc = context.GetItemSingle<RenderingContext>(RenderingContext.ContextName)!;
                retVal = rc.HistoryBasedConf.createComponent({ componentName: localComponentWrapperName, props: props, events: outBoundEvents, scopedSlots: scopedSlots, componentCollection: componentCollection, asChildType: asChildType, asChildName });
                retVal.compGroupType = CompGroupType.Local
                break;
            case CType.child:
            default:
                // let createdComp = compCreator({ context, parent: parentInst, events: outBoundEvents, props });
                retVal.instance = <IComponent>compCreator;
                retVal.el = compCreator.Render() as IDomElement | undefined;
                retVal.compGroupType = CompGroupType.Local
                break;
        }
        this.doStyle(dory, componentCollection);
        return retVal;
    }
    private doStyle(dory: Dory, componentCollection: IComponentCollection) {

        //Delete pageBackgroundColor from the pageComponent when inside the blockComponent.
        const createrComp = (componentCollection.context.GetItemSingle('HistoryManager') as any as IHistoryManager)?.GetCreaterComp();
        if (createrComp) {
            Object.keys(componentCollection.compStyleValues ?? {}).forEach((breakPoint) => {
                const compStyleValue = componentCollection.compStyleValues?.[breakPoint];
                if (typeof compStyleValue === "object" && (compStyleValue?.['pageBackgroundColor'] || compStyleValue?.['statusBarColor'])) {
                    delete compStyleValue['pageBackgroundColor'];
                    delete compStyleValue['statusBarColor'];
                };
            });
        };
        if (componentCollection.qjsonInfo && componentCollection.qjsonInfo.ver && componentCollection.qjsonInfo.ver > 1.0 && componentCollection.compStyleValues != undefined) {  //#QJsonVersion
            dory.OperatorCollection?.componentOperator?.applyStyle?.(componentCollection);
        }
    }
}