import { IContextItem } from "./IContextItem";

export class ContextManager {
    private contextItems: Record<string, Array<IContextItem> | undefined> = {};
    constructor() {
        
    }

    /**
     * Register item to context
     * @param item item to register
     */
    public RegisterItem(item: IContextItem, options: {override?:boolean} = {override:false}   ) {
        let contextGroup = this.contextItems[item.contextName];
        if (!contextGroup || options.override) {
            contextGroup = [];
            this.contextItems[item.contextName] = contextGroup;
        }

        contextGroup.push(item);
    }

    public GetOrCreate<contextItemType extends IContextItem>(contextItemName: string, contextCreatorCb: () => contextItemType): contextItemType{
        let contextItem = this.GetItemSingle<contextItemType>(contextItemName);
        if (!contextItem){
            contextItem = contextCreatorCb();
            this.RegisterItem(contextItem);
        }
        return contextItem;
    }
    /**
     * Gets last Item. Use ! if it must exist.
     */
    public GetItemSingle<contextItemType extends IContextItem>(contextItemName: string): contextItemType | undefined {
        const contextGroup = this.GetItemGroup<contextItemType>(contextItemName);
        if (!contextGroup) {
            return undefined;
        }
        return contextGroup[contextGroup.length - 1]; //contextItem.name
    }

    public GetItemGroup<contextItemType extends IContextItem>(contextItemName: string): Array<contextItemType> | null {
        return (this.contextItems[contextItemName] as Array<contextItemType>) || null;
    }

    public ClearItemGroup(contextItemName: string) {
        delete this.contextItems[contextItemName];
    }

    public ClearItem(c: IContextItem) {
        const itemGroup = this.GetItemGroup(c.contextName);
        if (!itemGroup) {
            return;
        }
        const itemIndex = itemGroup.indexOf(c);
        itemGroup.splice(itemIndex, 1);
    }

    /**
     * Shallow copy of self
     * @param fields if specified, only puts those fields in the clone
     */
    public Clone(fields?: Array<string>): ContextManager {
        const contextCopy = new ContextManager();
        const keys = fields ?? Object.keys(this.contextItems);
        keys.forEach(contextKey => contextCopy.contextItems[contextKey] = this.contextItems[contextKey]?.map(item => item));
        return contextCopy;
    }

    /**
     * Get Contexts registered context item keys
     */
    public GetKeys(){
        return Object.keys(this.contextItems);
    }
}
