import { IQJSon } from "../../interfaces/ComponentInterfaces/IQJson";

interface PageClass {
    new(): PageInst;
    prototype: PageInst;
}
interface PageInst extends Record<string, Function | undefined> {

}
export class PageScripting {
    private cFiles: IQJSon["cFiles"];
    private pageClass?: PageClass;
    private pageClassGenerated?: boolean;
    private pageInst?: PageInst;

    constructor(cFiles: IQJSon["cFiles"]) {
        this.cFiles = cFiles;
    }

    public GetPageClass() {
        if (this.pageClassGenerated) { return this.pageClass; }

        this.pageClass = this.createPageClass();
        this.pageClassGenerated = true;
        return this.pageClass;
    }
    public GetPageInstance() {
        if (this.pageInst) { return this.pageInst; }

        return this.pageInst = this.createPageInstance();
    }

    private createPageInstance() {
        const pageClass = this.GetPageClass();
        if (!pageClass) { return undefined; }

        const pageInst = new pageClass();
        return pageInst;
    }

    private createPageClass() {
        if (!this.cFiles) { return undefined; }

        const cFiles = this.cFiles;
        const script = Object.keys(cFiles).map(fileName => cFiles[fileName]).join("\r\n");
        const pageGenerator = new Function("exports", script);
        const exports: { Page?: PageClass } = {};
        pageGenerator(exports);
        if (!exports.Page) { return undefined; }

        return exports.Page;
    }

}