






import Vue from "vue";
export default Vue.extend({
  name: "VCol",
  props: {
    customCompClass: {
      default: "col",
      type: String,
    },
      noGutters: {
         type: Boolean,
    },
  },
  destroyed() {
    this.$el?.remove();
  },
  methods: {
    setClass() {
      if (this.noGutters == false) {
        return this.customCompClass;
      } else {
        return "no-gutters";
      }
    },
  },
});
