import { ContextManager } from "../../../../shrimp/context";
import { IComponentCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IDynamicPropComponent } from "../../../../shrimp/interfaces/ComponentInterfaces/IDynamicComponentProp";
import { IExternalEvents } from "../../../../shrimp/interfaces/ComponentInterfaces/IExternalEvents";
import { IExternalProp } from "../../../../shrimp/interfaces/ComponentInterfaces/IExternalProp";
import { IVnode } from "../../../../shrimp/interfaces/ComponentInterfaces/IVnode";
import { TSComponentBase } from "../../../../shrimp/interfaces/ComponentInterfaces/TSComponentBase";
import { IDictionary } from "../../../../shrimp/interfaces/IDictionary";
import { PartialDisplayHookCb } from "../../../../shrimp/interfaces/RenderingInterfaces/IDory";
import { IDoryJr } from "../../../../shrimp/interfaces/RenderingInterfaces/IDoryJr";
import { RenderingContext } from "../../Context/RenderingContext";
import { ScopeContext } from "../../Context/ScopeContext";
import { Dory } from "../../Dory";

export default class CustomChild extends TSComponentBase implements IDynamicPropComponent {
    protected props: IDictionary<any>
    protected events: IDictionary<Function>
    protected parentInstance: any
    protected dory: Dory | undefined;
    public doryJr: IDoryJr | undefined;
    protected childName: string;
    protected context: ContextManager;
    protected containerView: unknown;
    protected renderingContext: RenderingContext;
    protected customChilds: IDictionary<IVnode> | undefined;
    protected tsWrapperInstance?: any;
    protected displayCb: PartialDisplayHookCb | undefined;

    constructor({ props, events, parent, context }: { props: IDictionary<any>, events: IDictionary<Function>, parent: any, context: ContextManager }) {
        super({ events: events, selfEventList: ["onLoad"] });
        this.props = props;
        this.childName = this.props.ChildName;
        this.props.QJsonPath = { get: () => { return this.ChildName }, set: (value) => { this.ChildName = value }, watcher: true };
        this.events = events;
        this.parentInstance = parent;
        this.context = context;
        this.renderingContext = this.context.GetItemSingle<RenderingContext>(RenderingContext.ContextName)!;
        const selfCompColl = this.props._renderingProps.compCollCb() as IComponentCollection;
        this.containerView = this.renderingContext.HistoryBasedConf.createContainerView!(selfCompColl.GetCompUID());
    }

    public Render(tsWrapperInstance?) {
        this.tsWrapperInstance = tsWrapperInstance;
        this.updateRender();
        return this.containerView;
    }

    set ChildName(_childName: string) {
        this.childName = _childName;
        this.updateRender();
        this.Render({});
    }
    get ChildName() {
        return this.childName;
    }

    private updateRender() {
        const scopeContext = this.context.GetItemSingle<ScopeContext>(ScopeContext.ContextName);
        const doryContext = this.context.GetItemSingle<Dory>(Dory.ContextName);
        if (!scopeContext && doryContext?.isEditorInEditState) {
            this.renderingContext.HistoryBasedConf.updateContainerView!({ givenObject: this.containerView, elements: this.ChildName ? this.ChildName : 'Warning: Set ChildName property!', pageShift: false });
            return;
        }
        this.renderingContext.HistoryBasedConf.updateContainerView!({ givenObject: this.containerView, elements: scopeContext ? scopeContext.children![this.ChildName] : '', pageShift: false });
    }

    async GetProps(propsCB: (props?: Array<IExternalProp>) => void) {

        const props = this.doryJr?.GetProps();
        return propsCB(props);
    }

    async GetEvents(eventsCB: (events?: Array<IExternalEvents>) => void) {

        const events = this.doryJr?.GetEvents();
        return eventsCB(events);
    }

    $destroy() {
        if (!this.doryJr) {
            return;
        }
        this.doryJr.Destroy();
    }
}