import { IContextDepTarget, IStore } from "../../../shrimp/interfaces/quick/IStore";
import { PropCalcContext } from "./PropCalcContext";
import { HistoryManager } from "./HistoryManager";

export class PropDepTracker {

    /**
     * Create a new object with one way binded
     * @param bindObject Object on which to add or modify the property. This can be a native JavaScript object (that is, a user-defined object or a built in object) or a DOM object.
     * @param field The property name
     * @param depGetter Get method of subscribed fields
     * @param depTriggerer Trigger method of subscribers
     */
    public createDepTracker(bindObject: IStore, field: string | number | symbol, depGetter: (context: PropCalcContext) => IContextDepTarget | undefined, depTriggerer: Function, propCalcContext: PropCalcContext) {
        let fieldValue: any;

        Object.defineProperty(bindObject["contextItems"], field, {
            set: (value) => {
                fieldValue = value;
                let currPage = propCalcContext.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last();
                let pageID = currPage?.pageId;
                let propCalcParam = currPage?.context.GetItemSingle<PropCalcContext>(PropCalcContext.ContextName);

                let dpndnts = bindObject.getDependants(field, pageID!)
                this.propSetter(depTriggerer, dpndnts, propCalcParam!);
            },
            get: () => {    
                let currPage = propCalcContext.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.currentLevel().last();
                let pageID = currPage?.pageId;
                let propCalcParam = currPage?.context.GetItemSingle<PropCalcContext>(PropCalcContext.ContextName);

                bindObject.addDependant(field, pageID!, propCalcParam!);
                this.propGetter(depGetter, propCalcParam!);
                return fieldValue;
            }, enumerable: true,
            configurable: true
        }
        );
    }
    /**
     * Getter of one way binded object
     * @param depGetter Get method of subscribed fields
     * @param subscribers Subscriber list of new object
     * @param retVal Return value of new object
     */
    private propGetter(depGetter: (context: PropCalcContext) => IContextDepTarget | undefined, propCalcContext: PropCalcContext) {
        depGetter(propCalcContext);
    }

    /**
     * Setter of one way binded object
     * @param deepTriggerer Trigger method of subscribers
     * @param subscribers Subscriber list of new object
     */
    private propSetter(deepTriggerer: Function, subscribers: Record<string, IContextDepTarget>, propCalcContext: PropCalcContext) {
        if(!subscribers){ return; }
        Object.keys(subscribers).forEach(key => deepTriggerer({ depTarget: subscribers[key], self: propCalcContext }))
    }
}