import { IComponent } from "../../../../../common/shrimp/interfaces/ComponentInterfaces/IComponent";
import { CompGroupType } from "../../../../../common/shrimp/interfaces/quick/IWebSDK";
import { devExpressLibrary } from "./DevexpressComponents";
import { localComponents } from "./LocalComponents";
import { vuetifyLibrary } from './VuetifyComponents';
import { chartLibrary } from './ChartComponents';


export interface ComponentTarget {
    compInstance: Function | any,
    CompType?: CompGroupType
}
export class ComponentProvider {
    public static fetchLibrary: Function

    public static getComponentList = function (companyLibraryGroup: Array<CompGroupType>): Record<CompGroupType, any> {
        var componentLibrary: Record<string, Record<string, any>> = {};
        if (companyLibraryGroup.includes(CompGroupType.Vuetify)) {
            componentLibrary[CompGroupType.Vuetify] = vuetifyLibrary;
        } if (companyLibraryGroup.includes(CompGroupType.DevExpress)) {
            componentLibrary[CompGroupType.DevExpress] = devExpressLibrary;
        } if (companyLibraryGroup.includes(CompGroupType.Chart)) {
            componentLibrary[CompGroupType.Chart] = chartLibrary;
        } if (companyLibraryGroup.includes(CompGroupType.Local)) {
            componentLibrary[CompGroupType.Local] = localComponents
        }
        return componentLibrary;
    }

    public static getComponentProps = function (companyLibraryGroup: string, name: any) {
        return ComponentProvider.getLibrary(companyLibraryGroup)[name].props
    }

    public static getComponentEvents = function (companyLibraryGroup: string, name: any) {
        return ComponentProvider.getLibrary(companyLibraryGroup)[name].methods
    }

    public static getComponentConst = function (name: any): ComponentTarget | undefined {
        let overrideCompParent = ComponentProvider.overrideComp?.(name);
        let returnTarget: ComponentTarget | undefined;
        if (overrideCompParent) {
            returnTarget = { compInstance: overrideCompParent, CompType: CompGroupType.Vuetify }
        } else if (vuetifyLibrary[name]) {
            returnTarget = { compInstance: vuetifyLibrary[name], CompType: CompGroupType.Vuetify }
        } else if (localComponents[name]) {
            returnTarget = { compInstance: localComponents[name].module ? localComponents[name].module : localComponents[name], CompType: CompGroupType.Local }
        } else if (devExpressLibrary[name]) {
            returnTarget = { compInstance: devExpressLibrary[name].module, CompType: CompGroupType.DevExpress }
            returnTarget.compInstance.options.name = name;
        } else if (chartLibrary[name]) {
            returnTarget = { compInstance: chartLibrary[name], CompType: CompGroupType.Chart }
        }
        return returnTarget;
    }
    public static editorcompList = {
        "Messaging": 1,
        "QTimer": 1,
        "QIdleTimer": 1

    }
    public static checkComponents(name: string): Promise<true> | Boolean {
        if (vuetifyLibrary[name] || ComponentProvider.editorcompList[name]) {
            return true;
        }
        const lazyComponent = localComponents[name] || devExpressLibrary[name];
        if (lazyComponent) {
            if (lazyComponent.loaded === false) {
                return new Promise((res, rej) => {
                    lazyComponent.importFunc().then?.(() => {
                        res(true);
                    });
                });
            }
            return true;
        }
        return false;
    }

    public static getLibrary(companyLibraryGroup: string) {
        var componentLibrary: any = { ...vuetifyLibrary, ...devExpressLibrary, ...chartLibrary };
        return componentLibrary;
    }

    public static overrideComp: (compName: string) => (IComponent | null) | undefined;


    public static setOverrideComp(cb: (compName: string) => IComponent | null) {
        ComponentProvider.overrideComp = cb;
    }
};
