import { ContextManager, IContextItem } from "../../../shrimp/context";
import { Hook } from "../../../shrimp/helpers/hook";
import { Logger } from "../../../shrimp/helpers/logger";
import { ILRID } from "../../../shrimp/interfaces/ComponentInterfaces/ILocalResource";
import { IQJSon } from "../../../shrimp/interfaces/ComponentInterfaces/IQJson";
import { IDictionary } from "../../../shrimp/interfaces/IDictionary";
import { CreatePromiseData } from "../../../shrimp/interfaces/IPromiseData";
import { IDory } from "../../../shrimp/interfaces/RenderingInterfaces/IDory";
import { ISettingModelsContext } from "../../../shrimp/interfaces/RenderingInterfaces/IRenderer";
import { IContainerModel } from "@stechquick/algae/lib/quick/IContainerModel";
import { HostTriggerMockManager } from "../Managers/HostTriggerMockManager";
import { HosTriggerClientStaticMockType, HosTriggerClientStaticType, HostTriggerClientStaticFuncType } from "@stechquick/algae/lib/quick/HostTrigger";


export class SettingModelsContext implements ISettingModelsContext, IContextItem {
    static ContextName: string = "SettingModelsContext";
    contextName: string = SettingModelsContext.ContextName;

    public PipelineChangeHook: Hook<() => void> = new Hook("PipelineChangeHook");
    public AlertChangeHook: Hook<() => void> = new Hook("AlertChangeHook");
    public ContainerConfigurationChangeHook: Hook<() => void> = new Hook("ContainerConfigurationChangeHook");


    private context: ContextManager;
    private pipelineMethods?: IDictionary<Function> = {};
    private pipelineQjson?: IQJSon;
    private alertQjson?: IQJSon;
    private loadingQjson?: IQJSon;
    private hostTriggerModel?: IContainerModel;

    private dory: IDory;

    constructor({ context, dory }: { context: ContextManager, dory: IDory }) {
        this.context = context;
        this.dory = dory;
    }

    public async SetPipelineQjson(qjson: IQJSon): Promise<void> {
        this.pipelineQjson = qjson;
        this.PipelineChangeHook.triggerer();
        const renderedPipelineQJson = await this.renderQJson({ qjson, compUIDPrefix: "pipeline", methodCB: (methods) => this.pipelineMethods = methods });
        const logBulkSize = this.dory.GetLogParams()?.logBulkSize;
        Logger.Init({ callBack: (messages) => this.TriggerPipeline("logHandler", { messages }), bulkSize: logBulkSize })

        return renderedPipelineQJson.prom;
    }

    public GetPipelineQjson(): IQJSon | undefined {
        return this.pipelineQjson;
    }

    private async renderQJson({ qjson, compUIDPrefix, methodCB }: { qjson: IQJSon, compUIDPrefix: string, methodCB?: (methods: IDictionary<Function> | undefined) => void }) {
        const prom = CreatePromiseData<void>();
        const retDoryJr = this.dory.CreateDoryJr({ compUIDPrefix });
        retDoryJr.setCallbackDisplay(async () => {
            methodCB?.(retDoryJr.GetMethods());
            prom.resolver();
        });
        await retDoryJr.Render({ qjson: qjson, compParentInst: null });
        return { prom: prom.promise, retDoryJr };
    }
    public SetAlertQjson(qjson: IQJSon) {
        this.alertQjson = qjson;
        this.AlertChangeHook.triggerer();
    }
    public GetAlertQjson(): IQJSon | undefined {
        return this.alertQjson;
    }

    public SetLoadingQjson(qjson: IQJSon) {
        this.loadingQjson = qjson;
    }
    public GetLoadingQjson(): IQJSon | undefined {
        return this.loadingQjson;
    }
    public SetGlobalLocalizationQjson(qJson: IQJSon) {
        const globalLR: ILRID | undefined = qJson.clrids;
        this.dory.SetGlobalLRDict(globalLR);
    }

    public SetHostTriggerModel(model: IContainerModel | undefined) {
        this.hostTriggerModel = model;
        this.ContainerConfigurationChangeHook.triggerer();
    }
    public GetHostTriggerModel(): IContainerModel | undefined {
        return this.hostTriggerModel;
    }

    public TriggerPipeline(methodName: string, methodArgs: Record<string, any>): Promise<any> | undefined {
        if (this.pipelineMethods?.[methodName]) {
            return this.pipelineMethods![methodName]?.call(undefined, methodArgs);
        }
        return undefined;
    }

    public RerenderPipelineQjson() {
        if (!this.pipelineQjson) {
            return undefined;
        }
        return this.SetPipelineQjson(this.pipelineQjson);
    }
}