import { CssOrderManager } from "./cssOrderManager";
export class StyleDomInteractor {
  private styleTag: HTMLStyleElement;
  private isAttached = false;
  private ruleList?: CSSRuleList;
  constructor(
    private readonly uid: string,
  ) {
    this.styleTag = this.createStyleTag();
  }
  private createStyleTag() {
    const styleTag = document.createElement("style");
    styleTag.id = "styleManager_" + this.uid;
    styleTag.appendChild(document.createTextNode(""));
    
    this.attach(styleTag);
    
    return styleTag; 
  }


  public attach(styleTag?: HTMLStyleElement) {
    if (this.isAttached) { return; }

    styleTag && (this.styleTag = styleTag);
    if (!this.styleTag) { return; }

    CssOrderManager.getPlaceHolderDom("style").insertAdjacentElement("afterend", this.styleTag);

    this.ruleList && Array.from(this.ruleList).reverse().forEach(rule => this.styleTag.sheet!.insertRule(rule.cssText));
    this.isAttached = true;
  }
  public detach() {
    if (!this.isAttached) { return; }

    this.isAttached = false;    
    this.ruleList = this.styleTag.sheet!.rules;
    this.styleTag.remove();
  }
  public reset() {
    this.detach();
    this.styleTag = this.createStyleTag();
  }

  public clearRule(index: number) {
    this.styleTag.sheet!.deleteRule(index);
    this.styleTag.sheet!.insertRule("_{}", index);
  }
  public insertRule(mediaRule: string, index: number) {
    this.styleTag.sheet!.insertRule(mediaRule, index);
  }
}