import { TypeHelper } from "./typeHelper";

export interface IClonable extends Record<string, any> {
    CreateSelf(): IClonable;
}

export class Clonable{
    public static DeepClone(obj: IClonable){
        const clone = obj.CreateSelf();
        const th = new TypeHelper();
        Object.keys(obj).forEach(key => clone[key] = th.cloneObject(obj[key]));
        return clone;
    }
}