import { Logger, LogType } from "../../../../shrimp/helpers/logger";
import { IComponentCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IDirectiveCollection, IDirectiveElement } from "../../../../shrimp/interfaces/ComponentInterfaces/IDirectiveCollection";
import { IEventFunctions } from "../../../../shrimp/interfaces/ComponentInterfaces/IEventFunctions";
import { IDictionary } from "../../../../shrimp/interfaces/IDictionary";
import { IDictionaryNullable } from "../../../../shrimp/interfaces/IDictionaryNullable";
import { Dory } from "../../Dory";
import { Evaluator } from "../../Managers/Evaluator";
import { EventData } from "./EventData";

export class DirectiveCollection implements IDirectiveCollection {
    private directiveEventDict: IDictionary<Record<string, EventData>>;
    private directives: Array<IDirectiveElement> | undefined;
    private ownerComponent!: IComponentCollection;
    private dory: Dory;

    constructor(dory: Dory) {
        this.directiveEventDict = {};
        this.dory = dory;
    }

    SetOwner({ ownerComponent }: { ownerComponent: IComponentCollection }): void {
        this.ownerComponent = ownerComponent;
        Object.keys(this.directiveEventDict).map(directiveName => {
            for (const key in this.directiveEventDict[directiveName]) {
                this.directiveEventDict[directiveName]?.[key]?.SetOwner({ ownerComponent: this.ownerComponent });
            }
        });
    }

    Trigger({ directiveName, handlerName, eventArguments = [] }: { directiveName: string, handlerName: string, eventArguments?: Array<any> | IDictionaryNullable<any> }): any {
        const targetEvent = this.directiveEventDict[directiveName];
        if (!targetEvent) {
            const engineYamlLogType = this.dory.GetLogParams()?.minEngineLogLevel;
            Logger.Log({ logMessage: `no directive named ${directiveName} on ${this.ownerComponent!.compId} type ${this.ownerComponent!.compType}`, logType: LogType.Warning, minEngineLogLevel: engineYamlLogType });
            return null;
        }

        const evaluator = new Evaluator();
        Object.keys(targetEvent).map(handlerName => {

            let dataSource: IDictionary<any> | null;
            //from comp code
            if (eventArguments instanceof Array) {
                //possible vue fix :/
                dataSource = this.MatchEventDataToArguments({ eventData: targetEvent[handlerName], eventArguments });
            } else {
                dataSource = eventArguments;
            }
            dataSource = Object.assign({}, targetEvent.scopeObject, dataSource);
            const retVal = targetEvent[handlerName].Trigger({ evaluator, dataSource });
            return retVal;
        })

    }

    private GenerateTriggerFunction({ directiveName, handlerName }: { directiveName: string, handlerName: string }): Function {
        var self = this;
        return function (...eventArguments: Array<any>): any {
            return self.Trigger({ directiveName, handlerName, eventArguments });
        };
    }

    GetEventBoundFunctions(): IEventFunctions {
        let eventFunctions: IDictionary<Function> = {};
        let targetDict: IDictionary<Function>;
        Object.keys(this.directiveEventDict).map(directiveName => {
            targetDict = eventFunctions;
            Object.keys(this.directiveEventDict[directiveName]).map(handlerName => {
                targetDict[directiveName] = this.GenerateTriggerFunction({ directiveName, handlerName });
            });
        });
        return { eventFunctions };
    }

    private MatchEventDataToArguments({ eventData, eventArguments }: { eventData: EventData, eventArguments: Array<any> }) {
        if (!eventData.eventParams) {
            return null;
        }

        const loopLimit = eventData.eventParams.length < eventArguments.length ? eventData.eventParams.length : eventArguments.length;

        let retVal: Record<string, any> = {};
        for (let i = 0; i < loopLimit; i++) {
            let paramName = eventData.eventParams[i].name;
            retVal[paramName] = eventArguments[i];
        }
        return retVal;

    }

    /**
     * Collect all the event as a directive
     * @param directiveName Directive name that will be registered with its name 
     * @param eventData Directive event that will be added on dictionary
     */
    RegisterDirectiveEvent(directiveName: string, eventData: EventData): void {
        this.directiveEventDict[directiveName] = { [eventData.eventName]: eventData };
    }


    /**
     * Register a directive on the lise
     * @param directive Current directive that will be added on the list
     */
    RegisterDirective(directive: IDirectiveElement) {
        this.directives = this.directives || [];
        this.directives.push(directive);
    }

    /**
     * Return all the directives on this collecyion
     */
    GetDirectives() {
        return this.directives;
    }
}