import { ContextManager } from "../../../../shrimp/context";
import { IComponentCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IEventData } from "../../../../shrimp/interfaces/ComponentInterfaces/IEventData";
import { QJsonHelper } from "../../../../shrimp/helpers/QJsonHelper";
import { ScriptLang } from "../../../../shrimp/interfaces/Scripting/scriptLang";
import { IEvaluator } from "../../../../shrimp/interfaces/Scripting/IEvaluator";
import { PerfSingles } from "../../../../shrimp/performance";
import { RenderingContext } from "../../Context/RenderingContext";
import { Dory } from "../../Dory";
import { IQPromiseInterractionCollection, QPromiseInterractionCb, QPromise } from "../../Managers/QPromise";
import { IQuickFlowModelRuntime } from "../../../../shrimp/interfaces/ComponentInterfaces/ICompJson";
import { Logger, LogType } from "../../../../shrimp/helpers/logger";

type EventDataType = "event" | "directive";
export class EventData implements IEventData {
    eventName: string
    private ownerComponent?: IComponentCollection;
    handler: string | IQuickFlowModelRuntime;
    eventParams?: Array<{ name: string, type?: string }>;
    creationContext: ContextManager;
    public immediate: boolean;
    customEvent: boolean;
    propertyEvent: boolean;
    triggerOrder: number | null;
    externalVisible?: boolean;
    handlerLang?: ScriptLang;
    scopeObject?: Record<string, any>;
    private promisses: Array<Promise<any>>;
    private promiseCallbacks: IQPromiseInterractionCollection;
    private eventType: EventDataType;
    constructor({ eventName, handler, context, eventParameters = undefined, immediate, customEvent, propertyEvent, triggerOrder = null, externalVisible, handlerLang, scopeObject, eventType }:
        { eventName: string, handler: string | IQuickFlowModelRuntime, context: ContextManager, eventParameters?: string | undefined, immediate?: boolean | undefined, customEvent?: boolean, propertyEvent?: boolean | undefined, triggerOrder?: number | null, externalVisible?: boolean, handlerLang?: ScriptLang, scopeObject?: Record<string, any>, eventType: EventDataType }) {
        this.handler = handler;
        this.eventParams = QJsonHelper.ParseParameters(eventParameters, handlerLang);
        this.eventName = eventName;
        this.immediate = immediate == undefined ? false : immediate;
        this.customEvent = customEvent || false;
        this.propertyEvent = propertyEvent || false;
        this.triggerOrder = triggerOrder;

        this.creationContext = context;
        this.externalVisible = externalVisible;
        this.handlerLang = handlerLang;
        this.scopeObject = scopeObject;
        this.eventType = eventType;
        this.promisses = [];
        const onPromiseInterraction: QPromiseInterractionCb = (prom: QPromise<any>) => { if (prom.Finalized) { return; } this.promisses.push(prom); };
        this.promiseCallbacks = { onThen: onPromiseInterraction, onCatch: onPromiseInterraction, onFinally: onPromiseInterraction };
    }

    public SetOwner({ ownerComponent }: { ownerComponent: IComponentCollection }) {
        this.ownerComponent = ownerComponent;
    }
    public GetOwner(owners: { ownerComponent?: IComponentCollection, ownerEvent?: IEventData }, dory: Dory) {
        dory.errorInfo.errorOwnerInfo!.componentId = owners.ownerComponent!.compEID;
        dory.errorInfo.errorOwnerInfo!.componentType = owners.ownerComponent!.compType;
        dory.errorInfo.errorOwnerInfo!.fieldName = owners.ownerEvent!.eventName;
    }
    public Trigger({ evaluator, dataSource }: { evaluator: IEvaluator, dataSource: any }): any {
        const renderingContext = this.creationContext.GetItemSingle<RenderingContext>(RenderingContext.ContextName);
        const dory = this.creationContext.GetItemSingle<Dory>(Dory.ContextName)!;

        var returnVal = renderingContext?.executeOrDelayEvent({
            cb: () => {
                PerfSingles.Instance().Start(this.ownerComponent!.compId + "." + this.eventName + "_Real");
                const oldOwners = renderingContext.SetOwnersAndRetrieveOld({ ownerComponent: this.ownerComponent, ownerEvent: this });
                const oldCbs = QPromise.setStaticCallbacksAndRetrieveOld(this.promiseCallbacks);
                let retVal: any;
                try {
                    retVal = evaluator.Evaluate({ script: this.handler, dataSource, context: this.creationContext, lang: this.handlerLang, eventName: this.eventName });
                } catch (err) {
                    const error = err as Error;
                    const logMessage = `Evaluation Failed on ${this.eventType}: ${this.eventName} on compUID: ${this.ownerComponent!.GetCompUID()} >> ${error.message}`;
                    Logger.Log({ logMessage, logType: LogType.Error });
                    error.message = logMessage;
                    throw error;
                } finally {
                    QPromise.setStaticCallbacksAndRetrieveOld(oldCbs);
                    let owners = renderingContext.SetOwnersAndRetrieveOld(oldOwners);
                    PerfSingles.Instance().End(this.ownerComponent!.compId + "." + this.eventName + "_Real", this as IEventData);
                    this.GetOwner(owners, dory);
                    this.awaitAllPromises(() => dory.OperatorCollection?.eventOperator?.eventCompleteCb({
                        pageId: this.ownerComponent!.RootHistoryItem.pageId,
                        compUID: this.ownerComponent!.GetCompUID(), eventName: this.eventName, isPageComplete: dory.PageCompletedHook.IsTriggered()
                    }));
                }
                return retVal;
            },
            immediateEvent: this.immediate,
            triggerOrder: this.triggerOrder
        });

        return returnVal;

    }

    public AttachCompletionPromise(prom: Promise<any>) {
        this.promisses.push(prom);
    }

    private async awaitAllPromises(cb: () => void) {
        let proms: Array<Promise<any>>;
        const sweep = () => { proms = this.promisses, this.promisses = []; };
        sweep();

        while (proms! && proms.length > 0) {  // typescript@4.2.4 iin npm install pls 
            const prom = Promise.all(proms);
            prom.finally(sweep);
            await prom;
        }
        cb();
    }
}