import InputMask from "inputmask";
import { ContextManager } from "../../../../../../common/shrimp/context";
import { DomHelper } from "../../../../../../common/shrimp/helpers/domHelper";
import { IComponent } from "../../../../../../common/shrimp/interfaces/ComponentInterfaces/IComponent";
import { IComponentCollection } from "../../../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IVnode } from "../../../../../../common/shrimp/interfaces/ComponentInterfaces/IVnode";
import { IWebSDKSettingsWrapper } from "../../../../../../common/shrimp/interfaces/quick/IWebSDK";
import { RegionalManagerImpl } from "../../../implementation/RegionalManagerImpl";
import { IExtensionData } from "./IExtensionData";
import  {QuickInputFormat} from "../../../../../../library/quickInputFormat/quickInputFormat";

export class VTextFieldFormattingExtensionData implements IExtensionData {
    private settingsWrapper: IWebSDKSettingsWrapper;
    private websdkContext: ContextManager;
    private datadata = ["groupSeparator", "radixPoint", "enforceDigitsOnBlur", "digits", "digitsOptional"];
    private ibanStructures = "(AL99 9999 9999 9999 9999 9999 9999)|(AD99 9999 9999 9999 9999 9999)|" +
        "(AT99 9999 9999 9999 9999)|(AZ99 9999 9999 9999 9999 9999 9999)|(BH99 AAAA 9999 9999 9999 99)|" +
        "(BY99 AAAA 9999 9999 9999 9999 9999)|(BE99 9999 9999 9999)|(BA99 9999 9999 9999 99)|" +
        "(BR99 9999 9999 9999 9999 9999 999A 9)|(BG99 AAAA 9999 9999 9999 99)|(CR99 9999 9999 9999 9999 99)|" +
        "(HR99 9999 9999 9999 9999 9)|(CY99 9999 9999 9999 9999 9999 9999)|(CZ99 9999 9999 9999 9999 9999)|" +
        "(DK99 9999 9999 9999 99)|(DO99 9999 9999 9999 9999 9999 9999)|(EG99 9999 9999 9999 9999 9999 9999)|" +
        "(SV99 AAAA 9999 9999 9999 9999 9999)|(EE99 9999 9999 9999 9999)|(FO99 9999 9999 9999 99)|" +
        "(FI99 9999 9999 9999 99)|(FR99 9999 9999 9999 9999 9999 999)|(GE99 AA99 9999 9999 9999 99)|" +
        "(DE99 9999 9999 9999 9999 99)|(GI99 AAAA 9999 9999 9999 9999)|(GR99 9999 9999 9999 9999 9999 999)|" +
        "(GL99 9999 9999 9999 99)|(GT99 AAAA 9999 9999 9999 9999 9999)|(VA99 9999 9999 9999 9999 99)|" +
        "(HU99 9999 9999 9999 9999 9999 9999)|(JO99 AAAA 9999 9999 9999 9999 9999 99)|(KZ99 9999 9999 9999 9999)|" +
        "(XK99 9999 9999 9999 9999)|(KW99 AAAA 9999 9999 9999 9999 9999 99)|(LV99 AAAA 9999 9999 9999 9)|" +
        "(LB99 9999 9999 9999 9999 9999 9999)|(LY99 9999 9999 9999 9999 9999 9)|(LI99 9999 9999 9999 9999 9)|" +
        "(LT99 9999 9999 9999 9999)|(LU99 9999 9999 9999 9999)|(MT99 AAAA 9999 9999 9999 9999 9999 999)|" +
        "(MD99 AA99 9999 9999 9999 9999)|(MC99 9999 9999 9999 9999 9999 999)|(ME99 9999 9999 9999 9999 99)|" +
        "(NL99 AAAA 9999 9999 99)|(MK99 9999 9999 9999 999)|(NO99 9999 9999 999)|" +
        "(PK99 AAAA 9999 9999 9999 9999)|(PS99 AAAA 9999 9999 9999 9999 9999 9)|(PL99 9999 9999 9999 9999 9999 9999)|" +
        "(PT99 9999 9999 9999 9999 9999 9)|(QA99 AAAA 9999 9999 9999 9999 9999 9)|(RO99 AAAA 9999 9999 9999 9999)|" +
        "(SM99 A999 9999 9999 9999 9999 9999)|(SA99 9999 9999 9999 9999 9999)|(RS99 9999 9999 9999 9999 99)|" +
        "(SK99 9999 9999 9999 9999 9999)|(SI99 9999 9999 9999 999)|(ES99 9999 9999 9999 9999 9999)|" +
        "(SE99 9999 9999 9999 9999 9999)|(CH99 9999 9999 9999 9999 9)|(TN99 9999 9999 9999 9999 9999)|" +
        "(TR99 9999 9999 9999 9999 9999 99)|(UA99 9999 9999 9999 9999 9999 9999 9)|(AE99 9999 9999 9999 9999 999)|" +
        "(GB99 AAAA 9999 9999 9999 99)|(VG99 AAAA 9999 9999 9999 9999)|(IS99 9999 9999 9999 9999 9999 99)|" +
        "(IQ99 AAAA 9999 9999 9999 999)|(IE99 AAAA 9999 9999 9999 99)|(IL99 9999 9999 9999 9999 999)|(IT99 A999 9999 9999 9999 9999 999)";

    
    constructor(settingsWrapper: IWebSDKSettingsWrapper, websdkContext: ContextManager) {
        this.settingsWrapper = settingsWrapper;
        this.websdkContext = websdkContext;
    }

    create(compCollection: IComponentCollection, attrs: Record<string, any>): void {
        if (!compCollection.component) { return; }

        this.initialize(compCollection, attrs);
        const compFieldAttacher = (component: IComponent) => {
            let formatValue = null;
            Object.defineProperty(component, "formatValues", {
                set: (value) => {
                    formatValue = value;
                    attrs["formatValues"] = value;
                    this.initialize(compCollection, attrs);
                }, get: () => formatValue
            });

            let format = null;
            Object.defineProperty(component, "format", {
                set: (value) => {
                    format = value;
                    attrs["format"] = value;
                    this.initialize(compCollection, attrs);
                }, get: () => format
            
        })};
        compFieldAttacher(compCollection.component);
        compCollection.WatchCreatedFields((created) => {
            if (!created.instance) { return; }
            compFieldAttacher(created.instance);
        });
    }

    readonly extensionDictionary: Record<string, (componentCollection: IComponentCollection, attrs: Record<string, any>) => void> = {
        'number': (componentCollection, attrs) => {
            let formatValues = attrs.formatValues;
            formatValues = this.setDefaultConfigurations(componentCollection, formatValues, this.websdkContext);
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection, formatValues, attrs.preventPaste);
        },
        'bigdecimal': (componentCollection, attrs) => {
            let formatValues = attrs.formatValues as InputMask.Options;
            formatValues = this.setDefaultConfigurations(componentCollection, formatValues, this.websdkContext);
            formatValues.onBeforeMask = function (value, opts) {
                return value;
            }
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection, formatValues, attrs.preventPaste);
        },
        'creditcard': (componentCollection, attrs) => {
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection, { "mask": "9999 9999 9999 9999", "jitMasking": true }, attrs.preventPaste);
        },
        'phone': (componentCollection, attrs) => {
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection, { "jitMasking": true }, attrs.preventPaste, '999 999 99 99');
        },
       'email': (componentCollection, attrs) => {VTextFieldFormattingExtensionData.createNewInputMask(componentCollection, {"jitMasking": true, "mask": "*{1,}@*{1,}.*{2,4}", "definitions": {'*': {validator: "[a-zA-Z0-9._-]", cardinality: 1}}}, attrs.preventPaste, "email");
        },
        'ipaddress': (componentCollection, attrs) => {
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection, { "jitMasking": true }, attrs.preventPaste, "ip");
        },
        'iban': (componentCollection, attrs) => {
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection, { "jitMasking": true, casing:"upper" }, attrs.preventPaste, this.ibanStructures);
        },
        'onlyAlphabetic': (componentCollection, attrs) => {
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection, { regex: '[a-zA-Z ]+', "greedy": false, "jitMasking": true }, attrs.preventPaste);
        },
        'alphaNumeric': (componentCollection, attrs) => {
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection, { regex: '^[a-zA-ZiI0-9\.]*$' }, attrs.preventPaste);
        },
        'turkishMobilePhone': (componentCollection, attrs) => {
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection, { regex: '^5([0-9]\{2} )([0-9]\{3} )([0-9]\{2} )([0-9]\{2})$', "greedy": false }, attrs.preventPaste);
        },
        'turkishPhoneWithCountryCode': (componentCollection, attrs) => {
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection,
                { "mask": "+\\90 599 999 99 99", "placeholder":"" }, attrs.preventPaste);
        },
        'internationalPhone': (componentCollection, attrs) => {
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection,
                { "mask": "(+(999) 999 9999|+(999) 999 999 9999|+(999) 99 999 9999|+(999) 9999 9999|+(99) 9999999999|+(99) 9999 9999|+(99) 99999999|+(9) 999 999 9999)" }, attrs.preventPaste);
        },
        'multipleEmails': (componentCollection, attrs) => {
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection,
                { regex: '^(([a-zA-Z0-9\.\-_]+)@((\[[0-9]\{1,3}\.[0-9]\{1,3}\.[0-9]\{1,3}\.)|(([a-zA-Z0-9\-]+\.)+))([a-zA-Z]\{2,4}|[0-9]\{1,3})(\]?)( *; *| *$))*$' }, attrs.preventPaste);
        },
        'regex': (componentCollection, attrs) => {
            let formatValues = attrs.formatValues as InputMask.Options;
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection, { regex: formatValues?.regex, "jitMasking": true }, attrs.preventPaste);
        },
        'turkishIban': (componentCollection, attrs) => {
            VTextFieldFormattingExtensionData.createNewInputMask(componentCollection,
                { "mask": "99 9999 9999 9999 9999 9999 99", "placeholder":"" }, attrs.preventPaste);
        }
    };

    private static createNewInputMask(componentCollection: IComponentCollection, opts, preventPaste: boolean | undefined, maskType?: string) {
        const _domElement = componentCollection.component!.$el;
        opts.onBeforePaste = (pastedValue, options) => VTextFieldFormattingExtensionData.beforePaste(componentCollection, pastedValue, options, preventPaste);
       
        let htmlElement = this.getComponentHtmlElement(componentCollection.compType);

        let selector: any = _domElement?.querySelectorAll?.(htmlElement)?.[0] || undefined;
        if(opts.quick == true){
            if(opts.regex){
                opts.alias = 'regex'
            }
            opts.preventPaste = preventPaste;
            let inputDOM = this.findInputDOM(componentCollection);
            let inputDomElm = componentCollection.compType == "VTextarea" ? <HTMLTextAreaElement>inputDOM?.elm : <HTMLInputElement>inputDOM?.elm; 
            const quickMask = new QuickInputFormat(inputDomElm, opts);
            quickMask.run();
            componentCollection.component!.extention["getUnmaskedValue"] = componentCollection.component!["getUnmaskedValue"] = () => {
                return quickMask.getUnmaskValue();
            }
            opts = {};
            maskType = undefined;
            selector = undefined;
        }
        else{
            opts = opts as InputMask.Options;
        }

        
        if (selector) {
            const mask = maskType ? new InputMask(maskType, opts) : new InputMask(opts);
            mask.mask(selector);
            componentCollection.subscribeOnDestroy(() => {
                if (componentCollection.component) { componentCollection.component.extention = {} };
                InputMask.remove(selector);
            });
            componentCollection.component!.extention["getUnmaskedValue"] = componentCollection.component!["getUnmaskedValue"] = () => {
                //return mask.unmaskedvalue(); ????
                return (<any>componentCollection.domElement?.querySelectorAll(htmlElement))[0].inputmask.unmaskedvalue();
            }
        }
    }

    private initialize(compCollection: IComponentCollection, attrs: Record<string, any>) {
        if (attrs && attrs["format"] && compCollection.domElement && this.extensionDictionary[attrs["format"]]) {
            this.extensionDictionary[attrs["format"]](compCollection, attrs);
            // compCollection.component!.extention["getUnmaskedValue"] = compCollection.component!["getUnmaskedValue"] = () => {
            //     return (<any>compCollection.domElement?.querySelectorAll('input'))[0].inputmask.unmaskedvalue();
            // }
        }
    }

    private setDefaultConfigurations(componentCollection: IComponentCollection, attrs: Record<string, any>, websdkContext: ContextManager): InputMask.Options {
        if (!attrs)
            return {};
        const component = componentCollection.component!;
        // ? What if CompCollection has props on it, then check the DiscardFormat prop on the collection instead of the instance
        if (component.$options?.propsData?.DiscardFormat === true || component.$vnode?.["data"]?.props?.DiscardFormat === true)
            return attrs;
        var params = JSON.parse(JSON.stringify(attrs));

        const LRType = componentCollection.getDory().GetLRType();
        let currentFormattingDefinition = websdkContext.GetItemSingle<RegionalManagerImpl>(RegionalManagerImpl.ContextName)!.getFormattingDefinitionBySelectedRegion(LRType.substring(0, 2));
        
        if (currentFormattingDefinition) {
            var formatDef = currentFormattingDefinition;
            Object.keys(formatDef).map((key, index) => {
                if (this.datadata.includes(key)) {
                    if (formatDef[key].overwrite) {
                        params[key] = formatDef[key].value;
                    }
                    else {
                        if (params[key] == undefined) {
                            params[key] = formatDef[key].value;
                        }
                    }
                }
            });
        }
        return params;
    }

    private static maskComplete(componentCollection: IComponentCollection, preventPaste: boolean | undefined) {
        // Index of the input component(tag) in children array may change depends on the textfield props
        // It can be seen in 0 or 1 index 
        let inputDOM = VTextFieldFormattingExtensionData.findInputDOM(componentCollection);
        let inputComp: IVnode | undefined = inputDOM?.["context"];

        if (inputComp) {
            let value = "";
            let inputDomElm = componentCollection.compType == "VTextarea" ? <HTMLTextAreaElement>inputDOM?.elm : <HTMLInputElement>inputDOM?.elm; 
            if (preventPaste) {

                if (inputDOM && inputDOM.elm && inputDomElm?.value) {
                    inputDomElm.value = value;
                }
                inputComp["lazyValue"] = value;
            }
            else {
                value = inputDomElm?.value;
                inputComp["lazyValue"] = value;
            }
            if ((<any>inputComp).listeners$ && (<any>inputComp).listeners$.input) {
                (<any>inputComp).listeners$.input(value);
            }
        }
    }



    private static beforePaste = (componentCollection: IComponentCollection, pastedValue: string, opts: InputMask.Options, preventPaste: boolean | undefined) => {
        // Index of the input component(tag) in children array may change depends on the textfield props
        // It can be seen in 0 or 1 index 
        if (preventPaste) {
            pastedValue = "";
        }

        let inputComp: IVnode | undefined = VTextFieldFormattingExtensionData.findInputDOM(componentCollection)?.["context"];

        if (!inputComp) {
            return pastedValue;
        }
        inputComp["lazyValue"] = pastedValue;
        setTimeout(() => {
            VTextFieldFormattingExtensionData.maskComplete(componentCollection, preventPaste)
        }, 0);
        return pastedValue;
    }
    static findInputDOM(componentCollection:IComponentCollection) {
        let inputChildArray: Array<IVnode> = componentCollection.component!["_vnode"].children[0].children[0].children;
        let inputComp: IVnode | undefined;

        let htmlElement = this.getComponentHtmlElement(componentCollection.compType);

        DomHelper.findCompInNodeArray((node: IVnode) => {
            if (node.tag == htmlElement) {
                inputComp = node;
                return true;
            }
            return false;
        }, inputChildArray);
        return inputComp;
    }

    private static getComponentHtmlElement(compType:string){
        return (compType == "VTextarea" ?  "textarea" : "input");        
    }
}

