type compParams = { componentName: string, ctrl: ICompJson, e: any, props: IDictionary<any>, events: IEventCollection, compCollection: IComponentCollection, parentInstance: any, component: ICreatedComponent | null };

export class RenderingManager implements IRenderingManager {

    static ContextName: string = "RenderingManager";
    contextName: string = RenderingManager.ContextName;
    pjson: Array<ICompJson | null>;
    context: ContextManager;
    //all controls DIRECTLY including children in a dictionary. (children are also contained in ComponentCollection)
    controlDict: IDictionary<IComponentCollection>;
    controlList: Array<IComponentCollection> = [];
    evaluator = new Evaluator();
    compCommunicator = new ComponentCommunicator();
    typeHelper: TypeHelper = new TypeHelper();
    blockInfo: BlockInfo;
    private dataItem: object | null;
    private asChildType: CType;
    private asChildName?: string;
    private QLang?: ScriptLang;
    private parentCompCollection?: IComponentCollection;
    private compareCompValue: boolean;
    private dynamicCompIDSuffix?: string;
    private pageScrtipting?: PageScripting;
    private qjsonVersion?: number;
    public QJsonManagerForAIService: QJsonManagerForAIService | undefined;


    constructor({ pjson, context, dataItem = null, asChildType = CType.child, asChildName, controlDict, qLang, parentCompCollection, compareCompValue,
        dynamicCompIDSuffix, pageScripting, qjsonVersion }:
        {
            pjson: Array<ICompJson | null>, context: ContextManager, dataItem?: object | null, asChildType?: CType, asChildName?: string,
            controlDict: IDictionary<IComponentCollection>, qLang?: ScriptLang,
            parentCompCollection?: IComponentCollection, compareCompValue: boolean, dynamicCompIDSuffix?: string,
            pageScripting?: PageScripting, qjsonVersion?: number
        }) {
        this.pjson = pjson;
        this.context = context;
        this.dataItem = dataItem;
        this.asChildType = asChildType;
        this.asChildName = asChildName;
        this.controlDict = controlDict;
        const renderingmanagerContext = this.context.GetItemSingle<RenderingManager>(RenderingManager.ContextName);
        if (!renderingmanagerContext) {
            this.context.RegisterItem(this);
        }

        this.blockInfo = new BlockInfo({});
        this.context.RegisterItem(this.blockInfo);
        this.QLang = qLang;
        this.parentCompCollection = parentCompCollection;
        this.compareCompValue = compareCompValue;
        this.dynamicCompIDSuffix = dynamicCompIDSuffix;
        this.pageScrtipting = pageScripting;
        this.qjsonVersion = qjsonVersion;
        if (this.isEditMode()) {
            this.QJsonManagerForAIService = new QJsonManagerForAIService();
        }
    }

    private getLang({ self, ctrl }: { self?: ScriptLang, ctrl?: ICompJson }) {
        return LangCalc.getLang({ self, ctrl, qLang: this.QLang });
    }
    private WillRender(ctrl: ICompJson): boolean {
        if (ctrl.P.Render?.H && !this.isEditMode()) {
            // don't render

            const display = this.CalculatePropValue({ ctrl, prop: "Render", defaultValue: true });
            if (!display) {
                const dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
                const engineYamlLogType = dory?.GetLogParams()?.minEngineLogLevel;
                Logger.Log({ logMessage: `Not displaying component: ${ctrl.T} - ID : ${ctrl.P.ID.H}`, minEngineLogLevel: engineYamlLogType });
                return false;
            }
        }
        return true;
    }

    private CalculatePropValue({ ctrl, prop, defaultValue = undefined, compRef }: { ctrl: ICompJson, prop: string, defaultValue?: any, compRef?: IDictionary<IComponentCollection | null> }): any {
        let retVal: any = defaultValue;
        const value = ctrl.P[prop]?.H;
        const handlerLang = ctrl.P[prop]?.HL || 'QS';
        const handlerFile = ctrl.P[prop]?.HF;

        let propContextManager = this.context.GetItemSingle<PropCalcContext>(PropCalcContext.ContextName)!;
        if (compRef && value) {
            propContextManager.setContextTarget({ targetInstance: <object>compRef }, prop, value);
        }
        try {
            if (handlerFile) {
                retVal = this.evaluator.Evaluate2({ scriptName: value as string, pageScripting: this.pageScrtipting!, dataSource: this.dataItem, context: this.context })
            } else {
                retVal = this.evaluator.Evaluate({ script: value, dataSource: this.dataItem, context: this.context, lang: this.getLang({ self: handlerLang, ctrl }) });
            }
        } catch (e: any) {
            const dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
            const engineYamlLogType = dory?.GetLogParams()?.minEngineLogLevel;
            Logger.Log({ logMessage: `${ctrl.P.ID.H}.P.${prop} failed on comp(${ctrl.T}): ${e.message}`, error: e, logType: LogType.Warning, minEngineLogLevel: engineYamlLogType });
        } finally {
            propContextManager.clearPropTarget();
        }
        return retVal;
    }

    private GenerateProps(ctrl: ICompJson, compRef: IDictionary<IComponentCollection | null>, compCollection: IComponentCollection) {
        const props: IDictionary<any> = {};
        let attrs: IDictionary<any> = { "q_UID": compCollection.GetCompUID() };
        let compClass: string | undefined;
        let text: string | undefined;
        if (ctrl._Editor && ctrl._Editor.eID) {
            props.eID = ctrl._Editor.eID;
        }

        for (const prop in ctrl.P) {
            const propJson = ctrl.P[prop]!;
            switch (prop) {
                case "ID": {
                    props[prop] = propJson.H;
                    continue;
                }
                // case "rules": {
                //     let readonlyProp = ctrl.P?.["readonly"] ? this.evaluator.Evaluate({ script: ctrl.P["readonly"].H, context: this.context, lang: this.getLang({ ctrl }) }) : false;
                //     let disabledProp = ctrl.P?.["disabled"] ? this.evaluator.Evaluate({ script: ctrl.P["disabled"].H, context: this.context, lang: this.getLang({ ctrl }) }) : false;

                //     if (readonlyProp || disabledProp) {
                //         continue;
                //     }
                // }
            }

            if (propJson.T == PType.prop && !this.compCommunicator.ComponentHasProp(ctrl.T, ctrl.O!, prop, this.context)) {
                const dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
                const engineYamlLogType = dory?.GetLogParams()?.minEngineLogLevel;
                Logger.Log({ logMessage: `P is not implemented on comp ${ctrl.T}, prop: ${prop}`, minEngineLogLevel: engineYamlLogType, logType: LogType.Debug, });
                continue;
            }
            let value = propJson.DE ? propJson.H : this.CalculatePropValue({ ctrl, prop, compRef });

            switch (propJson.T) {
                default:
                case PType.prop: props[prop] = value; break;
                case PType.class: compClass = value; break;
                case PType.text: text = value; break;
                case PType.attr: {
                    attrs = attrs ?? {};
                    attrs[prop] = value; break;
                }
            }
        }

        if (ctrl.O && ctrl.O.layout) {
            if (compClass === undefined) {
                compClass = ctrl.O.layout.class;

            } else {
                compClass = `${compClass} ${ctrl.O.layout.class}`;
            }
        }

        if (ctrl.SA) {
            compClass = compClass ? `${compClass} ${ctrl.SA[0]}` : ctrl.SA[0];
        }

        return { props, compClass, text, attrs };
    }

    private GenerateEvents(ctrl: ICompJson, events: IEventCollection) {
        for (const key in ctrl.E) {
            const eJson = ctrl.E[key];
            if (typeof eJson.H == "object" && eJson.H.type != "flow_r") { throw new Error("uncompiled flow model in qjson"); }
            events.RegisterEvent({
                eventData: new EventData({
                    eventType: "event",
                    eventName: key,
                    handler: eJson.H,
                    context: this.context,
                    eventParameters: eJson.P,
                    immediate: eJson.I,
                    customEvent: eJson.C,
                    propertyEvent: eJson.PE,
                    triggerOrder: eJson.TO,
                    externalVisible: eJson.EV,
                    handlerLang: this.getLang({ self: eJson.HL, ctrl }),
                    scopeObject: this.dataItem as Record<string, any>
                })
            });
        }
    }

    /**
     * Generate directives of the component. Collect them into DirectiveCollection on directives fields.
     * Set handler method which has been set already on ctrl with the same name event.
     * @param ctrl ICompJson of the component
     * @param directives Directive collection of the component. Get all the directives from this collection
     */
    private GenerateDirectives({ ctrl, directives }: { ctrl: ICompJson, directives: IDirectiveCollection }) {
        if (!directives) return;
        for (const directiveName in ctrl.D) {
            let directiveElement: IDirectiveElement = { name: directiveName, value: { handler: () => { }, options: {} } };
            if (Object.prototype.hasOwnProperty.call(ctrl.D, directiveName)) {
                const directiveJson = ctrl.D[directiveName];
                Object.keys(directiveJson).map(field => {
                    const directiveField = directiveJson[field];
                    if (directiveField.P) {
                        directives.RegisterDirectiveEvent(directiveName,
                            new EventData({
                                eventType: "directive",
                                eventName: field,
                                handler: directiveField.H,
                                context: this.context,
                                eventParameters: directiveField.P,
                                handlerLang: directiveField.HL
                            }));
                    } else {
                        directiveElement.value.options = directiveElement.value.options || {};
                        directiveElement.value.options[field] = this.evaluator.Evaluate({ script: directiveField.H, dataSource: this.dataItem, context: this.context, lang: this.getLang({ ctrl }) })
                    }
                })
            }
            const directiveFunctions = directives.GetEventBoundFunctions().eventFunctions;
            directiveElement.value.handler = directiveFunctions[directiveName];
            directives.RegisterDirective(directiveElement);
        }
    }

    private CalculateStyle({ compType, compStyles, compStyleNames }: { compStyleNames?: Array<string>, compType: string, compStyles?: StyleValue }) {
        const compInlineStyles: StyleValue | undefined = new TypeHelper().cloneObject(compStyles);
        const dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
        const assetValue = dory?.GetAssetValues();
        const currentTheme = dory?.GetCurrentTheme();
        const theme = dory?.GetThemes()?.find(theme => theme.name == currentTheme?.name);
        const themeColors = currentTheme?.isLight ? theme?.lightTheme.colors : theme?.darkTheme?.colors;
        const themeFonts = currentTheme?.isLight ? theme?.lightTheme.fonts : theme?.darkTheme?.fonts;

        let calculatedStyles: StyleValue = {};
        const compStyle = assetValue?.[compType];
        compStyleNames?.forEach(name => calculatedStyles = { ...calculatedStyles, ...compStyle?.[name] })
        calculatedStyles = { ...calculatedStyles, ...compInlineStyles };
        this.matchTheme(calculatedStyles, themeColors, themeFonts);
        return Object.keys(calculatedStyles).length > 0 ? calculatedStyles : compStyle?.default;
    }

    private matchTheme(compInlineStyles: StyleValue | undefined, themeColors: IThemeColor[] | undefined, themeFonts: IThemeFont[] | undefined) {
        if (!themeColors || !themeFonts || !compInlineStyles) {
            return;
        }
        Object.keys(compInlineStyles).forEach(breakPoint => {
            if (typeof compInlineStyles[breakPoint] !== "object") {
                return;
            }
            Object.keys((<Record<string, string>>compInlineStyles)[breakPoint]).forEach(param => {
                let styleValue = compInlineStyles[breakPoint]?.[param];
                const color = themeColors.find(color => color.name == styleValue);
                compInlineStyles[breakPoint]![param] = color?.color || styleValue;
                styleValue = styleValue + ""
                if (styleValue && styleValue.indexOf("_") == 0 && styleValue.lastIndexOf("_") == styleValue.length - 1 && themeFonts) {
                    compInlineStyles[breakPoint]![param] = this.matchThemeFonts(themeFonts, styleValue);
                }
            });
        });
    }

    private matchThemeFonts(themeFonts: IThemeFont[], styleValue: string) {
        const themeSplit = styleValue.replaceAll("_", "").split(".");
        if (themeSplit.length != 2) {
            return styleValue;
        }
        const currentTheme = themeFonts.find(theme => theme.fontClassName == themeSplit[0])!;
        const configValue = currentTheme.configs[themeSplit[1]];
        if (configValue == "Italic") {
            return configValue;
        }
        return currentTheme.configs[themeSplit[1]].replace("Italic", "")
    }

    private generatePropsFromStyle(comJson: ICompJson, styles?: StyleValue) {
        let additonalCompType: propStyleArray | null = StyleHelper.additionalPropsFromStyle[comJson.T]
        if (!additonalCompType || !styles) {
            return;
        }
        let forcedProps: Record<string, boolean> | undefined = undefined;
        Object.keys(additonalCompType!).forEach(styleName => {
            const propArray = additonalCompType![styleName]
            let definedStyle = true;
            for (let index = 0; index < Object.keys(styles!).length; index++) {
                const breakPointName = Object.keys(styles!)[index];
                definedStyle = styles![breakPointName]?.[styleName] === "unset" || styles![breakPointName]?.[styleName] === "" || styles![breakPointName]?.[styleName] === undefined || styles![breakPointName]?.[styleName] === "false"
                if (!definedStyle) {
                    forcedProps = forcedProps || {};
                    forcedProps[propArray[0]] = styles![breakPointName]?.[styleName];
                    styles![breakPointName]?.[styleName] === 'true' && (forcedProps[propArray[0]] = true);
                    break;
                }
            }
            if (definedStyle) {
                return;
            }
        })
        return forcedProps;
    }

    private getItemValue(item: object, itemKey: string) {
        if (!item || !itemKey) {
            return "";
        }

        let index = itemKey.indexOf(".");
        if (index > -1) {
            let newKey = itemKey.substring(0, index);
            return this.getItemValue(item[newKey], itemKey.substring(index + 1));
        }
        else {
            return item[itemKey];
        }
    }

    private GenerateChildren(compJson: ICompJson, props: IDictionary<any>, parentCompCollection: IComponentCollection): { children?: IDictionary<Array<IComponentCollection>>, childrenCreators?: IDictionary<ScopedSlotCreatorFunc>, } | null {
        if (Object.keys(compJson.C).length <= 0) {
            return null;
        }

        let retVal: { children: IDictionary<Array<IComponentCollection>>, childrenCreators?: IDictionary<ScopedSlotCreatorFunc> } = { children: {} };

        let renderedByOutside = false;

        const calcChildForKey = ({ key, childJson, parentCompCollection, dataItem, props, renderedByOutside }:
            { key: string, childJson: IChildJson, parentCompCollection?: IComponentCollection, dataItem?: any, props: IDictionary<any>, renderedByOutside: boolean }) => {
            // This code block added for environment differences. If dataItem has no item you don't have to recreate it. 
            const hasScopedData = dataItem && Object.keys(dataItem!).length == 0 ? false : true;

            if (retVal.children[key] && !hasScopedData) {
                return retVal.children[key];
            }

            let computedDataItem = Object.assign({}, this.dataItem, dataItem);

            let newCompJson = childJson.c
            if (renderedByOutside) {
                newCompJson = new TypeHelper().cloneObject(childJson.c);
                dataItem.data.newCompID = dataItem.newCompID;
                this.changeChildJsonIDs(newCompJson, computedDataItem);
            }

            if (props.itemKey && props.uniqueSlots) {
                newCompJson.forEach(childJson => {
                    const splittedID = childJson.P.ID.H.split("_q_");
                    childJson.P.ID.H = splittedID[0] + "_q_" + this.getItemValue(dataItem?.item, props["itemKey"]);
                })
            }

            if (this.compareCompValue && parentCompCollection && compDiffHelper.componentValueList[parentCompCollection.compType] &&
                parentCompCollection.component?.[ComparisonEnum.compareValueField]) {
                compDiffHelper.compareAndSetFields({ compJson: childJson, valueField: dataItem!["item"], compareValue: parentCompCollection.component![ComparisonEnum.compareValueField], compareDataField: props["CompareDataField"], headerValue: dataItem!["header"]["value"] })
            }

            const managerOptions = {
                pjson: newCompJson,
                dataItem: computedDataItem,
                context: this.context,
                asChildType: <CType>childJson.cType,
                asChildName: key,
                controlDict: this.controlDict,
                parentCompCollection,
                compareCompValue: this.compareCompValue,
                dynamicCompIDSuffix: this.dynamicCompIDSuffix,
                pageScripting: this.pageScrtipting,
                qjsonVersion: this.qjsonVersion
            }
            const rm = new RenderingManager(managerOptions);
            rm.Render(rm.blockInfo.blockJsonIndex);
            retVal.children[key] = rm.controlList;
            if (this.isEditMode()) {
                this.QJsonManagerForAIService?.setModelChild(props.ID, key, rm.QJsonManagerForAIService);
            }
            parentCompCollection?.RootHistoryItem.ReCalculateCompInstDict();
            return rm.controlList;
        };


        for (let key in compJson.C) {
            const childJson: IChildJson = <IChildJson>compJson.C[key];
            const childFields = { key, childJson, parentCompCollection, props, renderedByOutside };
            if (compJson.T == 'InfiniteScroll') {
                childFields.renderedByOutside = true
            }
            switch (childJson.cType) {
                case CType.child:
                    calcChildForKey(childFields);
                    break;
                case CType.scopedSlot:
                    retVal.childrenCreators = retVal.childrenCreators ?? {};
                    retVal.childrenCreators[key] = ((closureObj: { key: string, childJson: IChildJson, parentCompColl: IComponentCollection, renderedByOutside: boolean }) => (scopeObject?: IDictionary<any>) => calcChildForKey({ key: closureObj.key, childJson: closureObj.childJson, parentCompCollection: closureObj.parentCompColl, dataItem: scopeObject, props, renderedByOutside: childFields.renderedByOutside }))({ key, childJson, parentCompColl: parentCompCollection, renderedByOutside });
                    break;
                case CType.slot:
                    calcChildForKey(childFields);
                    break;
            }
        }

        return retVal;
    }

    private changeChildJsonIDs(childJson: Array<ICompJson>, dataItem) {
        QJsonHelper.ForEachComp(childJson, (compJson: ICompJson) => {
            compJson.P.ID.H += '_' + dataItem.newCompID;
            return true;
        });
    }

    private isEditMode() {
        return this.context.GetItemSingle<Dory>(Dory.ContextName)?.isEditorInEditState;
    }
    private getComponentParentInstance() {
        return this.context.GetItemSingle<RenderingContext>(RenderingContext.ContextName)?.getCompParentInst();
    }
    async RenderRoot() {
        try {
            this.Render(this.blockInfo.blockJsonIndex);
        } catch (ex) {
            if (!this.blockInfo.blockPromises) {
                throw ex;
            }
            try {
                await PromiseAllSettled(this.blockInfo.blockPromises);
                //await Promise.all(this.blockInfo.blockPromises);
            } catch (ex2) {
            }
            this.Render(this.blockInfo.blockJsonIndex);
        }
    }

    Render(jsonIndexParam: number) {
        PerfSingles.Instance().Start("RenderingManagerRender");
        for (let jsonIndex = jsonIndexParam; jsonIndex < this.pjson.length; jsonIndex++) {
            const compJson = this.pjson[jsonIndex]!;
            PerfSingles.Instance().Start(compJson.T);
            if (!this.WillRender(compJson)) {
                continue;
            }

            CompJsonMigrator.fixCtrlOldStyle(compJson);
            let component: ICreatedComponent | null = null;
            let children: IDictionary<Array<IComponentCollection>> | undefined;
            let childrenCreators: IDictionary<ScopedSlotCreatorFunc> | undefined;
            let props: IDictionary<any> = {};
            let attrs: IDictionary<any> | undefined;
            let parentInstance: any;
            let compCollection: IComponentCollection = new ComponentCollection({
                asChildIndex: jsonIndex, compJson: compJson, context: this.context, asChildType: this.asChildType, asChildName: this.asChildName,
                parentCompCollection: this.parentCompCollection, compareComponent: this.compareCompValue, dynamicCompIdSuffix: this.dynamicCompIDSuffix, qjsonVersion: this.qjsonVersion
            });
            let events: IEventCollection = compCollection.events;
            let directiveFields = compCollection.directiveFields;
            let compClass: string | undefined = undefined;
            let text: string | undefined = undefined;
            try {
                const compRefCollection: IDictionary<IComponentCollection | null> = { ref: compCollection };
                ({ props, text, compClass, attrs } = this.GenerateProps(compJson, compRefCollection, compCollection));
                if (this.compareCompValue && compDiffHelper.componentValueList[compCollection.compType] && props.CompareDataField) {
                    compDiffHelper.createChipComponentOnNonSlotChilds(compJson, props);
                }
                let styles: StyleValue | undefined = undefined
                if (this.qjsonVersion && this.qjsonVersion > 1.0) {
                    styles = this.CalculateStyle({ compType: compJson.T, compStyleNames: compJson.SA, compStyles: compJson.S });
                    this.overrideChildStylesFromParent(compJson, styles);
                    const newProps = this.generatePropsFromStyle(compJson, styles);
                    Object.assign(props, newProps);
                    compCollection.SetComponentStyles(styles);
                }
                const generatedChildren = this.GenerateChildren(compJson, props, compCollection);
                children = generatedChildren?.children;
                childrenCreators = generatedChildren?.childrenCreators;
                this.GenerateEvents(compJson, events);
                this.GenerateDirectives({ ctrl: compJson, directives: directiveFields });
                parentInstance = this.getComponentParentInstance();

                compCollection.SetChildrenFields({ children, childrenCreators });
                compRefCollection.ref = compCollection;
                events.SetOwner({ ownerComponent: compCollection });
                directiveFields.SetOwner({ ownerComponent: compCollection });

                component = this.compCommunicator.CreateComponent({
                    componentName: compJson.T, props: props, events: events, context: this.context, asChildType: this.asChildType, asChildName: this.asChildName, children,
                    parentInst: parentInstance, childrenCreators, compCollection, compClass, text, attrs, compareCompValue: this.compareCompValue, directives: directiveFields.GetDirectives(),
                    styles
                });
            } catch (e) {
                const dory = this.context.GetItemSingle<Dory>(Dory.ContextName)!;
                if (mobileOnlyComponents.includes(compJson.T)) {
                    this.createMobileComp({ ctrl: compJson, e, props: props, events, compCollection, parentInstance, component, componentName: "MobileComponentWrapper" })
                } else {
                    this.createErrorComp({ ctrl: compJson, e, props: {}, events: new EventCollection(dory), compCollection, parentInstance, component, componentName: "DError" });
                }
            }
            finally {
                if (compCollection) {
                    this.Chain({ ctrl: compJson, compCollection });
                }
            }

            PerfSingles.Instance().End(compJson.T, compCollection);

            const blockInfo = this.context.GetItemSingle<BlockInfo>(BlockInfo.ContextName);
            if (blockInfo?.blockRender) {
                blockInfo.blockJsonIndex = jsonIndex + 1;
                blockInfo.blockRender = false;
                PerfSingles.Instance().End("RenderingManagerRender");
                throw new Error("Render has been blocked");
            }
        }
        PerfSingles.Instance().End("RenderingManagerRender");
    }


    private overrideChildStylesFromParent(compJson: ICompJson, styleValue?: StyleValue) {
        if (!compJson.SC || !styleValue) {
            return;
        }

        QJsonHelper.ForEachComp([compJson], (childComp, path) => {
            // works only 3 sub children below
            if (path.length < 8) {
                Object.keys(compJson.SC!).forEach(overrider => {
                    const overrideDictionary = compJson.SC![overrider];
                    const foundComp = Object.keys(overrideDictionary).find(compName => compName == childComp.T);
                    if (!foundComp) {
                        return;
                    }
                    childComp.S = childComp.S || {};
                    this.removeReservedFields(childComp, overrideDictionary[foundComp]);
                    Object.keys(styleValue!).forEach(item => {
                        if (item != 'layoutMap' && item != 'selectedLayout' && typeof styleValue![item] == "object") {
                            childComp.S![item] = childComp.S![item] || {};
                            if (this.qjsonVersion && this.qjsonVersion > 2.2 && childComp.S![item]![overrideDictionary[foundComp]]) {
                                return;
                            }
                            childComp.S![item]![overrideDictionary[foundComp]] = styleValue![item]?.[overrider];
                        }
                    });

                });
            }
            return true;
        });
    }

    private removeReservedFields(compJson: ICompJson, fieldName: string) {
        Object.keys(compJson.S!).forEach(item => {
            const styleItem = compJson.S![item];
            delete styleItem?.[fieldName];

        })
    }

    /**
     * Error component property setter function
     * @param props Property dictionary that will be used for error component
     * @param componentName Which component has not rendered
     * @param componentID PropertyID of component that was not rendered
     * @param errorMessage Message that will be prompted 
     * @returns void
     */
    private setErrorComponentProperties({ props, componentName, componentID, errorMessage }: { props: IDictionary<any>, componentName: string, componentID: string, errorMessage: string }) {
        props["visible"] = true;
        props["componentName"] = componentName;
        props["componentID"] = componentID;
        props["errorMessage"] = errorMessage;
    }

    private Chain({ ctrl, compCollection }: { ctrl: ICompJson, compCollection: IComponentCollection | null }) {
        if (!compCollection) {
            return;
        }
        this.controlDict[ctrl.P.ID.H] = compCollection;

        this.controlList.push(compCollection);

        const historyManager = this.context.GetItemSingle<HistoryManager>('HistoryManager');
        const ownerDoryLike = historyManager?.doryLike.OwnerDoryLike;
        const isRenderCompleted = (ownerDoryLike as Dory).PageCompletedHook?.IsTriggered?.() || (ownerDoryLike as DoryJr).PageJrCompletedHook?.IsTriggered?.();
        if (!this.isEditMode() && isRenderCompleted) {
            const lastHistoryItem = historyManager!.currentLevel().last();
            lastHistoryItem!.ReCalculateCompInstDict();
        }
        if (this.isEditMode()) {
            this.QJsonManagerForAIService?.setModels(ctrl);
        }
    }

    /**
     * Create a new error component when a acomponent creation is failed in somehow
     * @param ctrl Failed component json
     * @param e Message of the error
     * @param props New error component properties
     * @param events New error component events
     * @param compCollection Will be created component collection
     * @param jsonIndex Index of failed render length
     * @param parentInstance Parent of the failed component
     * @param component Wll be created component instance
     */
    private createErrorComp(params: compParams) {
        const dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
        const engineYamlLogType = dory?.GetLogParams()?.minEngineLogLevel;
        Logger.Log({ logMessage: `control name : ${params.ctrl.T}: ${params.e.message}`, logType: LogType.Error, error: params.e, minEngineLogLevel: engineYamlLogType });
        this.setErrorComponentProperties({ props: params.props, componentName: params.ctrl.T, componentID: params.ctrl.P.ID.H, errorMessage: params.e.message });

        this.createCustomComp(params)
    }

    private createMobileComp(params: compParams) {
        this.createCustomComp(params);
    }

    createCustomComp(params: compParams) {
        let { ctrl, compCollection, events, props, component, parentInstance, e, componentName } = params;
        events.SetOwner({ ownerComponent: compCollection });
        parentInstance = this.getComponentParentInstance();
        component = this.compCommunicator.CreateComponent({ componentName: componentName, props: props, events: events, context: this.context, asChildType: this.asChildType, asChildName: this.asChildName, parentInst: parentInstance, compCollection, compareCompValue: false });
        compCollection.SetComponentCreatedFields({ createdComponent: component, keepAnEyeOnCreatedCompInst: true });
    }
}
