export class WebSDK implements IWebSDK {
    // public static PlateauIAM = new PlateauIAM();
    // public static plugins: Array<any> = new Array()
    // private pageManager: PageManager;
    public static servicesWrapper: IContainerServicesWrapper; // neden static?
    public config: IRendererConfig = { SiteSettings: { compSettings: undefined } };
    private qshell: QShell;
    private rendererManager: RendererManager;
    private windowHistoryManager: WindowHistoryManager;
    public qJsonRetriever: QJsonRetrieverImpl;
    private network?: NetworkManagerImpl;
    private settingsWrapper: IWebSDKSettingsWrapper;
    private storeManager: StoreManager;
    private cookieManager: CookieManager;
    private ux: UXManagerImpl;
    private platformType: PlatformType;
    private platformManagerImp: PlatformManagerImp;
    private region: RegionalManagerImpl;
    private dataManager: DataManager;
    private hostTriggererImp: HostTriggererImp;
    private languageExtensions: LanguageExtensions;
    private websdkContext: ContextManager;
    private moduleManager: ModuleManager;
    private cssTagManager = new CssTagManager("plateauStudioClass"); // class for all websdk style tags

    constructor(settings: IWebSDKSettings, services?: IContainerServices, environmentInfo?: IDictionary<string>) {
        CssOrderManager.init();
        this.platformType = "Web";
        this.settingsWrapper = { settings: settings || {} };
        const servicesWrapper = WebSDK.servicesWrapper = { services: services || {} };
        if (services?.extensions?.customFunctions) {
            var customFuncs = services?.extensions?.customFunctions;
            Object.keys(customFuncs).forEach(key => {
                this.config[key] = customFuncs[key];
            });
        }
        this.initContainerServices(servicesWrapper);

        const websdkContext = this.websdkContext = new ContextManager();
        this.dataManager = new DataManager();
        this.dataManager.environmentName = environmentInfo?.environmentName;
        websdkContext.RegisterItem(this.dataManager);

        this.storeManager = new StoreManager({ contextName: "StoreManager" });
        this.UpdateWebSDKSettings(settings, { isSourceFromPath: true });
        this.cookieManager = new CookieManager({ contextName: "CookieManager" });
        this.ux = new UXManagerImpl(this.settingsWrapper, servicesWrapper);
        this.hostTriggererImp = new HostTriggererImp(servicesWrapper);
        this.platformManagerImp = new PlatformManagerImp(servicesWrapper);
        this.region = new RegionalManagerImpl(this.settingsWrapper);
        websdkContext.RegisterItem(this.region);
        this.qJsonRetriever = new QJsonRetrieverImpl({ contextName: 'QJsonRetriever', network: this.network!, settingsWrapper: this.settingsWrapper });
        this.moduleManager = new ModuleManager(this.settingsWrapper?.settings?.versions);
        this.languageExtensions = new LanguageExtensions(servicesWrapper);
        this.websdkContext.RegisterItem(this.languageExtensions);
        this.qshell = new QShell({
            qjsonRetriever: this.qJsonRetriever, moduleManager: this.moduleManager, network: this.network!, ux: this.ux, region: this.region,
            hostTriggerer: this.hostTriggererImp, platformType: this.platformType, platformManager: this.platformManagerImp,
        }, {
            componentOperator: new ComponentOperator(), loadingOperator: new LoadingOperator(this.storeManager), authenticationOperator: new AuthenticationOperator(), permanentStoreOperator: new PermanentStoreOperator()
        });
        this.rendererManager = new RendererManager(this.storeManager, this.settingsWrapper, servicesWrapper, websdkContext);
        this.windowHistoryManager = new WindowHistoryManager("WindowHistoryManager", this.settingsWrapper, this.rendererManager, servicesWrapper);
        if (services?.navigationManager) {
            this.config.navigationManager = services.navigationManager;
        }
        new fontDomInteractor().createFontTag(this.settingsWrapper.settings.blockFontRequest);
        this.init();
    }

    public UpdateSettings(allSettings: IWebSDKSetingsBus) {
        if (allSettings.containerServices) {
            this.UpdateServices(allSettings.containerServices);
        }
        if (allSettings.websdkSettings) {
            this.UpdateWebSDKSettings(allSettings.websdkSettings);
        }
    }

    public async NamedComponentInit(compName: string, compPath: string, compModel?: INamedCompModel){
        await this.moduleManager.namedCompInit(compName, compPath, compModel);
    }
    public static getPlateauIAM(): IPlateauIAM {
        return WebSDK.servicesWrapper.services.getPlateauIAM?.();
    }
    public SetExternalQJsonRetriever(externalQJsonRetriever: IExternalQJsonRetriever) {
        this.qJsonRetriever.SetExternalQJsonRetriever(externalQJsonRetriever);
        this.moduleManager.SetExternalQJsonRetriever(externalQJsonRetriever);
    }
    public subscribeStoreData(name: string, cb: StoreManagerHookCb) {
        this.storeManager.subscribe(name, cb);
    }
    public unSubscribeStoreData(name: string, cb: StoreManagerHookCb) {
        this.storeManager.unsubscribe(name, cb);
    }
    private UpdateServices(services: IContainerServices) {
        WebSDK.servicesWrapper.services = services;
        this.languageExtensions.manageShellObject();
    }
    private UpdateWebSDKSettings(settings: IWebSDKSettings, options?: { isSourceFromPath?: boolean }) {
        this.settingsWrapper.settings = settings;

        this.setQJsonUrlPrefix(this.settingsWrapper.settings.qjsonURLPrefix);
        this.setServiceUrlPrefix(this.settingsWrapper.settings.serviceURL);
        this.setServiceDefaultHeader(this.settingsWrapper.settings.defaultServiceHeader);
        this.setSelectedServiceUrl(this.settingsWrapper.settings.selectedServiceUrl);
        this.setRegionalDefinition(this.settingsWrapper.settings.regionalDefinition);
        this.setProxySettingsYamlContent(this.settingsWrapper.settings.settingsProxyYaml);
        this.rendererManager?.setThemes(this.settingsWrapper.settings.appSettings?.themeSettings)
        this.setSettingFonts(this.settingsWrapper.settings.fontPath, options?.isSourceFromPath);

        this.network = new NetworkManagerImpl('NetworkManager', this.settingsWrapper, WebSDK.servicesWrapper, this.storeManager);
    }

    private initContainerServices(servicesWrapper: IContainerServicesWrapper) {
        servicesWrapper.services.getContainerName = () => this.settingsWrapper.settings.backendInf?.containerName;

        servicesWrapper.services.extensions = servicesWrapper.services.extensions || {};

        servicesWrapper.services.print = servicesWrapper.services.print || ((data: any) => {
            var url = "http://localhost:4852/BranchDeviceConnector/PrinterServiceRest/PrintFileContent?responseFormat=json"
            return this.network!.postHeader({ headers: { "Content-Type": "application/json" }, url, body: JSON.stringify(data.parameters[0]) });
        });
    }

    LoadModuleCompLists(modulePaths: Record<string, string>): Promise<any> {
        if (!modulePaths) { return Promise.resolve(); }

        var count = 0
        var length = Object.keys(modulePaths).length
        return new Promise((res, rej) => {
            Object.keys(modulePaths).forEach(key => {
                new JSLoader(modulePaths[key], function () {
                    count++;
                    (<any>window).vuetify_microUI.ml[key].modulePath = modulePaths[key]
                    if (count == length) {
                        res((<any>window).vuetify_microUI.ml);
                    }
                }, function (evt) {
                    count++;
                    if (count == length) {
                        rej(key);
                    }
                }, 1, true, new Date().getTime()).LoadJS();
            })
        })
    }



    init() {
        const vuetify_microUI = (<any>window).vuetify_microUI = { ml: {} };
        let domaincomponentList: Array<String> = [];
        (<any>vuetify_microUI).registerModule = function (moduleFileName) {
            return {
                registerCompNames: function (compNames) {
                    vuetify_microUI.ml[moduleFileName.replace(".js", "")] = { componentNames: compNames, modulePath: undefined }
                    Object.keys(compNames).forEach(name => {
                        domaincomponentList.push(name);
                    });
                },
                registerComponent: function (compName, comp) {
                    Vue.component(compName.default.name, compName.default);
                },
            };
        };

        //Service Worker Registration
        if (this.settingsWrapper.settings.serviceWorkerPath != undefined) {
            if ('serviceWorker' in navigator) {
                var currentScript: HTMLOrSVGScriptElement | null = document.currentScript
                if (currentScript) {
                    var scriptPath: string | null = currentScript.getAttribute('src');
                    if (scriptPath) {
                        scriptPath = scriptPath.replace(/^.*\/\/[^\/]+/, '')
                        //scriptPath = scriptPath.substring(0, scriptPath.indexOf(this.settingsWrapper.settings.serviceWorkerPath) + this.settingsWrapper.settings.serviceWorkerPath.length)                
                        scriptPath = scriptPath.substring(0, scriptPath.indexOf(this.settingsWrapper.settings.serviceWorkerPath))
                        navigator.serviceWorker.register(scriptPath + 'plateau-service-worker.js?path=' + scriptPath, { scope: scriptPath })
                    }
                }
            }
        }

    }
    async LoadPipeline() {
        if (!this.settingsWrapper.settings.pipelineqjson) { return; }

        const qJsonString = (await LazyLoader.FetchModuleBody(this.settingsWrapper.settings.pipelineqjson)).body;
        const qJsonExtracted = <IQJSon>QJsonExtractHelper.qJsonExtractor(qJsonString);
        if (!qJsonExtracted) {
            throw new Error("pipeline.qjson has error");
        }
        // this.pageManager.setPipelineQJson(qJsonExtracted);
    }

    async LoadAlert() {
        if (!this.settingsWrapper.settings.alertqjson) { return; }

        const qJsonString = (await LazyLoader.FetchModuleBody(this.settingsWrapper.settings.alertqjson)).body;
        const qJsonExtracted = <IQJSon>QJsonExtractHelper.qJsonExtractor(qJsonString);
        if (!qJsonExtracted) {
            throw new Error("alert.qjson has error");
        }
        // this.pageManager.setAlertQJson(qJsonExtracted);
    }

    async LoadLoading() {
        if (!this.settingsWrapper.settings.loadingqjson) { return; }

        const qJsonString = (await LazyLoader.FetchModuleBody(this.settingsWrapper.settings.loadingqjson)).body;
        const qJsonExtracted = <IQJSon>QJsonExtractHelper.qJsonExtractor(qJsonString);
        if (!qJsonExtracted) {
            throw new Error("loading.qjson has error");
        }
        return qJsonExtracted;
    }

    async LoadGlobalLocalization() {
        if (!this.settingsWrapper.settings.globalLocalizationqjson) { return; }

        const qJsonString = (await LazyLoader.FetchModuleBody(this.settingsWrapper.settings.globalLocalizationqjson)).body;
        const qJsonExtracted = <IQJSon>QJsonExtractHelper.qJsonExtractor(qJsonString);
        if (!qJsonExtracted) {
            throw new Error("globalLocalization.qjson has error");
        }
        // this.pageManager.setGlobalLocalizationQJson(qJsonExtracted);
        return qJsonExtracted;
    }

    CreateRenderer({ mountPoint, config }: { mountPoint: IDomElement, config?: IRendererConfig }): ICreateRendererResponse {
        if (config) {
            Object.keys(config).forEach(item => {
                this.config[item] = config[item];
            });
        }
        return this.rendererManager.createRenderer({ mountPoint, qshell: this.qshell, config: this.config, windowHistoryManager: this.windowHistoryManager });
    }

    GetDomContainer(renderer: IRenderer): IDomElement {
        return this.rendererManager.getDomContainer(renderer);
    }

    GetParentInstance(renderer: IRenderer) {
        return this.rendererManager.getParentInstance(renderer);
    }

    public destroyRenderer(renderer: IRenderer): void {
        this.rendererManager.destroyRenderer(renderer);
    }
    public destroy() {
        this.resetQJsonPool();
        this.rendererManager.destroy();
        this.cssTagManager.destroyStyleTagNames();
    }
    public resetQJsonPool() {
        this.qshell.ResetQJsonPool();
    }

    setComponentOverrider(cb: (compName: string) => IComponent | null) {
        return ComponentProvider.setOverrideComp(cb);
    }

    GetComponentList(compGroupNameArray: Array<CompGroupType>): Record<CompGroupType, any> {
        return ComponentProvider.getComponentList(compGroupNameArray);
    }

    /**
     * Set domain prefix of qJson retriever in nemo
     * @param prefix Domain prefix of retriever
     */
    setQJsonUrlPrefix(prefix?: string) {
        this.settingsWrapper.settings.qjsonURLPrefix = prefix?.endsWith("/") ? prefix.substring(0, prefix.length - 1) : prefix;
    }

    setServiceUrlPrefix(prefix?: string) {
        this.settingsWrapper.settings.serviceURL = prefix?.endsWith("/") ? prefix.substring(0, prefix.length - 1) : prefix;
    }

    setServiceDefaultHeader(header?: string) {
        this.settingsWrapper.settings.defaultServiceHeader = header;
    }

    setSelectedServiceUrl(url?: string) {
        this.settingsWrapper.settings.selectedServiceUrl = url;
    }

    setProxySettingsYamlContent(content?: object) {
        this.settingsWrapper.settings.settingsProxyYaml = content;
    }

    private setSettingFonts(content?: Array<IFontSettings>, isSourceFromPath?: boolean) {
        this.settingsWrapper.settings.fontPath = content;
        new fontDomInteractor().createFontFace(content, isSourceFromPath);
    }

    setLocalProxyOnClient(content: boolean) {
        this.settingsWrapper.settings.useLocalProxyOnClient = content;
    }

    setBackEndInfHeaders(backendInf) {
        this.settingsWrapper.settings.backendInf = backendInf;
    }
    setRegionalDefinition(regionalSettings: Array<IRegionalDefinition> | undefined) {
        this.settingsWrapper.settings.regionalDefinition = regionalSettings;
    }

    setGlobalLR(regionalSettings: Array<IRegionalDefinition> | undefined) {
        this.settingsWrapper.settings.regionalDefinition = regionalSettings;
    }
    setEnvironment(env: any) {
        this.rendererManager.setEnvironment(env);
    }
    setYamlLogType(logType: string) {
        this.rendererManager.setYamlLogType(logType);
    }
    setPageTitle(title: string) {
        this.settingsWrapper.settings.title;
    }
    setFavicon(favicon: string) {
        this.settingsWrapper.settings.favicon;
    }
    getUXManager() {
        return this.ux;
    }

    public migrateLegacySettings(settings: any) {
        return Legacy.migrateLegacySettings(settings);
    }

    public showCssTags(show: boolean, pageName: string) {
        if ((window as any).__manageCssTags != true) { return; }

        const toggleTagTime = 50; // tm websdk style taglerine mdahale 200ms gecikme ile yaplrsa, tab deiikliinde performans kazanlabilir.
        show ? this.cssTagManager.showStyleTags(toggleTagTime, pageName) : this.cssTagManager.hideStyleTags(toggleTagTime, pageName);
    }
}

(window as any).__manageCssTags !== false && ((window as any).__manageCssTags = true);