import { LogType, Logger } from "./logger";

export class catcher {
    static tryLog({ cb, catchLog }: { cb: () => void, catchLog?: (err: any) => string }) {
        try {
            cb();
        } catch (error) {
            const logMessage = catchLog ? catchLog(error) : "tryLog error happened";
            Logger.Log({ logMessage, error, logType: LogType.Error });
        }
    }

    static try<RetType>(cb: () => RetType, params:{ catchLog?: (err: Error) => string }) {
        params = params || {};
        try {
           return cb();
        } catch (error) {
            const err = error as Error;            
            const logMessage = params.catchLog ? params.catchLog(err) : "tryLog error happened" + err.message;            
            Logger.Log({ logMessage, error: err, logType: LogType.Error });
            throw error; 
        }
    }
}


