import { CompositorTypes } from "../externalInterfaces";
import { Context } from "./core/context";
import { Decimal } from "./compositors/decimal";
import { Regex } from "./compositors/regex";
import { ICompositor, ICompositorCtor } from "./compositors/ICompositor";


const compositorDict: Record<CompositorTypes, ICompositorCtor> = {
    decimal: Decimal,
    regex: Regex
};
export class InputFormat {
    constructor(
        private readonly context: Context,
        public readonly compDict = new compositorDict[context.options.alias](context)
    ){
    }

    public run() {
        this.compDict.compose();
    }
    public getUnMaskedValue(){
        return this.compDict.getUnmaskedValue();
    }
}