import { CreatePromiseData } from "../interfaces/IPromiseData";
import { INamedCompModel } from "@stechquick/algae/lib/quick/INamedCompModel";
import { JSLoader } from "./JSLoader";

export class NamedComponentHelper {
    public loadNamedComponent(compName: string, compPath: string, options: { compModel?: INamedCompModel }, compVersion?: number): Promise<any> | undefined {
        const prom = CreatePromiseData();
        const compModuleCb = (module: any) => prom.resolver(module);
        if (options.compModel) {
            const script = document.createElement("script");
            if (!options.compModel.files) { prom.reject(`${compName} has no files`); return prom.promise; }
            const fileContent = options.compModel.files[`${compName}.js`] as string; // web is packed;
            if (fileContent.length <= 100){ prom.reject(`${compName} js load error`); return prom.promise; }
            script.innerHTML = fileContent;
            (script as any)["data-custom-onload"] = compModuleCb
            try {
                document.head.appendChild(script);
                script.src = compName; //if document.currentScript fails, fallback to src search code inside component pack.
            } catch (ex) { prom.reject(ex); }
        } else {
            const options = { scriptTagProps: { "data-custom-onload": compModuleCb } };
            new JSLoader(compPath, undefined, function (evt: unknown) { prom.reject(evt); }, 1, true, compVersion ?? new Date().getTime(), options).LoadJS();
        }
        return prom.promise;
    }
}


