import { Node } from "./Node"
export class QSCError extends Error implements QSCError {
    public static ErrorName = "QSCError";
    constructor({ intelliMsg, message, stLine, stColumn, eLine, eColumn, responsibleNode }:
        { intelliMsg: string, message: string, stLine: number, stColumn: number, eLine: number, eColumn: number, responsibleNode?: Node }) {
        super(message);
        this.intelliMsg = intelliMsg;
        this.stLine = stLine;
        this.stColumn = stColumn;
        this.name = QSCError.ErrorName;
        this.eLine = eLine;
        this.eColumn = eColumn;
        this.responsibleNode = responsibleNode;
    }
}
export interface QSCError extends Error {
    intelliMsg: string,
    stLine: number,
    stColumn: number,
    eLine: number,
    eColumn: number,
    responsibleNode?: Node
};