import { IFontSettings, IWebSDKSettings } from "../../../../../common/shrimp/interfaces/quick/IWebSDK";

export class fontDomInteractor {
  public createFontFace(fonts?: Array<IFontSettings>, isSourceFromPath?: boolean) {
    if (!fonts) { return };
    let customFontTag = document.getElementById('fontFace');

    if (!customFontTag) {
      customFontTag = document.createElement("style");
      customFontTag.id = "fontFace";
    }

    let fontStr = "";
    fonts.forEach((item) => {
      if ((typeof item.fontWeights === "number" || typeof item.fontWeights === "string") && isSourceFromPath) {
        fontStr += ` @font-face {font-family: "${item.fontName}"; font-weight: ${item.fontWeights}; src:url("${item.path}");}`;
      } else if (Array.isArray(item.fontWeights)) {
        const convertWeight = item.fontWeights[0].split('-')[1].trim();
        fontStr += ` @font-face {font-family: "${item.fontName}"; font-weight: ${convertWeight}; src:url("${item.id.replaceAll(`'`, '')}");}`;
      } else { return }
    })
    customFontTag.innerText = fontStr;
    document.head.appendChild(customFontTag);
  }
  public createFontTag(blockFontRequest: IWebSDKSettings["blockFontRequest"]) {
    if (blockFontRequest) { return; }

    const fontTag = document.createElement("link");
    fontTag.href = "https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Nunito:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;0,1000;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900;1,1000&family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&family=Oswald:wght@200;300;400;500;600;700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&family=Rubik:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Source+Sans+Pro:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700;1,900&display=swap"
    fontTag.rel = "stylesheet";
    document.head.appendChild(fontTag);
  }
}