import { ContextManager, IContextItem } from "../../../shrimp/context";
import { HosTriggerClientStaticType, HostTriggerClientStaticFuncType, HosTriggerClientStaticMockType } from "@stechquick/algae/lib/quick/HostTrigger";
import { ISettingModelsContext } from "../../../shrimp/interfaces/RenderingInterfaces/IRenderer";
import { SettingModelsContext } from "../Context/SettingModelsContext";

export class HostTriggerMockManager implements IContextItem {
    public static readonly ContextName: string = "HostTriggerMockManager";
    public readonly contextName: string = HostTriggerMockManager.ContextName;

    private context: ContextManager;
    private clientStaticMockDict: HosTriggerClientStaticType = {};

    constructor(context: ContextManager) { this.context = context; }

    public tryGetMock(funcName: string, params: any[]): HostTriggerClientStaticFuncType | undefined {
        const clientStaticMock = this.clientStaticMockDict[funcName];
        if (clientStaticMock) { return clientStaticMock; }

        const containerModel = this.context.GetItemSingle<ISettingModelsContext>(SettingModelsContext.ContextName)?.GetHostTriggerModel();
        if (!containerModel) { return undefined; }

        const mock = containerModel.functions.find(func => func.funcName == funcName)?.mock;
        if (!mock) { return undefined; }

        return this.cacheHostTriggerClientStatic(funcName, { type: mock.mockType, value: mock.mockBody });
    }

    public setHostTriggerClientStatic(funcName: string, clientStatic: HostTriggerClientStaticFuncType): HostTriggerClientStaticFuncType {
        let clientStaticItem = this.clientStaticMockDict[funcName];
        if (clientStaticItem) { return clientStaticItem; }

        return this.cacheHostTriggerClientStatic(funcName, clientStatic);
    }
    public clearHostTriggerClientMock(funcName: string | undefined) {
        if (funcName) { delete this.clientStaticMockDict[funcName]; return; }
        this.clientStaticMockDict = {};
    }

    private cacheHostTriggerClientStatic(funcName: string, clientStatic: HostTriggerClientStaticFuncType) {
        let clientStaticItem: HostTriggerClientStaticFuncType;
        switch (clientStatic.type) {
            case "static": clientStaticItem = { type: "static", value: clientStatic.value }; break;
            case "dynamic": clientStaticItem = { type: "dynamic", value: this.buildFunction(clientStatic.value) }; break;
            default: throw new Error(`Unknown client static mock type: ${clientStatic.type}`);
        }

        this.clientStaticMockDict[funcName] = clientStaticItem;
        return clientStaticItem;
    }

    private buildFunction(funcValue: string) {
        const func = Function("module", funcValue);
        const module: { exports?: Function } = {}; //TODO: Check exports type again...
        func(module);
        return module.exports;
    }
}