import { ContextManager, IContextItem } from "../../../shrimp/context";
import { ILRID } from "../../../shrimp/interfaces/ComponentInterfaces/ILocalResource";
import { IEvaluator } from "../../../shrimp/interfaces/Scripting/IEvaluator";
import { Evaluator } from "./Evaluator";
import { HistoryManager } from "./HistoryManager";
import { Compress } from "../../../shrimp/omniScriptCompiler/Compressor";
import { Lex } from "../../../shrimp/omniScriptCompiler/Lexer";
import { ParseObj } from "../../../shrimp/omniScriptCompiler/Parser";


export class DoryBasedLRManager implements IContextItem {
    static ContextName: string = "DoryBasedLRManager";
    contextName: string = DoryBasedLRManager.ContextName;
    evaluator: IEvaluator
    currentContext: ContextManager;
    resourceDict: ILRID | undefined;
    private currentLRType?: string;
    private currentLRTypeRTL: boolean = false;
    private RTL: string = "_%RTL%_";

    constructor(currentContext: ContextManager) {
        this.currentContext = currentContext;
        this.evaluator = new Evaluator();
    }

    public set LRType(lrType: string | undefined) {
        this.currentLRType = lrType;
    }

    public get LRType() {
        return this.currentLRType;
    }

    public set LRTypeRTL(rtl: boolean) {
        if (rtl) {
            this.currentLRTypeRTL = rtl;
        }
        else {
            this.setCurrentLRTypeRTLByResourceDict();
        }
    }

    public get LRTypeRTL() {
        return this.currentLRTypeRTL;
    }

    public set ResourceDict(CLRs: ILRID | undefined) {
        this.resourceDict = CLRs;
        this.setCurrentLRTypeRTLByResourceDict();
    }

    private setCurrentLRTypeRTLByResourceDict() {
        this.currentLRTypeRTL = false;
        if (this.resourceDict && Object.keys(this.resourceDict).length > 0) {
            const globalLR = this.resourceDict[this.currentLRType ?? "tr-TR"];
            if (globalLR?.[this.RTL]) {
                this.currentLRTypeRTL = true;
            };
        }
    }

    GetResource({ context, key, dataSource }: { context: ContextManager, key: string, dataSource?: object | null }) {
        var result: string | null | undefined;
        if (this.resourceDict && Object.keys(this.resourceDict).length > 0) {
            const globalLR = this.resourceDict[this.currentLRType ?? "tr-TR"];
            result = globalLR[key];
            if (result != null) {
                const historyManager = context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
                const historyItem = historyManager?.currentLevel().last();
                result = this.evaluator.Evaluate({ script: result, dataSource: dataSource, context: context, lang: historyItem?.getQLang() });
                return result;
            }
        }
    }

    public ImportResourceDict(langCode: string, localization: Record<string, string>, options: { overrite?: boolean } = {}) {

        const override = options?.overrite ?? false;

        const parse = ParseObj.Parse;

        if (langCode && localization && Object.keys(localization).length > 0) {
            if (this.resourceDict == undefined) {
                this.resourceDict = {};
            }
            if (this.resourceDict[langCode] == undefined) {
                this.resourceDict[langCode] = {};
            }
            Object.keys(localization).forEach(item => {
                if (!localization[item] || localization[item].trim().length <= 0) { return; }
                try {
                    let script = this.addQuotesToStr(localization[item]);
                    let compressedItem = Compress(parse(Lex(script)));
                    if (override) {
                        this.resourceDict![langCode][item] = compressedItem;
                    }
                    else {
                        if (this.resourceDict![langCode][item]) {
                            return;
                        }
                        this.resourceDict![langCode][item] = compressedItem;
                    }
                } catch (err) {
                }
            });
        }
    }

    private addQuotesToStr(lRvalue: string) {
        const lRlength = lRvalue.length;
        if (0 < lRlength && ((lRvalue.charAt(0) !== "'" && lRvalue.charAt(0) !== '"') && (lRvalue.charAt(lRlength - 1) !== "'" && lRvalue.charAt(lRlength - 1) !== '"'))) {
            lRvalue = "'" + lRvalue + "'";
        }
        return lRvalue;
    }
}

