import { IDirectiveElement } from "../../../../../common/shrimp/interfaces/ComponentInterfaces/IDirectiveCollection";
import { TouchValue } from 'vuetify/types';
export class VuetifyDirectiveMap {
    static convertDricetiveToVuetify(directive: IDirectiveElement) {
        let retVal;
        switch (directive.name) {
            case "click-outside":
                let clickOutside: ClickOutsideDirective = { name: directive.name, value: { handler: directive.value.handler, closeConditional: directive.value.options.closeConditional, include: directive.value.options.include } };
                retVal = clickOutside;
                break;
            case "intersect":
                let intersection: ObserveVNodeDirective = { name: directive.name, value: directive.value, modifiers: { once: directive.value.options.once, quiet: directive.value.options.quiet } };
                retVal = intersection;
                break;
            case "mutation":
                let mutation: MutateVNodeDirective = { name: directive.name, value: directive.value, modifiers: directive.value.options.modifiers };
                retVal = mutation;
                break;
            case "resize":
                let resize: ResizeVNodeDirective = { name: directive.name, value: directive.value.handler, options: directive.value.options };
                retVal = resize;
                break;
            case "ripple":
                let ripple: RippleDirective = { name: directive.name, value: directive.value.options.handler }
                retVal = ripple;
                break;
            case "scroll":
                let scroll: ScrollVNodeDirective = { name: directive.name, value: directive.value, modifiers: { self: directive.value.options.self } }
                retVal = scroll;
                break;
            case "touch":
                let touch: TouchVNodeDirective = { name: directive.name, value: directive.value }
                retVal = touch;
                break;
            default:
                break;
        }
        return retVal;
    }
}

interface ClickOutsideBindingArgs {
    handler: (e: Event) => void
    closeConditional?: (e: Event) => boolean
    include?: () => HTMLElement[]
}

interface ClickOutsideDirective extends IDirectiveElement {
    value?: ((e: Event) => void) | ClickOutsideBindingArgs
}

type ObserveHandler = (
    entries: IntersectionObserverEntry[],
    observer: IntersectionObserver,
    isIntersecting: boolean,
) => void

interface ObserveVNodeDirective extends Omit<IDirectiveElement, 'modifiers'> {
    value?: ObserveHandler | { handler: ObserveHandler, options?: IntersectionObserverInit }
    modifiers?: {
        once?: boolean
        quiet?: boolean
    }
}

type MutateHandler = (
    mutationsList: MutationRecord[],
    observer: MutationObserver,
) => void

interface MutateVNodeDirective extends Omit<IDirectiveElement, 'modifiers'> {
    value?: MutateHandler | { handler: MutateHandler, options?: MutationObserverInit }
    modifiers?: {
        once?: boolean
        attr?: boolean
        child?: boolean
        sub?: boolean
        char?: boolean
    }
}

interface ResizeVNodeDirective extends IDirectiveElement {
    value?: () => void
    options?: boolean | AddEventListenerOptions
}

interface RippleOptions {
    class?: string
    center?: boolean
    circle?: boolean
}
interface RippleDirective extends IDirectiveElement {
    value: boolean | RippleOptions
}

interface ScrollVNodeDirective extends Omit<IDirectiveElement, 'modifiers'> {
    value: EventListener | {
        handler: EventListener
        options?: boolean | AddEventListenerOptions
    } | EventListenerObject & { options?: boolean | AddEventListenerOptions }
    modifiers?: {
        self?: boolean
    }
}

interface TouchVNodeDirective extends IDirectiveElement {
    value?: TouchValue
}