import { Logger, LogType } from "../../../../common/shrimp/helpers/logger";
import { IComponent } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IComponent";
import { IComponentCollection } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";

const memLeakFixCompSet = {
    VContainer: 1,
    VRow: 1,
    VCol: 1,
    VCard: 1,
    QLabel: 1,
    VIcon: 1,
    VSheet: 1,
    VChip: 1,
    VCombobox: 1,
    VTextField: 1,
    VInlineDatepicker: 1,
    VBtn: 1,
    VDataTable: 1,
    VMenu: 1,
    VTreeview: 1,
    VCheckbox: 1,
    CustomComponent: 1,
    VListItemGroup: 1,
    VListItem: 1,
    VDivider: 1,
    VList: 1,
    Tooltip: 1,
    VPagination: 1,
};

(window as any).__overCpuFix = true;
(window as any).__memLeakFix = true;

export function willFixMount(compCollection: IComponentCollection) {
    const retVal = memLeakFixCompSet[compCollection.compType] == 1 && willFixCpu();
    return retVal;
}
function willFixCpu() {
    return (window as any).__overCpuFix === true;
}
export function willFixMemLeak() {
    return (window as any).__memLeakFix === true;
}

let queue: Array<{createdComp: IComponent, compType: string, compId: string, parentCompId: string}> = [];
let timer: number | undefined;
const queueSystem = false;
export function controlledDestroy(createdComp: IComponent, compCollection: IComponentCollection) {
    const {compId, compType} = compCollection;
    const parentCompId = compCollection.parentCompCollection?.compId || "-";
    if (queueSystem) {
        queue.push({ createdComp, compId, compType, parentCompId });
        if (timer) { return; }
    
        timer = setTimeout(destroyer);
    } else {
        realDestroy(createdComp, compId, compType, parentCompId);
    }
}

function destroyer() {
    if (queue.length <= 0) { timer = undefined; return; }

    const _queue = queue;
    queue = [];

    _queue.forEach(item => realDestroy(item.createdComp, item.compType, item.compId, item.parentCompId));
    timer = setTimeout(destroyer);
}

function realDestroy(createdComp: IComponent, compType: string, compId: string, parentCompId: string) {
    try {
        (!createdComp.$el) && (createdComp.$el = {remove: () => {}} as any); // FX (console.error log) -> TypeError: Cannot read properties of undefined (reading '_observe') at Object.unbind  at VueComponent.destroyed
        createdComp.$destroy();
    } catch(ex) { // stteki fix bu catch'e gerek brakmam olabilir.
        const err = ex as Error;
        Logger.Log({ logType: LogType.Debug, logMessage: `temp fix applied: $destroy problem for T: ${compType} Id: ${compId} parentId: ${parentCompId}. ex: ` + err.message });
        const vm = createdComp as any;
        vm.$off(); // turn off all instance listeners.
        if (vm.$el) { vm.$el.__vue__ = null; } // remove __vue__ reference
        if (vm.$vnode) { vm.$vnode.parent = null; } // release circular reference (#6759)
    }
}