import { IContextItem } from "../../../shrimp/context";
import { CreatePromiseData, IPromiseData } from "../../../shrimp/interfaces/IPromiseData";

export class BlockInfo implements IContextItem {

    static ContextName: string = "BlockInfo";

    contextName: string = BlockInfo.ContextName;
    blockRender: boolean | null;
    blockJsonIndex: number;
    blockPromises: Array<Promise<any>> | null;
    private manualPromise: IPromiseData<void> | undefined;
    private manualPromiseCounter = 0;

    constructor({ blockRender = null, blockPromises = null }: { blockRender?: boolean | null, blockPromises?: Array<Promise<string>> | null }) {
        this.blockRender = blockRender;
        this.blockPromises = blockPromises;
        this.blockJsonIndex = 0;
    }

    /**
     * Add a promise when the render is blocked
     * @param blockPromise Add new promise to handle them asynchronously
     */
    public addBlockPromise(blockPromise: Promise<any>): void {
        this.blockRender = true;
        this.blockPromises = this.blockPromises || new Array<Promise<any>>();
        this.blockPromises.push(blockPromise);
    }
    
    public addManualPromise() {
        if (!this.manualPromise) {
            this.manualPromise = CreatePromiseData<void>();
            this.addBlockPromise(this.manualPromise.promise);
        }
        this.manualPromiseCounter++;
    }
    public resolveManualPromise() {
        if (--this.manualPromiseCounter <= 0) {
            this.manualPromiseCounter = 0;
            this.manualPromise?.resolver();
        }
    }
}