import { IDictionary } from "../interfaces/IDictionary";

let appWideStore: ObjectStore | null;

/**
 * project wide object store helper
 */
export function GetObjectStore() {
    return appWideStore || (appWideStore = new ObjectStore());
}

class ObjectData {
    private value: any;
    private watches: Array<(newValue?: any, oldValue?: any) => void> = [];
    constructor(value: any) {
        this.value = value;
    }
    public getValue() {
        return this.value;
    }
    public addWatch(cb: (newValue?: any, oldValue?: any) => void) {
        this.watches.push(cb);
    }
    public setValue(value: any) {
        const oldValue = this.value;
        this.value = value;
        this.watches.forEach((cb) => cb(this.value, oldValue));
    }
}

class ObjectStore {

    private data: IDictionary<ObjectData> = {};

    private getObject(name: string, create: boolean = true): ObjectData {
        let object = this.data[name];
        if (!object && create) {
            object = new ObjectData(null);
            this.data[name] = object;
        }
        return object;
    }

    public retrieveObject(name: string): any {
        let object = this.getObject(name, false);
        return object && object.getValue();
    }

    public storeObject(name: string, value: any): ObjectData {
        let object = this.getObject(name);
        object.setValue(value);
        return object;
    }

    public watch(name: string, cb: (newValue?: any, oldValue?: any) => void): ObjectData {
        let object = this.getObject(name);
        object.addWatch(cb);
        return object;
    }
}