import { Logger, LogType } from "../../../../shrimp/helpers/logger";
import { TypeHelper } from "../../../../shrimp/helpers/typeHelper";
import { IComponentCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IEventCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IEventCollection";
import { IEventData } from "../../../../shrimp/interfaces/ComponentInterfaces/IEventData";
import { IEventFunctions } from "../../../../shrimp/interfaces/ComponentInterfaces/IEventFunctions";
import { IDictionary } from "../../../../shrimp/interfaces/IDictionary";
import { IDictionaryNullable } from "../../../../shrimp/interfaces/IDictionaryNullable";
import { PerfSingles } from "../../../../shrimp/performance";
import { Dory } from "../../Dory";
import { Evaluator } from "../../Managers/Evaluator";
import { EventData } from "./EventData";

export class EventCollection implements IEventCollection {
    private eventDict: IDictionary<EventData>;
    private ownerComponent: IComponentCollection | null;
    private dory: Dory;

    constructor(dory: Dory) {
        this.eventDict = {};
        this.dory = dory;
        this.ownerComponent = null;
    }

    RegisterEvent({ eventData }: { eventData: EventData; }): void {
        this.eventDict[eventData.eventName] = eventData;
    }

    SetOwner({ ownerComponent }: { ownerComponent: IComponentCollection }): void {
        this.ownerComponent = ownerComponent;
        for (const key in this.eventDict) {
            this.eventDict[key].SetOwner({ ownerComponent: this.ownerComponent });
        }
    }

    Trigger({ eventName, eventArguments = [], immediate }: { eventName: string, eventArguments?: Array<any> | IDictionaryNullable<any>, immediate?: boolean }): any {
        const targetEvent = this.eventDict[eventName];
        if (!targetEvent) {
            const engineYamlLogType = this.dory.GetLogParams()?.minEngineLogLevel;
            Logger.Log({ logMessage: `no event named ${eventName} on ${this.ownerComponent!.compId} type ${this.ownerComponent!.compType}`, logType: LogType.Warning, minEngineLogLevel: engineYamlLogType });
            return null;
        }
        immediate && (targetEvent.immediate = immediate);
        PerfSingles.Instance().Start(this.ownerComponent!.compId + "." + eventName);

        const evaluator = new Evaluator();
        let dataSource: IDictionary<any> | null;
        if (!targetEvent.customEvent) {
            //from comp code
            if (eventArguments instanceof Array) {
                //possible vue fix :/
                dataSource = this.MatchEventDataToArguments({ eventData: targetEvent, eventArguments });
            } else {
                dataSource = eventArguments;
            }
        }
        else {
            //MM.trigger or (vue emit :()
            if (new TypeHelper().isArray(eventArguments)) {
                //possible vue fix :/
                dataSource = eventArguments[0];
            } else {
                //MM.trigger
                dataSource = eventArguments;
            }
        }
        dataSource = Object.assign({}, targetEvent.scopeObject, dataSource);
        const retVal = targetEvent.Trigger({ evaluator, dataSource });

        PerfSingles.Instance().End(this.ownerComponent!.compId + "." + eventName, <IEventData>targetEvent);
        return retVal;
    }

    private GenerateTriggerFunction({ eventName }: { eventName: string }): Function {
        var self = this;
        return function (...eventArguments: Array<any>): any {
            return self.Trigger({ eventName, eventArguments });
        };
    }

    GetExternalEventBoundFunctions(): IDictionary<Function> {
        let eventFunctions: IDictionary<Function> = {};
        Object.keys(this.eventDict).map(key => this.eventDict[key]).filter(event => event.externalVisible).forEach(event => {
            eventFunctions[event.eventName] = this.GenerateTriggerFunction({ eventName: event.eventName });
        })
        return eventFunctions;
    }

    GetEventBoundFunctions(): IEventFunctions {
        let eventFunctions: IDictionary<Function> = {};
        let propertyEventFunctions: IDictionary<Function> = {};
        let targetDict: IDictionary<Function>;
        for (const key in this.eventDict) {
            targetDict = this.eventDict[key].propertyEvent ? propertyEventFunctions : eventFunctions;
            targetDict[key] = this.GenerateTriggerFunction({ eventName: key });
        }
        return { eventFunctions, propertyEventFunctions };
    }

    private MatchEventDataToArguments({ eventData, eventArguments }: { eventData: EventData, eventArguments: Array<any> }) {
        if (!eventData.eventParams) {
            return null;
        }

        const loopLimit = eventData.eventParams.length < eventArguments.length ? eventData.eventParams.length : eventArguments.length;
        if (eventData.eventParams.length != eventArguments.length) {
            const log = `event (${eventData.eventName}) has ${eventData.eventParams.length} parameters but called with ${eventArguments.length} arguments. Go on with ${loopLimit} parameters`;
            const engineYamlLogType = this.dory.GetLogParams()?.minEngineLogLevel;
            Logger.Log({ logMessage: log, minEngineLogLevel: engineYamlLogType });
        }

        let retVal: Record<string, any> = {};
        for (let i = 0; i < loopLimit; i++) {
            let paramName = eventData.eventParams[i].name;
            retVal[paramName] = eventArguments[i];
        }
        return retVal;

    }

}