export type QPromiseInterractionCb = (prom: QPromise<any>) => void;

export interface IQPromiseInterractionCollection {
    onThen?: QPromiseInterractionCb;
    onCatch?: QPromiseInterractionCb;
    onFinally?: QPromiseInterractionCb;
}

export class QPromise<T> implements Promise<T> {
    public static staticCallbacks: IQPromiseInterractionCollection = {};
    public static setStaticCallbacksAndRetrieveOld(staticCallbacks: IQPromiseInterractionCollection) {
        const oldCb = QPromise.staticCallbacks;
        QPromise.staticCallbacks = staticCallbacks;
        return oldCb;
    }

    private static noop = () => { }

    private static directCreate<T>(selfProm: Promise<T>): QPromise<T> {
        const prom = new QPromise<T>(QPromise.noop);
        prom.selfPromise = selfProm;
        return prom;
    }

    public static all<T>(values: readonly (T | PromiseLike<T>)[]): Promise<T[]> {
        return QPromise.directCreate(Promise.all(values));
    }

    public static race<T>(values: readonly T[]): Promise<T extends PromiseLike<infer U> ? U : T> {
        return QPromise.directCreate(Promise.race(values));
    }

    public static reject<T = never>(reason?: any): Promise<T> {
        return QPromise.directCreate(Promise.reject(reason));
    }
    public static resolve<T>(value?: T | PromiseLike<T>): Promise<T | void> {
        const retVal = value !== void (0) ? Promise.resolve(value) : Promise.resolve();
        return QPromise.directCreate<T | void>(retVal);
    }
    public static readonly [Symbol.species]: PromiseConstructor = Promise;





    private selfPromise: Promise<T>;
    private finalized?: boolean;
    public get Finalized() { return this.finalized; }

    private finalize = () => this.finalized = true; // caution!! must be lambda. Attention to "What is this"!!
    public callbacks: IQPromiseInterractionCollection = {};

    constructor(executor: (resolve: (value: T | PromiseLike<T>) => void, reject: (reason?: any) => void) => void) {
        this.selfPromise = executor === QPromise.noop ? undefined as any : new Promise<T>(executor);
        this.callbacks = QPromise.staticCallbacks;
    }



    public then<TResult1 = T, TResult2 = never>(onfulfilled?: ((value: T) => TResult1 | PromiseLike<TResult1>) | undefined | null, onrejected?: ((reason: any) => TResult2 | PromiseLike<TResult2>) | undefined | null): Promise<TResult1 | TResult2> {
        //property'ler typescript'e geerken, await ve then'in ya kapatlmas lazm, ya da propertyCalculationContext'in resurrect edilip tekrar hibernate'e alnmas lazm.(ikiside burada)
        this.callbacks.onThen?.(this);
        const fulfillWrapper = !onfulfilled ? undefined : (value: T) => {
            const old = QPromise.setStaticCallbacksAndRetrieveOld(this.callbacks);
            const retVal = onfulfilled(value);
            QPromise.setStaticCallbacksAndRetrieveOld(old);
            return retVal;
        };
        const rejectWrapper = !onrejected ? undefined : (reason: any) => {
            const old = QPromise.setStaticCallbacksAndRetrieveOld(this.callbacks);
            const retVal = onrejected(reason);
            QPromise.setStaticCallbacksAndRetrieveOld(old);
            return retVal;
        };

        const childPromise = this.selfPromise.then(fulfillWrapper, rejectWrapper);
        this.selfPromise.finally(this.finalize);
        return QPromise.directCreate(childPromise);
    }

    public catch<TResult = never>(onrejected?: ((reason: any) => TResult | PromiseLike<TResult>) | undefined | null): Promise<T | TResult> {
        this.callbacks.onCatch?.(this);
        const rejectWrapper = !onrejected ? undefined : (reason: any) => {
            const old = QPromise.setStaticCallbacksAndRetrieveOld(this.callbacks);
            const retVal = onrejected(reason);
            QPromise.setStaticCallbacksAndRetrieveOld(old);
            return retVal;
        };

        const childPromise = this.selfPromise.catch(rejectWrapper);
        this.selfPromise.finally(this.finalize);
        return QPromise.directCreate(childPromise);
    }

    public finally(onfinally?: (() => void) | undefined | null): Promise<T> {
        this.callbacks.onFinally?.(this);
        const finallyWrapper = !onfinally ? undefined : () => {
            const old = QPromise.setStaticCallbacksAndRetrieveOld(this.callbacks);
            const retVal = onfinally();
            QPromise.setStaticCallbacksAndRetrieveOld(old);
            return retVal;
        };

        const childPromise = this.selfPromise.finally(finallyWrapper);
        this.selfPromise.finally(this.finalize);
        return QPromise.directCreate(childPromise);
    }

    public readonly [Symbol.toStringTag]: string = "Promise";
}