
import { IContainerServicesWrapper } from "../../../../common/shrimp/interfaces/quick/IContainerServices";
import { IHostTriggerer } from "../../../../common/qshell";



export class HostTriggererImp implements IHostTriggerer {
    servicesWrapper: IContainerServicesWrapper;
    public contextName: string = "context";
    constructor(servicesWrapper: IContainerServicesWrapper) {
        this.servicesWrapper = servicesWrapper;
    }
    

    public hostTrigger(functionName: string, params: Record<string, any>, pageNameArray: Array<string>) {
        const obj = this.servicesWrapper.services.extensions![functionName];
        return obj.apply(obj, params);
        // return this.servicesWrapper.services.hostTriggerCb?.(functionName, params, pageNameArray);
    }
    
    public containerTrigger(functionName: string, params: Record<string, any>, pageNameArray: Array<string>) {
        const obj = this.servicesWrapper.services.extensions![functionName];
        return obj.apply(obj, params);
    }

}