import { GetObjectStore } from "../../../shrimp/helpers/objectStore";

export class CachedEvalHelper {
    private cachedEvals: Object;

    constructor() {
        this.cachedEvals = GetObjectStore().retrieveObject("evalGlobals");
        if(!this.cachedEvals) {
            GetObjectStore().storeObject("evalGlobals", this);
        }
    }

    /**
     * Merges global variables with "Quick" fields and replaces fields 
     * from globals with '_' prefix if * names are matched
     * @param globalVariables Global variables of evaluator
     */
    MergeEvalGlobals(globalVariables: Object) {
        let QVariables = this.QuickVariables();
        Object.keys(QVariables).map(variable => {
            if(globalVariables.hasOwnProperty(variable)){
                globalVariables["_" + variable] = globalVariables[variable] 
                delete globalVariables[variable];
            }
        })
        //globalVariables["Quick"] = QVariables;
    }

    /**
     * Defines Quick variables
     */
    QuickVariables(): Object{
        // let Quick = {
        //     copy(object: Object) {
        //         let cloneObject = new TypeHelper().cloneObject(object);
        //         return cloneObject;
        //     },
        // }
        // return Quick;
        return {};
    }
}