import { IExternalQJsonRetriever } from "@stechquick/algae/lib/quick/IExternalQJsonRetriever";
import { IQJsonRetriever } from "../../../../common/qshell";
import { QJsonRetrievedType } from "../../../../common/qshell/src/interfaces/construction/IQJsonRetriever";
import { CreatePromiseData } from "../../../../common/shrimp/interfaces/IPromiseData";
import { IWebSDKSettingsWrapper } from "../../../../common/shrimp/interfaces/quick/IWebSDK";
import NetworkManagerImpl from "./NetworkManagerImpl";

export default class QJsonRetrieverImpl implements IQJsonRetriever {
    contextName: string
    private network: NetworkManagerImpl
    private settingsWrapper: IWebSDKSettingsWrapper;
    private externalQJsonRetriever?: IExternalQJsonRetriever;

    private get qjsonDirectPath(): boolean {
        return this.settingsWrapper.settings.qjsonDirectPath === false ? false : true;
    }
    private get qjsonUrlPrefix() {
        return this.settingsWrapper.settings.qjsonURLPrefix;
    }

    public get disablePagePool(){
        return this.settingsWrapper?.settings?.disablePagePool;
    }

    constructor({ contextName, network, settingsWrapper }: { contextName: string, network: NetworkManagerImpl, settingsWrapper: IWebSDKSettingsWrapper }) {
        this.contextName = contextName
        this.network = network;
        this.settingsWrapper = settingsWrapper;
    }

    private arrangeQJsonPath(qjsonPath: string): string {
        if (this.qjsonDirectPath) {
            return qjsonPath;
        }
        let path = qjsonPath;  // sample static/qjsons/login
        if (path.startsWith("static/") || path.startsWith("settings/") || path.startsWith("microui/")) { // for QUI pages 
            return path;
        }
        const firstSlash = path.indexOf("/"); // for old microUI pages go(modul/qjson) -> go("customer/create") --> microui/customer/qjson/create
        if (firstSlash >= 0) {
            const moduleName = path.substring(0, firstSlash);
            const qJsonName = path.substring(firstSlash + 1);
            path = `microui/${moduleName}/qjson/${qJsonName}`;
        }
        return path;
    }

    public SetExternalQJsonRetriever(externalQJsonRetriever: IExternalQJsonRetriever) {
        this.externalQJsonRetriever = externalQJsonRetriever;
    }

    private willExternalHandle(externalRetriever: IExternalQJsonRetriever, qjsonPath: string) {
        if (!externalRetriever.qjsonHandling) { return true; }

        const qjsonHandlingArr = Array.isArray(externalRetriever.qjsonHandling) ? externalRetriever.qjsonHandling : [externalRetriever.qjsonHandling];

        for (let i = 0; i < qjsonHandlingArr.length; i++) {
            const item = qjsonHandlingArr[i];
            if (item.type == "prefix" && qjsonPath.startsWith(item.value)) {
                return true;
            }
            if (item.type == "contains" && qjsonPath.indexOf(item.value) != -1) {
                return true;
            }
        }

        return false;
    }

    public async retrieveModelAsync({ modelPath, options }: { modelPath: string, options: { disableLoading: boolean } }) {
        const response = await this.network.getQjson({ url: modelPath, options });
        return response.data;
    }

    public async retrieveQJsonAsync(qjsonPath: string, options: { disableLoading: boolean }): Promise<QJsonRetrievedType> {
        await this.network.checkPlateauIAM(qjsonPath)
        const externalRetriever = this.externalQJsonRetriever;
        
        if (externalRetriever && this.willExternalHandle(externalRetriever, qjsonPath)) {
            const prom = CreatePromiseData<QJsonRetrievedType>(10000);
            try {
                const qjson = await externalRetriever.getQjson(qjsonPath);
                if (!qjson) { throw new Error("getQJson returned empty"); }

                prom.resolver(qjson);
            } catch (ex) {
                prom.reject(ex);
            }
            return await prom.promise;
        }

        let url = qjsonPath;

        if (this.qjsonUrlPrefix?.startsWith('{')) {
            const qjsonURLAddresses = JSON.parse(this.qjsonUrlPrefix);
            qjsonPath.startsWith('/') && delete qjsonURLAddresses['/'];
            const foundedKey = Object.keys(qjsonURLAddresses).find(key => qjsonPath.includes(key) && qjsonPath.startsWith('/'));
            if (foundedKey && qjsonURLAddresses[foundedKey]) {
                url = qjsonURLAddresses[foundedKey] + "/" + qjsonPath.replace(foundedKey, '');
            } else {
                url = qjsonURLAddresses['/'] + "/" + qjsonPath;
            }
        }
        else if(qjsonPath.startsWith("<<qjson:") && this.settingsWrapper.settings.fileMap){
            const qjsonId = qjsonPath.replace("<<qjson:", "").replace(">>", "");
            const pathObj = this.settingsWrapper.settings.fileMap!.find(i=>i.id==qjsonId);
            if(pathObj){
                url = pathObj.path + pathObj.name;
            }
        }
        else {
            const started = qjsonPath.startsWith("/");
            qjsonPath = this.arrangeQJsonPath(started ? qjsonPath.substring(1) : qjsonPath);
            const prefix = this.qjsonUrlPrefix || "";
            url = (prefix || started ? prefix + "/" : "") + qjsonPath;
        }

        const response = await this.network.getQjson<string | { qjson: string }>({ url, options });
        return response.data;
    }
}