import { CryptoHelper as algaeCryptoHelper } from "@stechquick/algae/lib/helpers/cryptoHelper";
export enum HashAlgorithm {
    SHA1 = "SHA-1",
    SHA256 = "SHA-256",
    SHA384 = "SHA-384",
    SHA512 = "SHA-512",
    HMACSHA1 = "SHA-1", 
    HMACSHA256 = "SHA-256", 
    HMACSHA384 = "SHA-384",
    HMACSHA512 = "SHA-512" 
}


export enum EncryptionAlgorithm {
    RSAOAEP = "RSA-OAEP",
    AESCTR = "AES-CTR",
    AESCBC = "AES-CBC",
    AESGCM = "AES-GCM"
}

export interface IHashDataResponse {
    hashedData: string
}

export interface IHashDataRequest {
    data: string,
    key?: string,
    algorithm: HashAlgorithm
}
export interface IEncryptDataResponse {
    encryptedData: string,
    iv?: string,
}
export interface IEncryptDataRequest {
    data: string,
    key: string,
    ivKey?: string,
    algorithm: EncryptionAlgorithm
}
export interface IDecryptDataResponse {
    decryptedData: string
}
export interface IDecryptDataRequest {
    encryptedData: string,
    key: string,
    iv?: string,
    ivKey?: string,
    algorithm: EncryptionAlgorithm
}

export class CryptoHelper {
    public static async hash(data: IHashDataRequest): Promise<IHashDataResponse> {
        const encodedData = new TextEncoder().encode(data.data);
        if (data.key && data.key.trim() != "") {
            const keyBuffer = new TextEncoder().encode(data.key);
            const key = await crypto.subtle.importKey(
                "raw",
                keyBuffer,
                { name: "HMAC", hash: { name: data.algorithm } },
                false,
                ["sign"]
            );

            const hmacBuffer = await crypto.subtle.sign("HMAC", key, encodedData);
            return {
                hashedData: this.encodeABToBase64(hmacBuffer)
            };
        } else {
            const hashBuffer = await crypto.subtle.digest(data.algorithm, encodedData);

            return {
                hashedData: this.encodeABToBase64(hashBuffer)
            };
        }
    }
    
    // Function to encrypt the string "alper" with the key "a@a.com"
    public static async encrypt(data: IEncryptDataRequest): Promise<IEncryptDataResponse> {
        const cryptoKey = await this.generateCryptoKey(data.key, data.algorithm, true);
        let iv;
        let encryptRequest;
        switch (data.algorithm) {
            case EncryptionAlgorithm.AESGCM:
                if(data.ivKey){
                    iv = new TextEncoder().encode(data.ivKey);
                }
                else{
                iv = window.crypto.getRandomValues(new Uint8Array(12)); // AES-GCM requires a 12-byte IV
                }
                encryptRequest = { name: data.algorithm, iv: iv };
                break;
            case EncryptionAlgorithm.AESCTR:
                if(data.ivKey){
                    iv = new TextEncoder().encode(data.ivKey);
                }
                else{
                    iv = window.crypto.getRandomValues(new Uint8Array(16));  // AES-CTR requires a 16-byte counter
                }
                encryptRequest = { name: data.algorithm, counter: iv, length: 64 };
                break;
            case EncryptionAlgorithm.AESCBC:
                // ivKey ="+OSPdSgZkYp5s6yi";
                if(data.ivKey){
                    iv = new TextEncoder().encode(data.ivKey);
                }
                else{
                    iv = window.crypto.getRandomValues(new Uint8Array(16)); // AES-CBC requires a 16-byte IV
                }
                
                encryptRequest = { name: data.algorithm, iv: iv };
                break;
            case EncryptionAlgorithm.RSAOAEP:
                encryptRequest = { name: data.algorithm };
                break;
        }
        const encodedData = new TextEncoder().encode(data.data);
        const encryptedData = await window.crypto.subtle.encrypt(
            encryptRequest,
            cryptoKey,
            encodedData
        );
        return {
            encryptedData: this.encodeABToBase64(encryptedData),
            iv: iv ? this.encodeABToBase64(iv) : undefined,
        };
    }

    // Function to decrypt the data
    public static async decrypt(data: IDecryptDataRequest): Promise<IDecryptDataResponse> {
        const cryptoKey = await this.generateCryptoKey(data.key, data.algorithm, false);
        let iv = data.iv ? this.decodeBase64ToAB(data.iv) : undefined;
        let encryptedData = this.decodeBase64ToAB(data.encryptedData);
        let decryptRequest;
        switch (data.algorithm) {
            case EncryptionAlgorithm.AESGCM:
                if(data.ivKey){
                    iv = new TextEncoder().encode(data.ivKey);
                }
                else{
                    iv = iv; // AES-GCM requires a 12-byte IV
                }
                decryptRequest = { name: data.algorithm, iv: iv };
                break;
            case EncryptionAlgorithm.AESCTR:
                if(data.ivKey){
                    iv = new TextEncoder().encode(data.ivKey);
                }
                else{
                    iv = iv; // AES-CTR requires a 16-byte counter
                }
                decryptRequest = { name: data.algorithm, counter: iv, length: 64 };
                break;
            case EncryptionAlgorithm.AESCBC:
                 // ivKey ="+OSPdSgZkYp5s6yi";
                if(data.ivKey){
                    iv = new TextEncoder().encode(data.ivKey);
                }
                else{
                    iv = iv; // AES-GCM requires a 12-byte IV
                }
                decryptRequest = { name: data.algorithm, iv: iv };
                break;
            case EncryptionAlgorithm.RSAOAEP:
                decryptRequest = { name: data.algorithm };
                break;
        }
        const decryptedData = await window.crypto.subtle.decrypt(
            decryptRequest,
            cryptoKey,
            encryptedData
        );
        const decryptedText = new TextDecoder().decode(decryptedData);
        return {
            decryptedData: decryptedText
        };
    }

    private static getSpkiPkcsDer(key: string, type: "public" | "private"): ArrayBuffer {
        const pemHeader = type === "public" ? "-----BEGIN PUBLIC KEY-----" : "-----BEGIN PRIVATE KEY-----";
        const pemFooter = type === "public" ? "-----END PUBLIC KEY-----" : "-----END PRIVATE KEY-----";
        const pemContents = key.substring(pemHeader.length, key.length - pemFooter.length).replace(/\n/g, '');
        return this.decodeBase64ToAB(pemContents);
    }

    // Import the key
    private static async generateCryptoKey(key: string, algorithm: EncryptionAlgorithm, isEncrypt: boolean = true) {
        if (algorithm === EncryptionAlgorithm.RSAOAEP) {
            const keyFormat = isEncrypt ? "spki" : "pkcs8";
            const keyData = this.getSpkiPkcsDer(key, isEncrypt ? "public" : "private");

            return await window.crypto.subtle.importKey(
                keyFormat,
                keyData,
                {
                    name: "RSA-OAEP",
                    hash: { name: "SHA-256" },
                },
                true,
                isEncrypt ? ["encrypt"] : ["decrypt"]
            );
        } else {
            const rawKey = this.createFixedLengthArrayBufferFromString(key, 256);
            return await window.crypto.subtle.importKey(
                "raw",
                rawKey,
                { name: algorithm, length: 256 },
                true,
                ["encrypt", "decrypt"]
            );
        }
    }


    private static createFixedLengthArrayBufferFromString(str: string, bits: 256 | 128 = 256) {
        if (![128, 256].includes(bits)) {
            throw new Error('Invalid key size. Key size must be 128 or 256 bits.');
        }
        const len = bits / 8;
        const buf = new Uint8Array(len);
        for (let i = 0; i < len; i++) {
            buf[i] = str.charCodeAt(i % str.length);
        }
        return buf.buffer;
    }

    public static encodeABToBase64(buffer: ArrayBuffer): string {
        let binary_string = "";
        const bytes = new Uint8Array(buffer);
        const len = bytes.byteLength;
        for (let i = 0; i < len; i++) {
            binary_string += String.fromCharCode(bytes[i]);
        }
        return window.btoa(binary_string);
    }
    public static decodeBase64ToAB(base64: string) {

        const binary_string = window.atob(base64);
        const len = binary_string.length;
        const bytes = new Uint8Array(len);
        for (let i = 0; i < len; i++) {
            bytes[i] = binary_string.charCodeAt(i);
        }
        return bytes.buffer;

    }


    public static stringToBase64(data: string) {
        // string TEXT to base64String 'e encode evrilmesi
        const encodedData = new TextEncoder().encode(data);
        return this.encodeABToBase64(encodedData);
    }

    public static base64ToString(data: string) {
        // base64String 'in decode edilmesi
        let decodedData = this.decodeBase64ToAB(data);
        return new TextDecoder().decode(decodedData);
    }
}