






import Vue from "vue";

export default Vue.extend({
   name: "VRow",
   props: {
      customCompClass: {
         default: "row",
         type: String
      },
       noGutters: {
         type: Boolean,
    },
   },
   destroyed() {
     this.$el?.remove();
   },
   methods:{
       setClass() {
       if (this.noGutters == false) {
            return this.customCompClass;
          } else {
            return "no-gutters";
          }
      }
   },
},
);
