




import { ContextManager } from "../../../../../../common/shrimp/context";
import { IExternalEvents } from "../../../../../../common/shrimp/interfaces/ComponentInterfaces/IExternalEvents";
import { IExternalProp } from "../../../../../../common/shrimp/interfaces/ComponentInterfaces/IExternalProp";
import { IDictionary } from "../../../../../../common/shrimp/interfaces/IDictionary";
import Vue from "vue";

export default Vue.extend({
  name: "TSComponentWrapper",
  props: {
    _renderingProps: {
      default: {
        context: undefined as unknown as () => ContextManager,
        events: undefined as unknown as () => IDictionary<Function>,
        props: undefined as unknown as () => IDictionary<Object>,
        targetLocalComponent: undefined as unknown as () => any
      }
    }
  },
  methods: {
    GetProps(propsCB: (props?: Array<IExternalProp>) => void) {
      return this._renderingProps.targetLocalComponent().GetProps(propsCB);
    },
    GetEvents(eventsCB: (events?: Array<IExternalEvents>) => void) {
      return this._renderingProps.targetLocalComponent().GetEvents(eventsCB);
    },
    GetQJson(){
      return this._renderingProps.targetLocalComponent().GetQJson?.();
    },
    OutBoundProps(props : IDictionary<any|Object>) {
      var self=this;
      Object.keys(props).forEach(prop => {
        if(props[prop]?.watcher)
        {
          var propDesc: PropertyDescriptor={};
          propDesc.get=props[prop].get;         
          propDesc.set=props[prop].set;
          Object.defineProperty(self,prop,propDesc);
        }
        else
        {
          (<any>this)[prop] = props[prop];
        }
      });
    },
    OutBoundEvents(events) {
      Object.keys(events).forEach(event => {
        (<any>this)[event] = events[event];
      });
    }
  },
  mounted() {
    const self = <any>this;
    let tlc=this._renderingProps.targetLocalComponent();
    let rendered = tlc.Render(this);
    self.$refs["comp-wrapper"].appendChild(rendered);

    Object.getOwnPropertyNames(tlc.__proto__) 
    .forEach(function (propName) {
        if(typeof tlc.__proto__[propName] == 'function'){
            if(propName!="constructor")
            self[propName] =tlc.__proto__[propName].bind(tlc);
            // wrappedComponent[prop] =() => { var args = Array.prototype.slice.call(arguments); return localComponent.__proto__[prop].apply(localComponent, args);}
            // wrappedComponent[prop]=(<any>localComponent).__proto__[prop].bind(localComponent);
        }
    });


  },
  destroyed() {
    this._renderingProps.targetLocalComponent?.().$destroy?.();
  }
});
