import ArrayStore from "devextreme/data/array_store";
import CustomStore from 'devextreme/data/custom_store';
import DataSource from 'devextreme/data/data_source';
import { IComponentCollection } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IDictionary } from "../../../../common/shrimp/interfaces/IDictionary";
import { IContainerServices, IContainerServicesWrapper } from "../../../../common/shrimp/interfaces/quick/IContainerServices";
import { IShell } from "../../../../common/shrimp/interfaces/quick/IShell";
import { ILanguageExtensions } from "../../../../common/qshell";
import { fixShell } from "../../../../common/shrimp/helpers/shellHelper";



export class LanguageExtensions implements ILanguageExtensions {
    public static ContextName = "LanguageExtensions";

    public contextName: string = LanguageExtensions.ContextName;
    private servicesWrapper: IContainerServicesWrapper;
    private osGlobal: {
        shell: IShell;
        devex: {};
    };

    constructor(servicesWrapper: IContainerServicesWrapper) {
        this.servicesWrapper = servicesWrapper;
        this.osGlobal = this.createOsGlobalObjectsInternal();
    }


    public manageShellObject() {
        const map: Array<{ shellKey: keyof IShell, serviceKey: keyof IContainerServices }> = [
            { shellKey: "isAuthenticated", serviceKey: "authenticatedCb" },
            { shellKey: "getResource", serviceKey: "resourceCb" },
            { shellKey: "callMessageBox", serviceKey: "callMessageBox" },
            { shellKey: "getUserContext", serviceKey: "userContextCb" },
            { shellKey: "getCustomerContext", serviceKey: "customerContextCb" },
            { shellKey: "getStoreData", serviceKey: "getStoreDataCb" },
            { shellKey: "print", serviceKey: "print" },
            { shellKey: "scan", serviceKey: "scan" },
            { shellKey: "ocr", serviceKey: "ocr" },
            { shellKey: "biometric", serviceKey: "biometric" },
            { shellKey: "logout", serviceKey: "logout" },
            { shellKey: "getContainerName", serviceKey: "getContainerName" },
            { shellKey: "extensions", serviceKey: "extensions" },
        ];
        map.forEach(item => this.osGlobal.shell[item.shellKey] = this.servicesWrapper.services[item.serviceKey] as any);
        fixShell(this.osGlobal.shell);
    }

    public createOsGlobalObjects(): IDictionary<any> {
        return this.osGlobal;
    }
    private createOsGlobalObjectsInternal() {
        const sc = this.servicesWrapper.services;
        const hasDevExLibrary = true; //functiondan alacak
        const langObjects = {
            shell: {},
            devex: {}
        };

        if (sc) {
            const shell: IShell = {
                isAuthenticated: sc.authenticatedCb,
                getResource: sc.resourceCb,
                callMessageBox: sc.callMessageBox,
                getUserContext: sc.userContextCb,
                getCustomerContext: sc.customerContextCb,
                getStoreData: sc.getStoreDataCb,
                print: sc.print,
                scan: sc.scan,
                ocr: sc.ocr,
                biometric: sc.biometric,
                logout: sc.logout,
                getContainerName: sc.getContainerName,
                extensions: sc.extensions
            };
            langObjects.shell = shell;
        }

        if (hasDevExLibrary) {
            const devex = {
                createDataSource({ ownerComp, parameters }: { ownerComp: IComponentCollection, parameters: Array<any> | undefined }) {
                    let store: ArrayStore | Array<any> = (parameters && parameters[0] && parameters[0].store) ?? arguments?.[0]?.store;

                    return new DataSource({
                        store: store
                        //Gelebilecek issuelar iin not:
                        // "reshapeOnPush: true" 
                        // ^Bu olmadan tablonun sonuna insert olmuyo. Bu varken de push methodunda index almyo. 
                        //Tablo sonuna push methoduyla insert yaplmak istenirse index setlenmeli.
                    });
                },
                createCustomStore({ ownerComp, parameters }: { ownerComp: IComponentCollection, parameters: Array<any> | undefined }) {
                    let loadUrl: string = (parameters && parameters[0] && parameters[0].loadUrl) ?? arguments?.[0]?.loadUrl;
                    let key: string = (parameters && parameters[0] && parameters[0].key) ?? arguments?.[0]?.key;
                    let load: ((loadOptions) => any[] | Promise<any>) | undefined = (parameters && parameters[0] && parameters[0].load) ?? arguments?.[0]?.load;
                    if (!load) {
                        load = (loadOptions) => {
                            let params = '?';
                            [
                                'skip',
                                'take',
                                'requireTotalCount',
                                'requireGroupCount',
                                'sort',
                                'filter',
                                'totalSummary',
                                'group',
                                'groupSummary',
                            ].forEach((i) => {
                                if (i in loadOptions && (loadOptions[i] != '' || loadOptions[i] != null || loadOptions[i] != null != undefined)) {
                                    params += `${i}=${JSON.stringify(loadOptions[i])}&`;
                                }
                            });
                            params = params.slice(0, -1);
                            return fetch(loadUrl + params)
                                .then((response) => response.json())
                                .then((data) => ({
                                    data: data.data,
                                    totalCount: data.totalCount,
                                    summary: data.summary,
                                    groupCount: data.groupCount,
                                }))
                                .catch(() => { throw new Error('Data Loading Error'); });
                        }
                    }

                    return new CustomStore({
                        load: load,
                        key: key
                    })
                },
                createArrayStore({ ownerComp, parameters }: { ownerComp: IComponentCollection, parameters: Array<any> | undefined }) {
                    let key: string = (parameters && parameters[0] && parameters[0].key) ?? arguments?.[0]?.key;
                    let data: Array<any> = (parameters && parameters[0] && parameters[0].data) ?? arguments?.[0]?.data;

                    return new ArrayStore({
                        key: key,
                        data: data
                    });
                },
                exportDxDataGrid({ options, worksheetName, fileName }: { options: { dataGridInstance: any, selectedRowsOnly?: boolean, autoFilterEnabled?: boolean, customizeCell?: ((options: { gridCell?: any; excelCell?: any }) => void) }, worksheetName?: string, fileName?: string }) {
                    Promise.all([import('exceljs'), import('file-saver'), import('devextreme/excel_exporter')]).then(([ExcelJS, FileSaver, ExcelExporter]) => {
                        const workbook = new ExcelJS.Workbook();
                        const worksheet = workbook.addWorksheet(worksheetName ?? 'Main Sheet');

                        ExcelExporter.exportDataGrid({
                            component: options.dataGridInstance,
                            worksheet: worksheet,
                            customizeCell: options.customizeCell ?? function (options) {
                                options.excelCell.font = { name: 'Arial', size: 12 };
                                options.excelCell.alignment = { horizontal: 'left' };
                            },
                            autoFilterEnabled: options.autoFilterEnabled,
                            selectedRowsOnly: options.selectedRowsOnly
                        }).then(() => {
                            workbook.xlsx.writeBuffer()
                                .then((buffer) => {
                                    FileSaver.default.saveAs(new Blob([buffer], { type: 'application/octet-stream' }), fileName ?? 'DataGrid.xlsx');
                                }).catch((error) => {
                                    throw new Error(`Unable to download Excel File ${fileName ?? 'DataGrid.xlsx'}. Reason: ${error}`);
                                });
                        }).catch((error) => {
                            throw new Error(`Unable to export DataGrid. Reason: ${error}`);
                        });

                    }).catch(error => {
                        throw new Error(`Please reach out to Plateau Studio team for support.. Error importing modules: ${error}`);
                    });
                }
            }
            langObjects.devex = devex;
        }

        return langObjects;
    }

}