import { IComponentCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IDomElement } from "../../../../shrimp/interfaces/RenderingInterfaces/IDomElement";

export class IndexManager {
    private compIdPropIndex: Record<string, IComponentCollection | undefined> = {};
    private compDomIndex = new WeakMap<IDomElement, IComponentCollection>();

    constructor(controlDict: Record<string, IComponentCollection>) {
        //TODO: move index createion to each function on demand
        Object.keys(controlDict).forEach(compId => {
            const compColl = controlDict[compId];

            let propId = compColl.component?.$props?.id;
            if (propId) { this.compIdPropIndex[propId] = compColl; }

            let dom = compColl.domElement;
            if (dom) {
                this.compDomIndex.set(dom, compColl);
            }

            compColl.WatchCreatedFields((created) => {
                if (created.el) {
                    if (dom) { this.compDomIndex.delete(dom); }
                    dom = created.el;
                    this.compDomIndex.set(dom, compColl);
                }
                if (created.instance) {
                    if (propId) { delete this.compIdPropIndex.propId; }
                    propId = created.instance.$props?.id;
                    if (propId) { this.compIdPropIndex[propId] = compColl; }
                }
            });
        });
    }

    public getComponentCollectionByIdProperty(compPropId: string/*, controlDict: Record<string, IComponentCollection>*/) {
        // if (!this.compIdPropIndex){
        //     this.compIdPropIndex = Object.keys(controlDict).reduce((prev, cur) => {
        //         const compColl = controlDict[cur];
        //         const propId = compColl.component!.$props?.id;
        //         if (!propId){
        //             return prev;
        //         }
        //         prev[propId] = compColl;
        //         return prev;
        //     }, <Record<string, IComponentCollection | undefined>>{})
        // }
        return this.compIdPropIndex[compPropId];
    }

    public getComponentCollectionByDom(element: IDomElement/*, controlDict: Record<string, IComponentCollection>*/) {
        // if (!this.compDomIndex){
        //     this.compDomIndex = Object.keys(controlDict).reduce((prev, cur) => {
        //         const compColl = controlDict[cur];
        //         prev.set(compColl.domElement!, compColl);
        //         return prev;
        //     }, new WeakMap<IDomElement, IComponentCollection>());
        // }
        return this.compDomIndex.get(element);
    }

}