import { IComponentCollection } from "../../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { Dory } from "../../Dory";
import { DoryJr } from "../../DoryJr";
import { HistoryManager } from "../../Managers/HistoryManager";

export class DoryLikeData {
    private ownerDoryLike: Dory | DoryJr; // kendi leveli
    private createrComp?: IComponentCollection // parent leveli // (pipeline/loading).qjson'da da creator yok(DoryJr)
    private compUIDPrefix?: string;
    constructor(ownerDoryLike: Dory | DoryJr, options: { creatorComp?: IComponentCollection, compUIDPrefix?: string }) {
        this.ownerDoryLike = ownerDoryLike;
        this.createrComp = options.creatorComp;
        this.compUIDPrefix = options.compUIDPrefix;
    }

    public GetCompUIDPrefix() {
        if (this.createrComp) { return this.createrComp.GetCompUID(); }
        if (this.compUIDPrefix) { return this.compUIDPrefix; }
        return "";
    }


    public get OwnerDoryLike(): Dory | DoryJr {
        return this.ownerDoryLike;
    }

    public GetCreaterRenderingComponent(): IComponentCollection | undefined {
        if (this.createrComp?.compType === "RenderingComponent" && this.createrComp?.component?.RenderGo) {
            return this.createrComp;
        }
        return this.createrComp?.context?.GetItemSingle<HistoryManager>(HistoryManager.ContextName)?.getCreaterRenderingComponent();
    }

    public GetCreatorComponent() {
        return this.createrComp;
    }

    public GetCreaterComp() {
        return this.createrComp;
    }

}