export class StringHelper {
    public formatString(selfString: String, ...args: Array<any>) {
        return this.formatStringArray(selfString, args);
    }
    public formatStringArray(selfString: String, args: Array<any>) {
        return selfString.replace(/{(\d+)}/g, function (match, number) {
            return typeof args[number] != 'undefined' ? args[number] : match ;
        });
    }
}