import { IVnode } from "../interfaces/ComponentInterfaces/IVnode";

export interface IStyle {
    paddingLeft?: string;
    marginLeft?: string;
    top?: string;
    left?: string;
    width?: string;
    height?: string;
    border?: string;
    position?: string;
    zIndex?: string;
    visibility? : "visible" | "hidden" | "collapse" | "initial" | "inherit";
}

export interface IAttrs {
    title?: string;
    href?: string;
    rel?: string;
    src?: string;
    scrolling?: "no";
}
export interface IEvent {
    load?: (ev: Event) => any;
}
export interface ICreateDivOptions {
    classes?: Array<string>, id?: string, children?: Array<HTMLElement> | HTMLElement, text?: string,
    style?: IStyle, events?: IEvent, attrs?: IAttrs, parent?: HTMLElement,
}
function objectKeys<T extends object>(object: T): Array<keyof T> {
    return Object.keys(object) as Array<keyof T>;
};
export class DomHelper {
    public static CreateDom<T extends keyof HTMLElementTagNameMap>(tag: T, options?: ICreateDivOptions): HTMLElementTagNameMap[T] {
        return DomHelper.UpdateDom(document.createElement(tag), options);
    }
    public static UpdateDom<T extends HTMLElement>(el: T, options?: ICreateDivOptions): T {
        if (!options) { return el; }

        if (options.id) {
            el.id = options.id;
        }
        if (options.classes) {
            options.classes.forEach(cls => el.classList.add(cls));
        }
        if (options.children) {
            DomHelper.AppendChildren(el, options.children);
        }
        if (options.text) {
            el.innerText = options.text;
        }
        if (options.style) {
            objectKeys(options.style).forEach(styleKey => el.style[styleKey] = options.style![styleKey]!);
        }
        if (options.attrs) {
            objectKeys(options.attrs).forEach(attr => el.setAttribute(attr, options.attrs![attr]!));
        }
        if (options.events){
            objectKeys(options.events).forEach(eventName => el.addEventListener(eventName, options.events![eventName]!));
        }
        if(options.parent) {
            DomHelper.AppendChildren(options.parent, el);
        }

        return el;
    }
    public static CreateDiv(options?: ICreateDivOptions) {
        return DomHelper.UpdateDom(document.createElement("div"), options);
    }

    public static CreateStyle(styleString: string) {
        const style = document.createElement("style");
        style.appendChild(document.createTextNode(styleString));
        return style;
    }
    public static ClearChildren(parent: HTMLElement) {
        while (parent.firstChild) {
            parent.removeChild(parent.firstChild);
        }
    }
    public static AppendChildren(parent: HTMLElement, children: Array<HTMLElement> | HTMLElement, options: { clear?: boolean } = {}) {
        if (options.clear) {
            DomHelper.ClearChildren(parent);
        }
        if (children instanceof Array) {
            children.forEach(child => parent.appendChild(child));
        } else {
            parent.appendChild(children);
        }
    }

    public static ToggleClass(element: HTMLElement, cls: string, status?: boolean) {
        const add = status === true || (status === undefined && !element.classList.contains(cls));
        if (add) {
            element.classList.add(cls);
        } else {
            element.classList.remove(cls);
        }
        return add;
    }

    public static findCompInNodeArray(cb: (node: IVnode) => boolean, childNodeArray?: Array<IVnode>) {
        if (!childNodeArray) {
            return;
        }
        for (let index = 0, arrLength = childNodeArray.length; index < arrLength; index++) {
            const childNode = childNodeArray[index];
            if (cb(childNode)) {
                return
            };
            DomHelper.findCompInNodeArray(cb, childNode["children"])
        }
    }
}