import { IContextItem } from "../../../../shrimp/context";
import { Dory } from "../../Dory";

export class MockObject<MockType> {
    private values: Array<MockType>;
    private playCounter: number = 0;
    private mockKey: string;
    constructor(mockKey: string) {
        this.values = [];
        this.mockKey = mockKey;
        this.Reset();
    }

    public Reset() {
        this.playCounter = 0;
    }

    public AddMock(value: MockType, overrideCapture: boolean) {
        if (overrideCapture) {
            this.values[0] = value;
        } else {
            this.values.push(value);
        }
    }
    public GetMock(overrideCapture: boolean): MockType {
        if (overrideCapture) {
            return this.values[0];
        }
        if (this.values.length <= this.playCounter) { throw new Error("Not enough capture was taken for key: " + this.mockKey + " access counter:" + this.playCounter); }
        return this.values[this.playCounter++];
    }
}

export enum MockMode {
    off = "off",
    captureOnly = "captureOnly",
    playOnly = "playOnly",
}

interface IMockOptions {
    mockMode: MockMode;
    /**
     * Each capture with same key overrides. False for indexed captures. ex: for same key: xx -> xx[1], xx[2], xx[3], ...
     */
    overrideCapture: boolean;
}

export class MockManager<MockType> implements IContextItem {
    public static ContextName = "MockManager";

    public static GetMocker<MockType>(options: IMockOptions, dory: Dory) {
        const interDoryContext = dory.GetInterDoryContext();
        let mm = interDoryContext.GetItemSingle<MockManager<MockType>>(MockManager.ContextName);
        if (!mm) {
            mm = new MockManager<MockType>(options);
            interDoryContext.RegisterItem(mm);
        }
        return mm;
    }



    public contextName = MockManager.ContextName;
    private mocks: Record<string, MockObject<MockType> | undefined>;
    private options: IMockOptions;
    private constructor(options: IMockOptions) {
        this.mocks = {};
        this.options = options;
    }

    public set MockMode(mockMode: MockMode) {
        this.options.mockMode = mockMode;
        this.Reset();
    }

    public Reset() {
        Object.keys(this.mocks).forEach(key => this.mocks[key]!.Reset());
    }

    public Execute(key: string, realCall: () => MockType): MockType {
        let retVal: MockType | undefined = undefined;
        switch (this.options.mockMode) {
            case MockMode.off:
            default:
                retVal = realCall();
                break;
            case MockMode.captureOnly:
                retVal = realCall();
                this.SetMock(key, retVal);
                break;
            case MockMode.playOnly:
                const mockObj = this.mocks[key];
                if (!mockObj) { throw new Error("No mock captured for: " + key); }
                retVal = mockObj.GetMock(this.options.overrideCapture);
                break;
        }

        return retVal;
    }

    public SetMock(key: string, value: MockType, options: { overrideCapture?: boolean, overrideMockMode?: boolean } = {}) {
        if (options.overrideMockMode != true) {
            switch (this.options.mockMode) {
                case MockMode.off: case MockMode.playOnly: default:
                    return;
                case MockMode.captureOnly:
                    break;
            }
        }
        const curMock = this.mocks[key] || (this.mocks[key] = new MockObject<MockType>(key));
        const overrideCapture = options.overrideCapture !== undefined ? options.overrideCapture : this.options.overrideCapture;
        curMock.AddMock(value, overrideCapture);
    }
}