import { ContextManager } from "../../../shrimp/context";
import { Logger, LogType } from "../../../shrimp/helpers/logger";
import { ICompParentLocation, QJsonHelper } from "../../../shrimp/helpers/QJsonHelper";
import { TypeHelper } from "../../../shrimp/helpers/typeHelper";
import { ElementLocation } from "../../../shrimp/interfaces/ComponentInterfaces/ElementLocation";
import { CType, ICompJson } from "../../../shrimp/interfaces/ComponentInterfaces/ICompJson";
import { IComponentCollection } from "../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { ICreatedDynamicComp, IDynamicChildrenCreator, IDynamicCompCreator } from "../../../shrimp/interfaces/RenderingInterfaces/IRenderer";
import { Dory } from "../Dory";
import { HistoryItem } from "../Model/History/HistoryItem";
import { Evaluator } from "./Evaluator";
import { RenderingManager } from "./RenderingManager";


export class DynamicCompManager {

    static _itemCounter: number = 0;
    public static get itemCounter(): number {
        return this._itemCounter;
    }
    public static set itemCounter(v: number) {
        this._itemCounter = v;
    }
    /**
     * Create a new component from given template json
     * @param historyItem Current history item of the rendering
     * @returns Created component collection
     */
    public static createDynamicComp(params: IDynamicCompCreator): ICreatedDynamicComp | undefined {
        const { templateCompQID, newCompID, dataSource, context, historyItem, reverseLook, placeQID } = params;
        let compJson = (historyItem as HistoryItem).getComponentJsonByCompId(templateCompQID, reverseLook);
        if (!compJson) {
            const dory = context.GetItemSingle<Dory>(Dory.ContextName);
            const engineYamlLogType = dory?.GetLogParams()?.minEngineLogLevel;
            Logger.Log({ logMessage: "Component json couldn't be found in qjson", logType: LogType.Warning, minEngineLogLevel: engineYamlLogType });
            return;
        }
        let parentCompCollection = placeQID ? historyItem.getCompCollectionById(placeQID) : DynamicCompManager.renderedParentComp((historyItem as HistoryItem), context, compJson?.path);
        let newComponent = DynamicCompManager.createComp({ historyItem: (historyItem as HistoryItem), templateJson: compJson?.compLoc.compJson, newCompID, parentCompCollection, context, dataSource });
        return { newComponent, parentCompCollection };
    }

    public static createDynamicChildren(params: IDynamicChildrenCreator): ICreatedDynamicComp[] | undefined {
        const { parentCompId, templateChildName, newChildName, historyItem, context } = params;
        let parentCompPJson = (historyItem as HistoryItem).getComponentJsonByCompId(parentCompId)?.compLoc?.compJson;
        let childJson = parentCompPJson?.C[templateChildName];
        if (childJson) {
            return Object.keys(childJson.c).map(child => {
                let templateCompQID = childJson!.c[child].P.ID.H;
                let createdComp = DynamicCompManager.createDynamicComp({ templateCompQID, newCompID: newChildName, context, historyItem });
                return createdComp;

            }).filter((item): item is ICreatedDynamicComp => item != null);
        }
    }

    public static appendDynamicCreatedComp({ newComponents, parentComp, context, place = ElementLocation.After, historyItem, childName }:
        {
            newComponents: Array<IComponentCollection>, parentComp?: IComponentCollection, context: ContextManager, place?: ElementLocation,
            historyItem: HistoryItem, childName: string
        }) {
        const dory = context.GetItemSingle<Dory>(Dory.ContextName);
        historyItem.ReCalculateCompInstDict();
        let targetList = childName && parentComp ? (parentComp.children![childName] ||= []) : historyItem.controlList;
        newComponents.forEach(comp => {
            targetList.push(comp);
        });
        dory?.shellConfigs.dynamicCompHandler?.({ createdComps: newComponents, compLocater: place, parentCollection: parentComp, childName });
    }

    public static generateCompID(sourceCompID: string, dynamicCompIdSuffix: string | undefined) {
        dynamicCompIdSuffix = dynamicCompIdSuffix ? dynamicCompIdSuffix : (++this.itemCounter).toString();
        return sourceCompID + "_" + dynamicCompIdSuffix;
    }

    /**
     * Deep copy the templateJson and create a new RenderingManager and render the new component
     * @param historyItem Current history item of the rendering
     * @param templateJson Template json of the new component
     * @param newCompID Creaetd component key id. New id will be: compJson.id + '_' + newCompID
     * @returns New componentcollection by new id
     */
    private static createComp({ historyItem, templateJson, newCompID, parentCompCollection, context, dataSource }:
        { historyItem: HistoryItem, templateJson: ICompJson, newCompID?: string, parentCompCollection?: IComponentCollection, context: ContextManager, dataSource?: any }): IComponentCollection {
        const newCompJson = new TypeHelper().cloneObject(templateJson);

        QJsonHelper.ForEachComp([newCompJson], childCompJson => {
            this.deletePropRender(childCompJson);
            const newID = DynamicCompManager.generateCompID(childCompJson.P.ID.H, newCompID);
            childCompJson.P.ID.H = childCompJson._Editor.eID = newID;
            return true;
        });

        const rm = new RenderingManager({
            pjson: [newCompJson], context, dataItem: { data: dataSource }, asChildType: <CType>parentCompCollection?.childrenType,
            compareCompValue: historyItem.compareCompValue, controlDict: historyItem.controlDict, parentCompCollection,
            dynamicCompIDSuffix: newCompID, qjsonVersion: historyItem.qjsonVersion
        });
        rm.Render(rm.blockInfo.blockJsonIndex);
        return historyItem.getCompCollectionById(newCompJson.P.ID.H)!;
    }

    /**
     * Find the parent component which can be rendered
     * If there is no parent, return undefined
     * @param historyItem Current history item of the rendering
     * @param parentLocation Parent location to find the porent from the json
     * @returns parent component collection or undefined
     */
    private static renderedParentComp(historyItem: HistoryItem, context: ContextManager, parentLocation: ICompParentLocation) {
        if (!parentLocation.parent) {
            return;
        }
        let parentRenderVal = new Evaluator().Evaluate({ script: parentLocation.parent.P.Render?.H, context });
        const isParentRenderable = (parentRenderVal === null || parentRenderVal || parentRenderVal == 'true');
        if (!isParentRenderable) {
            return;
        }

        return historyItem.getCompCollectionById(parentLocation.parent.P.ID.H)!;
    }

    private static deletePropRender(compJson: ICompJson) {
        delete compJson.P.Render;
    }
}