import { PerfManager } from "./PerfManager";

export class PerfSingles {
    private static self: PerfSingles;

    public static Register(){
        PerfSingles.findSelf();
    }
    
    public static Activate(name?: string) {
        const targetPerf = this.findSelf().findPerf(name);
        targetPerf.Activate();
    }
    
    public static Instance(name?: string): PerfManager {
        return this.findSelf().findPerf(name);
    }
    
    public static Reset(): void {
        this.findSelf().perfSingles = {};
    }
    
    private static findSelf(): PerfSingles {
        if (PerfSingles.self) { return PerfSingles.self; }
        if (typeof window === "undefined") { return PerfSingles.self = new PerfSingles(); }
        return PerfSingles.self = (<any>window)["perfQuickShrimp"] = (<any>window)["perfQuickShrimp"] || new PerfSingles();
    }
    
    
    //Instance
    private perfSingles: Record<string, PerfManager> = {};
    private perfSingle?: PerfManager;

    private findPerf(name?: string) {
        let targetPerf = name ? this.perfSingles[name] : this.perfSingle;
        if (targetPerf) { return targetPerf; }

        targetPerf = name ?  this.perfSingles[name] = new PerfManager() : this.perfSingle = new PerfManager();

        return targetPerf;
    }
    public Activate(name?: string) {
        const targetPerf = this.findPerf(name);
        targetPerf.Activate();
    }
}

PerfSingles.Register();