import { Legacy } from "../../build/legacy";
import { IContainerServicesWrapper } from "../../../../common/shrimp/interfaces/quick/IContainerServices";
import { INavigationDemand, INavigationDemandType } from "../../../../common/shrimp/interfaces/quick/INavigationDemand";
import { IWebSDKSettingsWrapper } from "../../../../common/shrimp/interfaces/quick/IWebSDK";
import { RendererManager } from "./RendererManager";


export class WindowHistoryManager {

    contextName: string;
    private settingsWrapper: IWebSDKSettingsWrapper;
    private servicesWrapper: IContainerServicesWrapper;
    private rendererManager: RendererManager;

    constructor(contexName: string, settingsWrapper: IWebSDKSettingsWrapper, rendererManager: RendererManager, servicesWrapper: IContainerServicesWrapper) {
        this.contextName = contexName;
        this.settingsWrapper = settingsWrapper;
        this.servicesWrapper = servicesWrapper;
        this.rendererManager = rendererManager;
        this.Init();
    }

    public async pushState(pageId?: string, pageName?: string, navigationDirection?: INavigationDemandType, override?: boolean) {
        if (
            this.settingsWrapper.settings?.disableRoute ||
            this.settingsWrapper.settings.disableBackManagement || 
            navigationDirection == INavigationDemandType.back || 
            navigationDirection == INavigationDemandType.forward) {
            return;
        }
        var protocolAndPath = window.location.protocol + '//' + window.location.host;
        
        let state = { rendering: true, pageId: pageId };

        if (!override) {
            if(!pageName){
                window.history.pushState(state, '',  window.location.href);
                return;
            }
            var queryString = Legacy.fixQueryString()?.replace(".qjson", "")  ?? "";  
            /*LP'de sayfa refresh edildiinde, sw tarafndan pass geilip, 
            tekrar index.html'e dnmesi iin.
            Ayrca websdk.ts iindeki open function'na bak            
            */
            if (pageName.includes("<<qjson:")){
                pageName = pageName.replace("<<qjson:", "<<qjsonpass:"); 
                pageName = location.pathname + "?q=" + pageName
                queryString = "";
            }
            pageName = pageName?.startsWith("/") ? pageName.substring(1) : pageName;
            var url = queryString == "" ?  protocolAndPath + "/" +pageName!.replace('.qjson', '') : protocolAndPath + queryString; 
            const rootQjson = this.settingsWrapper.settings.rootqjson?.replace('.qjson', '');
            rootQjson && (url = url.replace(rootQjson, "/"));
            
            window.history.pushState(state, '', url);
        } else {
            window.history.replaceState(state, '', window.location.href);
        }
    }
    

    public Init() {
        let self=this;
        if (this.settingsWrapper.settings.disableBackManagement) {
            return;
        }
        let navigationDemand: INavigationDemand | null | undefined = null;
        
        (function (origPopstate) {
        window.onpopstate = (event: PopStateEvent) => {
            if (origPopstate) {
              origPopstate.call(window, event);
            }

            let activeRenderer = self.servicesWrapper.services.activeRendererCb?.() || self.rendererManager.getLastRenderer();
            navigationDemand = null;
            if (event.state !== null && event.state.rendering && event.state.pageId) {
                navigationDemand = activeRenderer?.GetNavigationDemand(event.state.pageId);

            }
            if (navigationDemand) {
                activeRenderer?.GoHistory(navigationDemand);
            }
            
          };

        })(window.onpopstate);
        

    }
}