import { IContextItem } from "../../../shrimp/context";
import { IComponentCollection } from "../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IEventData } from "../../../shrimp/interfaces/ComponentInterfaces/IEventData";
import { IHistoryBasedShellConfiguration } from "../../../shrimp/interfaces/quick/IShellConfiguration";
import { IRenderingContext } from "../../../shrimp/interfaces/RenderingInterfaces/IRenderingContext";
import { SimpleCallBack } from "../../../shrimp/interfaces/SimpleCallBack";
import { OrderedEvent } from "../Model/Component/OrderedEvent";

export class RenderingContext implements IRenderingContext, IContextItem {
    static ContextName: string = "RenderingContext";
    contextName: string = RenderingContext.ContextName;
    ownerComponent?: IComponentCollection;
    ownerEvent?: IEventData;
    private componentInCreation?: IComponentCollection;
    private isRendering: boolean = false;
    private isRenderingEventsComplete: boolean = false;
    private callbacks = new Array<Function>();
    private sequentialCallBacks: Array<OrderedEvent> = new Array<OrderedEvent>();
    private postRenderRootCallbacks = new Array<SimpleCallBack>();
    private compParentInst?: any;
    private _historyBasedConf: IHistoryBasedShellConfiguration;

    public get HistoryBasedConf(): IHistoryBasedShellConfiguration {
        return this._historyBasedConf;
    }
    public set ComponentInCreation(value) {
        this.componentInCreation = value;
    }
    public get ComponentInCreation() {
        return this.componentInCreation;
    }


    constructor({ compParentInst, historyBasedConf }: { compParentInst?: any, historyBasedConf: IHistoryBasedShellConfiguration }) {
        this.compParentInst = compParentInst;
        this._historyBasedConf = historyBasedConf;
    }

    public SetOwnersAndRetrieveOld(newOwners: { ownerComponent?: IComponentCollection, ownerEvent?: IEventData }) {
        const oldOwners = { ownerComponent: this.ownerComponent, ownerEvent: this.ownerEvent };
        this.ownerComponent = newOwners.ownerComponent;
        this.ownerEvent = newOwners.ownerEvent;
        return oldOwners;
    }

    getCompParentInst() {
        return this.compParentInst;
    }

    startRendering() {
        this.isRendering = true;
        this.isRenderingEventsComplete = false;
    }

    postRenderRoot() {
        this.componentInCreation = undefined;
        this.postRenderRootCallbacks.forEach(cb => cb());
    }

    endRendering() {
        this.isRendering = false;

        this.callbacks.forEach((callbackFunc) => callbackFunc());
        this.sequentialCallBacks.sort((cb1, cb2) => cb2.triggerOrder - cb1.triggerOrder).forEach((cb) => cb.handler());

        this.callbacks = new Array<Function>();
        this.sequentialCallBacks = new Array<OrderedEvent>();

        this.isRenderingEventsComplete = true;
    }
    IsRendering() {
        return this.isRendering;
    }
    public IsRenderingEventsComplete() {
        return this.isRenderingEventsComplete;
    }
    delayPostRenderRoot({ cb }: { cb: SimpleCallBack }) {
        this.postRenderRootCallbacks.push(cb);
    }

    executeOrDelayEvent({ cb, immediateEvent, triggerOrder }: { cb: Function, immediateEvent: boolean, triggerOrder: number | null }) {
        if (!this.isRendering || immediateEvent) {
            return cb();
        }

        if (triggerOrder) {
            this.sequentialCallBacks.push(new OrderedEvent({ handler: cb, triggerOrder: triggerOrder }));
            return;
        }

        this.callbacks.push(cb);
    }


}

