import { IIntegrationsManager } from "../../../../common/qshell/src/interfaces/construction/IIntegrationsManager";
import { IWebSDKSettingsWrapper } from "../../../../common/shrimp/interfaces/quick/IWebSDK";


export class IntegrationsManagerImpl implements IIntegrationsManager {
    contextName: string;
    private settingsWrapper: IWebSDKSettingsWrapper;

    constructor({ contextName, settingsWrapper }: { contextName: string, settingsWrapper: IWebSDKSettingsWrapper }) {
        this.contextName = contextName;
        this.settingsWrapper = settingsWrapper;
    }

    customIntegrationEventTrigger(name: string, params: any[]) {
        const integrationsSettings = this.settingsWrapper.settings.appSettings?.integrationsSettings;
        const customIntegrations = integrationsSettings?.customIntegrations;
        if (customIntegrations) {
            customIntegrations.forEach((integration) => {
                if (!integration.isActivated) return;
                const event = integration.events?.find(event => event.eventName === name);
                if (event) {
                    try {
                        const paramNames = event.parameters?.map(param => param.parameterName).join(', ');
                        if (!paramNames) return;
                        const eventFunctionString = `(${paramNames}) => { ${event.eventContent} }`;
                        const eventFunction = eval(eventFunctionString);
                        eventFunction(...params);
                    } catch (e) {
                        console.error(e);
                    }
                }
            });
        }
    }
}