import { IDictionary } from "../../../../shrimp/interfaces/IDictionary";
import { Dory } from "../../Dory";

export class TSProxy {
    private dory: Dory;
    private globals;
    private TSProxyMapping: IDictionary<any> = {
        "shell": "containerServices",
    }
    constructor({ dory, globals }: { dory: Dory, globals: any }) {
        this.dory = dory;
        this.globals = globals;
    }
    generateTsProxy() {
        if (this.dory && this.dory.shellConfigs.osGlobalObjects) {
            const globalObjects = this.dory.shellConfigs.osGlobalObjects;
            Object.keys(globalObjects).forEach(key => {
                let objectName = (this.TSProxyMapping[key]) ?? key;
                
                this.globals[objectName] = globalObjects[key];
            });
            return this.globals;
        }
    }

}