import { ContextManager } from "../../../shrimp/context";
import { Logger } from "../../../shrimp/helpers/logger";
import { IComponentCollection } from "../../../shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IDictionaryNullable } from "../../../shrimp/interfaces/IDictionaryNullable";
import { RenderingContext } from "../Context/RenderingContext";
import { Dory } from "../Dory";
import { HistoryManager } from "./HistoryManager";

export class MessagingManager {
    static MessagingCompName = "Messaging";
    context: ContextManager;

    constructor(context: ContextManager) {
        this.context = context;
    }

    /**
     * Returns a messaging component if it is exists on the given level index
     * @param levelIndex Index that will be searched for Messaging component
     * @returns IComponentCollection
     */
    GetMessagingComponentByLevelIndex(levelIndex: number): IComponentCollection | null {
        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        const dory = this.context.GetItemSingle<Dory>(Dory.ContextName);
        const engineYamlLogType = dory?.GetLogParams()?.minEngineLogLevel;
        const messagingComponent = historyManager?.GetComponentByType(MessagingManager.MessagingCompName, levelIndex);
        if (!messagingComponent) {
            Logger.Log({ logMessage: `${MessagingManager.MessagingCompName} component couldn' t found on { ${levelIndex} + } index level`, minEngineLogLevel: engineYamlLogType })
            return null;
        }

        return messagingComponent;
    }

    /**
     * Get component property value by eventName
     * @param eventName Name of the component event
     * @returns void | null
     */
    Get(eventName: string) {
        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        if (!historyManager) {
            return;
        }
        const messagingComponent = this.GetMessagingComponentByLevelIndex(historyManager.CurrentLevelIndex);
        if (messagingComponent && messagingComponent.events) {
            const eventBoundFuntions = messagingComponent.events.GetEventBoundFunctions();
            return eventBoundFuntions.eventFunctions[eventName] ?? eventBoundFuntions.propertyEventFunctions?.[eventName];
        }
    }

    // /**
    //  * Set component property value by propertyName
    //  * @param propertyName Name of the component property
    //  * @param propertyValue Value that will be setted to the @propertyName
    //  * @returns void
    //  */
    // Set(propertyName: string, propertyValue: object) {
    //     const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
    //     const messagingComponent = this.GetMessagingComponentByLevelIndex(historyManager.CurrentLevelIndex);
    //     if (messagingComponent) {
    //         messagingComponent[propertyName] = propertyValue;
    //     }
    // }

    /**
     * Trigger an event on current index level of currentLevel
     * @param eventName Event that will be triggered
     * @param parameters Which parameters or arguments that will be passed
     * @returns void
     */
    Trigger({ eventName, parameters }: { eventName: string | undefined, parameters: Array<any> | IDictionaryNullable<any> | undefined }) :any {
        const historyManager = this.context.GetItemSingle<HistoryManager>(HistoryManager.ContextName);
        if (!historyManager) {
            return;
        }
        let messagingComponent = this.GetMessagingComponentByLevelIndex(historyManager.CurrentLevelIndex);
        
        const immediate = true; 
        if (!messagingComponent) {
            const renderingContext = this.context.GetItemSingle<RenderingContext>(RenderingContext.ContextName);
            const ownerComp = renderingContext?.ownerComponent;
            messagingComponent = ownerComp && ownerComp.compType == MessagingManager.MessagingCompName ? ownerComp : null;
        }
        let retVal = null;
        if (messagingComponent) {
            retVal = messagingComponent.events.Trigger({ eventName: eventName!, eventArguments: parameters, immediate });
        }
        return retVal;
    }
}