import { template } from "@babel/core";
import { ContextManager, IContextItem } from "../../../shrimp/context";
import { IConfig } from "../../../shrimp/interfaces/quick/IConfig";

export class ConfigManager implements IContextItem {
    static ContextName: string = "ConfigManager";
    contextName: string = ConfigManager.ContextName;
    currentContext: ContextManager;
    private configValues?: IConfig[];

    constructor(currentContext: ContextManager) {
        this.currentContext = currentContext;
    }

    public SetConfigValues(configValues?: IConfig[]) {
        this.configValues = configValues;
    }

    public GetConfigValues(key: string) {
        if (!this.configValues || this.configValues.length == 0) { return undefined; }

        return this.configValues.find(item => item.key == key);
    }
}