import { IShell } from "../interfaces/quick/IShell";
import { IUserContext } from "../interfaces/quick/IUserContext";

function fixShell(shell: IShell) {
    if (!shell.getUserContext) {
        let userContext: IUserContext = { userId: "" };
        shell.getUserContext = () => { return userContext; };
        shell.setUserContext = (user) => { userContext = user; };
    }
}


export { fixShell }