import { QShell } from "../../../../../common/qshell";
import { ContextManager } from "../../../../../common/shrimp/context";
import { IComponentCollection } from "../../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IWebSDKSettingsWrapper } from "../../../../../common/shrimp/interfaces/quick/IWebSDK";
//Bu importlar DI ile zme durumumuz olabilir.
import { VTextFieldFormattingExtensionData } from "./extensionData/VTextFieldFormattingExtensionData";

export class ExtensionManager {

    private static initialze(compCol: IComponentCollection) {
        compCol.component!.extention = {};
    }

    public static ManageComponent(compCollection: IComponentCollection, attrs: Record<string, any>, settingsWrapper: IWebSDKSettingsWrapper, websdkContext: ContextManager) {
        if (!compCollection.component) return;

        this.initialze(compCollection);
        if (ExtensionManager.extensionDomLocator[compCollection.compType])
            ExtensionManager.extensionDomLocator[compCollection.compType](compCollection, attrs, settingsWrapper, websdkContext);
    }

    public static UpdateComponent(compCollection: IComponentCollection, attrs: Record<string, any>, settingsWrapper: IWebSDKSettingsWrapper, websdkContext: ContextManager) {
        if (!compCollection.component) return;

        this.initialze(compCollection);
        if (ExtensionManager.extensionDomLocator[compCollection.compType])
            ExtensionManager.extensionDomLocator[compCollection.compType](compCollection, attrs, settingsWrapper, websdkContext);
    }

    private static extensionDomLocator: Record<string, (compCollection: IComponentCollection, attrs: Record<string, any>, settingsWrapper: IWebSDKSettingsWrapper, websdkContext: ContextManager) => void> = {
        'VTextField': (compCollection, attrs, settingsWrapper, websdkContext) => {
            let formattingData = new VTextFieldFormattingExtensionData(settingsWrapper, websdkContext);
            formattingData.create(compCollection, attrs);
        },

        'VCombobox': (compCollection, attrs, settingsWrapper, websdkContext) => {
            let formattingData = new VTextFieldFormattingExtensionData(settingsWrapper, websdkContext);
            formattingData.create(compCollection, attrs);
        },

        'VAutocomplete': (compCollection, attrs, settingsWrapper, websdkContext) => {
            let formattingData = new VTextFieldFormattingExtensionData(settingsWrapper, websdkContext);
            formattingData.create(compCollection, attrs);
        },

        'VTextarea': (compCollection, attrs, settingsWrapper, websdkContext) => {
            let formattingData = new VTextFieldFormattingExtensionData(settingsWrapper, websdkContext);
            formattingData.create(compCollection, attrs);
        }
    }
}