import { IContextItem } from "../../../../shrimp/context";
import { IClonable } from "../../../../shrimp/helpers/clonable";
import { BaseStoreContext } from "./BaseStoreContext";

export class SharedStoreContext extends BaseStoreContext implements IContextItem, IClonable {
    public static ContextName: string = "SharedStoreContext";
    contextName: string = SharedStoreContext.ContextName;

    public constructor() {
        super();
    }

    public CreateSelf() {
        return new SharedStoreContext();
    }

    public OverrideAllContextItems(newContextItems: any) {
        this.contextItems = newContextItems;
    }
}
