import { getOptionsClass, layoutBarComponentClassList, LayoutOption, spacingOptionsPrefix } from "../helpers/layoutBarHelper";
import { CType, ICompJson, ILayoutJson, IOptJson, IPropertyJson, PType } from "../interfaces/ComponentInterfaces/ICompJson";
import { IDictionary } from "../interfaces/IDictionary";

interface IOptJsonOld extends IOptJson {
    class?: string,
    text?: string,
};

export interface IFixCtrlNotification {
    message: string,
    type?: "error" | "warn"
}

export class CompJsonMigrator {
    public static fixCtrlOldStyle(ctrl: ICompJson, options: { mode?: "designtime" | "runtime" } = {}): IDictionary<IFixCtrlNotification> {
        const notifies: IDictionary<IFixCtrlNotification> = {};
        ctrl.O = ctrl.O || { cType: CType.child };
        ctrl.O.cType = ctrl.O.cType || CType.child;
        Object.keys(ctrl.C).forEach(childKey => {
            const curChild = ctrl.C[childKey];
            //fix old C type. before IChildJson
            if (Array.isArray(curChild)) {
                ctrl.C[childKey] = { cType: ctrl.O!.cType == CType.slot ? CType.slot : CType.child, c: curChild }; // defaulting comp child type to each child itself
            }
        });

        //each P used to be string. fix that
        Object.keys(ctrl.P).forEach(propKey => {
            let propValue: string | IPropertyJson = ctrl.P[propKey]!;
            if (typeof (propValue) === "string") {
                ctrl.P[propKey] = { H: propValue, T: PType.prop };
            }

        });

        //move old text & class on O to P with type
        const o: IOptJsonOld = ctrl.O;
        if (o.text) {
            ctrl.P.text = { H: o.text, T: PType.text };
            delete o.text;
        }
        if (o.class) {
            ctrl.P.class = { H: o.class, T: PType.class };
            delete o.class;
        }

        //move old class to layout.class
        if (options.mode && options.mode == "designtime" && ctrl.P[PType.class]) {
            const oldClass = ctrl.P[PType.class]?.H;
            if (oldClass !== "" && oldClass?.startsWith("'") && oldClass?.endsWith("'")) {
                ctrl.O = ctrl.O || {} as IOptJson;
                let propClass = [] as Array<string>;
                let prepareClass = false;
                let layout = ctrl.O?.layout || {} as ILayoutJson;
                layout.dOpt = layout.dOpt || {};

                oldClass?.substr(1, oldClass.length - 2).split(" ").forEach(old => {
                    if (old !== "") {
                        let isFound = false;
                        for (let compType in layoutBarComponentClassList) {
                            isFound = false;
                            if (!(compType === "All" || compType === ctrl.T)) {
                                continue;
                            }
                            const componentClassList = layoutBarComponentClassList[compType];
                            for (let layoutKey in componentClassList) {
                                let option = old;
                                let breakpoint = "xs";
                                let dOpt: Array<string> = [];
                                if (layoutKey == LayoutOption.column || layoutKey == LayoutOption.spacing || layoutKey == LayoutOption.display) {
                                    const classParts = old.split("-");
                                    if (classParts.length > 2) {
                                        breakpoint = classParts[1];
                                        option = `${classParts[0]}-${classParts[2]}`;
                                    }
                                    const result = componentClassList[layoutKey].find(opt => option.startsWith(opt));
                                    if (result !== undefined) {
                                        if (classParts[0] === LayoutOption.paddingAll || classParts[0] === LayoutOption.marginAll) {
                                            const spacingOptions = spacingOptionsPrefix.filter(sp => sp.startsWith(classParts[0][0]) && !sp.startsWith(LayoutOption.marginAll) && !sp.startsWith(LayoutOption.paddingAll));
                                            dOpt = layout.dOpt[breakpoint];
                                            if (dOpt === undefined) {
                                                dOpt = [];
                                            }
                                            for (let index = 0; index < spacingOptions.length; index++) {
                                                const element = spacingOptions[index];
                                                if (index === 0) {
                                                    option = `${element}${classParts[classParts.length - 1]}`;
                                                } else {
                                                    dOpt.push(`${layoutKey}:${element}${classParts[classParts.length - 1]}`);
                                                }
                                            }
                                            layout.dOpt[breakpoint] = dOpt;
                                        }

                                        isFound = true;
                                    }
                                } else {
                                    const index = componentClassList[layoutKey].findIndex(f => f == old);
                                    if (index > -1) {
                                        option = old;
                                        isFound = true;
                                    }
                                }

                                if (isFound) {
                                    prepareClass = true;
                                    dOpt = layout.dOpt[breakpoint];
                                    if (dOpt === undefined) {
                                        dOpt = [];
                                    }
                                    //if col-x exists don't add
                                    if (layoutKey === LayoutOption.column && dOpt.findIndex(opt => opt.startsWith(`${LayoutOption.column}:`)) > -1) {
                                        break;
                                    }

                                    dOpt.push(`${layoutKey}:${option}`);
                                    layout.dOpt[breakpoint] = dOpt;

                                    break;
                                }
                            }
                            if (isFound) {
                                break;
                            }
                        }

                        if (!isFound) {
                            propClass.push(old);
                        }
                    }
                });

                if (prepareClass) {
                    ctrl.O!.layout = layout;
                    ctrl.O.layout!.class = getOptionsClass(layout.dOpt);

                    notifies["classMigrator"] = { type: "warn", message: "The page migrated the new grid-flex structure. Please check the page layout before the save!" };
                }
                ctrl.P.class = { H: `'${propClass.join(" ")}'`, T: PType.class }
            }
        }

        return notifies;
    }

    static convertDesignTimeToStyleBP(designOptions: Array<string>) {
        const retVal: Record<string, string> = {}
        for (let i = 0; i < designOptions.length; i++) {
            let replacedValue = "";
            const [key, value] = designOptions[i].split(':');
            switch (key) {
                case 'a':
                    replacedValue = value.replace("align-", "");
                    replacedValue = replacedValue == "start" || replacedValue == "end" ? "flex-" + replacedValue : replacedValue;
                    retVal.alignItems = replacedValue;
                    break;
                case "j":
                    replacedValue = value.replace("justify-", "")
                    replacedValue = replacedValue == "start" || replacedValue == "end" ? "flex-" + replacedValue : replacedValue;
                    retVal.justifyContent = replacedValue;
                    break;
                case "c":
                    replacedValue = value.replace("col-", "").replace("d-", "");
                    if (replacedValue == "none") {
                        retVal.display = replacedValue;
                    } else {
                        retVal.col = replacedValue == "auto" ? "0" : replacedValue;
                    }
                    break;
                case "fd":
                    retVal.flexDirection = value.replace("flex-", "");
                    break;
                case "sp":
                    let [spKey, spValue] = value.split('-');
                    spValue = (Number(spValue) * 4) + "px"
                    switch (spKey) {
                        case "pl":
                            retVal.paddingLeft = spValue;
                            break;
                        case "pr":
                            retVal.paddingRight = spValue;
                            break;
                        case "pt":
                            retVal.paddingTop = spValue;
                            break;
                        case "pb":
                            retVal.paddingBottom = spValue;
                            break;
                        case "ml":
                            retVal.marginLeft = spValue;
                            break;
                        case "mr":
                            retVal.marginRight = spValue;
                            break;
                        case "mt":
                            retVal.marginTop = spValue;
                            break;
                        case "mb":
                            retVal.marginBottom = spValue;
                            break;
                        default:
                            break;
                    }
                    break;
                default:
                    break;
            }
        }
        return retVal;
    }
}

interface styleBPKeys {
    flexDirection?: string,
    alignItems?: string,
    justifyContent?: string,
    col?: string,
    paddingLeft?: string,
    paddingRight?: string,
    paddingTop?: string,
    paddingBottom?: string,
    marginLeft?: string,
    marginRight?: string,
    marginTop?: string,
    marginBottom?: string
}