export class CssOrderManager {
    private static cssPlaceholders = {
        plateau: { tagName: "quickPlateauCssPlaceholder", domEl: document.createElement("style") },
        style: { tagName: "quickStyleManagerPlaceholder", domEl: document.createElement("style") },
        custom: { tagName: "quickCustomCssPlaceholder", domEl: document.createElement("style") }
    };


    public static getPlaceHolderDom(name: keyof typeof CssOrderManager["cssPlaceholders"]) {
        return CssOrderManager.cssPlaceholders[name].domEl;
    }

    public static init() {
        Object.entries(CssOrderManager.cssPlaceholders).forEach(([key, css]) => {
            css.domEl.classList.add(css.tagName)
            document.head.appendChild(css.domEl);
        });
    }
}