import { ContextManager, IContextItem } from "../../../shrimp/context";
import { ScriptLang } from "../../../shrimp/interfaces/Scripting/scriptLang";
import { IEvaluator } from "../../../shrimp/interfaces/Scripting/IEvaluator";
import { Evaluator } from "./Evaluator";
import { PropDepTracker as PropDepTracker } from "./PropDepTracker";
import { ICompDepTarget, IContextDepTarget, IContextTargetInst, IStore } from "../../../shrimp/interfaces/quick/IStore";

export class PropCalcContext implements IContextItem {
    //property'ler typescript'e geerken, await ve then'in ya kapatlmas lazm, ya da propertyCalculationContext'in resurrect edilip tekrar hibernate'e alnmas lazm.(ikiside burada)
    static ContextName: string = "PropCalcContext";
    contextName: string = PropCalcContext.ContextName;

    private evaluator: IEvaluator = new Evaluator();
    private contextTarget?: IContextDepTarget;
    public context: ContextManager;

    constructor(context: ContextManager, contextTarget?: IContextDepTarget) {
        this.context = context;
        const propCalcContext = this.context.GetItemSingle<PropCalcContext>(PropCalcContext.ContextName);
        if (!propCalcContext) {
            this.context.RegisterItem(this);
        }
    }

    public watch(bindObject: IStore, field: string) {
        new PropDepTracker().createDepTracker(bindObject, field, this.propContextDepGetter, this.propContextDepTriggerer, this)
    }

    /**
     * Return prop context value
     * @param self 'this' will be lost when triggering from Object defined get method
     */
    public propContextDepGetter(self: PropCalcContext) {
        return self.contextTarget;
    }

    /**
     * Trigger the dependent target with new value
     * @param depTarget Dependency target 
     * @param dataItem Evaluator dataSource
     * @param self 'this' will be lost when triggering from Object defined get method
     * @param lang Evaluator language option
     */
    public propContextDepTriggerer({ depTarget, dataItem, self, lang }: { depTarget: IContextDepTarget , dataItem: object, self: PropCalcContext, lang?: ScriptLang }) {
        if (depTarget && (<ICompDepTarget>depTarget.instanceHolder.targetInstance).ref.component) {
            const evaluationResult = self.evaluator.Evaluate({ script: depTarget.depScriptValue, dataSource: dataItem, context: self.context, lang: lang });
            const targetComp = (<ICompDepTarget>depTarget.instanceHolder.targetInstance).ref.component;
            if (targetComp![depTarget.field] == evaluationResult) {
                return;
            }
            targetComp![depTarget.field] = evaluationResult;
        }
    }

    /**
     * Set prop context value with field and scriptValue
     * @param instanceHolder Target object instance
     * @param field Binded field
     * @param scriptValue Binded script value
     */
    public setContextTarget(instanceHolder: IContextTargetInst, field: string, scriptValue: string | object) {
        if(typeof scriptValue != "string" ) {
            return;
        }
        this.contextTarget = { instanceHolder, field, depScriptValue: scriptValue };
    }

    /**
     * Reset prop context
     */
    public clearPropTarget() {
        this.contextTarget = undefined;
    }

    public isTargetCompInstance(targetInstance: ICompDepTarget): targetInstance is ICompDepTarget {
        return targetInstance.ref !== undefined;
    }
}