import axios, { AxiosRequestConfig } from 'axios';
import { INetworkManager } from "../../../../common/qshell";
import { qjsonVersionHelper } from "../../../../common/shrimp/helpers/qjsonVersionHelper";
import { TypeHelper } from "../../../../common/shrimp/helpers/typeHelper";
import { IDictionary } from "../../../../common/shrimp/interfaces/IDictionary";
import { IContainerServicesWrapper, INetwork, IRequestOptions } from "../../../../common/shrimp/interfaces/quick/IContainerServices";
import { INetworkResponse } from "../../../../common/shrimp/interfaces/quick/INetworkResponse";
import { IWebSDKSettingsWrapper } from "../../../../common/shrimp/interfaces/quick/IWebSDK";
import { WebSDK } from "../WebSDK";
import { StoreManager } from "../implementation/StoreManager";
import RequestHandler, { headerTransportKey } from '../managers/network/request/RequestHandler';
import ErrorHandler from '../managers/network/response/ErrorHandler';
import SuccessHandler from '../managers/network/response/SuccessHandler';
import utils from "../managers/network/utils";
// import { Legacy } from "../../build/legacy";

export default class NetworkManagerImpl implements INetworkManager {
    contextName: string;
    private settingsWrapper: IWebSDKSettingsWrapper;
    private typeHelper = new TypeHelper();
    private storeManager: StoreManager;
    private servicesWrapper: IContainerServicesWrapper;

    constructor(contextName: string, settingsWrapper: IWebSDKSettingsWrapper, servicesWrapper: IContainerServicesWrapper, storeManager: StoreManager) {
        this.contextName = contextName;
        this.settingsWrapper = settingsWrapper;
        this.storeManager = storeManager
        this.servicesWrapper = servicesWrapper;

        axios.interceptors.request.use(request => RequestHandler(request, settingsWrapper, storeManager));
        axios.interceptors.response.use((response) => SuccessHandler(response, storeManager), (error) => ErrorHandler(error, storeManager));
    }

    public async checkPlateauIAM(qjson) {
        if (WebSDK.getPlateauIAM()) {
            let securePage = true;
            let unsecurePages = (<any>WebSDK.getPlateauIAM().settingsIAM?.iam)?.unsecurePages;
            if (unsecurePages) {
                for (let i = 0; i < unsecurePages.length; i++) {
                    if (unsecurePages[i] === qjson) {
                        securePage = false;
                    }
                }
            }

            if (!WebSDK.getPlateauIAM().isAuthenticated() && securePage) {
                // let queryString = Legacy.fixQueryString(qjson);
                // let queryString = qjson.split("?");
                // let queryParam: string = "";
                // if (queryString.length === 2) {        
                //     queryParam = queryString[1];
                // }
                // window.location.href = window.location.protocol + '//' + window.location.host + this.settingsWrapper.settings.rootqjson?.replace(".qjson", "") + "?" + queryParam;
                await WebSDK.getPlateauIAM().login();
                throw Error("Authentication: Must logon");
            }
        }

    }
    private getConfiguration(config?: AxiosRequestConfig, headers?: IDictionary<string> | undefined, withCredentials?: boolean, timeout?: number): AxiosRequestConfig | undefined {

        if (headers || this.settingsWrapper.settings.defaultServiceHeader) {
            config = config || {};
            //config.headers = headers;
            config.headers = {};
            config.headers[headerTransportKey] = JSON.stringify(headers) ?? JSON.stringify(this.settingsWrapper.settings.defaultServiceHeader);


        }

        let isDevData = this.storeManager.getItemFromLocalStorage?.({ key: 'isDev' })
        if (isDevData) {
            let parsedData = JSON.parse(isDevData)
            config = config || {};
            config.headers = config.headers || {}
            Object.keys(parsedData).forEach(key => { config!.headers[key] = parsedData[key] });
        }

        if (withCredentials) {
            config = config || {};
            config.withCredentials = withCredentials;
        }

        if (timeout) {
            config = config || {};
            config.timeout = timeout;
        }

        return config;
    }


    private manageOtherDomains(url: string, qjsonRetrive: boolean) {
        let pageVersions: Record<string, any> | undefined = this.settingsWrapper.settings.versions?.pageVersions;
        if (!utils.isDirectUrl(url)) {
            //FOR DIFFERENT DOMAINS
            if (this.servicesWrapper.services.getURL) {
                if (qjsonRetrive) {
                    url = this.servicesWrapper.services.getURL(url, "ui") || url;
                } else {
                    url = this.servicesWrapper.services.getURL(url, "service") || url;
                }
            }
            if(qjsonRetrive){
                url += this.urlDecider(url, pageVersions);
            }
        }
        return url;
    }

    private urlDecider(url: string, pageVersions: Record<string, any> | undefined) {
        let versionParameter = `${url.indexOf("?") == -1 ? "?" : "&"}ver=`;
        versionParameter += pageVersions ? qjsonVersionHelper.traversePageVersions(url, pageVersions) : Math.random();
        return versionParameter;
    }

    private getUrl(url: string, urlParams?: object, qjsonRetrive: boolean = false): string {
        const paramKeys = urlParams ? Object.keys(urlParams) : null;
        let retVal = "";
        let prefix = "";
        if (urlParams && paramKeys && paramKeys.length > 0) {
            prefix = url.indexOf("?") < 0 ? "?" : (url.endsWith("&") ? "" : "&");
            retVal = paramKeys.map(function (prop) {
                return [prop, urlParams[prop]].map(encodeURIComponent).join("=");
            }).join("&");
        }
        let finalUrl = url + prefix + retVal;
        if (qjsonRetrive && this.settingsWrapper.settings.qjsonURLPrefix && !utils.isDirectUrl(url)) { // her model (flow, qjson, ...) qjsonURLPrefix'e bakyor
            url = url.startsWith("/") ? url.substring(1, url.length) : url;
            finalUrl = this.settingsWrapper.settings.qjsonURLPrefix + "/" + url;
        }
        else if (!qjsonRetrive && this.settingsWrapper.settings.serviceURL && !utils.isDirectUrl(url)) {
            url = url.startsWith("/") ? url.substring(1, url.length) : url;
            finalUrl = this.settingsWrapper.settings.serviceURL + "/" + url + prefix + retVal;
        }
        else if (this.settingsWrapper.settings.useLocalProxyOnClient && this.settingsWrapper.settings.settingsProxyYaml) {
            const localProxy = this.settingsWrapper.settings.settingsProxyYaml["localProxy"];
            let foundedUrlKey = Object.keys(localProxy).find(item => url.includes(item));
            if (foundedUrlKey && localProxy[foundedUrlKey]) {
                let checkPath = localProxy[foundedUrlKey].startsWith('http');
                finalUrl = checkPath ? localProxy[foundedUrlKey] + url.replace(foundedUrlKey, '') + prefix + retVal : finalUrl;
            }
        }
        finalUrl = this.manageOtherDomains(finalUrl, qjsonRetrive);
        return finalUrl;
    }

    private openLoading(options?: IRequestOptions) {
        if (!options?.disableLoading) {
            let loadingCounter: number = this.storeManager.get("loadingCounter") || 0;
            this.storeManager.set("loadingCounter", loadingCounter + 1);
        }
    }

    private closeLoading(options?: IRequestOptions) {
        if (!options?.disableLoading) {
            let self = this;
            setTimeout(() => {
                let loadingCounter: number = self.storeManager.get("loadingCounter") || 0;
                if (--loadingCounter <= 0) { loadingCounter = 0 }
                self.storeManager.set("loadingCounter", loadingCounter);
            }, 300);
        }
    }




    async axiosCommon(type: keyof INetwork, url: string, body?: string | object | BinaryType | FormData, axiosConfig?: AxiosRequestConfig, options?: IRequestOptions) {
        try {
            this.openLoading(options);
            let axiosResponse;
            switch (type) {
                case "get":
                    axiosResponse = await axios.get(url, axiosConfig);
                    break;
                case "post":
                    axiosResponse = await axios.post(url, body, axiosConfig);
                    break;
                case "patch":
                    axiosResponse = await axios.patch(url, body, axiosConfig);
                    break;
                case "put":
                    axiosResponse = await axios.put(url, body, axiosConfig);
                    break;
                case "delete":
                    axiosResponse = await axios.delete(url, axiosConfig);
                    break;
                default:
                    break;
            }
            this.closeLoading(options);
            return axiosResponse;
        } catch (ex) {
            this.closeLoading(options);
            throw ex;
        }
    }

    getQjson<RetType extends string | object>({ url, urlParams, headers, options }: { url: string, urlParams?: object, headers?: IDictionary<string> | undefined, options: { disableLoading: boolean } }): Promise<INetworkResponse<RetType>> {
        return this.axiosCommon("get", this.getUrl(url, urlParams, true), undefined, this.getConfiguration(undefined, headers, undefined, undefined), options);
    }

    get<RetType extends string | object>({ url, urlParams, headers, config, options }: { url: string, urlParams?: object, headers?: IDictionary<string> | undefined, config?: AxiosRequestConfig, options?: IRequestOptions }): Promise<INetworkResponse<RetType>> {
        return this.axiosCommon("get", this.getUrl(url, urlParams), undefined, this.getConfiguration(config, headers, options?.withCredentials, options?.timeout), options);
    }
    post<RetType extends string | object>({ url, urlParams, body, headers, config, options }: { url: string, urlParams?: object, body?: string | object | BinaryType, headers?: IDictionary<string> | undefined; config?: AxiosRequestConfig, options?: IRequestOptions }): Promise<INetworkResponse<RetType>> {
        return this.axiosCommon("post", this.getUrl(url, urlParams), body, this.getConfiguration(config, headers, options?.withCredentials, options?.timeout), options);
    }
    postHeader<RetType extends string | object>({ url, urlParams, body, headers, config }: { url: string, urlParams?: object, body?: string | object | BinaryType, headers?: IDictionary<string> | undefined; config?: AxiosRequestConfig }): Promise<INetworkResponse<RetType>> {
        return this.axiosCommon("post", this.getUrl(url, urlParams), body, { headers }, undefined);
    }
    patch<RetType extends string | object>({ url, urlParams, body, headers, options }: { url: string, urlParams?: object, body?: string | object | BinaryType, headers?: IDictionary<string> | undefined, options?: IRequestOptions }): Promise<INetworkResponse<RetType>> {
        return this.axiosCommon("patch", this.getUrl(url, urlParams), body, this.getConfiguration(undefined, headers, options?.withCredentials, options?.timeout), options);
    }
    multipart<RetType extends string | object>({ url, urlParams, body, headers, options }: { url: string, urlParams?: object, body?: string | object | Blob, headers?: IDictionary<string> | undefined, options?: IRequestOptions }): Promise<INetworkResponse<RetType>> {
        let formData = new FormData();
        if (body && this.typeHelper.isObject(body)) {
            Object.keys(body).map(item => {
                switch(typeof body[item]){
                    case "object":
                        body[item] instanceof File ? formData.append(item, body[item]) : formData.append(item, JSON.stringify(body[item]));
                        break;
                    default:
                        formData.append(item, body[item]);
                        break;
                }
            })
        } else {
            formData.append("data", <string | File>body);
        }
        let multiFormHeader = { 'Content-Type': 'multipart/form-data' };
        headers = { ...headers, ...multiFormHeader };
        let axiosConfig = this.getConfiguration(undefined, headers, options?.withCredentials, options?.timeout);
        return this.axiosCommon("post", this.getUrl(url, urlParams), formData, axiosConfig, options);
    }

    put<RetType extends string | object>({ url, urlParams, body, headers, options }: { url: string, urlParams?: object, body?: string | object; headers?: IDictionary<string> | undefined, options?: IRequestOptions }): Promise<INetworkResponse<RetType>> {
        return this.axiosCommon("put", this.getUrl(url, urlParams), body, this.getConfiguration(undefined, headers, options?.withCredentials, options?.timeout), options);
    }
    delete<RetType extends string | object>({ url, urlParams, headers, body, options }: { url: string, urlParams?: object, headers?: IDictionary<string> | undefined, body?: string | object, options?: IRequestOptions }): Promise<INetworkResponse<RetType>> {
        const axiosConfig = this.getConfiguration(undefined, headers, options?.withCredentials, options?.timeout);
        if (axiosConfig) {
            axiosConfig.data = body;
        }
        return this.axiosCommon("delete", this.getUrl(url, urlParams), undefined, axiosConfig, options);
    }
}
