import { IDomElement } from "../../../../common/shrimp/interfaces/RenderingInterfaces/IDomElement";
import Vue from 'vue';
import Vuetify from 'vuetify';
import root from "./root.vue";

import(<any>"vuetify/dist/vuetify.min.css");


export class VueInitter {
    
    public static postProcess(renderer) {
    }
    
    public static GenerateRootInstance({ mountPoint }: { mountPoint: IDomElement }): Vue {
        const rootComponent: any = root;
        Vue.use(Vuetify);
        Vue.config.silent = true

        const instance = new Vue({
            el: mountPoint,
            render: (h) => h(<any>root),
            vuetify: new Vuetify({
                theme: {
                    themes: {
                        light: {
                            primary: '#3f51b5',
                            secondary: '#b0bec5',
                            accent: '#8c9eff',
                            error: '#b81c1c',
                        },
                    }
                },
                icons: {
                    iconfont: 'mdi', // default - only for display purposes
                },
            }),
            components: { rootComponent }
        });
        return instance;
    }
}
