import { IModuleManager } from "../../../../common/qshell";

interface IVuetifyMicroUI {
    ml: Record<string, {
        componentNames: Record<string, {
            file: string;
            props: Record<string, { type: string }>;
            events: Array<{ name: string, desc: string, params?: Array<{ name: string, type: string, desc: string }> }>;
            loaded?: boolean;
        }>,
        modulePath: string;
    }>;
}


import { IExternalQJsonRetriever } from "@stechquick/algae/lib/quick/IExternalQJsonRetriever";
import Vue from "vue";
import { JSLoader } from "../../../../common/shrimp/helpers/JSLoader";
import { NamedComponentHelper } from "../../../../common/shrimp/helpers/namedComponentHelper";
import { ICompJson } from "../../../../common/shrimp/interfaces/ComponentInterfaces/ICompJson";
import { IVersion } from "../../../../common/shrimp/interfaces/ComponentInterfaces/IVersion";
import { ComponentProvider } from "../components/providers/ComponentProvider";
import { INamedCompModel } from "@stechquick/algae/lib/quick/INamedCompModel";
export class ModuleManager implements IModuleManager {
    private externalQJsonRetriever?: IExternalQJsonRetriever;
    private namedCompsVersions?: IVersion;
    public SetExternalQJsonRetriever(externalQJsonRetriever: IExternalQJsonRetriever) {
        this.externalQJsonRetriever = externalQJsonRetriever;
    }
    constructor(namedCompsVersions?: IVersion) {
        this.namedCompsVersions = namedCompsVersions;
    }

    private getNamedCompsVersions(compName: string) {
        return this.namedCompsVersions?.namedComponents?.[compName];
    }


    private async checkNamedComponent(compName: string, compPath: string) {
        const compModel = this.externalQJsonRetriever ? await this.externalQJsonRetriever.getNamedCompModel(compPath) : undefined;

        await this.namedCompInit(compName, compPath, compModel);
    }

    public async namedCompInit(compName: string, compPath: string, compModel?: INamedCompModel){
        const compVersion = this.getNamedCompsVersions(compName);

        const module = await new NamedComponentHelper().loadNamedComponent(compName, compPath, { compModel }, compVersion);
        const comp = module.default;
        Vue.component(comp.name, comp);
    }

    public CheckComponent(compName: string, compPath: ICompJson["CP"]): Promise<any> | undefined {
        //check if component needs lazy load and return true if it does. And call success when loaded.
        //return false if component is allready loaded.
        if (compPath) {
            return this.checkNamedComponent(compName, compPath);
        }

        const checkComponentRes = ComponentProvider.checkComponents(compName);
        if (checkComponentRes instanceof Promise) {
            return checkComponentRes;
        }

        if (checkComponentRes) {
            return undefined;
        } else {
            var path = "";
            const vuetify_microUI: IVuetifyMicroUI = (<any>window).vuetify_microUI;
            let targetDomainComp: IVuetifyMicroUI["ml"]["x"]["componentNames"]["x"] | undefined;
            Object.keys(vuetify_microUI.ml).find(key => {
                if (vuetify_microUI.ml[key] != undefined) {
                    const targetModule = vuetify_microUI.ml[key];
                    const domainComp = targetModule.componentNames[compName];
                    if (!domainComp) { return undefined; }

                    targetDomainComp = domainComp;
                    var modulePath = targetModule.modulePath
                    let suffix = compName + ".js";
                    let pathSplitIndex = modulePath.indexOf("compVNames");
                    if (pathSplitIndex == -1) {
                        pathSplitIndex = modulePath.lastIndexOf("/") + 1;
                        suffix = targetDomainComp.file;
                    }
                    path = modulePath.substring(0, pathSplitIndex) + suffix;
                    return true;
                }
            })

            if (!targetDomainComp || targetDomainComp.loaded) { return undefined; }

            return new Promise((res, rej) => {
                new JSLoader(path, function () {
                    const target = Vue.component(compName);
                    res(target['options']);
                    targetDomainComp!.loaded = true;
                }, function (evt) {
                    rej(evt);
                }, 1, true, new Date().getTime()).LoadJS();
            });
        }

    }
}