import { IContextItem } from "../../../shrimp/context";
import { IDictionary } from "../../../shrimp/interfaces/IDictionary";
import { IDomElement } from "../../../shrimp/interfaces/RenderingInterfaces/IDomElement";

export class ScopeContext implements IContextItem {
    static ContextName: string = "ScopeContext";
    contextName: string = ScopeContext.ContextName;

    private props?: IDictionary<any>;
    private events?: IDictionary<Function>;
    children?: Record<string, Array<unknown>>;


    constructor({ props, events, children }: { props?: IDictionary<any>, events?: IDictionary<Function>, children?: Record<string, Array<unknown>> }) {
        this.events = events;
        this.props = props;
        this.children = children;
    }

    public GetProps(){
        return this.props;
    }

    public GetEvents(){
        return this.events;
    }

    getProp(name: string) {
        return this.props && this.props[name];
    }

    setProp(name: string, value: any) {
        if (this.props) {
            this.props[name] = value;
        }
    }

    triggerEvent(name: string, parameters?: Array<any>) {
        return this.events && this.events[name](parameters);
    }
}