import { IUserContext } from "../../../shrimp/interfaces/quick/IUserContext";
import { IShell } from "../../../shrimp/interfaces/quick/IShell";
import { ILanguageExtensions } from "../interfaces/construction/ILanguageExtensions";
import { IDictionary } from "../../../shrimp/interfaces/IDictionary";
import { fixShell } from "../../../shrimp/helpers/shellHelper";

export class ShellManager {
    private shell: IShell = {};
    private osGlobals: IDictionary<any>;
    private langImpl?: ILanguageExtensions;
    constructor(langImpl?: ILanguageExtensions) {
        this.langImpl = langImpl;

        this.osGlobals = this.langImpl?.createOsGlobalObjects() || {};
        this.shell = this.osGlobals.shell = this.osGlobals.shell || {};
        fixShell(this.shell);
    }

    public get UserContext() {
        return this.shell.getUserContext?.();
    }
    public set UserContext(user: IUserContext | undefined) {
        if (!user) {
            return;
        }
        this.shell.setUserContext?.(user);
    }

    public PollyFill() {
        return this.osGlobals;
    }


}