




import Vue from "vue";
export default Vue.extend({
  name: "GeneralWrapper",
  props: {
    __domElements: {
      type: Object,
      default: null
    },
    width: {
      type: String
    },
    height: {
      type: String
    },
    customStyle: {
      type: Boolean
    },
    customClass: {
      type: String
    },
    vElemnt: {
      type: Object
    },
    createCompo: {
      type: Object
    }
  },
  computed: {
    computedClass() {
      return this.customClass;
    },
    computedStyle() {
      if (this.customStyle) {
        return `width:${this.width}; height:${this.height}; position: relative; margin: auto`
      }
      return undefined;
    }
  },
  mounted() {
    const self = <any>this;
    let holderDiv = <HTMLElement>self.$refs["holder"];
    holderDiv.appendChild(self.__domElements);
  }
});
