import { InitDory } from "../../../dory";
import { ContextManager } from "../../../shrimp/context";
import { IComponentInstanceTracker } from "../../../shrimp/interfaces/ComponentInterfaces/IComponentInstanceTracker";
import { IQJSon } from "../../../shrimp/interfaces/ComponentInterfaces/IQJson";
import { ExternalLocalizationFuncContextName, IExternalLocalizationFunc } from "../../../shrimp/interfaces/quick/IExternalLocalizationFunc";
import { IRoleBaseAuthorizationFunc, RoleBaseAuthorizationFuncContextName } from "../../../shrimp/interfaces/quick/IRoleBaseAuthorizationFunc";
import { ComponentHasPropDelegate, CreateComponentDelegate, RetrieveCjsonDelegate, UpdateContainerViewDelegate } from "../../../shrimp/interfaces/quick/IShellConfiguration";
import { IUrlOptions } from "../../../shrimp/interfaces/quick/IUrl";
import { IUXManager, UXManagerContextName } from "../../../shrimp/interfaces/quick/IUXManager";
import { IDory } from "../../../shrimp/interfaces/RenderingInterfaces/IDory";
import { IDoryJr } from "../../../shrimp/interfaces/RenderingInterfaces/IDoryJr";
import { IRendererOperatorCollection } from "../../../shrimp/interfaces/RenderingInterfaces/Operators/IRendererOperatorCollection";
import { CompInstanceTrackerContextName } from "../interfaces/construction/CompInstanceTracker";
import { ComponentManagerContextName, IComponentManager } from "../interfaces/construction/IComponentManager";
import { CookieManagerContextName, ICookieManager } from "../interfaces/construction/ICookieManager";
import { CryptographyContextName, ICryptographyManager } from "../interfaces/construction/ICryptographyManager";
import { GlobalMethodsContextName, IGlobalMethods } from "../interfaces/construction/IGlobalMethods";
import { HostTriggererContextName, IHostTriggerer } from "../interfaces/construction/IHostTriggerer";
import { IIntegrationsManager, IntegrationsManagerContextName } from "../interfaces/construction/IIntegrationsManager";
import { ILanguageExtensions, LanguageExtensionsContextName } from "../interfaces/construction/ILanguageExtensions";
import { INavigationManager, NavigationManagerContextName } from "../interfaces/construction/INavigationManager";
import { INetworkManager, NetworkManagerContextName } from "../interfaces/construction/INetworkManager";
import { IPlatformManager, PlatformManagerContextName } from "../interfaces/construction/IPlatformManager";
import { IProcessManager, ProcessManagerContextName } from "../interfaces/construction/IProcessManager";
// import { IQJsonRetriever, QJsonRetrieverContextName } from "../interfaces/construction/IQJsonRetriever";
import { IRegionalManager, RegionalManagerContextName } from "../interfaces/construction/IRegionalManager";
import { IStoreManager, StoreManagerContextName } from "../interfaces/construction/IStoreManager";
import { ShellManager } from "../managers/shellManager";
import { QJsonPool } from "./QJsonHelpers/QJsonPool";
import { Renderer } from "./Renderer";

export class DoryHelper {
  Dory: IDory;
  store: any = null;
  cookie: any = null;
  parentInstance: any;
  isEditor: boolean = false;
  private doryJr?: IDoryJr;
  contextManager: ContextManager;
  shellManager: ShellManager;

  constructor({ doryInstName, context, renderer, operatorCollection, }: { doryInstName?: string; context: ContextManager; renderer: Renderer; operatorCollection: IRendererOperatorCollection; }) {
    this.contextManager = context;
    const langImpl = <ILanguageExtensions | undefined>(this.contextManager.GetItemSingle<ILanguageExtensions>(LanguageExtensionsContextName));
    this.shellManager = new ShellManager(langImpl);
    this.Dory = this.createDoryInstance(operatorCollection, renderer, doryInstName);
  }
  private createDoryInstance(operatorCollection: IRendererOperatorCollection, renderer: Renderer, doryInstName?: string,) {
    const self = this;
    let globalMethods: IGlobalMethods = this.contextManager.GetItemSingle<IGlobalMethods>(GlobalMethodsContextName)!;
    let componentManager: IComponentManager = this.contextManager.GetItemSingle<IComponentManager>(ComponentManagerContextName)!;
    let navigationManager: INavigationManager = this.contextManager.GetItemSingle<INavigationManager>(NavigationManagerContextName)!;
    let integrationsManager: IIntegrationsManager = this.contextManager.GetItemSingle<IIntegrationsManager>(IntegrationsManagerContextName)!;
    // let qjsonRetriever: IQJsonRetriever | undefined = this.contextManager.GetItemSingle<IQJsonRetriever>(QJsonRetrieverContextName);
    let compInstTracker: IComponentInstanceTracker = this.contextManager.GetItemSingle<IComponentInstanceTracker>(CompInstanceTrackerContextName)!;
    let externalLocalizationFunc: IExternalLocalizationFunc = this.contextManager.GetItemSingle<IExternalLocalizationFunc>(ExternalLocalizationFuncContextName)!;
    let roleBaseAuthFunc: IRoleBaseAuthorizationFunc = this.contextManager.GetItemSingle<IRoleBaseAuthorizationFunc>(RoleBaseAuthorizationFuncContextName)!;
    let storeManager: IStoreManager | undefined = this.contextManager.GetItemSingle<IStoreManager>(StoreManagerContextName);
    let cookieManager: ICookieManager | undefined = this.contextManager.GetItemSingle<ICookieManager>(CookieManagerContextName);
    let uxManager: IUXManager = this.contextManager.GetItemSingle<IUXManager>(UXManagerContextName)!;
    let regionalManager: IRegionalManager = this.contextManager.GetItemSingle<IRegionalManager>(RegionalManagerContextName)!;
    let processManager: IProcessManager | undefined = this.contextManager.GetItemSingle<IProcessManager>(ProcessManagerContextName);
    let networkManager: INetworkManager = self.contextManager.GetItemSingle<INetworkManager>(NetworkManagerContextName)!;
    let cryptoManager: ICryptographyManager = self.contextManager.GetItemSingle<ICryptographyManager>(CryptographyContextName)!;
    const hostTriggererImpl = this.contextManager.GetItemSingle<IHostTriggerer>(HostTriggererContextName)!;
    let platformManager = this.contextManager.GetItemSingle<IPlatformManager>(PlatformManagerContextName);

    const createComponent: CreateComponentDelegate = (options) => {
      let retVal = componentManager.createComponent(options);
      retVal.lockedFields = {};
      retVal.lockedFields["visible"] = this.initRoleBaseAuthFunc(options);
      return retVal;
    };

    const componentHasProp: ComponentHasPropDelegate = ({ componentName, compInstance, propName }) => {
      return componentManager.componentHasProp(componentName, compInstance, propName);
    };

    const createContainerView = (compUID: string) => {
      const retVal = componentManager.createContainerView?.(compUID);
      return retVal;
    };

    const updateContainerView: UpdateContainerViewDelegate = (options) => {
      componentManager.updateContainerView?.(options);
    };

    const dory = InitDory({ doryInstName });

    const retrieveCjson: RetrieveCjsonDelegate = async ({ qjsonPath, doryJr }) => {
    const qjsonPool = self.contextManager.GetItemSingle<QJsonPool>(QJsonPool.contextName)!;
      let qjson: IQJSon | undefined = qjsonPool.getCJson(qjsonPath);
      if (qjson) {
        return { qjson};
      }

      const collectResult = await qjsonPool.retrieveAndFillQjsonAsync({ qjson: { type: "path", path: qjsonPath }, dory, doryJr });
      return collectResult;
    };
    dory.SetOperatorCollection(operatorCollection);
    dory.SetShellGlobalConfiguration({
      renderer,
      coreComponentOverrider: componentManager.coreComponentOverrider, componentInstanceTracker: compInstTracker, historyBasedConf: { componentHasProp, createComponent, isEditor: this.isEditor, retrieveCjson, createContainerView, updateContainerView, },
      go: async function (...args) {
        if (args == undefined) {
          return;
        }
        let goArgs = { ...args };
        let navigationParams;
        if (args.length == 1) {
          navigationParams = { url: goArgs[0] };
        } else {
          navigationParams = { url: goArgs };
        }
        navigationManager?.go && args.length > 1 ? navigationManager.go(navigationParams) : renderer?.Render({ pjsonPath: goArgs[0] });
      },
      go2: function (qjsonPath, options) {
        if (dory.platformType !== "Web") {
          return renderer?.Render({ pjsonPath: qjsonPath, options: options });
        }
        if (!options?.newTab) {
          return renderer?.Render({ pjsonPath: qjsonPath, options });
        }
        navigationManager.go2!(qjsonPath, options, () =>
          renderer?.Render({ pjsonPath: qjsonPath })
        );
      },
      integrationsEventTrigger: function (name: string, params: any[]) {
        if (dory.platformType !== "Web") {
          throw new Error("Integration Custom Event trigger not supported in UI Editor")
        }
        integrationsManager.customIntegrationEventTrigger(name, params);
      },
      hostTrigger: function (functionName: string, params: Record<string, any>, pageNameArray: Array<string>) {
        return hostTriggererImpl?.hostTrigger?.(functionName, params, pageNameArray);
      },
      containerTrigger: function (functionName: string, params: Record<string, any>, pageNameArray: Array<string>) {
        return hostTriggererImpl.containerTrigger(functionName, params, pageNameArray);
      },
      processManager: processManager,
      triggerPreviousPage: navigationManager?.backOverride,
      storeAccess: storeManager,
      cookieAccess: cookieManager,
      lridHelper: externalLocalizationFunc ? function (label: string) {
        var returnVal: string;
        try {
          returnVal = externalLocalizationFunc(label);
        } catch (error) {
          returnVal = "";
        }
        return returnVal;
      } : undefined,
      roleBaseViewHelper: function (key: string) {
        try {
          return roleBaseAuthFunc(key);
        } catch (error) {
          return true;
        }
      },
      resetTextField: function (component) {
        component.$refs.input.value = null;
      },
      platform: platformManager,
      setTheme: function (themeName) {
        return uxManager.setTheme(themeName);
      },
      getThemeList: function () {
        const themeList = uxManager.getThemeList();
        if (!themeList) {
          return [];
        }
        //if (!(themeList instanceof Array)) { return themeList;}
        else {
          return themeList;
        }
      },
      addTheme: function (themeName) {
        return uxManager.addTheme(themeName);
      },
      removeTheme: function (themeName) {
        return uxManager.removeTheme(themeName);
      },
      getRegionList: function () {
        const regionList = regionalManager.getRegionList();
        if (!regionList) {
          return [];
        } else {
          return regionList;
        }
      },
      getCurrentRegion: function () {
        const region = regionalManager.getRegion()!;
        // return regionalManager.getRegion;
        // const region = regionalManager.getRegion;
        if (!region) {
          return "";
        } else {
          return region;
        }
      },
      getFormattingDefinitionByCurrentRegion:
        regionalManager.getFormattingDefinitionByCurrentRegion.bind(regionalManager),

      redirect({ url, options }: { url: string; options?: IUrlOptions }) {
        navigationManager.redirect({ url, options });
      },

      setRegion: function (regionName) {
        return regionalManager.setRegion(regionName);
      },

      // getThemeList: function () {
      //     const themeList = uxManager.getThemeList();
      //     if (!themeList) { return undefined; }

      //     if (!(themeList instanceof Array)) { return themeList;}

      //     return { default: themeList };
      // },

      network: networkManager,
      shellGlobalMethods: globalMethods,
      osGlobalObjects: this.shellManager.PollyFill(),
      compPropsAutoLrid: true,
      formatConfiguration: {
        dateFormat: function () {
          return "YYYY/MM/DD";
        },
        moneyFormat: function () {
          return "tr-TR";
        },
      },
      redirectTo({ url }: { url: string }) {
        if (navigationManager.redirectFunc)
          navigationManager.redirectFunc({ url });
      },
      getUrl(options) {
        if (navigationManager.getUrl) {
          return navigationManager.getUrl(options);
        }
        return;
      },
      performance: globalMethods.performance,
      dynamicCompHandler: !this.isEditor ? globalMethods.dynamicCompHandler : undefined,
      deleteComponent: !this.isEditor ? globalMethods.deleteComponent : undefined,
      exportToXlsxHandler: globalMethods.exportToXlsxHandler,
      xlsxToJson: globalMethods.xlsxToJson,
      setComponentClass: globalMethods.setComponentClass,
      setComponentsProperty: globalMethods.setComponentsProperty,
      setVisibility: componentManager.setVisibility,
      setDisable: componentManager.setDisable,
      getComponentValue: componentManager.getComponentValue,
      setComponentValue: componentManager.setComponentValue,
      getComponentStyle: componentManager.getComponentStyle,
      setComponentStyle: componentManager.setComponentStyle,
      onComponentDestroy: componentManager.onComponentDestroy,
      createComponentQValue: componentManager.createComponentQValue,
      onLRChange: regionalManager.onLRChange,
      setDarkTheme: uxManager.setDarkTheme,
      clearPageClose: globalMethods.clearPageClose,
      setPageClose: globalMethods.setPageClose,
      scrollIntoView: globalMethods.scrollIntoView,
      YOffset: globalMethods.YOffset,
      XOffset: globalMethods.XOffset,
      toTop: globalMethods.toTop,
      getEnvironmentVariable: globalMethods.getEnvironmentVariable,
      getPageTitle: globalMethods.getPageTitle,
      setPageTitle: globalMethods.setPageTitle,
      copyToClipboard: globalMethods.copyToClipboard,
      getCurrentPosition: globalMethods.getCurrentPosition,
      initializePositioning: globalMethods.initializePositioning,
      cleanupPositioning: globalMethods.cleanupPositioning,
      getFavicon: globalMethods.getFavicon,
      downloadHandler: globalMethods.downloadHandler,
      shareData: globalMethods.shareData,
      getComponent: globalMethods.getComponent,
      getNetworkConnection: globalMethods.getNetworkConnection,
      hash:cryptoManager.hash,
      encrypt:cryptoManager.encrypt,
      decrypt:cryptoManager.decrypt,
      stringToBase64: cryptoManager.stringToBase64,
      base64ToString: cryptoManager.base64ToString
    });
    dory.SetTSComponentWrapper(componentManager.tsComponentWrapper?.());
    return dory;
  }

  //TODO Tipini yaz nulable
  private initRoleBaseAuthFunc(options): boolean {
    let roleBaseAuthFunc: IRoleBaseAuthorizationFunc | undefined =
    this.contextManager.GetItemSingle(RoleBaseAuthorizationFuncContextName);
    if (options.props && options.props["useRoleBaseAuthorization"] && roleBaseAuthFunc) {
      return true;
    }
    return false;
  }

  private delayTillPostDisplay(cb: () => void) {
    (this.doryJr || this.Dory).delayTillPostDisplay(cb);
  }
}
