import { Stack, StackReason, StackMessage } from "./Stack";

/**
 * * Evaluation scope Context Object which lasts for whole evaluation.
 * * Context object must be destroyed when evaluation completes. It must not leak to another scope of evaluation//56041
 * @param {*} dataSource
 * Data Source object if any exists.
 * value="{}"
 * @param {*} response
 * Response object
 * @param {*} symDict
 * type="SymbolDict"
 * value="new SymbolDict()"
 * Symbol dictionary for current scope
 * @param {*} global
 * @param {*} controlDict
 * @param {*} extensions
 * @param {*} context
 * Context of Dory instance
 * type=IContext
 */
class EvalContext {
    public dataSource;
    public response;
    public symDict;
    public global;
    public controlDict;
    public extensions;
    public context;
    private forestStack: Array<Stack>;

    constructor(dataSource, response, symDict, globals, controls, extensions, context) {
        this.dataSource = dataSource;
        this.response = response;
        this.symDict = symDict;
        this.global = globals;
        this.controlDict = controls || {};
        this.extensions = extensions;
        this.context = context;
        this.forestStack = [];
    }

    public addStack(stack: Stack) {
        this.forestStack.push(stack);
        stack.setContextIndex(this.forestStack.length - 1);
    }

    public popStack(): Stack | undefined {
        const lastStack = this.forestStack.pop();
        lastStack?.clearContextIndex();
        return lastStack;
    }

    public getStackWithReason(reason: StackReason){
        for (var i = this.forestStack.length - 1; i >= 0; i--){
            if (this.forestStack[i].Reason == reason){
                return this.forestStack[i];
            }
        }
        return null;
    }
    public messageStacksAbove(stack: Stack, message: StackMessage){
        for(let index = stack.ContextIndex!; index < this.forestStack.length; index++){
            this.forestStack[index].setMessage(message);
        }
    }
}

export { EvalContext };