import { ContextManager } from "../shrimp/context";
import { GetObjectStore } from "../shrimp/helpers/objectStore";
import { IDory } from "../shrimp/interfaces/RenderingInterfaces/IDory";

const interDoryContext = new ContextManager();

function InitDory({ doryInstName = "dory" }: { doryInstName?: string }): IDory {
    let doryInst: IDory = GetObjectStore().retrieveObject(doryInstName);

    if (!doryInst) {
        const doryImp = require("./src/Dory");
        doryInst = new doryImp.Dory(interDoryContext);
        GetObjectStore().storeObject(doryInstName, doryInst);
    }

    return doryInst;
}
export { InitDory };

