  public setComponentClass(
    compCollection: IComponentCollection,
    classes: Array<string>
  ) {
    if (!compCollection.component) {
      return;
    }
    classes.forEach((classKey) => {
      compCollection.component!.classes =
        compCollection.component!.classes || {};
      compCollection.component!.classes[classKey] = true;
    });
    compCollection.component.$mount();
  }

  public setComponentsProperty(
    componentCollection: IComponentCollection,
    propertyName: string,
    propertyValue: any
  ) {
    const setPropertyValueDeep = (coll) => {
      Object.keys(coll).map((item) => {
        coll![item].forEach((comp) => {
          if (comp.component) {
            comp.component[propertyName] = propertyValue;
          }
          if (comp?.children) {
            setPropertyValueDeep(comp.children);
          }
        });
      });
    };
    
    if (componentCollection?.component) {
      componentCollection.component[propertyName] = propertyValue;
    }     
    if(componentCollection?.children){
      setPropertyValueDeep(componentCollection.children);
    }
  }

  clearPageClose() {
    GlobalMethodsImpl.onCloseIsActive = false;
  }

  setPageClose() {
    GlobalMethodsImpl.onCloseIsActive = true;
    window.addEventListener("beforeunload", (event) => {
      if (GlobalMethodsImpl.onCloseIsActive) {
        GlobalMethodsImpl.renderer?.Trigger("onPageClose");
        // set a truthy value to property returnValue
        event.returnValue = "page closed";
      }
    });
  }
  listenNetworkConnection() {
    //https://developer.mozilla.org/en-US/docs/Web/API/Navigator/onLine
    window.addEventListener("online", (event) => {
      Logger.Log({ logMessage: "You are now connected to the network." });
      GlobalMethodsImpl.renderer?.settingModels?.TriggerPipeline("onNetworkStatusChange", { isNetworkConnected: true });

    });


    window.addEventListener("offline", (event) => {
      Logger.Log({ logMessage: "The network connection has been lost." });
      GlobalMethodsImpl.renderer?.settingModels?.TriggerPipeline("onNetworkStatusChange", { isNetworkConnected: false });

    });
  }

  getNetworkConnection(): boolean {
    return window.navigator.onLine;
  }


  scrollIntoView(component: IComponent) {
    (<HTMLElement>component.$el)?.scrollIntoView();
  }
  YOffset() {
    //https://developer.mozilla.org/docs/Web/API/Window/scrollY
    return window.scrollY;
  }
  XOffset() {
    //https://developer.mozilla.org/docs/Web/API/Window/scrollX
    return window.scrollX;
  }

  toTop(options?: IScreenToTopOptions) {
    //https://developer.mozilla.org/docs/Web/API/Element/scrollIntoView

    if (options?.elementId) {
      let htmlElement = document.getElementById(options.elementId);
      if (htmlElement) {
        htmlElement.scrollIntoView();
        return;
      }
    }

    const errorContainerDiv = document.getElementsByClassName("errorContainerTarget")?.[0];
    if (errorContainerDiv) {
      errorContainerDiv.scrollIntoView();
      return;
    }

    const shellContainerDiv = document.getElementsByClassName("shellContainerTarget")?.[0];
    if (shellContainerDiv) {
      shellContainerDiv.scrollIntoView();
      return;
    }
  }

