export class CompUIDHelper {
    /**
     * Gets First compID from compUID.   
     * @example "CustComp.MainComp.topBtn" -> {root: "CustComp", sub: "MainComp.topBtn"}
     * @example "topBtn" -> {root: "topBtn"}
     */
    public static GetRootCompId(compUID: string) {
        const dotIndex = compUID.indexOf(".");
        if (dotIndex == -1) { return { rootCompId: compUID }; }

        const rootCompId = compUID.substring(0, dotIndex);
        const subCompUID = compUID.substring(dotIndex + 1, compUID.length);
        return { rootCompId, subCompUID };
    }

    public static CompIDConcat(compUIDPrefix: string | undefined, compId: string) {
        if (!compUIDPrefix) { return compId; } // carefull  "", and undefined

        return compUIDPrefix + "." + compId;
    }
}
