import { IRequest } from "../interfaces/NetworkInterfaces/IRequest";
import { IDory } from "../interfaces/RenderingInterfaces/IDory";

export class HTTPHeader {

    static createDefaultHeaders(dory: IDory, request: IRequest) {
        request.headers = request.headers || {}
        request.headers["Content-Language"] = request.headers["Content-Language"] || dory.GetLRType();
    }
}