import { StyleHelper } from "../../../../../common/shrimp/helpers/StyleHelper";
import { IComponentCollection } from "../../../../../common/shrimp/interfaces/ComponentInterfaces/IComponentCollection";
import { IStyleManager, matchStyle, StyleCB } from "./IStyleManager";
import { styleConstants } from "./styleConstants";
import { StyleDomInteractor } from "./styleDomInteractor";


export class StyleManager implements IStyleManager {
  public static AdditionalName = "StyleManager" as const;
  public static current(compCollection: IComponentCollection) {
    const hi = compCollection.getCurrentHistoryItem();
    return hi.getOrCreateAdditional<StyleManager>(StyleManager.AdditionalName, () => new StyleManager(hi.pageId));
  }

  private index = 0;
  private cssIndexDict: Record<string, Array<{ selector: string, styleKeys: string, styleValue: string, index: number }>> = {};
  private domInteractor: StyleDomInteractor;
  constructor(
    readonly uid,
  ){
    this.domInteractor = new StyleDomInteractor(uid);
  }


  public attach() { this.domInteractor.attach(); }
  public detach() { this.domInteractor.detach(); }


  public destroy() {
    this.detach();
    Object.keys(this).forEach(key => delete this[key]);
  }
  public hibernate() { this.detach(); }
  public resurrect() { this.attach(); }

  private decideCompStyleWithDict ({ compCollection, currCompStyle, stylePropName, styleValue, breakPoint }: { compCollection: IComponentCollection, currCompStyle: StyleCB, stylePropName: string, styleValue: string, breakPoint?: string }) {
    let notProperties = ["unlink", "sides", "unlinkMax", "unlinkMin", "isSizeCollapsed", "selectedLayout", "layoutMap"];
    if (notProperties.includes(stylePropName)) { return; }

    if (currCompStyle && currCompStyle[stylePropName]) {
      currCompStyle[stylePropName]({ compCollection, styleValue, breakPoint, styleManager: this });
    } else if (breakPoint) {
      //currCompStyle[stylePropName]?.({ compCollection, styleValue, breakPoint });
      let selector = styleConstants.exceptionalComponents[compCollection.compType] ? `${styleConstants.exceptionalComponents[compCollection.compType]}.${compCollection.GetCompUID()}` : `[q_UID='${compCollection.GetCompUID()}']`;
      let selectorValue = `${this.fromCamelCase(stylePropName)}: ${styleValue} !important;`;
      let propNames = ["flexDirection", "justifyContent", "alignItems"];
      if (propNames.includes(stylePropName)) {
        selectorValue += `display: flex !important;`;
      }
      this.addStyleRule({ selector, selectorValue, styleValue: styleValue, breakPoint: breakPoint, compType: compCollection.compType });
    } else {
      compCollection.domElement?.style?.setProperty?.(this.fromCamelCase(stylePropName), styleValue, "important");
    }
  };

  public applyStyle = (compCollection: IComponentCollection) => {
    const currCompStyle = styleConstants.compStyleDict[compCollection.compType];
    if (!compCollection.compStyleValues) return;

    Object.keys(compCollection.compStyleValues).forEach((styleKey) => {
      if (!compCollection.compStyleValues || !compCollection.compStyleValues[styleKey] || styleKey == 'layoutMap') {
        return;
      }
      if (typeof compCollection.compStyleValues![styleKey] === "object") {
        Object.keys(<Record<string, string>>compCollection.compStyleValues[styleKey]).forEach((breakPointStyle) => {
          this.decideCompStyleWithDict({ compCollection, currCompStyle, stylePropName: breakPointStyle, styleValue: compCollection.compStyleValues![styleKey]![breakPointStyle], breakPoint: styleKey });
        });
      } else {
        this.decideCompStyleWithDict({ compCollection, currCompStyle, stylePropName: styleKey, styleValue: <string>compCollection.compStyleValues[styleKey] });
      }
    });
  };
  
  public addStyleRule = (matchStyleParam: matchStyle) => {
    const { selector, selectorValue, styleValue, breakPoint, compType } = matchStyleParam;
    const splittedBreakPoint = breakPoint?.split('-');
    const minWidth = StyleHelper.matchSizeValue(splittedBreakPoint?.[0]);
    const maxWidth = StyleHelper.matchSizeValue(splittedBreakPoint?.[1]);
    const cssDictSelector = breakPoint ? `${maxWidth}_${minWidth}_${selector}` : selector;
    const styleKeys = selectorValue.split(/[:;]+/).filter((_, i) => i % 2 === 0).join('-');


    if (this.cssIndexDict[compType]) {
      let findRule = this.cssIndexDict[compType].find(item => item.selector === cssDictSelector && item.styleKeys === styleKeys);

      if (findRule) {
        if (findRule.styleValue === selectorValue) { return; }
        this.deleteSheet(findRule.index, compType);
        this.addSheet(selector, selectorValue, compType, breakPoint);
      } else if (styleValue) {
        this.addSheet(selector, selectorValue, compType, breakPoint);
      }
    } else if (styleValue) {
      this.addSheet(selector, selectorValue, compType, breakPoint);
    }
  };

  private deleteSheet = (index: number, compType: string) => {
    this.domInteractor.clearRule(index);
    
    this.cssIndexDict[compType].forEach((item, ind) => {
      if (item.index === index) {
        this.cssIndexDict[compType].splice(ind, 1);
        return;
      }
    })
  }

  private addSheet = (selector: string, selectorValue: string, compType: string, breakPoint?: string) => {
    const splittedBreakPoint = breakPoint?.split('-');
    const minWidth = StyleHelper.matchSizeValue(splittedBreakPoint?.[0]);
    const maxWidth = StyleHelper.matchSizeValue(splittedBreakPoint?.[1]);
    const mediaRule = breakPoint ? `@media screen and (max-width: ${maxWidth}px) and (min-width: ${minWidth}px) {body .v-application ${selector}{${selectorValue}}}` : `body .v-application ${selector}{${selectorValue}}`;

    this.domInteractor.insertRule(mediaRule, this.index);
    const styleKeys = selectorValue.split(/[:;]+/).filter((_, i) => i % 2 === 0).join('-');
    const deleteSelectorRule = breakPoint ? `${maxWidth}_${minWidth}_${selector}` : selector;
    if (this.cssIndexDict[compType]) {
      this.cssIndexDict[compType].push({ selector: deleteSelectorRule, styleKeys, styleValue: selectorValue, index: this.index })
    } else {
      this.cssIndexDict[compType] = [{ selector: deleteSelectorRule, styleKeys, styleValue: selectorValue, index: this.index }];
    }
    this.index++;
  }

  private fromCamelCase = (message: string) => {
    if (message) {
      message = message[0].toLowerCase() + message.substring(1);
      for (var i = 0; i < message.length; i++) {
        if (message[i] == message[i].toUpperCase()) {
          message = message.substring(0, i) + "-" + message[i].toLocaleLowerCase() + message.substring(i + 1);
        }
      }
    }
    return message;
  };

  public deleteCompCollectionStyle = (compCollection: IComponentCollection) => {
    for (let i = 0; i < this.cssIndexDict[compCollection.compType].length; i++) {
      const cssValue = this.cssIndexDict[compCollection.compType][i];
      if (cssValue.selector.includes(`[q_UID='${compCollection.GetCompUID()}']`)) {
        this.deleteSheet(cssValue.index, compCollection.compType);
        i--;
      }
    }
  }
}
