
import { IContainerServicesWrapper } from "../../../../common/shrimp/interfaces/quick/IContainerServices";
import { IPlatformManager } from "../../../../common/qshell";
import { IBrandInfo, IClientInfo, INavigatorUAData, IWebInfo } from "../../../../common/shrimp/interfaces/quick/IPlatform";


export class PlatformManagerImp implements IPlatformManager {
    servicesWrapper: IContainerServicesWrapper;
    public contextName: string = "context";
    constructor(servicesWrapper: IContainerServicesWrapper) {
        this.servicesWrapper = servicesWrapper;
    }
    public getDeviceId() {
        return this.servicesWrapper.services.getDeviceIdCb?.();
    }

    public getClientInfo(): IClientInfo {

        let brands: IBrandInfo[] = [];
        if ((<any>navigator).userAgentData?.brands && (<any>navigator).userAgentData?.brands.length > 0) {
            (<any>navigator).userAgentData?.brands.forEach(element => {
                brands.push({ brand: element.brand, version: element.version });
            });
        }

        let userAgentData: INavigatorUAData = {
            brands: (brands.length > 0 ? brands : undefined),
            mobile: (<any>navigator).userAgentData?.mobile,
            platform: (<any>navigator).userAgentData?.platform
        }

        let webInfo: IWebInfo = {
            userAgent: navigator.userAgent,
            userAgentData: ((<any>navigator).userAgentData ? userAgentData : undefined),
            cookieEnabled: navigator.cookieEnabled,
            deviceMemory: (<any>navigator).deviceMemory ? ((<any>navigator).deviceMemory * 1024) : undefined,
            language: navigator.language,
            onLine: navigator.onLine,
            pdfViewerEnabled: (<any>navigator).pdfViewerEnabled,
            platform: (<any>navigator).platform,
            screenHeight: window?.screen?.height,
            screenwidth: window?.screen?.width
        }
        let clientInfo: IClientInfo = { webInfo };
        return clientInfo;
    }
}