import { ICryptographyManager } from "../../../../common/qshell";
import { CryptoHelper, IDecryptDataRequest, IDecryptDataResponse, IEncryptDataRequest, IEncryptDataResponse, IHashDataRequest, IHashDataResponse } from "../../../../common/shrimp/helpers/cryptoHelper";

export class CryptographyManager implements ICryptographyManager {
    contextName: string;
    constructor({ contextName }: { contextName: string }) {
        this.contextName = contextName
    }
    async hash(data: IHashDataRequest): Promise<IHashDataResponse> {
        return await CryptoHelper.hash(data);
    }

    async encrypt(data: IEncryptDataRequest): Promise<IEncryptDataResponse> {
        return await CryptoHelper.encrypt(data);
    }

    async decrypt(data: IDecryptDataRequest): Promise<IDecryptDataResponse> {
        return await CryptoHelper.decrypt(data);
    }

    public stringToBase64(data: string) {
        return CryptoHelper.stringToBase64(data);
    }

    public base64ToString(data: string) {
        return CryptoHelper.base64ToString(data);
    }
}